import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import {
  IContainerViewComponentDefinition,
  IViewComponentDefinition,
  IViewDefinition,
  RxViewComponentType,
  RxViewDefinitionParserService
} from '@helix/platform/view/api';
import { filter, flow, map, sortBy } from 'lodash';
import { RX_ASSOCIATION } from '../association.types';

@Injectable()
export class RxAssociationDefinitionAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition>
{
  constructor(private rxViewDefinitionParserService: RxViewDefinitionParserService) {}

  adaptDefinition(associationComponentDefinition: IContainerViewComponentDefinition, viewDefinition: IViewDefinition) {
    const targetRecordEditorComponentDefinition = this.getTargetRecordEditorComponentDefinition(
      viewDefinition,
      associationComponentDefinition
    );

    const childButtons = associationComponentDefinition.componentDefinitions.filter(
      (component) => component.type === RxViewComponentType.ActionButton
    );

    const childSortedButtons = sortBy(childButtons, (componentDefinition: IViewComponentDefinition) =>
      componentDefinition.propertiesByName.action === RX_ASSOCIATION.actions.associate ? 0 : 1
    );

    associationComponentDefinition.propertiesByName.state = targetRecordEditorComponentDefinition.propertiesByName.mode;

    associationComponentDefinition.propertiesByName.useDefaultRoles =
      associationComponentDefinition.propertiesByName.useDefaultRoles === 'true';

    associationComponentDefinition.propertiesByName.fields = flow(
      (componentDefinitions) =>
        filter(componentDefinitions, {
          type: RX_ASSOCIATION.componentTypes.associatedRecordField
        }),
      (componentDefinitions) =>
        map(componentDefinitions, (fieldComponentDefinition) => ({
          fieldId: fieldComponentDefinition.propertiesByName.fieldId,
          label: fieldComponentDefinition.propertiesByName.label,
          index: Number(fieldComponentDefinition.propertiesByName.index)
        })),
      (componentDefinitions) => sortBy(componentDefinitions, 'index')
    )(associationComponentDefinition.componentDefinitions);

    associationComponentDefinition.layout = JSON.stringify({
      outlets: [
        {
          name: 'DEFAULT',
          columns: [
            {
              children: childSortedButtons.map((component: IViewComponentDefinition) => component.guid)
            }
          ]
        }
      ]
    });

    associationComponentDefinition.componentDefinitions
      .filter(
        (componentDefinition: IViewComponentDefinition) => componentDefinition.type === RxViewComponentType.ActionButton
      )
      .forEach((componentDefinition: IViewComponentDefinition) => {
        componentDefinition.propertiesByName.disabled =
          associationComponentDefinition.propertiesByName.disabled +
          ' OR ${view.components.' +
          associationComponentDefinition.guid +
          '.isDisabled}';
      });
  }

  getTargetRecordEditorComponentDefinition(
    viewDefinition: IViewDefinition,
    extensionContainerComponentDefinition: IViewComponentDefinition
  ): IContainerViewComponentDefinition {
    return this.rxViewDefinitionParserService.findParentComponentDefinition(
      viewDefinition,
      extensionContainerComponentDefinition,
      (parentComponentDefinition) => parentComponentDefinition.type === RxViewComponentType.RecordEditor
    );
  }
}
