import { Injector } from '@angular/core';
import {
  IFieldDefinition,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxFieldDefinitionService,
  RxRecordDefinitionCacheService
} from '@helix/platform/record/api';
import { RxDefinitionNameService } from '@helix/platform/shared/api';
import {
  ExpressionInspectorControlComponent,
  IExpressionInspectorControlOptions,
  ISelectFormControlOptions,
  ITextFormControlOptions,
  IValidationFormControlOptions,
  SelectFormControlComponent,
  TextFormControlComponent,
  ValidationFormControlComponent
} from '@helix/platform/shared/components';
import {
  getDisabledFieldInspectorConfig,
  getStandardPropsInspectorConfigs,
  IViewComponentDesignSandbox,
  IViewComponentDesignSettablePropertiesDataDictionary,
  IViewComponentDesignValidationIssue,
  IViewDesignerInspectorConfig,
  validateStandardProps,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { find, includes, isEmpty, isEqual } from 'lodash';
import { combineLatest, EMPTY, Observable, of } from 'rxjs';
import {
  catchError,
  distinctUntilChanged,
  filter,
  map,
  mergeMap,
  pairwise,
  shareReplay,
  startWith,
  switchMap,
  take,
  withLatestFrom
} from 'rxjs/operators';
import { IRecordEditorProperties } from '../../../record-editor/design';
import { RecordEditorMode } from '../../../record-editor/common/record-editor.types';
import { IBaseRecordEditorFieldProperties } from './base-record-editor-field-design.types';
import {
  RX_DISABLED_PROP_DEFAULT_VALUE,
  RX_STANDARD_PROPS_DEFAULT_VALUES,
  RxViewComponentType
} from '@helix/platform/view/api';

export abstract class BaseRecordEditorFieldDesign extends ViewDesignerComponentModel {
  protected rxRecordDefinitionCacheService =
    this.injector.get<RxRecordDefinitionCacheService>(RxRecordDefinitionCacheService);

  protected rxDefinitionNameService = this.injector.get<RxDefinitionNameService>(RxDefinitionNameService);
  protected rxFieldDefinitionService = this.injector.get<RxFieldDefinitionService>(RxFieldDefinitionService);

  label$: Observable<string>;
  recordEditorMode$: Observable<RecordEditorMode>;
  parentGuid$: Observable<string>;
  recordDefinitionName$: Observable<string>;
  availableFieldDefinitions$: Observable<IFieldDefinition[]>;
  recordDefinition$: Observable<IRecordDefinition>;
  selectedFieldDefinition$: Observable<IFieldDefinition>;
  isRequired$: Observable<boolean>;
  private isSystemField$: Observable<boolean>;

  abstract fieldResourceTypes: string[];

  static getInitialProperties(initialProperties?: IBaseRecordEditorFieldProperties): IBaseRecordEditorFieldProperties {
    return {
      fieldId: null,
      label: null,
      value: null,
      ...RX_DISABLED_PROP_DEFAULT_VALUE,
      ...RX_STANDARD_PROPS_DEFAULT_VALUES,
      ...initialProperties
    };
  }

  constructor(
    protected injector: Injector,
    protected sandbox: IViewComponentDesignSandbox<IBaseRecordEditorFieldProperties>
  ) {
    super(injector, sandbox);
  }

  rxInit(): void {
    this.pipeline();
  }

  private pipeline(): void {
    this.parentGuid$ = this.sandbox.getParentComponentGuid(RxViewComponentType.RecordEditor).pipe(take(1));

    this.recordEditorMode$ = this.parentGuid$.pipe(
      switchMap((recordEditorGuid) => {
        return this.sandbox.getComponentPropertyValue<'mode', IRecordEditorProperties>('mode', recordEditorGuid);
      })
    );

    this.recordDefinitionName$ = this.parentGuid$.pipe(
      switchMap((recordEditorGuid) => {
        return this.sandbox.getComponentPropertyValue<'recordDefinitionName', IRecordEditorProperties>(
          'recordDefinitionName',
          recordEditorGuid
        );
      }),
      shareReplay(1)
    );

    this.recordDefinition$ = this.recordDefinitionName$.pipe(
      switchMap((recordDefinitionName: string) => {
        if (recordDefinitionName) {
          return this.rxRecordDefinitionCacheService
            .getRecordDefinition(recordDefinitionName)
            .pipe(catchError((error) => EMPTY));
        } else {
          return of(null);
        }
      }),
      distinctUntilChanged()
    );

    this.selectedFieldDefinition$ = this.sandbox.getComponentPropertyValue('fieldId').pipe(
      switchMap((fieldId) =>
        this.recordDefinition$.pipe(
          map((recordDefinition) => {
            if (recordDefinition && fieldId) {
              return (
                recordDefinition.fieldDefinitions.find(
                  (fieldDefinition: IFieldDefinition) => fieldDefinition.id === Number(fieldId)
                ) || null
              );
            } else {
              return null;
            }
          })
        )
      ),
      shareReplay(1)
    );

    this.isRequired$ = this.selectedFieldDefinition$.pipe(
      map(
        (selectedFieldDefinition: IFieldDefinition) =>
          selectedFieldDefinition && selectedFieldDefinition.fieldOption === RX_RECORD_DEFINITION.fieldOptions.required
      )
    );

    this.isSystemField$ = this.selectedFieldDefinition$.pipe(
      startWith(false),
      map((selectedFieldDefinition: IFieldDefinition) =>
        this.rxFieldDefinitionService.isSystemField(selectedFieldDefinition)
      )
    );

    // set field label
    this.selectedFieldDefinition$
      .pipe(
        pairwise(),
        withLatestFrom(this.sandbox.componentProperties$),
        filter(
          ([[oldField, newField], componentProperties]: [
            [IFieldDefinition, IFieldDefinition],
            IBaseRecordEditorFieldProperties
          ]) => newField && ((oldField && oldField.name === componentProperties.label) || !componentProperties.label)
        ),
        map(
          ([[oldField, newField]]: [[IFieldDefinition, IFieldDefinition], IBaseRecordEditorFieldProperties]) =>
            newField.name
        )
      )
      .subscribe((label) => {
        this.sandbox.updateComponentProperties({ label });
      });

    this.availableFieldDefinitions$ = combineLatest([this.recordDefinition$, this.recordEditorMode$]).pipe(
      map(([recordDefinition, recordEditorMode]) => {
        if (recordDefinition && recordDefinition.fieldDefinitions) {
          return recordDefinition.fieldDefinitions
            .filter((fieldDefinition) => includes(this.fieldResourceTypes, fieldDefinition.resourceType))
            .filter((fieldDefinition) => {
              const isSystemField = this.rxFieldDefinitionService.isSystemField(fieldDefinition);

              return !isSystemField || (isSystemField && recordEditorMode === RecordEditorMode.Edit);
            });
        } else {
          return [];
        }
      }),
      shareReplay(1)
    );

    this.label$ = this.sandbox.componentProperties$.pipe(
      map((componentProperties: IBaseRecordEditorFieldProperties) => {
        return componentProperties && componentProperties.fieldId ? componentProperties.label : '<No field selected>';
      })
    );

    this.sandbox.componentProperties$
      .pipe(
        mergeMap((componentProperties) => this.validate(componentProperties)),
        distinctUntilChanged(isEqual)
      )
      .subscribe((validationIssues: IViewComponentDesignValidationIssue[]) => {
        this.sandbox.setValidationIssues(validationIssues);
      });

    this.availableFieldDefinitions$
      .pipe(switchMap(() => this.getInspectorConfig()))
      .subscribe((inspectorConfig: IViewDesignerInspectorConfig) => {
        this.sandbox.updateInspectorConfig(inspectorConfig);
      });

    this.sandbox.componentProperties$
      .pipe(
        switchMap(() => this.getBreadcrumb()),
        distinctUntilChanged()
      )
      .subscribe((breadcrumb: string) => {
        this.sandbox.setBreadcrumbs(breadcrumb);
      });

    combineLatest([this.sandbox.getComponentPropertyValue('label'), this.selectedFieldDefinition$])
      .pipe(
        map(
          ([label, fieldDefinition]) =>
            label || (fieldDefinition ? `[${fieldDefinition.name}]` : `[${this.sandbox.descriptor.name}]`)
        ),
        distinctUntilChanged()
      )
      .subscribe((componentName) => {
        this.sandbox.setSettablePropertiesDataDictionary(componentName, this.getSettableProperties());
      });
  }

  getPropertiesByName(properties: IBaseRecordEditorFieldProperties): IBaseRecordEditorFieldProperties {
    const result: IBaseRecordEditorFieldProperties = {
      ...properties
    };

    if (!result.styles) {
      delete result.styles;
    }

    return result;
  }

  validate(componentProperties: IBaseRecordEditorFieldProperties): Observable<IViewComponentDesignValidationIssue[]> {
    const validationIssues = [];

    if (isEmpty(componentProperties.fieldId)) {
      validationIssues.push(this.sandbox.createError('Field name cannot be blank.', 'fieldId'));
    }

    validationIssues.push(...validateStandardProps(componentProperties));

    return of(validationIssues);
  }

  getBreadcrumb(): Observable<string> {
    return of(null).pipe(
      withLatestFrom(this.sandbox.componentProperties$),
      map(([initial, componentProperties]) => componentProperties.label)
    );
  }

  getInspectorConfig(): Observable<IViewDesignerInspectorConfig> {
    return of(null).pipe(
      withLatestFrom(
        this.availableFieldDefinitions$,
        this.recordDefinitionName$,
        this.parentGuid$,
        this.isSystemField$
      ),
      map(([initial, recordFieldsSelectItems, recordDefinitionName, parentGuid, isSystemField]) => {
        return this.getBaseInspectorConfig(recordFieldsSelectItems, recordDefinitionName, parentGuid, isSystemField);
      })
    );
  }

  getSettableProperties(): IViewComponentDesignSettablePropertiesDataDictionary {
    const disabledPropDataDictionary: IViewComponentDesignSettablePropertiesDataDictionary = find(
      this.sandbox.descriptor.properties,
      { name: 'disabled' }
    )
      ? [
          {
            label: 'Disabled',
            expression: `\${view.components.${this.sandbox.guid}.disabled}`
          }
        ]
      : [];

    const hiddenPropDataDictionary: IViewComponentDesignSettablePropertiesDataDictionary = find(
      this.sandbox.descriptor.properties,
      { name: 'disabled' }
    )
      ? [
          {
            label: 'Hidden',
            expression: `\${view.components.${this.sandbox.guid}.hidden}`
          }
        ]
      : [];

    return [...disabledPropDataDictionary, ...hiddenPropDataDictionary];
  }

  private getBaseInspectorFields(selectedFieldDefinitions: IFieldDefinition[], isSystemField: boolean) {
    return [].concat(
      [
        {
          name: 'fieldId',
          component: SelectFormControlComponent,
          options: {
            label: 'Field name',
            required: true,
            options: selectedFieldDefinitions.map((definition) => ({
              name: definition.name,
              id: String(definition.id)
            }))
          } as ISelectFormControlOptions
        },
        {
          name: 'label',
          component: TextFormControlComponent,
          options: {
            label: 'Display label'
          } as ITextFormControlOptions
        }
      ],
      isSystemField
        ? []
        : [
            {
              name: 'value',
              component: ExpressionInspectorControlComponent,
              options: {
                label: 'Value'
              } as IExpressionInspectorControlOptions
            },
            getDisabledFieldInspectorConfig()
          ],
      getStandardPropsInspectorConfigs()
    );
  }

  private getBaseInspectorConfig(
    recordFieldsSelectItems: IFieldDefinition[],
    recordDefinitionName: string,
    parentGuid: string,
    isSystemField: boolean
  ): IViewDesignerInspectorConfig {
    const displayName = this.rxDefinitionNameService.getDisplayName(recordDefinitionName || '');

    let controls;

    if (recordDefinitionName) {
      if (recordFieldsSelectItems.length) {
        controls = this.getBaseInspectorFields(recordFieldsSelectItems, isSystemField);
      } else {
        controls = [
          {
            component: ValidationFormControlComponent,
            options: {
              text: `${displayName} does not have fields that can be bound to this field type.`,
              componentGuid: parentGuid,
              propertyName: 'recordDefinitionName'
            } as IValidationFormControlOptions
          }
        ];
      }
    } else {
      controls = [
        {
          component: ValidationFormControlComponent,
          options: {
            text: 'You must select a Record definition before editing field component.',
            componentGuid: parentGuid,
            propertyName: 'recordDefinitionName' // todo remove that hardcode
          } as IValidationFormControlOptions
        }
      ];
    }

    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls
        }
      ]
    };
  }
}
