import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Inject,
  Output,
  Renderer2,
  ViewChild
} from '@angular/core';
import {
  CanvasItemApi,
  CanvasItemContainerComponent,
  CanvasOutletHelperService,
  ViewDesignerCanvasItemApiToken
} from '@helix/platform/view/designer';
import { IOutputData } from 'angular-split/lib/interface';
import { SplitComponent } from 'angular-split';

@Component({
  selector: 'rx-container-canvas-item',
  templateUrl: './container-canvas-item.component.html',
  styleUrls: ['./container-canvas-item.component.scss']
})
export class ContainerCanvasItemComponent extends CanvasItemContainerComponent implements AfterViewInit {
  @ViewChild(SplitComponent, { static: true })
  private splitComponent: SplitComponent;

  private readonly maxNumberOfColumns = 12;
  private readonly defaultGutterStep = 50;

  percentSpanSize = 100 / this.maxNumberOfColumns;
  gutterStep = this.defaultGutterStep;

  @Output()
  columnResize: EventEmitter<number[]> = new EventEmitter<number[]>();

  constructor(
    @Inject(ViewDesignerCanvasItemApiToken) protected canvasItemApi: CanvasItemApi,
    protected canvasOutletHelperService: CanvasOutletHelperService,
    protected elementRef: ElementRef<HTMLElement>,
    private renderer: Renderer2
  ) {
    super(canvasItemApi, canvasOutletHelperService);
  }

  @HostListener('window:resize')
  onWindowResize(): void {
    this.setGutterStep();
  }

  ngAfterViewInit(): void {
    const spans = this.outlet.columns.map((column) => column.span);
    this.setSpanSizes(spans);
    this.setGutterStep();
  }

  onDragEnd(event: IOutputData): void {
    const spans = this.getPercentToSpanSizes(event.sizes as number[]);

    this.columnResize.emit(spans);

    this.setSpanSizes(spans);
  }

  getPercentToSpanSizes(percentSizes: number[]): number[] {
    return percentSizes.map((size) => Math.round((size * this.maxNumberOfColumns) / 100));
  }

  setSpanSizes(spans: number[]): void {
    this.splitComponent.setVisibleAreaSizes(this.getSpanToPercentSizes(spans));
  }

  private getSpanToPercentSizes(spans: number[]): number[] {
    return spans.map((span) => span * this.percentSpanSize);
  }

  private setGutterStep(): void {
    const element = this.renderer.selectRootElement(this.elementRef.nativeElement, true);

    this.gutterStep = Math.round(element.clientWidth / this.maxNumberOfColumns) || this.defaultGutterStep;
  }
}
