import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, combineLatest, EMPTY, iif, Observable, of, Subject, throwError } from 'rxjs';
import { catchError, finalize, map, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import { noop, reject, sortBy } from 'lodash';
import { RxGuidService } from '@helix/platform/utils';
import { RxModalService, RxUtilityModalsService } from '@helix/platform/ui-kit';
import { BaseViewComponent, RuntimeViewModelApi } from '@helix/platform/view/runtime';
import {
  IViewPresetSelectorConfig,
  IViewPresetSelectorDropdownOption,
  IViewPresetSelectorState
} from './view-preset-selector.types';
import { RxViewPresetSelectorUserPreferencesService } from './view-preset-selector-user-preferences.service';
import { IViewPresetSelectorApi } from './view-preset-selector-api.interface';
import { RenameViewPresetComponent } from './rename-view-preset/rename-view-preset.component';
import { ShareViewPresetComponent } from './share-view-preset/share-view-preset.component';
import { RxFeatureService, RxNotificationService } from '@helix/platform/shared/api';
import { ISharedViewPresetItem, IShareViewPresetModalData } from './share-view-preset/share-view-preset.types';
import { RX_SHARABLE_VIEW_RESETS_FEATURE_NAME } from '../view-preset-selector.types';
import { AddSharedViewPresetsComponent } from './add-shared-view-presets/add-shared-view-presets.component';
import {
  IAddSharedViewPresetsModalData,
  ISharedViewPresetDescriptor
} from './add-shared-view-presets/add-shared-view-presets.types';
import { RxShareViewPresetService } from './share-view-preset/share-view-preset.service';
import { IViewPresetsByViewComponentGuid } from '@helix/platform/view/api';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'rx-view-preset-selector',
  templateUrl: './view-preset-selector.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [RxViewPresetSelectorUserPreferencesService],
  styleUrls: ['./view-preset-selector.component.scss']
})
export class ViewPresetSelectorComponent extends BaseViewComponent implements OnInit, OnDestroy {
  @Input()
  config: Observable<IViewPresetSelectorConfig>;

  @Input()
  runtimeViewModelApi: RuntimeViewModelApi;

  api: IViewPresetSelectorApi = {
    getGuid: () => this.guid,
    markAppliedViewPresetAsEdited: this.markAppliedViewPresetAsEdited.bind(this)
  };

  private isOperationInProgressSubject = new BehaviorSubject<boolean>(false);
  private isViewPresetEditedSubject = new BehaviorSubject<boolean>(false);

  viewPresetSelectorModel$: Observable<{
    isOperationInProgress: boolean;
    isViewPresetEdited: boolean;
  }> = combineLatest([this.isOperationInProgressSubject, this.isViewPresetEditedSubject]).pipe(
    map(([isOperationInProgress, isViewPresetEdited]) => ({
      isOperationInProgress,
      isViewPresetEdited
    }))
  );

  state: IViewPresetSelectorState;

  strings = {
    deleteLabel: this.translateService.instant('com.bmc.arsys.rx.client.common.delete.label'),
    discardChangesButtonLabel: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.discard-changes.button.label'
    ),
    editedViewPresetTagLabel: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.edited-view-preset-tag.label'
    ),
    renameLabel: this.translateService.instant('com.bmc.arsys.rx.client.common.rename.label'),
    saveLabel: this.translateService.instant('com.bmc.arsys.rx.client.common.save.label'),
    saveAsLabel: this.translateService.instant('com.bmc.arsys.rx.client.common.save-as.label'),
    shareLabel: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.share.button.label'
    ),
    systemPresetsTitle: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.preset-type.system.label'
    ),
    sharedPresetsTitle: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.preset-type.shared-with-me.label'
    ),
    customPresetsTitle: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.preset-type.created-by-me.label'
    ),
    sharedTooltip: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.shared-preset.tooltip'
    ),
    addSharedViewPresets: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.title'
    )
  };

  private userPreferencesChangedSubject = new Subject<void>();
  isSharingFeatureEnabled = this.rxFeatureService.isFeatureEnabled(RX_SHARABLE_VIEW_RESETS_FEATURE_NAME);

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private rxGuidService: RxGuidService,
    private rxModalService: RxModalService,
    private rxUtilityModalsService: RxUtilityModalsService,
    private rxViewPresetSelectorUserPreferencesService: RxViewPresetSelectorUserPreferencesService,
    private translateService: TranslateService,
    private rxFeatureService: RxFeatureService,
    private rxNotificationService: RxNotificationService,
    private rxShareViewPresetService: RxShareViewPresetService
  ) {
    super();

    this.userPreferencesChangedSubject
      .pipe(
        switchMap(() => this.rxViewPresetSelectorUserPreferencesService.save(this.state)),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  ngOnInit() {
    super.ngOnInit();

    this.notifyPropertyChanged('api', this.api);

    this.config
      .pipe(
        take(1),
        map((config: IViewPresetSelectorConfig) => ({
          systemViewPresetOptions: config.viewPresets,
          customViewPresetOptions: [],
          sharedViewPresetOptions: [],
          appliedViewPresetOption: null,
          isSharingEnabled: config.enableSharing && this.isSharingFeatureEnabled
        })),
        switchMap((state: IViewPresetSelectorState) =>
          this.rxViewPresetSelectorUserPreferencesService.applyUserPreferences(this.guid, state)
        ),
        tap(({ state, shouldUpdatePreferences, removedPresetGuid }) => {
          this.state = state;
          this.changeDetectorRef.detectChanges();
          this.isOperationInProgressSubject.next(true);

          if (removedPresetGuid) {
            this.runtimeViewModelApi.deleteViewPreset(removedPresetGuid).subscribe();
          }

          if (shouldUpdatePreferences) {
            this.userPreferencesChangedSubject.next();
          }
        }),
        switchMap(() => this.applyViewPreset(this.state.appliedViewPresetOption)),
        finalize(() => this.isOperationInProgressSubject.next(false))
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
    this.isOperationInProgressSubject.complete();
    this.isViewPresetEditedSubject.complete();
    this.userPreferencesChangedSubject.complete();
  }

  deleteViewPreset(viewPresetOptionToRemove: IViewPresetSelectorDropdownOption): void {
    this.rxUtilityModalsService
      .confirm(
        this.translateService.instant(
          viewPresetOptionToRemove.userSharedViewPresetGuid
            ? 'com.bmc.arsys.rx.client.view-components.view-preset-selector.delete-shared-view-preset-confirmation.message'
            : 'com.bmc.arsys.rx.client.view-components.view-preset-selector.delete-view-preset-confirmation.message'
        )
      )
      .then((isConfirmed: boolean) => {
        if (isConfirmed) {
          const viewPresetGuidToDelete = this.state.appliedViewPresetOption.guid;

          this.state.appliedViewPresetOption = this.state.systemViewPresetOptions[0];
          this.isViewPresetEditedSubject.next(false);

          this.state.customViewPresetOptions = reject(this.state.customViewPresetOptions, {
            guid: viewPresetGuidToDelete
          });

          this.state.sharedViewPresetOptions = reject(this.state.sharedViewPresetOptions, {
            guid: viewPresetGuidToDelete
          });

          this.userPreferencesChangedSubject.next();
          this.isOperationInProgressSubject.next(true);

          this.runtimeViewModelApi
            .applyViewPreset(this.guid, this.state.appliedViewPresetOption.guid)
            .pipe(
              finalize(() => {
                const deleteSharedViewPreset$ = viewPresetOptionToRemove.userSharedViewPresetGuid
                  ? this.rxShareViewPresetService.deleteSharedViewPreset(
                      viewPresetOptionToRemove.userSharedViewPresetGuid
                    )
                  : of(null);

                deleteSharedViewPreset$
                  .pipe(
                    switchMap(() => this.runtimeViewModelApi.deleteViewPreset(viewPresetGuidToDelete)),
                    finalize(() => this.isOperationInProgressSubject.next(false))
                  )
                  .subscribe();
              })
            )
            .subscribe();
        }
      })
      .catch(noop);
  }

  discardViewPresetChanges(): void {
    this.rxUtilityModalsService
      .confirm(
        this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.discard-changes-confirmation.message'
        )
      )
      .then((isConfirmed: boolean) => {
        if (isConfirmed) {
          this.isViewPresetEditedSubject.next(false);
          this.isOperationInProgressSubject.next(true);

          const appliedViewPresetOption = this.state.appliedViewPresetOption;

          iif(
            () => appliedViewPresetOption.isShared,
            this.getSharedViewPresetData(appliedViewPresetOption).pipe(
              switchMap((sharedViewPresets) =>
                this.runtimeViewModelApi.discardViewPresetChanges(appliedViewPresetOption.guid, sharedViewPresets)
              )
            ),
            this.runtimeViewModelApi.discardViewPresetChanges(appliedViewPresetOption.guid)
          )
            .pipe(finalize(() => this.isOperationInProgressSubject.next(false)))
            .subscribe();
        }
      })
      .catch(noop);
  }

  renameViewPreset(): void {
    this.openRenamingModal(this.strings.renameLabel, true)
      .then((viewPresetName: string) => {
        this.state.appliedViewPresetOption.label = viewPresetName;
        this.state.customViewPresetOptions = sortBy(this.state.customViewPresetOptions, 'label');
        this.userPreferencesChangedSubject.next();
        this.changeDetectorRef.detectChanges();

        // if preset is shared with others - updating with new name
        if (this.state.appliedViewPresetOption.userSharedViewPresetGuid) {
          this.saveSharedViewPreset().subscribe();
        }
      })
      .catch(noop);
  }

  selectPreset(viewPresetOption: IViewPresetSelectorDropdownOption): void {
    this.state.appliedViewPresetOption = viewPresetOption;
    this.isViewPresetEditedSubject.next(false);
    this.userPreferencesChangedSubject.next();
    this.isOperationInProgressSubject.next(true);

    this.applyViewPreset(viewPresetOption)
      .pipe(finalize(() => this.isOperationInProgressSubject.next(false)))
      .subscribe();
  }

  saveAsViewPreset(): void {
    this.openRenamingModal(this.strings.saveAsLabel)
      .then((viewPresetName: string) => {
        const viewPreset = {
          guid: this.rxGuidService.generate(),
          label: viewPresetName
        };

        this.state.customViewPresetOptions.push(viewPreset);
        this.state.customViewPresetOptions = sortBy(this.state.customViewPresetOptions, 'label');
        this.state.appliedViewPresetOption = viewPreset;

        this.isViewPresetEditedSubject.next(false);
        this.userPreferencesChangedSubject.next();
        this.isOperationInProgressSubject.next(true);

        this.runtimeViewModelApi
          .applyViewPreset(this.guid, this.state.appliedViewPresetOption.guid)
          .pipe(finalize(() => this.isOperationInProgressSubject.next(false)))
          .subscribe();
      })
      .catch(noop);
  }

  saveViewPreset(): void {
    this.isViewPresetEditedSubject.next(false);
    this.isOperationInProgressSubject.next(true);

    const saveSharedViewPreset$ = this.state.appliedViewPresetOption.userSharedViewPresetGuid
      ? this.saveSharedViewPreset()
      : of(null);

    saveSharedViewPreset$
      .pipe(
        switchMap(() => this.runtimeViewModelApi.saveViewPreset(this.state.appliedViewPresetOption.guid)),
        finalize(() => this.isOperationInProgressSubject.next(false))
      )
      .subscribe();
  }

  shareViewPreset(): void {
    this.rxModalService
      .openModal({
        content: ShareViewPresetComponent,
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.title'
        ),
        data: {
          runtimeViewModelApi: this.runtimeViewModelApi,
          currentViewPreset: this.state.appliedViewPresetOption,
          presetSelectorGuid: this.guid
        } as IShareViewPresetModalData,
        size: 'sm'
      })
      .then((sharedViewPresetGuid) => {
        this.state.appliedViewPresetOption.userSharedViewPresetGuid = sharedViewPresetGuid;
        this.userPreferencesChangedSubject.next();
      })
      .catch(noop);
  }

  onAddSharedPresetsClick(): void {
    this.rxShareViewPresetService.getSharedViewPresetsForCurrentUser(this.guid).subscribe((presets) => {
      const existingSharedPresetsGuids = this.state.sharedViewPresetOptions.map((option) => option.guid);
      const newPresets = presets.filter((preset) => !existingSharedPresetsGuids.includes(preset.guid));

      if (newPresets.length) {
        this.openAddSharedPresetsModal(newPresets);
      } else {
        this.rxNotificationService.addInfoMessage(
          this.translateService.instant(
            'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.notification.no-new-presets.message'
          ),
          this.translateService.instant(
            'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.notification.no-new-presets.title'
          )
        );
      }
    });
  }

  private openAddSharedPresetsModal(newPresets: ISharedViewPresetItem[]) {
    this.rxModalService
      .openModal({
        content: AddSharedViewPresetsComponent,
        title: this.strings.addSharedViewPresets,
        data: {
          presetSelectorGuid: this.guid,
          newPresets
        } as IAddSharedViewPresetsModalData,
        size: 'sm'
      })
      .then((addedPresets: ISharedViewPresetDescriptor[]) => {
        this.state.sharedViewPresetOptions = this.state.sharedViewPresetOptions.concat(
          addedPresets.map((preset) => ({ ...preset, isShared: true }))
        );
        this.userPreferencesChangedSubject.next();

        if (addedPresets.length) {
          this.rxNotificationService.addSuccessMessage(
            this.translateService.instant(
              'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.notification.presets-added.message'
            ),
            this.translateService.instant(
              'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.notification.presets-added.title'
            )
          );
        }
      })
      .catch(noop);
  }

  private markAppliedViewPresetAsEdited(): void {
    this.isViewPresetEditedSubject.next(true);
  }

  private openRenamingModal(fieldLabel: string, isEdit?: boolean): Promise<string> {
    let existingViewPresets = [...this.state.customViewPresetOptions, ...this.state.systemViewPresetOptions];

    if (isEdit) {
      existingViewPresets = existingViewPresets.filter(
        (viewPreset) => viewPreset.guid !== this.state.appliedViewPresetOption.guid
      );
    }

    return this.rxModalService.openModal({
      content: RenameViewPresetComponent,
      data: {
        fieldLabel,
        existingViewPresetNames: existingViewPresets.map(({ label }) => label),
        viewPresetName: this.state.appliedViewPresetOption.label
      },
      size: 'sm'
    });
  }

  private applyViewPreset(viewPresetOption: IViewPresetSelectorDropdownOption): Observable<never> {
    if (viewPresetOption.isShared) {
      return this.getSharedViewPresetData(viewPresetOption).pipe(
        switchMap((sharedViewPresets) =>
          this.runtimeViewModelApi.applyViewPreset(this.guid, viewPresetOption.guid, sharedViewPresets)
        ),
        catchError((error) => this.handleSharedViewPresetLoadError(error, viewPresetOption))
      );
    } else {
      return this.runtimeViewModelApi.applyViewPreset(this.guid, viewPresetOption.guid);
    }
  }

  private saveSharedViewPreset(): Observable<string> {
    return this.rxShareViewPresetService.saveSharedViewPreset(
      {
        runtimeViewModelApi: this.runtimeViewModelApi,
        currentViewPreset: this.state.appliedViewPresetOption,
        presetSelectorGuid: this.guid
      },
      this.state.appliedViewPresetOption.userSharedViewPresetGuid
    );
  }

  private getSharedViewPresetData(
    viewPresetOption: IViewPresetSelectorDropdownOption
  ): Observable<IViewPresetsByViewComponentGuid> {
    return this.rxShareViewPresetService.getSharedViewPresetData(viewPresetOption.guid).pipe(
      map(({ viewPresetName, sharedViewPresets }) => {
        viewPresetOption.label = viewPresetName;

        return sharedViewPresets;
      })
    );
  }

  private handleSharedViewPresetLoadError(
    error: HttpErrorResponse,
    viewPresetOption: IViewPresetSelectorDropdownOption
  ): Observable<never> {
    if (error.status === 404) {
      this.state.sharedViewPresetOptions = this.state.sharedViewPresetOptions.filter(
        (option) => option.guid !== viewPresetOption.guid
      );

      this.runtimeViewModelApi.deleteViewPreset(viewPresetOption.guid).subscribe();

      // if preset was removed - selecting first system preset instead
      this.selectPreset(this.state.systemViewPresetOptions[0]);

      return EMPTY;
    } else {
      return throwError(error);
    }
  }
}
