type ColumnsDataDictionaryBuilder = (gridPropertyName: string) => IViewComponentDesignCommonDataDictionary;
type ColumnDataDictionaryBranchBuilder = (gridPropertyName: string) => IViewComponentDesignCommonDataDictionaryBranch;

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridDesignUtilsService {
  constructor(
    private rxGuidService: RxGuidService,
    private rxRecordGridConfigUtilsService: RxRecordGridConfigUtilsService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxFeatureService: RxFeatureService,
    private rxModalService: RxModalService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxRecordGridFilterHelperService: RxRecordGridFilterHelperService,
    private translateService: TranslateService,
    private viewDesignerFacade: ViewDesignerFacade,
    private rxJsonParserService: RxJsonParserService
  ) {}

  private getColumnActionComponentPayloads(
    actions: IViewActionListItem[]
  ): IViewComponentDesignSetPayload<IViewActionDesignProperties>[] {
    return _map(
      actions,
      (action): IViewComponentDesignSetPayload<IViewActionDesignProperties> => ({
        ...action,
        type: RxViewComponentType.Action
      })
    );
  }

  private getNamedFilterOptionPayloads(
    namedFilterOptions: INamedFilterOptionDesignData[]
  ): IViewComponentDesignSetPayload<IRecordGridColumnNamedFilterOptionDesign>[] {
    return _map(namedFilterOptions, (namedFilterOption) => ({
      ...namedFilterOption,
      type: RX_RECORD_GRID.components.filterPreset
    }));
  }

  getGridFilterComponentPayloads(
    gridFilters: IRxRecordGridFilter[]
  ): IViewComponentDesignSetPayload<IRecordGridFilterDescriptorModel>[] {
    return _map(
      gridFilters,
      (gridFilter): IViewComponentDesignSetPayload<IRecordGridFilterDescriptorModel> => ({
        guid: gridFilter.guid,
        type: RX_RECORD_GRID.components.filter,
        data: {
          fieldId: gridFilter.fieldId,
          value: gridFilter.value,
          ...(gridFilter.$DISPLAYVALUE$ && { $DISPLAYVALUE$: gridFilter.$DISPLAYVALUE$ })
        }
      })
    );
  }

  getColumnComponentPayloads(
    columns: IRecordGridDesignInspectorColumnConfig[],
    initialSortColumn: IRecordGridSortEditorModel
  ): IViewComponentDesignSetPayload<IRecordGridColumnDesignModel>[] {
    return _map(columns, (inspectorColumn, index): IViewComponentDesignSetPayload<IRecordGridColumnDesignModel> => {
      let sortable: IRecordGridSortable = 'false';

      if (initialSortColumn?.fieldId === inspectorColumn.fieldId && inspectorColumn.sortable !== false) {
        sortable = JSON.stringify({
          direction: initialSortColumn.direction
        } as IRecordGridSortableObject) as IRecordGridSortable;
      } else if (isObject(inspectorColumn.sortable) || inspectorColumn.sortable === true) {
        sortable = 'true';
      }

      const columnDesignModel: IRecordGridColumnDesignModel = {
        alignment: inspectorColumn.alignment,
        fieldId: inspectorColumn.fieldId,
        filterable: inspectorColumn.filterable,
        filterType: inspectorColumn.filterType,
        index: String(inspectorColumn.index),
        sortable,
        title: inspectorColumn.title,
        searchable: inspectorColumn.searchable,
        visible: inspectorColumn.visible,
        cellDisplayProperties: inspectorColumn.cellDisplayProperties,
        width: inspectorColumn.width,
        wrapText: inspectorColumn.wrapText,
        typeaheadKeystrokeCount: inspectorColumn.typeaheadKeystrokeCount,
        additionalQueryCriteria: inspectorColumn.additionalQueryCriteria
      };

      return {
        type: RX_RECORD_GRID.components.column,
        guid: inspectorColumn.guid,
        insertIndex: index,
        outletName: RX_VIEW_DEFINITION.defaultOutletName,
        data: columnDesignModel,
        children: [
          ...this.getColumnActionComponentPayloads(inspectorColumn.actions),
          ...this.getNamedFilterOptionPayloads(inspectorColumn.namedFilterOptions)
        ]
      };
    });
  }

  getGridFilterPresetComponentPayloads(
    filterPresets: IRecordGridPredefinedFilterPreset[],
    columns: IRecordGridDesignInspectorColumnConfig[]
  ): IViewComponentDesignSetPayload<IRecordGridFilterPresetDescriptorModel>[] {
    const namedFilterGuids = columns.reduce(
      (result, column) => result.concat(...column.namedFilterOptions.map((namedFilter) => namedFilter.guid)),
      []
    );

    const selectedFieldIds = columns.map((column) => column.fieldId);

    return _map(
      filterPresets,
      (filterPreset): IViewComponentDesignSetPayload<IRecordGridFilterPresetDescriptorModel> => {
        const newRecordGridFilters = filterPreset.recordGridFilters
          .filter((filter) => selectedFieldIds.includes(filter.fieldId))
          .filter((filter) => {
            const value = isObject(filter.value) ? filter.value : this.rxJsonParserService.tryParseJson(filter.value);

            return (
              !value?.[RecordGridNamedFilterOptionKey] ||
              namedFilterGuids.includes(value[RecordGridNamedFilterOptionKey])
            );
          });

        const newFilterData = this.rxRecordGridFilterHelperService.getRecordGridFilterDataFromPredefinedFilter(
          filterPreset.filters,
          newRecordGridFilters
        );

        return {
          guid: filterPreset.guid,
          type: RX_RECORD_GRID.components.filterPreset,
          data: {
            title: filterPreset.title,
            filters: newFilterData
              ? this.rxRecordGridFilterHelperService.denormalizeFilterString(
                  JSON.stringify(newFilterData),
                  newRecordGridFilters
                )
              : null
          },
          children: _map(
            newRecordGridFilters,
            (recordGridFilter): IViewComponentDesignSetPayload<IRecordGridFilterDescriptorModel> => ({
              guid: recordGridFilter.guid,
              type: RX_RECORD_GRID.components.filter,
              data: {
                fieldId: recordGridFilter.fieldId,
                value: recordGridFilter.value,
                ...(recordGridFilter.$DISPLAYVALUE$ && { $DISPLAYVALUE$: recordGridFilter.$DISPLAYVALUE$ })
              }
            })
          )
        };
      }
    );
  }

  getFilterPresetDescriptorsFromChildData(
    definitions: IViewComponentDesignData[]
  ): IRecordGridPredefinedFilterPreset[] {
    return _map(
      definitions.filter((definition) => definition.type === RX_RECORD_GRID.components.filterPreset),
      (
        filterPresetsDescriptor: IViewComponentDesignData<IRecordGridFilterPresetDescriptorModel>
      ): IRecordGridPredefinedFilterPreset => ({
        filters: filterPresetsDescriptor.data.filters,
        recordGridFilters: _map(
          _filter(
            filterPresetsDescriptor.children,
            (definition) => definition.type === RX_RECORD_GRID.components.filter
          ),
          (presetFilter: IViewComponentDesignData<IRecordGridFilterDescriptorModel>): IRxRecordGridFilter => ({
            fieldId: presetFilter.data.fieldId,
            value: presetFilter.data.value,
            guid: presetFilter.guid,
            $DISPLAYVALUE$: presetFilter.data.$DISPLAYVALUE$
          })
        ),
        title: filterPresetsDescriptor.data.title,
        guid: filterPresetsDescriptor.guid
      })
    ).filter((filterPreset) => filterPreset.recordGridFilters?.length > 0);
  }

  private getActionsFromComponent(
    componentDesignData: IViewComponentDesignData
  ): IViewComponentDesignData<IViewActionDesignProperties>[] {
    return _map(
      _filter(
        componentDesignData.children,
        (definition) => definition.type === RxViewComponentType.Action
      ) as IViewComponentDesignData<IViewActionDesignProperties>[],
      (columnAction) => ({
        ...columnAction,
        data: { ...columnAction.data }
      })
    );
  }

  private getNamedFilterOptionsFromComponent(
    componentDesignData: IViewComponentDesignData
  ): INamedFilterOptionDesignData[] {
    return componentDesignData.children
      .filter((definition) => definition.type === RX_RECORD_GRID.components.filterPreset)
      .map((namedFilterOptionData: IViewComponentDesignData<IRecordGridColumnNamedFilterOptionDesign>) => ({
        ...namedFilterOptionData,
        data: { ...namedFilterOptionData.data }
      }));
  }

  getDefaultColumns(recordDefinition: IRecordDefinition): IRecordGridDesignInspectorColumnConfig[] {
    let columns: IRecordGridDesignInspectorColumnConfig[] = [];

    if (recordDefinition) {
      const fieldDefinitions = recordDefinition.fieldDefinitions;
      const coreFieldIds = RX_RECORD_DEFINITION.coreFieldIds;

      columns = [
        {
          title: find(fieldDefinitions, (fieldDefinition) => fieldDefinition.id === coreFieldIds.displayId).name,
          fieldId: String(coreFieldIds.displayId),
          searchable: true
        }
      ];

      if (recordDefinition.resourceType === RX_RECORD_DEFINITION.recordDefinitionTypes.regular.recordDefinitionType) {
        columns.push({
          title: find(fieldDefinitions, (fieldDefinition) => fieldDefinition.id === coreFieldIds.modifiedDate).name,
          fieldId: String(coreFieldIds.modifiedDate),
          searchable: false
        });

        columns.push({
          title: find(fieldDefinitions, (fieldDefinition) => fieldDefinition.id === coreFieldIds.description).name,
          fieldId: String(coreFieldIds.description),
          searchable: true
        });
      }

      const defaultValues = reduce(
        RX_RECORD_GRID.columnProperties,
        (result, columnProperty) => {
          result[columnProperty.name] = columnProperty.defaultValue;

          return result;
        },
        {} as IPlainObject
      );

      forEach(columns, (column, index) =>
        defaults(column, defaultValues, {
          index,
          guid: this.rxGuidService.generate()
        } as IRecordGridDesignInspectorColumnConfig)
      );
    }

    return columns;
  }

  getInitialSortColumnModel(columns: IRecordGridDesignInspectorColumnConfig[]): IRecordGridSortEditorModel {
    const selectedSortColumn = find(columns, (column) => isObject(column.sortable));

    return selectedSortColumn
      ? {
          direction: (selectedSortColumn.sortable as IRecordGridSortableObject).direction,
          fieldId: selectedSortColumn.fieldId
        }
      : null;
  }

  getInspectorColumnsFromDesignData(
    columnsDesignModel: IViewComponentDesignData<IRecordGridDesignColumnData>[]
  ): IRecordGridDesignInspectorColumnConfig[] {
    return columnsDesignModel.map((columnDescriptor): IRecordGridDesignInspectorColumnConfig => {
      const column = columnDescriptor.data;

      return {
        ...pick(column, [
          'alignment',
          'fieldId',
          'filterType',
          'searchable',
          'title',
          'cellDisplayProperties',
          'width',
          'typeaheadKeystrokeCount',
          'additionalQueryCriteria'
        ]),
        actions: this.getActionsFromComponent(columnDescriptor),
        namedFilterOptions: this.getNamedFilterOptionsFromComponent(columnDescriptor),
        guid: columnDescriptor.guid,
        filterable:
          this.rxRecordGridConfigUtilsService.getBooleanValue(column.filterable) || isUndefined(column.filterable),
        sortable:
          (this.rxRecordGridConfigUtilsService.parseConfigString(column.sortable as boolean) as any) ||
          isUndefined(column.sortable),
        visible: this.rxRecordGridConfigUtilsService.getBooleanValue(column.visible) || isUndefined(column.visible),
        wrapText: this.rxRecordGridConfigUtilsService.getBooleanValue(column.wrapText),
        index: Number(column.index)
      };
    });
  }

  getComponentProperties(componentDesignProperties: IRecordGridDesignProperties): IRecordGridProperties {
    return {
      bordered: componentDesignProperties.bordered ?? true,
      expandable: componentDesignProperties.expandable,
      name: componentDesignProperties.name,
      recordDefinitionName: componentDesignProperties.recordDefinitionName,
      showDataForAllLocales: componentDesignProperties.showDataForAllLocales,
      associationDefinitionName: componentDesignProperties.associationDefinitionName,
      associatedRecordNodeSide: componentDesignProperties.associatedRecordNodeSide,
      associatedRecordId: componentDesignProperties.associatedRecordId,
      associatedRoleName: componentDesignProperties.associatedRoleName,
      filterExpression: componentDesignProperties.filterExpression,
      filters: isObject(componentDesignProperties.filters)
        ? componentDesignProperties.filters.filtersJson
        : componentDesignProperties.filters,
      defaultFilterPreset: this.getDefaultFilterPreset(componentDesignProperties),
      cardLayoutWidth: componentDesignProperties.cardLayoutWidth,
      enableFiltering: componentDesignProperties.enableFiltering,
      requiredFilters: componentDesignProperties.requiredFilters ?? 0,
      getDataForHiddenColumns: componentDesignProperties.getDataForHiddenColumns ?? true,
      enableFilterPresets: componentDesignProperties.enableFilterPresets,
      enableRowSelection: componentDesignProperties.enableRowSelection,
      styles: componentDesignProperties.styles,
      striped: !!componentDesignProperties.striped,
      viewPresetSelector: componentDesignProperties.viewPresetSelector,
      [RX_AVAILABLE_ON_DEVICES_PROP_NAME]: componentDesignProperties[RX_AVAILABLE_ON_DEVICES_PROP_NAME]
    };
  }

  private getDefaultFilterPreset(componentDesignProperties: IRecordGridDesignProperties): string {
    if (componentDesignProperties.defaultFilterPreset && componentDesignProperties.filterPresets) {
      return componentDesignProperties.filterPresets.some(
        (filterPreset) => filterPreset.guid === componentDesignProperties.defaultFilterPreset
      )
        ? componentDesignProperties.defaultFilterPreset
        : null;
    } else if (componentDesignProperties.defaultFilterPreset && !componentDesignProperties.filterPresets) {
      return componentDesignProperties.defaultFilterPreset;
    }

    return null;
  }

  getComponentDesignProperties(
    componentProperties: IRecordGridProperties,
    initialComponentProperties: IRecordGridDesignProperties,
    columnsDesignModel: IViewComponentDesignData<IRecordGridDesignColumnData>[],
    basicRecordGridFilters: IRxRecordGridFilter[],
    filterPresets: IRecordGridPredefinedFilterPreset[]
  ): IRecordGridDesignProperties {
    const columns = this.getInspectorColumnsFromDesignData(columnsDesignModel);

    componentProperties = {
      ...initialComponentProperties,
      ...componentProperties
    };

    return {
      ...componentProperties,
      mode: componentProperties.associationDefinitionName
        ? RecordGridDesignDefinitionMode.Association
        : RecordGridDesignDefinitionMode.Record,
      filters: {
        basicFilters: basicRecordGridFilters,
        filtersJson: componentProperties.filters
      },
      filterMode: componentProperties.filterExpression ? RecordGridFilterMode.Expression : RecordGridFilterMode.Basic,
      filterPresets,
      initialSortColumn: this.getInitialSortColumnModel(columns),
      columns
    };
  }

  getActionButtonDescriptorsFromChildData(
    definitions: IViewComponentDesignData[]
  ): IViewComponentDesignData<IActionButtonProperties>[] {
    return definitions.filter((definition) => definition.type === RxViewComponentType.ActionButton);
  }

  getGridViewPresetsFromChildData(
    definitions: IViewComponentDesignData[]
  ): IViewComponentDesignData<IGridViewPresetProperties>[] {
    return definitions.filter(
      (definition) => definition.type === RX_RECORD_GRID.components.viewPreset
    ) as IViewComponentDesignData<IGridViewPresetProperties>[];
  }

  getBasicRecordGridFiltersFromChildData(definitions: IViewComponentDesignData[]): IRxRecordGridFilter[] {
    return _map(
      definitions.filter((definition) => definition.type === RX_RECORD_GRID.components.filter),
      (basicFilterDescriptor: IViewComponentDesignData<IRecordGridFilterDescriptorModel>): IRxRecordGridFilter => ({
        fieldId: basicFilterDescriptor.data.fieldId,
        value: basicFilterDescriptor.data.value,
        guid: basicFilterDescriptor.guid,
        $DISPLAYVALUE$: basicFilterDescriptor.data.$DISPLAYVALUE$
      })
    );
  }

  getRowActionButtonPayloads(rowActions: IRowAction[]): IViewComponentDesignSetPayload<IActionButtonProperties>[] {
    return rowActions.map((rowAction) => ({
      type: RxViewComponentType.ActionButton,
      guid: rowAction.guid,
      data: omit(rowAction, 'actions', 'guid'),
      outletName: RX_RECORD_GRID.rowActionsOutletName,
      children: this.getColumnActionComponentPayloads(rowAction.actions)
    }));
  }

  getInspector(
    model: IRecordGridDesignProperties,
    recordDefinition: IRecordDefinition,
    recordAssociationDefinitions: IAssociationDefinition[]
  ): IViewDesignerInspectorConfig {
    const hasRecordDefinition = Boolean(model.recordDefinitionName && recordDefinition);

    const generalControls: IFormControlBuilderConfig[] = [
      {
        name: 'name',
        component: TextFormControlComponent,
        options: {
          label: 'Name',
          tooltip: new Tooltip('Enter a name to uniquely identify the Record grid.')
        } as ITextFormControlOptions
      },
      {
        name: 'mode',
        component: GroupButtonFormControlComponent,
        options: {
          required: false,
          label: 'Mode',
          items: [
            {
              value: RecordGridDesignDefinitionMode.Record,
              name: 'Record'
            },
            {
              value: RecordGridDesignDefinitionMode.Association,
              name: 'Association'
            }
          ]
        } as IGroupButtonFormControlOptions
      },
      {
        name: 'recordDefinitionName',
        component: RxDefinitionPickerComponent,
        options: {
          label:
            model.mode === RecordGridDesignDefinitionMode.Association
              ? 'Record definition to show'
              : 'Record definition name',
          definitionType:
            model.mode === RecordGridDesignDefinitionMode.Association
              ? RxDefinitionPickerType.RegularDataRecord
              : RxDefinitionPickerType.Record,
          required: true,

          beforeValueChange: (oldValue: string, newValue: string): Promise<boolean> => {
            if (!isEmpty(model.columns) && Boolean(oldValue)) {
              return this.rxModalService.confirm({
                title: 'Warning',
                modalStyle: RX_MODAL.modalStyles.warning,
                message:
                  'Initial column sort, initial filters, and added grid columns will be cleared. Do you want to continue?'
              });
            } else {
              return Promise.resolve(true);
            }
          }
        } as IDefinitionPickerComponentOptions
      }
    ];

    const presentationControls: IFormControlBuilderConfig[] = [
      {
        name: 'cardLayoutWidth',
        component: SelectFormControlComponent,
        options: {
          label: 'Card layout width',
          sortAlphabetically: false,
          options: RX_RECORD_GRID.cardLayoutWidthOptions,
          emptyOption: true,
          tooltip: new Tooltip('The width of the Record grid view component at which it will switch to card layout.')
        } as ISelectFormControlOptions
      },
      {
        name: 'initialSortColumn',
        component: RecordGridSortEditorControlComponent,
        options: {
          label: 'Initial column sort',
          gridColumns: sortBy(model.columns, 'title'),
          recordDefinition
        } as IRecordGridSortEditorControlOptions
      },
      {
        name: 'enableRowSelection',
        component: OptionalSelectFormControlComponent,
        options: {
          label: 'Enable row selection',
          options: [
            {
              id: RX_RECORD_GRID.selectionTypes.multiple,
              name: 'Multiple rows'
            },
            {
              id: RX_RECORD_GRID.selectionTypes.single,
              name: 'Single row'
            }
          ]
        } as IOptionalSelectFormControlOptions
      },
      {
        name: 'expandable',
        component: SwitchFormControlComponent,
        hidden: !this.rxFeatureService.isFeatureEnabled('DRD21-44922'),
        options: {
          label: 'Enable grid width expansion',
          tooltip: new Tooltip(
            'If enabled, the grid will expand in width to accommodate wider columns. If one or more columns extend beyond the right border of the grid, a horizontal scroll bar will appear.<br>Column width will be converted to pixels if grid width expansion mode is enabled.'
          )
        } as ISwitcherFormControlOptions
      },
      {
        name: 'bordered',
        component: SwitchFormControlComponent,
        options: {
          label: 'Show border lines'
        } as ISwitcherFormControlOptions
      },
      {
        name: 'striped',
        component: SwitchFormControlComponent,
        options: {
          label: 'Show striped rows'
        } as ISwitcherFormControlOptions
      },
      {
        name: 'showDataForAllLocales',
        component: SwitchFormControlComponent,
        options: {
          label: 'Show data for all locales',
          tooltip: new Tooltip(
            'If enabled, data for all locales will be displayed in the grid at runtime. If disabled, only data for current locale will be displayed.'
          )
        } as ISwitcherFormControlOptions
      },
      getAvailableOnDevicesInspectorConfig(),
      getStylesFieldInspectorConfig()
    ];

    if (model.mode === RecordGridDesignDefinitionMode.Association) {
      if (hasRecordDefinition) {
        generalControls.push({
          name: 'associationDefinitionName',
          component: SelectFormControlComponent,
          options: {
            required: true,
            label: 'Association to use',
            options: _map(
              recordAssociationDefinitions,
              (associationDescriptor): ISelectOption => ({
                id: associationDescriptor.name,
                name: this.rxDefinitionNameService.getDisplayName(associationDescriptor.name)
              })
            ),
            emptyOption: true
          } as ISelectFormControlOptions
        });
      }

      const selectedAssociationDefinition = find(
        recordAssociationDefinitions,
        (definition) => definition.name === model.associationDefinitionName
      );

      if (
        selectedAssociationDefinition &&
        selectedAssociationDefinition.nodeAId === selectedAssociationDefinition.nodeBId
      ) {
        generalControls.push({
          name: 'associatedRecordNodeSide',
          component: SelectFormControlComponent,
          options: {
            required: true,
            label: 'Associated record node side',
            options: [
              {
                id: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.value,
                name: selectedAssociationDefinition.nodeAName || RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.defaultName
              },
              {
                id: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.value,
                name: selectedAssociationDefinition.nodeBName || RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.defaultName
              }
            ],
            emptyOption: true
          } as ISelectFormControlOptions
        });
      }

      generalControls.push({
        name: 'associatedRecordId',
        component: ExpressionInspectorControlComponent,
        options: {
          label: 'Associated record ID',
          isRequired: true
        } as IExpressionInspectorControlOptions
      });

      if (
        selectedAssociationDefinition &&
        selectedAssociationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.manyToMany.value
      ) {
        generalControls.push({
          name: 'associatedRoleName',
          component: ExpressionInspectorControlComponent,
          options: {
            label: 'Associated role name'
          } as IExpressionInspectorControlOptions
        });
      }
    }

    if (hasRecordDefinition) {
      generalControls.push({
        name: 'columns',
        component: RecordGridColumnEditorControlComponent,
        options: {
          recordDefinition
        } as IRecordGridColumnEditorControlOptions
      });

      generalControls.push({
        name: 'getDataForHiddenColumns',
        component: SwitchFormControlComponent,
        options: {
          label: 'Get data for hidden columns',
          tooltip: new Tooltip(
            this.translateService.instant(
              'com.bmc.arsys.rx.client.view-components.record-grid.get-data-for-hidden-columns.tooltip'
            )
          )
        } as ISwitcherFormControlOptions
      });

      generalControls.push({
        widgetName: 'rowActions',
        component: RxRecordGridRowActionEditorWidgetComponent
      });
    }

    const filtersControls: IFormControlBuilderConfig[] = [
      {
        name: 'enableFiltering',
        component: SwitchFormControlComponent,
        options: {
          label: 'Enable filtering'
        } as ISwitcherFormControlOptions
      },
      {
        name: 'requiredFilters',
        component: SelectFormControlComponent,
        isDisabled: !model.enableFiltering,
        options: {
          label: 'Required number of filters',
          sortAlphabetically: false,
          options: RX_RECORD_GRID.numberOfFiltersOptions,
          emptyOption: false,
          tooltip: new Tooltip(
            'Data will be displayed in the grid only if a) the number of applied filters is greater than or equal to the required number of filters, or b) a search is performed.'
          )
        } as ISelectFormControlOptions
      }
    ];

    if (hasRecordDefinition) {
      filtersControls.push({
        name: 'filterMode',
        component: GroupButtonFormControlComponent,
        options: {
          label: 'Initial filters',
          items: [
            {
              name: 'Basic',
              value: RecordGridFilterMode.Basic
            },
            {
              name: 'Expression',
              value: RecordGridFilterMode.Expression
            }
          ],
          tooltip: new Tooltip(
            'Select the records that should be displayed on the deployed application UI. Application users can add filters in the deployed application.'
          ),
          beforeValueChange: (oldValue: string, newValue: string): Promise<boolean> => {
            if (
              (newValue === RecordGridFilterMode.Expression && model.filters.filtersJson) ||
              (newValue === RecordGridFilterMode.Basic && model.filterExpression)
            ) {
              return this.rxModalService.confirm({
                title: 'Change filter type',
                modalStyle: RX_MODAL.modalStyles.warning,
                message: 'Current filter will be cleared. Do you want to continue?'
              });
            } else {
              return Promise.resolve(true);
            }
          }
        } as IGroupButtonFormControlOptions
      });

      if (model.filterMode === RecordGridFilterMode.Basic) {
        filtersControls.push({
          name: 'filters',
          component: RecordGridFilterSelectControlComponent,
          options: {
            primaryRecordDefinition: recordDefinition
          } as IRecordGridFilterSelectControlOptions
        });
      } else {
        filtersControls.push({
          name: 'filterExpression',
          component: ExpressionInspectorControlComponent,
          options: {
            label: 'Initial filters',
            isLabelHidden: true
          } as IExpressionInspectorControlOptions
        });
      }

      filtersControls.push({
        name: 'enableFilterPresets',
        component: SwitchFormControlComponent,
        isDisabled: !model.enableFiltering,
        options: {
          label: 'Enable filter presets'
        } as ISwitcherFormControlOptions
      });

      filtersControls.push({
        name: 'filterPresets',
        component: RecordGridFilterPresetEditorControlComponent,
        options: {
          label: 'Filter presets',
          tooltip: new Tooltip('Predefine sets of record grid filters users can apply.'),
          primaryRecordDefinition: recordDefinition,
          columns: model.columns.filter((column) => column.filterable)
        } as IRecordGridFilterPresetEditorControlOptions
      });
    }

    if (!isEmpty(model.filterPresets)) {
      filtersControls.push({
        name: 'defaultFilterPreset',
        component: SelectFormControlComponent,
        options: {
          label: 'Default filter preset',
          tooltip: new Tooltip('Select a filter preset to be applied when a user opens this view for the first time.'),
          options: _map(
            model.filterPresets,
            (filterPreset): ISelectOption => ({ id: filterPreset.guid, name: filterPreset.title })
          ),
          emptyOption: true
        } as ISelectFormControlOptions
      });
    }

    const inspectorSectionConfigs = [
      {
        label: 'General',
        controls: generalControls
      },
      {
        label: 'Filters',
        controls: filtersControls
      },
      {
        label: 'Presentation',
        controls: presentationControls
      },
      {
        label: 'View presets',
        controls: [
          {
            component: RecordGridViewPresetsWidgetComponent,
            options: {
              recordDefinition: recordDefinition
            } as IRecordGridViewPresetsWidgetOptions
          }
        ]
      }
    ];

    return { inspectorSectionConfigs };
  }

  getColumnDataDictionaryBranch(
    recordGridGuid: string,
    componentDescriptorName: string,
    componentName$: Observable<string>,
    columnComponents$: Observable<IViewComponentDesignData<IRecordGridDesignColumnData>[]>,
    recordDefinition$: Observable<IRecordDefinition>,
    associationDefinitionDisplayName$: Observable<string>
  ): Observable<IViewComponentDesignCommonDataDictionaryBranch> {
    const columnDataDictionaryBranch$ = combineLatest([columnComponents$, recordDefinition$]).pipe(
      switchMap(([columnComponents, recordDefinition]) =>
        recordDefinition
          ? this.getColumnsDataDictionaryBuilder(recordGridGuid, columnComponents, recordDefinition).pipe(
              map(
                (buildColumnDataDictionary): IViewComponentDesignCommonDataDictionaryBranch => ({
                  label: this.rxDefinitionNameService.getDisplayName(recordDefinition.name),
                  expression: `\${view.components.${recordGridGuid}.api}`,
                  children: [
                    {
                      label: 'First selected row',
                      expression: `\${view.components.${recordGridGuid}.firstSelectedRow}`,
                      children: buildColumnDataDictionary('firstSelectedRow')
                    },
                    {
                      label: 'Is user allowed to delete records',
                      expression: `\${view.components.${recordGridGuid}.isUserAllowedToDeleteRecords}`
                    },
                    {
                      label: 'Last action row',
                      expression: `\${view.components.${recordGridGuid}.clickableRow}`,
                      children: buildColumnDataDictionary('clickableRow')
                    },
                    {
                      label: 'Selected rows',
                      expression: `\${view.components.${recordGridGuid}.selectedRows}`,
                      children: buildColumnDataDictionary('fieldValuesByFieldId')
                    },
                    {
                      label: 'Selected row count',
                      expression: `\${view.components.${recordGridGuid}.selectedRowCount}`
                    },
                    {
                      label: 'Total row count',
                      expression: `\${view.components.${recordGridGuid}.totalRowCount}`
                    },
                    {
                      label: 'Query expression',
                      expression: `\${view.components.${recordGridGuid}.queryExpression}`
                    },
                    {
                      label: 'Last refresh time',
                      expression: `\${view.components.${recordGridGuid}.lastRefreshTime}`
                    }
                  ]
                })
              ),
              take(1)
            )
          : of(null)
      )
    );

    return combineLatest([componentName$, columnDataDictionaryBranch$, associationDefinitionDisplayName$]).pipe(
      map(([label, dataDictionaryBranch, associationDefinitionDisplayName]) =>
        dataDictionaryBranch
          ? ({
              ...dataDictionaryBranch,
              label: this.getComponentName(
                componentDescriptorName,
                dataDictionaryBranch.label,
                associationDefinitionDisplayName,
                label
              )
            } as IViewComponentDesignCommonDataDictionaryBranch)
          : null
      )
    );
  }

  private getColumnsDataDictionaryBuilder(
    recordGridGuid: string,
    columnComponents: IViewComponentDesignData<IRecordGridDesignColumnData>[],
    recordDefinition: IRecordDefinition
  ): Observable<ColumnsDataDictionaryBuilder> {
    return (
      columnComponents.length
        ? forkJoin(
            columnComponents.map((component) =>
              this.rxRecordGridUtilsService.getFieldDefinition(component.data.fieldId, recordDefinition).pipe(
                filter((fieldDefinition) => !!fieldDefinition),
                map((fieldDefinition) =>
                  this.getColumnDataDictionaryBranchBuilder(recordGridGuid, component, fieldDefinition)
                )
              )
            ) as Observable<ColumnDataDictionaryBranchBuilder>[]
          )
        : of([])
    ).pipe(
      map(
        (builders) => (gridPropertyName: string) =>
          builders.map((buildColumnDataDictionaryBranch) =>
            buildColumnDataDictionaryBranch.call(this, gridPropertyName)
          )
      )
    );
  }

  private getColumnDataDictionaryBranchBuilder(
    recordGridGuid: string,
    columnComponent: IViewComponentDesignData<IRecordGridDesignColumnData>,
    fieldDefinition: IFieldDefinition
  ): ColumnDataDictionaryBranchBuilder {
    const selectionList =
      fieldDefinition.resourceType === RX_RECORD_DEFINITION.resourceTypes.selection
        ? _map(fieldDefinition.optionNamesById, (optionName, optionValue) => ({
            label: optionName,
            expression: `\${view.components.${recordGridGuid}.recordDefinition.fieldDefinitionsById[${columnComponent.data.fieldId}].optionsById[${optionValue}].id}`
          }))
        : undefined;

    return (propertyName: string): IViewComponentDesignCommonDataDictionaryBranch => ({
      label: columnComponent.data.title?.trim() || `[${fieldDefinition.name}]`,
      expression: `\${view.components.${recordGridGuid}.${propertyName}.${columnComponent.data.fieldId}}`,
      autocompleteOptions: selectionList,
      children:
        fieldDefinition.resourceType === RX_RECORD_DEFINITION.resourceTypes.selection
          ? [
              {
                label: `${columnComponent.data.title} (Option name)`,
                expression: `\${view.components.${recordGridGuid}.${propertyName}.selectionFieldOptionNamesById.${columnComponent.data.fieldId}}`
              },
              {
                label: 'Options',
                children: selectionList
              }
            ]
          : null
    });
  }

  getComponentName(
    componentDescriptorName: string,
    recordDefinitionDisplayName?: string,
    associationDefinitionDisplayName?: string,
    name?: string
  ): string {
    const additionalLabels = [recordDefinitionDisplayName, associationDefinitionDisplayName, name].filter(Boolean);

    return additionalLabels.length
      ? `${componentDescriptorName} (${additionalLabels.join(': ')})`
      : componentDescriptorName;
  }

  getGridViewPresetPayload(
    viewPresetGuid: string,
    columns: IRecordGridDesignInspectorColumnConfig[]
  ): IViewComponentDesignSetPayload {
    return {
      type: RX_RECORD_GRID.components.viewPreset,
      guid: this.rxGuidService.generate(),
      data: {
        viewPresetGuid,
        filters: null
      },
      children: columns.map((column) => this.getColumnPresetPayload(column))
    };
  }

  getColumnPresetPayload(
    column: IRecordGridDesignInspectorColumnConfig
  ): IViewComponentDesignSetPayload<IGridViewColumnProperties> {
    return {
      type: RX_RECORD_GRID.components.columnViewPreset,
      data: {
        fieldId: column.fieldId,
        index: column.index,
        visible: column.visible,
        sortable: isObject(column.sortable) ? column.sortable : null,
        width: column.width
      }
    };
  }

  getPresetsList(guid: string): Observable<IPresetItem[]> {
    return guid
      ? this.viewDesignerFacade.getChildComponents<IViewPresetProperties>(guid).pipe(
          map((res) => res.sort((a, b) => a.data.index - b.data.index)),
          map((res) => res.map((item) => ({ guid: item.guid, name: item.data.label }))),
          debounceTime(50) // used to let store be `stabilized` when indexes are updating for each preset
        )
      : of([]);
  }
}
