import { Component, OnInit } from '@angular/core';
import { BaseViewComponent } from '@helix/platform/view/runtime';
import {
  IRecordDefinition,
  RxRecordDefinitionCacheService,
  RxRecordDefinitionService
} from '@helix/platform/record/api';
import { takeUntil } from 'rxjs/operators';
import { IRxExtensionContainerConfig } from './extension-container.types';

@Component({
  selector: 'rx-extension-container',
  templateUrl: './extension-container.component.html'
})
export class RxExtensionContainerComponent extends BaseViewComponent implements OnInit {
  recordDefinitions: { [name: string]: IRecordDefinition } = {};
  state: IRxExtensionContainerConfig;

  constructor(
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private rxRecordDefinitionService: RxRecordDefinitionService
  ) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.config.pipe(takeUntil(this.destroyed$)).subscribe((config: IRxExtensionContainerConfig) => {
      this.state = config;
    });
  }

  loadRecordDefinition(name: string): void {
    this.rxRecordDefinitionCacheService.getRecordDefinition(name).subscribe((recordDefinition) => {
      recordDefinition.fieldDefinitionsById =
        this.rxRecordDefinitionService.buildFieldDefinitionsByIdMap(recordDefinition);

      this.recordDefinitions[name] = recordDefinition;

      this.notifyPropertyChanged('recordDefinitions', this.recordDefinitions);
    });
  }
}
