import { Component, Input, OnInit } from '@angular/core';
import { ContainerDesignComponent } from '../../../container/design/container-design.component';
import { TabPanelDesignComponent } from '../tab-panel-design.component';
import { TabContainerDesignModel } from './tab-container-design.model';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'rx-tab-container-design',
  templateUrl: './tab-container-design.component.html'
})
export class TabContainerDesignComponent extends ContainerDesignComponent implements OnInit {
  @Input()
  model: any;

  constructor(private tabPanelDesignComponent: TabPanelDesignComponent) {
    super();
  }

  ngOnInit(): void {
    this.tabPanelDesignComponent.model.selectedTabGuid$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((this.model as TabContainerDesignModel).selectedTabGuid$);
  }
}
