import { Injector } from '@angular/core';
import { TextFieldDesignModel } from './text-field-design.model';
import {
  NamedListFilterExpressionConfigurator,
  RxViewComponentExpressionConfigurator
} from '@helix/platform/view/designer';
import { filter, switchMap } from 'rxjs/operators';
import { ExpressionOperatorGroup, ExpressionOperatorRowsByGroup } from '@helix/platform/shared/api';

export class TextFieldExpressionConfigurator extends RxViewComponentExpressionConfigurator {
  constructor(
    protected injector: Injector,
    protected componentGuid: string,
    protected componentModel: TextFieldDesignModel
  ) {
    super(injector, componentGuid, componentModel);

    const dataDictionary$ = this.componentModel.selectedFieldDefinition$.pipe(
      filter((fieldDefinition) => Boolean(fieldDefinition.namedListDefinition)),
      switchMap((fieldDefinition) => {
        const namedListFilterExpressionConfigurator = new NamedListFilterExpressionConfigurator(
          fieldDefinition,
          this.injector
        );

        return namedListFilterExpressionConfigurator.getDataDictionary();
      })
    );

    this.configureForProperty({
      propertyPath: 'additionalQueryCriteria',
      dataDictionary$,
      operators: ExpressionOperatorRowsByGroup.get(ExpressionOperatorGroup.AllServer)
    });
  }
}
