import { Component, Injector } from '@angular/core';
import { IViewComponent } from '@helix/platform/view/runtime';
import { BaseRecordEditorFieldComponent } from '../../base-record-editor-field/runtime/base-record-editor-field-component.class';
import { IBaseRecordEditorFieldComponentConfig } from '../../base-record-editor-field/runtime/base-record-editor-field-component.types';
import { RxFieldDefinitionService } from '@helix/platform/record/api';

@Component({
  selector: 'rx-textarea-field',
  templateUrl: './textarea-field.component.html',
  styleUrls: ['./textarea-field.component.scss']
})
export class TextareaFieldComponent extends BaseRecordEditorFieldComponent implements IViewComponent {
  maxLength: number;

  constructor(injector: Injector, private rxFieldDefinitionService: RxFieldDefinitionService) {
    super(injector);
  }

  onConfigInitialized(config: IBaseRecordEditorFieldComponentConfig): void {
    super.onConfigInitialized(config);

    if (this.fieldDefinition.maxLength && !this.rxFieldDefinitionService.isSystemField(this.fieldDefinition)) {
      this.maxLength = this.fieldDefinition.maxLength;
    }
  }
}
