import {
  AfterViewInit,
  Component,
  ElementRef,
  Injector,
  NgZone,
  OnInit,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { ActiveModalRef, AdaptAccordionTabComponent, AdaptModalService, DismissReasons } from '@bmc-ux/adapt-angular';
import { ActionListControlComponent, getAvailableOnDevicesInspectorConfig } from '@helix/platform/view/designer';
import { IRowAction, IRowActionData } from '../record-grid-row-action-editor-widget.types';
import { cloneDeep, isEqual, omit } from 'lodash';
import {
  IDefinitionPickerComponentOptions,
  IExpressionEditorPropertyChange,
  IExpressionFormControlOptions,
  IIconPickerFormControlOptions,
  ISelectFormControlOptions,
  RxDefinitionPickerType,
  RxExpressionEditorService
} from '@helix/platform/shared/components';
import { IExpressionConfigurator, IPlainObject, Tooltip } from '@helix/platform/shared/api';
import { CdkDrag, CdkDropList, moveItemInArray } from '@angular/cdk/drag-drop';
import { CdkDragDrop } from '@angular/cdk/drag-drop/';
import { TranslateService } from '@ngx-translate/core';
import { map } from 'rxjs/operators';
import { IRecordDefinition, RxRecordDefinitionCacheService } from '@helix/platform/record/api';
import { of } from 'rxjs';
import { RxModalClass } from '@helix/platform/ui-kit';
import { NgForm } from '@angular/forms';
import { RxGuidService } from '@helix/platform/utils';
import { RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE } from '@helix/platform/view/api';

@Component({
  selector: 'rx-record-grid-row-action-editor-modal',
  templateUrl: './record-grid-row-action-editor-modal.component.html',
  styleUrls: ['./record-grid-row-action-editor-modal.component.scss']
})
export class RecordGridRowActionEditorModalComponent extends RxModalClass implements OnInit, AfterViewInit {
  @ViewChildren(AdaptAccordionTabComponent, { read: ElementRef })
  private accordionTabEls: QueryList<ElementRef>;

  @ViewChild('rowActionsForm', { static: true, read: NgForm })
  rowActionsForm: NgForm;

  expressionConfigurator: IExpressionConfigurator;
  isReadOnly: boolean;
  rowActions: IRowActionData[] = [];

  selectFormControlOptions: ISelectFormControlOptions = {
    label: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.field-name.label'
    ),
    tooltip: new Tooltip(
      this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.field-name.tooltip'
      )
    ),
    options: [],
    emptyOption: true
  } as ISelectFormControlOptions;

  availableOnDevicesFormControlOptions: ISelectFormControlOptions = getAvailableOnDevicesInspectorConfig().options;

  recordDefinitionPickerOptions: IDefinitionPickerComponentOptions = {
    label: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.security-definition-name.label'
    ),
    tooltip: new Tooltip(
      this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.security-definition-name.tooltip'
      )
    ),
    definitionType: RxDefinitionPickerType.StandardDataRecord,
    required: false
  };

  iconPickerOptions: IIconPickerFormControlOptions = {
    label: this.translateService.instant('com.bmc.arsys.rx.client.view-components.record-grid-row-actions.icon.label'),
    appendToBody: true,
    required: false
  };

  @ViewChildren('draggableSelectedRowAction', { read: CdkDrag })
  draggableSelectedRowAction: QueryList<CdkDrag<IRowActionData>>;

  @ViewChild('selectedRowActionsDropList', { read: CdkDropList })
  selectedRowActionsDropList: CdkDropList;

  @ViewChildren(ActionListControlComponent)
  actionListControlComponents: QueryList<ActionListControlComponent>;

  expressionFormControlOptions: IExpressionFormControlOptions;

  private activeActionIndex: number;
  private rowActionIndex: number;

  constructor(
    public context: ActiveModalRef,
    private adaptModalService: AdaptModalService,
    private rxExpressionEditorService: RxExpressionEditorService,
    private translateService: TranslateService,
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private ngZone: NgZone,
    private rxGuidService: RxGuidService,
    protected injector: Injector
  ) {
    super(context, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
    const params = this.context.getData();

    this.expressionConfigurator = params.expressionConfigurator;
    this.rowActionIndex = params.rowActionIndex;
    this.activeActionIndex = params.activeActionIndex;
    this.isReadOnly = params.isReadOnly;

    this.rowActions = cloneDeep(params.rowActions).map((rowAction, index) => {
      this.setSecurityFieldDefinitions(rowAction);

      return {
        ...rowAction,
        isOpen: index === this.rowActionIndex
      };
    });
  }

  ngAfterViewInit() {
    this.openActiveAction();

    if (this.rowActionIndex) {
      this.accordionTabEls.toArray()[this.rowActionIndex].nativeElement.scrollIntoView({
        block: 'nearest'
      });
    }
  }

  cancel(): void {
    this.context.dismiss(DismissReasons.CLOSE_BTN);
  }

  trackByGuid(index: number, rowAction: IRowActionData): string {
    return rowAction.guid;
  }

  addRowAction(): void {
    this.markAsDirty();

    this.rowActions.push({
      guid: this.rxGuidService.generate(),
      label: 'New row action' + (this.rowActions.length === 0 ? '' : ' ' + this.rowActions.length),
      icon: '',
      recordDefinitionName: null,
      fieldId: null,
      recordInstance: null,
      disabled: null,
      hidden: null,
      actions: [],
      isOpen: true,
      securityFieldDefinitions$: of(this.getSelectFormControlOptions()),
      ...RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE
    });

    setTimeout(() => {
      this.accordionTabEls.last.nativeElement.scrollIntoView({
        block: 'nearest'
      });
    });
  }

  removeRowAction(index: number, $event: MouseEvent): void {
    $event.stopPropagation();
    this.markAsDirty();
    this.rowActions.splice(index, 1);
  }

  openExpressionEditor(event: IPlainObject, rowAction, propertyPath: string): void {
    this.rxExpressionEditorService
      .openEditor({
        property: {
          path: propertyPath,
          value: rowAction[propertyPath],
          label: this.getRowActionLabel(propertyPath)
        },
        isReadOnly: false,
        expressionConfigurator: this.expressionConfigurator,
        legend: [
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.function.label'),
            icon: 'd-icon-mathematical_function'
          },
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.keyword.label'),
            icon: 'd-icon-dollar'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-component.label'
            ),
            icon: 'd-icon-file_o'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-component-property.label'
            ),
            icon: 'd-icon-file_o_gear'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-input-parameter.label'
            ),
            icon: 'd-icon-arrow_right_square_input'
          }
        ]
      })
      .subscribe((expression: IExpressionEditorPropertyChange) => {
        this.markAsDirty();

        rowAction[propertyPath] = expression.value;
      });
  }

  getExpressionFormControlOptions(propertyPath: string): IExpressionFormControlOptions {
    return {
      label: this.getRowActionLabel(propertyPath),
      tooltip:
        propertyPath === 'recordInstance'
          ? new Tooltip(
              this.translateService.instant(
                'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.recordInstance.tooltip'
              )
            )
          : null,
      dataDictionary$: this.context.getData().expressionConfigurator.getDataDictionary(propertyPath),
      operators: this.context.getData().expressionConfigurator.getOperators(propertyPath)
    };
  }

  saveActions(): void {
    const rowActions: IRowAction[] = this.rowActions.map((rowAction) => ({
      ...omit(rowAction, ['isOpen', 'securityFieldDefinitions$']),
      action: 'rowAction'
    }));

    this.context.close(rowActions);
  }

  moveRowAction(fromIndex: number, toIndex: number): void {
    moveItemInArray(this.rowActions, fromIndex, toIndex);
    this.markAsDirty();
  }

  onDropInSelectedRowActionsContainer($event: CdkDragDrop<IRowActionData[], any>): void {
    moveItemInArray(this.rowActions, $event.previousIndex, $event.currentIndex);
    this.markAsDirty();
  }

  onRecordDefinitionChange(rowAction: IRowActionData): void {
    this.markAsDirty();

    rowAction.fieldId = null;

    this.setSecurityFieldDefinitions(rowAction);
  }

  setSecurityFieldDefinitions(rowAction: IRowActionData): void {
    rowAction.securityFieldDefinitions$ = rowAction.recordDefinitionName
      ? this.rxRecordDefinitionCacheService
          .getRecordDefinition(rowAction.recordDefinitionName)
          .pipe(map((recordDefinition) => this.getSelectFormControlOptions(recordDefinition)))
      : of(this.getSelectFormControlOptions());
  }

  private getRowActionLabel(key: string): string {
    let rowActionLabel;

    switch (key) {
      case 'disabled':
        rowActionLabel = 'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.disabled.label';
        break;
      case 'hidden':
        rowActionLabel = 'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.hidden.label';
        break;
      case 'recordInstance':
        rowActionLabel = 'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.recordInstance.label';
        break;
    }

    return this.translateService.instant(rowActionLabel);
  }

  private getSelectFormControlOptions(recordDefinition: IRecordDefinition = null): ISelectFormControlOptions {
    return {
      ...this.selectFormControlOptions,
      options: recordDefinition
        ? recordDefinition.fieldDefinitions.map((definition) => ({
            name: definition.name,
            id: String(definition.id)
          }))
        : []
    } as ISelectFormControlOptions;
  }

  private openActiveAction(): void {
    if (this.activeActionIndex >= 0 && this.rowActionIndex >= 0) {
      this.ngZone.runOutsideAngular(() => {
        // timeout is required to let control set initial value
        setTimeout(() => {
          const listControl = this.actionListControlComponents.find((control) =>
            isEqual(control.value, this.rowActions[this.rowActionIndex]?.actions)
          );

          if (listControl) {
            listControl.focus({ actionIndex: this.activeActionIndex });
          }
        });
      });
    }
  }

  toggleOpen(expandAll: boolean): void {
    this.rowActions.forEach((action) => (action.isOpen = expandAll));
  }
}
