import { Component, Injector } from '@angular/core';
import { IFormFocusable, IPlainObject } from '@helix/platform/shared/api';
import { InspectorWidgetBase } from '@helix/platform/shared/components';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import { OpenViewActionModalSize, RX_AVAILABLE_ON_DEVICES_PROP_NAME } from '@helix/platform/view/api';
import { TranslateService } from '@ngx-translate/core';
import { noop, omit, pick } from 'lodash';
import { from, Observable } from 'rxjs';
import { map, shareReplay, switchMap, take } from 'rxjs/operators';
import { RecordGridDesignModel } from '../../record-grid-design.model';
import { RecordGridRowActionValidationIssue } from '../../record-grid-design.types';
import { RecordGridRowActionEditorModalComponent } from './record-grid-row-action-editor-modal/record-grid-row-action-editor-modal.component';
import { IRowAction } from './record-grid-row-action-editor-widget.types';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'rx-record-grid-row-action-editor-widget',
  templateUrl: './record-grid-row-action-editor-widget.component.html',
  styleUrls: ['./record-grid-row-action-editor-widget.component.scss']
})
export class RxRecordGridRowActionEditorWidgetComponent
  extends InspectorWidgetBase<IPlainObject, RecordGridDesignModel>
  implements IFormFocusable
{
  isDisabled: boolean;

  rowActions$: Observable<IRowAction[]> = this.designerItemModel.rowActionButtonComponents$.pipe(
    map((rowActionButtons) => {
      return rowActionButtons.map((rowActionButtonComponent) => {
        const actions = rowActionButtonComponent.children.map((action) => omit(action, 'type', 'children'));

        return {
          ...pick(rowActionButtonComponent.data, [
            'recordDefinitionName',
            'recordInstance',
            'fieldId',
            'icon',
            'disabled',
            'hidden',
            'label',
            RX_AVAILABLE_ON_DEVICES_PROP_NAME
          ]),
          guid: rowActionButtonComponent.guid,
          actions
        } as IRowAction;
      });
    }),
    shareReplay(1)
  );

  constructor(
    protected injector: Injector,
    private rxModalService: RxModalService,
    private translateService: TranslateService
  ) {
    super(injector);
  }

  openEditor(rowActionIndex?: number, activeActionIndex?: number): void {
    this.rowActions$
      .pipe(
        take(1),
        switchMap((rowActions) =>
          from(
            this.rxModalService.openModal({
              content: RecordGridRowActionEditorModalComponent,
              title: this.translateService.instant(
                'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.modal.title'
              ),
              size: OpenViewActionModalSize.Large as any,
              testID: 'edit-row-actions',
              data: {
                expressionConfigurator: this.designerItemModel.expressionConfigurator,
                isReadOnly: this.isDisabled,
                rowActionIndex,
                rowActions,
                activeActionIndex
              }
            })
          )
        )
      )
      .subscribe({
        next: (newRowActions) => this.designerItemModel.setRowActions(newRowActions),
        error: noop
      });
  }

  trackByForRowActions(index: number, rowAction: IRowAction): string {
    return rowAction.label;
  }

  removeRowAction(index: number, rowActions: IRowAction[]): void {
    this.rxModalService
      .confirm({
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
        modalStyle: RX_MODAL.modalStyles.warning,
        message: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.record-grid-row-actions.row-action-delete.warning',
          { name: rowActions[index].label }
        )
      })
      .then((response) => {
        if (response) {
          rowActions.splice(index, 1);
          this.designerItemModel.setRowActions(rowActions);
        }
      });
  }

  focus(data: RecordGridRowActionValidationIssue['data']): void {
    this.openEditor(data.rowActionIndex, data.actionIndex);
  }
}
