import { CdkDrag, CdkDragDrop, CdkDropList, moveItemInArray } from '@angular/cdk/drag-drop';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Injector,
  NgZone,
  QueryList,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import {
  ActiveModalRef,
  AdaptAccordionTabComponent,
  AdaptTreeNodeTyped,
  DismissReasons,
  TreeWrap
} from '@bmc-ux/adapt-angular';
import { IAssociationDescriptor } from '@helix/platform/association/api';
import {
  IFieldDefinition,
  IRecordDefinition,
  RxFieldDefinitionService,
  RxRecordDefinitionCacheService,
  RX_RECORD_DEFINITION
} from '@helix/platform/record/api';
import { IExpressionConfigurator, RxDefinitionNameService, Tooltip } from '@helix/platform/shared/api';
import {
  ExpressionFormControlComponent,
  IExpressionEditorPropertyChange,
  IExpressionFormControlOptions,
  ISelectFormControlOptions,
  IStepperWithUnitsFormControlOptions,
  RxExpressionEditorService
} from '@helix/platform/shared/components';
import { RxModalClass, RxModalService } from '@helix/platform/ui-kit';
import { RxGuidService } from '@helix/platform/utils';
import { ActionListControlComponent, NamedListFilterExpressionConfigurator } from '@helix/platform/view/designer';
import { every, find, findIndex, forEach, get, isEmpty, isNil, remove } from 'lodash';
import { RxRecordGridUtilsService } from '../../../../common/services/record-grid-utils.service';
import { RX_RECORD_GRID } from '../../../../record-grid.constant';
import {
  IColumnEditorAvailableColumn,
  IColumnEditorColumnData,
  IRecordGridColumnEditorModalParams,
  IRecordGridColumnEditorModalResult,
  IRecordGridDesignColumnData
} from '../record-grid-column-editor.types';
import { IAvailableColumnTreeData } from './types/available-column-tree-data.interface';
import { IColumnEditorProperty } from './types/column-editor-property.types';
import { TranslateService } from '@ngx-translate/core';

@Component({
  templateUrl: './record-grid-column-editor-modal.component.html',
  styleUrls: ['./record-grid-column-editor-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RecordGridColumnEditorModalComponent extends RxModalClass implements AfterViewInit {
  columns: IColumnEditorColumnData[];
  recordDefinition: IRecordDefinition;
  columnProperties = RX_RECORD_GRID.columnProperties;
  actionsColumnProperties = RX_RECORD_GRID.actionsColumnProperties;
  filteredColumns: IColumnEditorAvailableColumn[] = [];
  availableColumnsTree: AdaptTreeNodeTyped<IAvailableColumnTreeData>[] = [];
  startedLoadingAssociationDescriptors = false;
  treeWrap: TreeWrap = TreeWrap.WrapAll;
  expressionConfigurator: IExpressionConfigurator;
  isReadOnly: boolean;

  columnWidthPropertyOptions: IStepperWithUnitsFormControlOptions = this.columnProperties.find(
    ({ name }) => name === 'width'
  ).options as IStepperWithUnitsFormControlOptions;

  typeaheadKeystrokeCountOptions: ISelectFormControlOptions = {
    ...this.columnProperties.find(({ name }) => name === 'typeaheadKeystrokeCount').options,
    tooltip: new Tooltip(
      this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid-column.keystroke-count.tooltip'
      )
    )
  } as ISelectFormControlOptions;

  @ViewChildren('draggableSelectedColumn', { read: CdkDrag })
  draggableSelectedColumns: QueryList<CdkDrag<IColumnEditorColumnData>>;

  @ViewChild('selectedColumnsDropList', { read: CdkDropList })
  selectedColumnsDropList: CdkDropList;

  @ViewChildren('draggableAssociatedAvailableColumn', { read: CdkDrag })
  draggableAssociatedAvailableColumns: QueryList<CdkDrag<IColumnEditorColumnData>>;

  @ViewChildren(ActionListControlComponent)
  actionListControlComponents: QueryList<ActionListControlComponent>;

  @ViewChild('associatedAvailableColumnsDropList', { read: CdkDropList })
  associatedAvailableColumnsDropList: CdkDropList;

  @ViewChildren(AdaptAccordionTabComponent, { read: ElementRef })
  private accordionTabEls: QueryList<ElementRef>;

  private readonly activeColumn: IRecordGridDesignColumnData;
  private readonly activeActionIndex: number;

  constructor(
    private activeModalRef: ActiveModalRef,
    private rxModalService: RxModalService,
    private definitionNameService: RxDefinitionNameService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxFieldDefinitionService: RxFieldDefinitionService,
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private rxGuidService: RxGuidService,
    private ngZone: NgZone,
    protected injector: Injector,
    private translateService: TranslateService,
    private rxExpressionEditorService: RxExpressionEditorService
  ) {
    super(activeModalRef, injector);
    const params = this.activeModalRef.getData().columnParams as IRecordGridColumnEditorModalParams;

    this.activeColumn = params.activeColumn;
    this.activeActionIndex = params.activeActionIndex;
    this.recordDefinition = params.recordDefinition;
    this.isReadOnly = params.isReadOnly;

    this.columns = params.columns
      .map((column): IColumnEditorColumnData => {
        const additionalQueryCriteriaExpressionOptions = column.fieldDefinition.namedListDefinition
          ? this.getAdditionalQueryCriteriaExpressionOptions(column)
          : null;

        return {
          ...column,
          isOpen: column === this.activeColumn,
          label: this.rxRecordGridUtilsService.getColumnLabel(column.fieldDefinition, column.associationDescriptor),
          clickable: !isEmpty(column.actions),
          additionalQueryCriteriaExpressionOptions
        };
      })
      .sort((a, b) => a.index - b.index);

    this.getAvailableColumnsTree();
  }

  ngAfterViewInit(): void {
    this.updateSelectedColumnsDropList();
    this.openActiveAction();

    const openGridColumnIndex = findIndex(this.columns, 'isOpen');

    if (openGridColumnIndex !== -1) {
      this.accordionTabEls.toArray()[openGridColumnIndex].nativeElement.scrollIntoView({
        block: 'nearest'
      });
    }
  }

  isActionsColumn(fieldId: string): boolean {
    return fieldId === RX_RECORD_GRID.actionsColumnFieldDefinition.id;
  }

  updateSelectedColumnsDropList(): void {
    this.updateColumnsDropList(this.draggableSelectedColumns, this.selectedColumnsDropList);
  }

  updateAssociatedAvailableColumnsDropList(): void {
    this.updateColumnsDropList(this.draggableAssociatedAvailableColumns, this.associatedAvailableColumnsDropList);
  }

  updateColumnsDropList(
    draggableColumnsList: QueryList<CdkDrag<IColumnEditorColumnData>>,
    dropList: CdkDropList<IColumnEditorColumnData[]>
  ): void {
    // Workaround: update due to injection bug with the adapt-accordion and adapt-tree
    const draggableColumns = draggableColumnsList.toArray();
    const columns = this.columns;

    draggableColumns.sort((column1, column2) => {
      return columns.indexOf(column1.data) - columns.indexOf(column2.data);
    });

    dropList._dropListRef.withItems(
      draggableColumns.map((drag) => {
        const dragRef = drag._dragRef as any;

        if (!dragRef._initialContainer) {
          dragRef._initialContainer = dropList._dropListRef;
        }

        return drag._dragRef;
      })
    );
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  onDropInSelectedColumnsContainer(event: CdkDragDrop<IColumnEditorColumnData[]>) {
    if (event.previousContainer !== event.container && this.isColumnEditorAvailableColumn(event.item.data)) {
      this.addColumn(event.item.data, event.currentIndex);
    } else if (event.previousIndex !== event.currentIndex) {
      this.moveColumn(event.previousIndex, event.currentIndex);
    }
  }

  onColumnCheckboxPropertyChange(
    value: boolean,
    column: IColumnEditorColumnData,
    columnProperty: IColumnEditorProperty
  ) {
    if (columnProperty.name === 'filterable' && column.fieldDefinition.namedListDefinition) {
      if (value) {
        column.typeaheadKeystrokeCount = RX_RECORD_GRID.defaultTypeaheadKeystrokeCount;
      } else {
        column.typeaheadKeystrokeCount = null;
        column.additionalQueryCriteria = null;
      }
    }

    this.markAsDirty();
  }

  onColumnDragStarted(): void {
    this.updateSelectedColumnsDropList();
  }

  onAssociatedAvailableColumnsDragStarted(): void {
    this.onColumnDragStarted();
    this.updateAssociatedAvailableColumnsDropList();
  }

  isColumnEditorAvailableColumn(
    column: IColumnEditorAvailableColumn | IColumnEditorColumnData
  ): column is IColumnEditorAvailableColumn {
    return Boolean((column as IColumnEditorAvailableColumn).fieldDefinitionName);
  }

  moveColumn(fromIndex: number, toIndex: number): void {
    this.markAsDirty();
    moveItemInArray(this.columns, fromIndex, toIndex);
    this.updateColumnIndexes();
  }

  removeColumn(index: number): void {
    this.markAsDirty();
    const column = this.columns[index];

    this.columns.splice(index, 1);

    const associatedAvailableColumnsNode = column.associationDescriptor
      ? this.getAssociatedAvailableColumnsNode(column.associationDescriptor.label)
      : this.availableColumnsTree[0];

    // variable can be null when we want remove column, but associations are not loaded yet
    if (associatedAvailableColumnsNode && associatedAvailableColumnsNode.data.allAvailableColumns) {
      associatedAvailableColumnsNode.data.allAvailableColumns.push(
        this.getColumnMetadata(column.fieldDefinition, column.associationDescriptor)
      );

      this.updateAvailableColumnsTree();
    }
  }

  private getAssociatedAvailableColumnsNode(
    associationDescriptorLabel: string
  ): AdaptTreeNodeTyped<IAvailableColumnTreeData> {
    return this.availableColumnsTree.find(
      (associatedAvailableColumn) =>
        associatedAvailableColumn.data.associationDescriptor &&
        associatedAvailableColumn.data.associationDescriptor.label === associationDescriptorLabel
    );
  }

  getAvailableColumnsTree(): void {
    const recordNode = {
      children: null,
      leaf: false,
      expanded: true,
      expandedIcon: 'd-icon-file_text',
      collapsedIcon: 'd-icon-file_text',
      label: this.definitionNameService.getDisplayName(this.recordDefinition.name),
      key: this.definitionNameService.getDisplayName(this.recordDefinition.name),
      data: {
        allAvailableColumns: null
      }
    };

    if (!this.startedLoadingAssociationDescriptors) {
      this.startedLoadingAssociationDescriptors = true;

      this.rxRecordGridUtilsService
        .getAssociationDescriptors(this.recordDefinition.name)
        .subscribe((associationDescriptors) => {
          associationDescriptors = associationDescriptors.slice();
          associationDescriptors.sort((descriptor1, descriptor2) => descriptor1.label.localeCompare(descriptor2.label));

          this.availableColumnsTree = associationDescriptors.map((associationDescriptor) => {
            return {
              children: null,
              leaf: false,
              expandedIcon: 'd-icon-arrow_schema',
              collapsedIcon: 'd-icon-arrow_schema',
              label: associationDescriptor.label,
              key: associationDescriptor.associationDefinition.name,
              data: {
                associationDescriptor,
                allAvailableColumns: null
              }
            };
          });

          this.availableColumnsTree.unshift(recordNode);

          this.availableColumnsTree.forEach((node) => {
            this.onNodeExpand({ node });
          });
        });
    }
  }

  updateAvailableColumnsTree(): void {
    this.availableColumnsTree.forEach((node) => {
      const allAvailableColumns = node.data.allAvailableColumns;

      if (allAvailableColumns) {
        const availableColumns = this.sortAvailableColumns(allAvailableColumns);
        const definitionName = node.data.associationDescriptor
          ? node.data.associationDescriptor.associationDefinition.name
          : this.recordDefinition.name;

        node.children = availableColumns.map((availableColumn): AdaptTreeNodeTyped<IAvailableColumnTreeData> => {
          return {
            children: null,
            leaf: true,
            label: availableColumn.fieldDefinitionName,
            key: `${definitionName}-${availableColumn.fieldDefinition.id}`,
            data: {
              availableColumn
            }
          };
        });
      }
    });
  }

  onNodeExpand({ node }: { node: AdaptTreeNodeTyped<IAvailableColumnTreeData> }): void {
    const associationDescriptor = node.data.associationDescriptor;

    if (associationDescriptor && !node.data.allAvailableColumns) {
      node.data.allAvailableColumns = [];

      this.rxRecordDefinitionCacheService
        .getRecordDefinition(associationDescriptor.recordDefinitionName)
        .subscribe((recordDefinition) => {
          node.data.allAvailableColumns = this.getAvailableColumns(recordDefinition, associationDescriptor);
          this.updateAvailableColumnsTree();
        });
    } else if (!associationDescriptor) {
      node.data.allAvailableColumns = this.getAvailableColumns(this.recordDefinition);
      this.updateAvailableColumnsTree();
    }
  }

  addColumn(availableColumn: IColumnEditorAvailableColumn, insertIndex?: number) {
    this.markAsDirty();

    if (!find(this.columns, (column) => column.fieldId === availableColumn.fieldId)) {
      const additionalQueryCriteriaExpressionOptions = availableColumn.fieldDefinition.namedListDefinition
        ? this.getAdditionalQueryCriteriaExpressionOptions(availableColumn)
        : null;

      const newColumn = {
        ...this.getColumnEditorColumnData(availableColumn),
        additionalQueryCriteriaExpressionOptions
      };

      if (isNil(insertIndex)) {
        this.columns.push(newColumn);
      } else {
        this.columns.splice(insertIndex, 0, newColumn);
      }

      this.updateColumnIndexes();
      this.removeFromAvailableColumns(availableColumn);

      setTimeout(() => {
        this.accordionTabEls.toArray()[insertIndex ?? this.columns.length - 1].nativeElement.scrollIntoView();
      });
    }
  }

  getColumnEditorColumnData(availableColumn: IColumnEditorAvailableColumn): IColumnEditorColumnData {
    const column: IColumnEditorColumnData = {
      guid: this.rxGuidService.generate(),
      label: availableColumn.label,
      fieldId: availableColumn.fieldId,
      associationDescriptor: availableColumn.associationDescriptor,
      fieldDefinition: availableColumn.fieldDefinition,
      actions: [],
      title: availableColumn.fieldDefinitionName,
      isOpen: true
    };

    forEach(RX_RECORD_GRID.columnProperties, (columnProperty) => {
      if (columnProperty.name === 'sortable') {
        column.sortable = availableColumn.isSortable;
      } else if (columnProperty.name === 'searchable') {
        column.searchable = this.rxRecordGridUtilsService.isSearchable(
          availableColumn.fieldDefinition,
          this.recordDefinition
        );
      } else {
        column[columnProperty.name] = columnProperty.defaultValue;
      }
    });

    return column;
  }

  removeFromAvailableColumns(column: IColumnEditorAvailableColumn): void {
    if (column.associationDescriptor) {
      const associatedAvailableColumnsNode = this.getAssociatedAvailableColumnsNode(column.associationDescriptor.label);

      remove(associatedAvailableColumnsNode.data.allAvailableColumns, column);
    } else {
      remove(this.availableColumnsTree[0].data.allAvailableColumns, column);
    }

    this.updateAvailableColumnsTree();
  }

  sortAvailableColumns(availableColumns: IColumnEditorAvailableColumn[]): IColumnEditorAvailableColumn[] {
    this.filteredColumns = availableColumns.sort((column1, column2) =>
      column1.fieldDefinitionName.localeCompare(column2.fieldDefinitionName)
    );

    return this.filteredColumns;
  }

  getAvailableColumns(
    recordDefinition: IRecordDefinition,
    associationDescriptor?: IAssociationDescriptor
  ): IColumnEditorAvailableColumn[] {
    return recordDefinition.fieldDefinitions
      .map((fieldDefinition) => this.getColumnMetadata(fieldDefinition, associationDescriptor))
      .filter((availableColumn) => !find(this.columns, (column) => column.fieldId === availableColumn.fieldId));
  }

  getColumnMetadata(
    fieldDefinition: IFieldDefinition,
    associationDescriptor?: IAssociationDescriptor
  ): IColumnEditorAvailableColumn {
    return {
      fieldDefinitionName: fieldDefinition.name,
      label: this.rxRecordGridUtilsService.getColumnLabel(fieldDefinition, associationDescriptor),
      fieldId: this.rxRecordGridUtilsService.getFieldIdForGridColumn(fieldDefinition.id, associationDescriptor),
      isSortable: this.rxFieldDefinitionService.isSortable(fieldDefinition),
      associationDescriptor,
      fieldDefinition
    };
  }

  saveChanges(): void {
    const result: IRecordGridColumnEditorModalResult = {
      columns: this.columns.map((column): IRecordGridDesignColumnData => {
        const isFilterableColumnWithNamedList = column.filterable && column.fieldDefinition.namedListDefinition;

        return {
          actions: column.actions,
          alignment: column.alignment,
          fieldId: column.fieldId,
          filterable: column.filterable,
          filterType: column.filterType,
          guid: column.guid,
          index: column.index,
          sortable: column.sortable,
          searchable: column.searchable,
          title: column.title,
          visible: column.visible,
          associationDescriptor: column.associationDescriptor,
          fieldDefinition: column.fieldDefinition,
          cellDisplayProperties: column.cellDisplayProperties,
          namedFilterOptions: column.namedFilterOptions,
          width: column.width,
          wrapText: column.wrapText,
          typeaheadKeystrokeCount: isFilterableColumnWithNamedList ? column.typeaheadKeystrokeCount : null,
          additionalQueryCriteria: isFilterableColumnWithNamedList ? column.additionalQueryCriteria : null
        };
      })
    };

    this.activeModalRef.close(result);
  }

  trackByForColumns(index: number, column: IColumnEditorColumnData): string {
    return column.fieldId;
  }

  trackByForColumnProperties(index: number, columnProperty: IColumnEditorProperty): string {
    return columnProperty.name;
  }

  isPropertyEditable(column: IColumnEditorColumnData, propertyName: string) {
    if (propertyName === 'searchable') {
      return (
        !this.isReadOnly && this.rxRecordGridUtilsService.isSearchable(column.fieldDefinition, this.recordDefinition)
      );
    } else {
      return !this.isReadOnly;
    }
  }

  private updateColumnIndexes(): void {
    this.columns.forEach((column, index) => {
      column.index = index;
    });
  }

  private openActiveAction(): void {
    if (this.activeColumn && this.activeActionIndex >= 0) {
      this.ngZone.runOutsideAngular(() => {
        // timeout is required to let control set initial value
        setTimeout(() => {
          const listControl = this.actionListControlComponents.find(
            (control) => control.value === this.activeColumn.actions
          );

          if (listControl) {
            listControl.focus({ actionIndex: this.activeActionIndex });
          }
        });
      });
    }
  }

  isTreeEmpty(): boolean {
    return every(this.availableColumnsTree, (node) => node.data.allAvailableColumns.length === 0);
  }

  openAdditionalQueryCriteriaExpressionEditor(
    column: IColumnEditorColumnData,
    control: ExpressionFormControlComponent
  ): void {
    const namedListFilterExpressionConfigurator = new NamedListFilterExpressionConfigurator(
      column.fieldDefinition,
      this.injector
    );

    this.rxExpressionEditorService
      .openEditor({
        property: {
          path: 'additionalQueryCriteria',
          value: column.additionalQueryCriteria,
          label: 'Additional named list filter'
        },
        isReadOnly: this.isReadOnly,
        expressionConfigurator: namedListFilterExpressionConfigurator,
        legend: [
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.function.label'),
            icon: 'd-icon-mathematical_function'
          },
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.keyword.label'),
            icon: 'd-icon-dollar'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-component.label'
            ),
            icon: 'd-icon-file_o'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-component-property.label'
            ),
            icon: 'd-icon-file_o_gear'
          },
          {
            label: this.translateService.instant(
              'com.bmc.arsys.rx.client.expression-editor.legend.view-input-parameter.label'
            ),
            icon: 'd-icon-arrow_right_square_input'
          }
        ]
      })
      .subscribe((expression: IExpressionEditorPropertyChange) => {
        column.additionalQueryCriteria = expression.value;
        control.value = expression.value;
        control.onWriteValue(expression.value);
        this.markAsDirty();
      });
  }

  getAdditionalQueryCriteriaExpressionOptions(column: IRecordGridDesignColumnData): IExpressionFormControlOptions {
    const namedListFilterExpressionConfigurator = new NamedListFilterExpressionConfigurator(
      column.fieldDefinition,
      this.injector
    );

    return {
      label: 'Additional named list filter',
      tooltip: new Tooltip(
        'Build a filter to apply at runtime in addition to the filter, if any, specified in the named list definition.'
      ),
      dataDictionary$: namedListFilterExpressionConfigurator.getDataDictionary(),
      operators: namedListFilterExpressionConfigurator.getOperators()
    };
  }
}
