import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { Observable, of } from 'rxjs';
import { IRecordGridColumn } from '../../../../record-grid/runtime/types/record-grid-column.types';
import { IRecordGridConfig } from '../../../../record-grid/runtime/types/record-grid-config.interface';
import { RecordGridComponent } from '../../../../record-grid/runtime/record-grid.component';
import { RowSelectionMode } from '../../../../record-grid/runtime/types/row-selection-mode.enum';
import { IRecordDefinition, RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IDataPageResult } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { IShareViewPresetUserPayload } from '../share-view-preset.types';

@Component({
  selector: 'rx-share-view-preset-users-grid',
  templateUrl: './share-view-preset-users-grid.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ShareViewPresetUsersGridComponent implements OnChanges {
  @ViewChild(RecordGridComponent, { static: true })
  recordGrid: RecordGridComponent;

  @Input()
  users: IShareViewPresetUserPayload[] = [];

  @Output()
  remove = new EventEmitter<string[]>();

  recordGridConfig$: Observable<IRecordGridConfig> = this.getRecordGridConfig();

  constructor(private translateService: TranslateService) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.users.currentValue && !changes.users.firstChange) {
      this.recordGrid.api.refresh().subscribe();
    }
  }

  private getRecordGridConfig(): Observable<IRecordGridConfig> {
    const gridColumns: IRecordGridColumn[] = [
      {
        fieldId: 'fullName',
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.grid.column.name.title'
        )
      },
      {
        fieldId: 'emailAddress',
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.grid.column.email.title'
        )
      }
    ];

    const gridRecordDefinition: IRecordDefinition = {
      fieldDefinitions: [
        {
          id: 'fullName',
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        },
        {
          id: 'emailAddress',
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        }
      ]
    };

    return of({
      columns: gridColumns,
      enableFiltering: false,
      enableRowSelection: RowSelectionMode.Multiple,
      recordIdField: 'loginId',
      styles: 'flex-fill',
      toolbarConfig: {
        filter: false,
        visibleColumnsMenu: false
      },
      useExternalFiltering: false,
      actionButtons: [
        {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.remove.label'),
          style: 'tertiary',
          iconCls: 'minus_circle_o',
          disabled: () => this.recordGrid.api.getSelectedRows().length === 0,
          actions: [
            {
              name: () => {
                const selectedUserLoginIds = this.recordGrid.api.getSelectedRows().map((row) => row.loginId);

                this.remove.emit(selectedUserLoginIds);
              }
            }
          ]
        }
      ],
      getRecordDefinition: () => of(gridRecordDefinition),
      getData: (): Observable<IDataPageResult> =>
        of({
          data: this.users,
          totalSize: this.users.length
        })
    });
  }
}
