import {
  getDisabledFieldInspectorConfig,
  getStandardPropsInspectorConfigs,
  IViewComponentDesignData,
  IViewComponentDesignSandbox,
  IViewComponentDesignSetPayload,
  IViewComponentDesignValidationIssue,
  IViewDesignerInspectorConfig,
  validateStandardProps,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { IValidationFormControlOptions, ValidationFormControlComponent } from '@helix/platform/shared/components';
import { Injector } from '@angular/core';
import { catchError, distinctUntilChanged, map, shareReplay, switchMap, take, withLatestFrom } from 'rxjs/operators';
import { filter as _filter, find, flow, isEmpty, map as _map, omit, pick, reject, sortBy, uniqBy } from 'lodash';
import { combineLatest, EMPTY, Observable, of } from 'rxjs';
import { FieldSetFormControlComponent } from './field-set-form-control.component';
import {
  RX_AVAILABLE_ON_DEVICES_PROP_NAME,
  RX_DISABLED_PROP_DEFAULT_VALUE,
  RX_STANDARD_PROPS_DEFAULT_VALUES,
  RxViewComponentType
} from '@helix/platform/view/api';
import {
  IFieldDefinition,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxFieldDefinitionService,
  RxRecordDefinitionCacheService
} from '@helix/platform/record/api';
import { RxSelectOption } from '@bmc-ux/adapt-angular';
import {
  ISelectGroupFieldProperties,
  ISelectGroupFormControlOptions,
  ISelectGroupProperties
} from './select-group-design.types';
import { RecordEditorMode } from '../../../record-editor/common/record-editor.types';
import { IRecordEditorProperties } from '../../../record-editor/design/record-editor-design.types';

export class SelectGroupDesignModel extends ViewDesignerComponentModel {
  rxRecordDefinitionCacheService = this.injector.get<RxRecordDefinitionCacheService>(RxRecordDefinitionCacheService);
  rxFieldDefinitionService = this.injector.get<RxFieldDefinitionService>(RxFieldDefinitionService);

  recordEditorGuid$ = this.sandbox
    .getParentComponentGuid(RxViewComponentType.RecordEditor)
    .pipe(take(1), shareReplay(1));

  recordEditorMode$ = this.recordEditorGuid$.pipe(
    switchMap((recordEditorGuid) => {
      return this.sandbox.getComponentPropertyValue<'mode', IRecordEditorProperties>('mode', recordEditorGuid);
    })
  );

  recordDefinitionName$ = this.recordEditorGuid$.pipe(
    switchMap((recordEditorGuid) => {
      return this.sandbox.getComponentPropertyValue<'recordDefinitionName', IRecordEditorProperties>(
        'recordDefinitionName',
        recordEditorGuid
      );
    }),
    shareReplay(1)
  );

  fieldDefinitions$ = this.recordDefinitionName$.pipe(
    switchMap((recordDefinitionName: string) => {
      if (recordDefinitionName) {
        return this.rxRecordDefinitionCacheService
          .getRecordDefinition(recordDefinitionName)
          .pipe(catchError((error) => EMPTY));
      } else {
        return of(null);
      }
    }),
    distinctUntilChanged(),
    map<IRecordDefinition, IFieldDefinition[]>((recordDefinition: IRecordDefinition) => {
      if (recordDefinition && recordDefinition.fieldDefinitions) {
        return _filter(recordDefinition.fieldDefinitions, {
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        });
      } else {
        return [];
      }
    }),
    shareReplay(1)
  );

  inspectorTargetFieldOptions$ = combineLatest([this.fieldDefinitions$, this.recordEditorMode$]).pipe(
    map(([fieldDefinitions, recordEditorMode]) => [
      _filter(fieldDefinitions, { resourceType: RX_RECORD_DEFINITION.resourceTypes.character }),
      recordEditorMode
    ]),
    map<[IFieldDefinition[], RecordEditorMode], RxSelectOption[]>(([characterFieldDefinitions, recordEditorMode]) =>
      flow(
        (fieldDefinitions) =>
          _filter(fieldDefinitions, (definition) => {
            const isSystemField = this.rxFieldDefinitionService.isSystemField(definition);

            return !isSystemField || (isSystemField && recordEditorMode === RecordEditorMode.Edit);
          }),
        (fieldDefinitions) =>
          _map(fieldDefinitions, (definition) => ({
            name: definition.name,
            id: String(definition.id)
          })),
        (options) => sortBy(options, 'name')
      )(characterFieldDefinitions)
    ),
    shareReplay(1)
  );

  fieldsDesignData$ = this.sandbox.getChildComponents<ISelectGroupFieldProperties>();

  fieldProperties$: Observable<ISelectGroupFieldProperties[]> = this.fieldsDesignData$.pipe(
    map((fieldComponentsDesignData) =>
      _map(fieldComponentsDesignData, (field) => ({
        ...field.data,
        guid: field.guid
      }))
    )
  );

  static getInitialProperties(initialProperties?: ISelectGroupProperties): ISelectGroupProperties {
    return {
      ...RX_DISABLED_PROP_DEFAULT_VALUE,
      ...RX_STANDARD_PROPS_DEFAULT_VALUES,
      ...initialProperties
    };
  }

  constructor(public injector: Injector, protected sandbox: IViewComponentDesignSandbox<ISelectGroupProperties>) {
    super(injector, sandbox);

    // initial inspector configuration and component properties set
    combineLatest([this.recordDefinitionName$, this.inspectorTargetFieldOptions$, this.recordEditorGuid$])
      .pipe(take(1))
      .subscribe(([recordDefinitionName, inspectorTargetFieldOptions, recordEditorGuid]) => {
        this.sandbox.updateInspectorConfig(
          this.getBaseInspectorConfig(inspectorTargetFieldOptions, recordDefinitionName, recordEditorGuid)
        );
      });

    this.inspectorTargetFieldOptions$
      .pipe(withLatestFrom(this.recordDefinitionName$, this.recordEditorGuid$))
      .subscribe(([inspectorTargetFieldOptions, recordDefinitionName, recordEditorGuid]) => {
        this.sandbox.updateInspectorConfig(
          this.getBaseInspectorConfig(inspectorTargetFieldOptions, recordDefinitionName, recordEditorGuid)
        );
      });

    combineLatest([this.sandbox.componentProperties$, this.fieldProperties$])
      .pipe(switchMap(([componentProperties, fieldProperties]) => this.validate(componentProperties, fieldProperties)))
      .subscribe((validationIssues: IViewComponentDesignValidationIssue[]) => {
        this.sandbox.setValidationIssues(validationIssues);
      });
  }

  onFieldPropertiesChange(fieldProperties: ISelectGroupFieldProperties[]): void {
    const fieldComponentPayloads = this.getFieldComponentPayloads(fieldProperties);

    this.sandbox.setChildren(fieldComponentPayloads);
  }

  isFieldRequired(field: IViewComponentDesignData<ISelectGroupFieldProperties>): Observable<boolean> {
    return this.fieldDefinitions$.pipe(
      map((fieldDefinitions) => {
        const fieldDefinition = field.data.fieldId && find(fieldDefinitions, { id: Number(field.data.fieldId) });

        return Boolean(fieldDefinition) && fieldDefinition.fieldOption === RX_RECORD_DEFINITION.fieldOptions.required;
      })
    );
  }

  getPropertiesByName(properties) {
    return pick(properties, ['disabled', 'hidden', 'styles', RX_AVAILABLE_ON_DEVICES_PROP_NAME]);
  }

  private validate(
    componentProperties: ISelectGroupProperties,
    fieldProperties: ISelectGroupFieldProperties[]
  ): Observable<IViewComponentDesignValidationIssue[]> {
    const validationErrorMessages: string[] = [];

    if (isEmpty(fieldProperties)) {
      validationErrorMessages.push('Field set cannot be empty.');
    } else {
      const fieldsWithLabel = _filter(fieldProperties, (model) => model.label);

      if (fieldsWithLabel.length !== fieldProperties.length) {
        validationErrorMessages.push('Display label cannot be blank.');
      }

      if (uniqBy(fieldsWithLabel, 'label').length !== fieldsWithLabel.length) {
        validationErrorMessages.push('Display labels must be unique.');
      }

      if (reject(fieldProperties, (model) => model.namedListDefinitionName).length) {
        validationErrorMessages.push('Named list for options cannot be blank.');
      }

      if (reject(fieldProperties, (model) => model.fieldId).length) {
        validationErrorMessages.push('Field for storing selected option value cannot be blank.');
      }
    }

    const validationIssues = validationErrorMessages
      .map((message) => this.sandbox.createError(message))
      .concat(validateStandardProps(componentProperties));

    return of(validationIssues);
  }

  private getFieldComponentPayloads(fieldProperties: ISelectGroupFieldProperties[]): IViewComponentDesignSetPayload[] {
    return _map(fieldProperties, (model) => ({
      type: RxViewComponentType.SelectGroupField,
      guid: model.guid,
      data: omit(model, 'guid')
    }));
  }

  private getBaseInspectorConfig(
    targetFieldOptions: RxSelectOption[],
    recordDefinitionName: string,
    recordEditorGuid: string
  ): IViewDesignerInspectorConfig {
    const generalGroup = {
      label: 'General',
      controls: []
    };

    if (recordDefinitionName) {
      generalGroup.controls = [getDisabledFieldInspectorConfig(), ...getStandardPropsInspectorConfigs()];
    } else {
      generalGroup.controls = [
        {
          component: ValidationFormControlComponent,
          options: {
            text: 'You must select a Record definition before editing field component.',
            propertyName: 'recordDefinitionName',
            componentGuid: recordEditorGuid
          } as IValidationFormControlOptions
        }
      ];
    }

    return {
      inspectorSectionConfigs: recordDefinitionName
        ? [
            generalGroup,
            {
              label: 'Field set',
              controls: [
                {
                  component: FieldSetFormControlComponent,
                  options: {
                    targetFieldOptions: targetFieldOptions
                  } as ISelectGroupFormControlOptions
                }
              ]
            }
          ]
        : [generalGroup]
    };
  }
}
