import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { AdaptRxListBuilderComponent, RxListBuilderItem } from '@bmc-ux/adapt-angular';
import { InspectorWidgetBase } from '@helix/platform/shared/components';
import { ViewPresetSelectorDesignModel } from '../view-preset-selector-design.model';
import { IFormFocusable, IFormWidgetComponent, IPlainObject } from '@helix/platform/shared/api';
import { RxViewComponentType } from '@helix/platform/view/api';
import { IViewComponentDesignSetPayload } from '@helix/platform/view/designer';
import { IViewPresetProperties } from '../../view-preset-selector.types';
import { first, takeUntil } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { chain, cloneDeep, trim } from 'lodash';

@Component({
  selector: 'rx-presets-list-widget',
  templateUrl: './presets-list-widget.component.html',
  styleUrls: ['./presets-list-widget.component.scss']
})
export class PresetsListWidgetComponent
  extends InspectorWidgetBase<IPlainObject, ViewPresetSelectorDesignModel>
  implements OnInit, IFormWidgetComponent, IFormFocusable
{
  private editedPreset: RxListBuilderItem = null;

  presets: RxListBuilderItem[] = [];

  isDisabled: boolean;

  @ViewChild(AdaptRxListBuilderComponent)
  adaptRxListBuilderComponent: AdaptRxListBuilderComponent;

  private strings = {
    cannotBeBlank: this.translateService.instant(
      'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
      {
        propertyName: 'View preset name'
      }
    ),
    duplicateValue: this.translateService.instant('com.bmc.arsys.rx.client.designer.validation.duplicate-value.message')
  };

  itemValidationFn = this.itemValidation.bind(this);

  constructor(protected injector: Injector, private translateService: TranslateService) {
    super(injector);
  }

  ngOnInit(): void {
    this.designerItemModel.sandbox
      .getChildComponents()
      .pipe(first(), takeUntil(this.designerItemModel.sandbox.destroyed$))
      .subscribe((res) => {
        res.sort((a, b) => a.data.index - b.data.index);

        this.presets = res.map((component) => ({
          name: component.data.label,
          id: component.guid
        }));
      });
  }

  onPresetsListChange(items: RxListBuilderItem[]) {
    items.forEach((item) => {
      item.name = trim(item.name);
    });

    this.presets = cloneDeep(items);

    const payload: IViewComponentDesignSetPayload<IViewPresetProperties>[] = items.map(({ name, id }, index) => ({
      type: RxViewComponentType.ViewPreset,
      guid: String(id),
      data: {
        label: name,
        index
      }
    }));

    this.designerItemModel.sandbox.setChildren(payload);
  }

  onListItemAdd(): void {
    this.presets.push(this.presets.shift());
  }

  onListItemEdit(preset: RxListBuilderItem): void {
    this.editedPreset = preset;
  }

  onListItemUpdate(): void {
    this.editedPreset = null;
  }

  focus(): void {
    this.adaptRxListBuilderComponent.inputEl.nativeElement.focus();
  }

  private itemValidation(value: string, items: RxListBuilderItem[], isEdit: boolean): string {
    let errorMessage: string = null;

    const otherItemNames = chain(items)
      .reject((item) => isEdit && item.id === this.editedPreset.id)
      .map('name')
      .value();

    const trimmedValue = trim(value);

    if (trimmedValue.length === 0) {
      errorMessage = this.strings.cannotBeBlank;
    } else if (otherItemNames.includes(trimmedValue)) {
      errorMessage = this.strings.duplicateValue;
    }

    return errorMessage;
  }
}
