import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { RxSelectOption } from '@bmc-ux/adapt-angular';
import { RX_APPLICATION, RxNotificationService } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { RX_GAINSIGHT, RxGainsightConfiguratorService } from '@helix/platform/shared/components';
import { BaseViewComponent } from '@helix/platform/view/runtime';
import { map, take, shareReplay, switchMap, filter } from 'rxjs/operators';

@Component({
  selector: 'rx-gainsight-admin-opt-in',
  templateUrl: './gainsight-admin-opt-in.component.html',
  styleUrls: ['./gainsight-admin-opt-in.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GainsightAdminOptInComponent extends BaseViewComponent implements OnInit {
  deploymentTypeOptions: RxSelectOption[] = RX_GAINSIGHT.deploymentTypes;
  environmentTypeOptions: string[] = RX_GAINSIGHT.environmentTypes;
  deploymentType: string[];
  environmentType: string[];

  vm$ = this.rxGainsightConfiguratorService.getGainsightConfiguration(RX_APPLICATION.innovationStudioBundleId).pipe(
    take(1),
    filter((config) => Boolean(config)),
    map((config) => config.settings),
    map((settings) => ({
      deploymentTypeOptions: this.deploymentTypeOptions.filter(
        (deploymentType) => deploymentType.name === settings.deploymentType
      ),
      environmentTypeOptions: [settings.environmentType],
      useAdaptRadar: settings.useAdaptRadar,
      loadGainsightFromBmcIt: settings.loadGainsightFromBmcIt,
      adaptAgreementState: {
        organizationPerformance: settings.enableGainsight,
        accountMarketing: false,
        accountPerformance: false,
        organizationMarketing: false
      }
    })),
    shareReplay(1)
  );

  constructor(
    private translateService: TranslateService,
    private rxNotificationService: RxNotificationService,
    private rxGainsightConfiguratorService: RxGainsightConfiguratorService
  ) {
    super();
  }

  optionFormatter(deploymentType: RxSelectOption): string {
    return deploymentType.name;
  }

  save(): void {
    this.vm$
      .pipe(
        take(1),
        map((vm) => ({
          ...vm,
          deploymentType: vm.deploymentTypeOptions[0].id,
          environmentType: vm.environmentTypeOptions[0],
          enableGainsight: vm.adaptAgreementState.organizationPerformance
        })),
        switchMap((gainsightSettings) =>
          this.rxGainsightConfiguratorService.saveGainsightConfiguration(gainsightSettings)
        )
      )
      .subscribe(() => {
        this.rxNotificationService.addSuccessMessage(
          this.translateService.instant('com.bmc.arsys.rx.client.gainsight.gainsight-settings-saved.success.message')
        );
      });
  }
}
