import { Injectable } from '@angular/core';

import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { IContainerViewComponentDefinition } from '@helix/platform/view/api';

@Injectable()
export class ViewPresetSelectorAdapterService implements IDefinitionAdapter<IContainerViewComponentDefinition> {
  adaptDefinition(viewPresetSelectorComponentDefinition: IContainerViewComponentDefinition): void {
    viewPresetSelectorComponentDefinition.componentDefinitions
      .sort((a, b) => a.propertiesByName.index - b.propertiesByName.index)
      .forEach((componentDefinition) => {
        Object.assign(componentDefinition.propertiesByName, {
          guid: componentDefinition.guid,
          isSystem: true
        });
      });
  }
}
