import { Injectable } from '@angular/core';
import { IRecordGridColumnPublicProperties, IRxRecordGridApi, RecordsSource } from '@helix/platform/view/api';
import { constant, forEach, find, get, includes, isFunction, isUndefined, some, sortBy } from 'lodash';
import { AsyncSubject, forkJoin, Observable, of } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';

import {
  IAssociationDefinition,
  IAssociationDescriptor,
  IAssociationNode,
  RX_ASSOCIATED_RECORD_NODE_SIDES,
  RX_ASSOCIATION_DEFINITION
} from '@helix/platform/association/api';
import {
  IFieldDefinition,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxRecordDefinitionCacheService
} from '@helix/platform/record/api';
import { RxDefinitionNameService, RxLogService } from '@helix/platform/shared/api';
import { RxStringService } from '@helix/platform/utils';

import { IColumnDescriptor } from '../types/column-descriptor.interface';
import { IFieldLocator } from '../types/field-locator.interface';
import { RX_RECORD_GRID } from '../../record-grid.constant';
import { IRecordGridFilterTag, ISharedFilterPresetTagModel } from '../types/record-grid-filter.types';
import { TagModel } from '@bmc-ux/adapt-angular';
import { RxFieldDefinitionService } from '@helix/platform/record/api';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridUtilsService {
  constructor(
    private rxLogService: RxLogService,
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private stringService: RxStringService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxFieldDefinitionService: RxFieldDefinitionService
  ) {}

  getColumnDescriptors(recordDefinitionName: string, source: RecordsSource): Promise<IColumnDescriptor[]> {
    return this.rxRecordDefinitionCacheService
      .getRecordDefinition(recordDefinitionName)
      .toPromise()
      .then((recordDefinition: IRecordDefinition) => {
        if (isFunction((<IRxRecordGridApi>source).getColumns)) {
          const columns: IColumnDescriptor[] = (<IRxRecordGridApi>source)
            .getColumns()
            .map((column: IRecordGridColumnPublicProperties, index) => ({
              fieldId: column.field,
              index,
              title: column.header,
              fieldDefinition: column.fieldDefinition
            }));

          return Promise.all(
            columns
              .filter((column) => this.isAssociatedRecordFieldId(column.fieldId))
              .map((column) =>
                this.getAssociationDescriptor(column.fieldId, recordDefinition)
                  .toPromise()
                  .then((associationDescriptor) => {
                    column.associationDescriptor = associationDescriptor;
                  })
              )
          ).then(constant(columns));
        } else {
          return recordDefinition.fieldDefinitions.map<IColumnDescriptor>(
            (fieldDefinition: IFieldDefinition, index: number) => ({
              fieldId: <string>fieldDefinition.id,
              index,
              title: fieldDefinition.name,
              fieldDefinition: fieldDefinition
            })
          );
        }
      });
  }

  getAssociationDescriptors(recordDefinitionName: string): Observable<IAssociationDescriptor[]> {
    return forkJoin([
      this.rxRecordDefinitionCacheService.getRecordAssociationDefinitions(recordDefinitionName),
      this.rxRecordDefinitionCacheService.getRecordDefinition(recordDefinitionName)
    ]).pipe(
      map(([associations, recordDefinition]) => {
        const associationDescriptors = [];

        forEach(associations[recordDefinitionName], (associationDefinition) => {
          const isOneToMany =
            associationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.oneToMany.value;

          const isManyToManyAssociation =
            associationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.manyToMany.value;

          if (!isManyToManyAssociation) {
            if (this.isNeedSide(associationDefinition.nodeBId, recordDefinitionName, recordDefinition)) {
              associationDescriptors.push({
                associationDefinition: associationDefinition,
                recordDefinitionName: associationDefinition.nodeAId,
                nodeSide: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.value,
                label: this.getAssociationLabel(associationDefinition, RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA)
              });
            }

            if (
              this.isNeedSide(associationDefinition.nodeAId, recordDefinitionName, recordDefinition) &&
              !isOneToMany
            ) {
              associationDescriptors.push({
                associationDefinition: associationDefinition,
                recordDefinitionName: associationDefinition.nodeBId,
                nodeSide: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.value,
                label: this.getAssociationLabel(associationDefinition, RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB)
              });
            }
          }
        });

        return sortBy(associationDescriptors, 'label');
      })
    );
  }

  getFieldDefinition(
    gridColumnFieldId: string,
    primaryRecordDefinition: IRecordDefinition
  ): Observable<IFieldDefinition> {
    const locator = this.getFieldLocator(gridColumnFieldId);
    const fieldDefinition$ = new AsyncSubject<IFieldDefinition>();
    let recordDefinitionPromise: Promise<IRecordDefinition>;

    if (locator.fieldId === RX_RECORD_GRID.actionsColumnFieldDefinition.id) {
      return of(RX_RECORD_GRID.actionsColumnFieldDefinition);
    } else {
      if (locator.associationGuid) {
        recordDefinitionPromise = this.getAssociationDescriptors(primaryRecordDefinition.name)
          .pipe(
            map((associationDescriptors) => this.findAssociationDescriptor(associationDescriptors, locator)),
            filter((associationDescriptor) => Boolean(associationDescriptor)),
            map(
              (associationDescriptor) =>
                associationDescriptor.associationDefinition[
                  RX_ASSOCIATED_RECORD_NODE_SIDES[associationDescriptor.nodeSide].recordDefinitionNameKey
                ]
            ),
            switchMap((recordDefinitionName) => {
              return this.rxRecordDefinitionCacheService.getRecordDefinition(recordDefinitionName);
            })
          )
          .toPromise();
      } else {
        recordDefinitionPromise = Promise.resolve(primaryRecordDefinition);
      }

      recordDefinitionPromise.then((recordDefinition) => {
        let fieldDefinition: IFieldDefinition;

        if (recordDefinition) {
          fieldDefinition = find(recordDefinition.fieldDefinitions, {
            id: Number(locator.fieldId) || locator.fieldId
          });
        }

        fieldDefinition$.next(fieldDefinition);
        fieldDefinition$.complete();
      });

      return fieldDefinition$;
    }
  }

  isAssociatedRecordFieldId(fieldId: string): boolean {
    return !some(this.getFieldLocator(fieldId), isUndefined);
  }

  getFieldIdForGridColumn(fieldId: string | number, associationDescriptor?: IAssociationDescriptor): string {
    return associationDescriptor
      ? `recordContext._associations.${associationDescriptor.associationDefinition.guid}.${associationDescriptor.nodeSide}[0].${fieldId}`
      : String(fieldId);
  }

  getAssociationDescriptor(
    gridColumnFieldId: string,
    primaryRecordDefinition: IRecordDefinition
  ): Observable<IAssociationDescriptor> {
    const locator = this.getFieldLocator(gridColumnFieldId);
    let associationDescriptor = of(null);

    if (locator.associationGuid) {
      associationDescriptor = this.getAssociationDescriptors(primaryRecordDefinition.name).pipe(
        map((associationDescriptors) => this.findAssociationDescriptor(associationDescriptors, locator))
      );
    }

    return associationDescriptor;
  }

  getDisplayFieldDescriptor(
    fieldId: string,
    primaryRecordDefinition: IRecordDefinition,
    associatedRecordDefinitionName: string
  ): Observable<IFieldDefinition> {
    const fieldLocator = this.getFieldLocator(fieldId);

    const fieldDefinition$ = this.isAssociatedRecordFieldId(fieldId)
      ? this.rxRecordDefinitionCacheService.getRecordDefinition(associatedRecordDefinitionName).pipe(
          map((associatedRecordDefinition) => {
            const isCoreIdField = fieldLocator.fieldId === String(RX_RECORD_DEFINITION.coreFieldIds.id);

            const displayFieldId = String(
              (isCoreIdField && associatedRecordDefinition.displayFieldIdInAssociation) || fieldLocator.fieldId
            );

            return find(
              associatedRecordDefinition.fieldDefinitions,
              (fieldDefinition) => String(fieldDefinition.id) === displayFieldId
            );
          })
        )
      : of(find(primaryRecordDefinition.fieldDefinitions, (fieldDefinition) => String(fieldDefinition.id) === fieldId));

    return fieldDefinition$;
  }

  getColumnLabel(fieldDefinition: IFieldDefinition, associationDescriptor?: IAssociationDescriptor): string {
    return associationDescriptor ? `${associationDescriptor.label} > ${fieldDefinition.name}` : fieldDefinition.name;
  }

  findAssociationDescriptor(
    associationDescriptors: IAssociationDescriptor[],
    locator: IFieldLocator
  ): IAssociationDescriptor {
    return find(associationDescriptors, (associationDescriptor: IAssociationDescriptor) => {
      return (
        associationDescriptor.associationDefinition.guid === locator.associationGuid &&
        associationDescriptor.nodeSide === locator.associationNodeSide
      );
    });
  }

  getFieldLocator(gridColumnFieldId: string): IFieldLocator {
    const match = new RegExp('(?:' + RX_RECORD_GRID.associatedFieldIdPrefixPattern + ')?(.+)').exec(gridColumnFieldId);

    return {
      associationGuid: match[1],
      associationNodeSide: match[2],
      fieldId: match[3]
    };
  }

  private isNeedSide(sideName: string, recordDefinitionName: string, recordDefinition: IRecordDefinition): boolean {
    return includes([recordDefinitionName, get(recordDefinition, 'inheritanceDescriptor.inheritingFrom')], sideName);
  }

  private getAssociationLabel(associationDefinition: IAssociationDefinition, nodeSide: IAssociationNode): string {
    let nodeSideName: string;
    let label = this.rxDefinitionNameService.getDisplayName(associationDefinition.name);

    if (associationDefinition.nodeAId === associationDefinition.nodeBId) {
      if (nodeSide === RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA) {
        nodeSideName = associationDefinition.nodeAName;
      }

      if (nodeSide === RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB) {
        nodeSideName = associationDefinition.nodeBName;
      }

      nodeSideName = nodeSideName || nodeSide.defaultName;

      label += ' (' + nodeSideName + ')';
    }

    return label;
  }

  isSharedFilterPresetTag(tag: IRecordGridFilterTag): tag is TagModel<ISharedFilterPresetTagModel> {
    return (tag.data as ISharedFilterPresetTagModel).isSharedFilterPreset;
  }

  getTestIdForGridColumn(fieldId: string): string {
    if (this.isAssociatedRecordFieldId(fieldId)) {
      const fieldLocator = this.getFieldLocator(fieldId);
      return `field-id-${fieldLocator.associationGuid}-${fieldLocator.fieldId}`;
    } else {
      return `field-id-${fieldId}`;
    }
  }

  isSearchable(fieldDefinition: IFieldDefinition, recordDefinition?: IRecordDefinition): boolean {
    return (
      includes(
        [
          RX_RECORD_DEFINITION.dataTypes.character.resourceType,
          RX_RECORD_DEFINITION.dataTypes.localizedCharacter.resourceType,
          RX_RECORD_DEFINITION.dataTypes.integer.resourceType,
          RX_RECORD_DEFINITION.dataTypes.decimal.resourceType,
          RX_RECORD_DEFINITION.dataTypes.real.resourceType
        ],
        fieldDefinition.resourceType
      ) &&
      !this.rxFieldDefinitionService.isPassword(fieldDefinition) &&
      !this.rxFieldDefinitionService.isSecured(fieldDefinition) &&
      !this.rxFieldDefinitionService.isDataProviderIdField(fieldDefinition, recordDefinition)
    );
  }
}
