import { ChangeDetectionStrategy, Component, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { RxViewComponentType } from '@helix/platform/view/api';
import { IViewComponentDropData } from '@helix/platform/view/designer';
import { isEqual, isObject, sortBy } from 'lodash';
import { SortEvent } from 'primeng/api';
import { Observable } from 'rxjs';
import { map, shareReplay, take } from 'rxjs/operators';
import { ActionButtonStyle, IActionButtonProperties } from '../../action-button/action-button.types';
import { RxRecordGridUtilsService } from '../common/services/record-grid-utils.service';
import { RxRecordGridConfigUtilsService } from '../runtime/services/record-grid-config-utils.service';
import { IAdaptTableColumnConfig } from '../runtime/types/record-grid-column.types';
import { ColumnSortDirection, IRecordGridSortableObject } from '../common/types/record-grid.types';
import { IRecordGridSortEditorModel } from './editors/record-grid-sort-editor-control';
import { RecordGridDesignModel } from './record-grid-design.model';
import { IRecordGridDesignAdaptTableConfig } from './record-grid-design.types';

@Component({
  selector: 'rx-record-grid-design',
  templateUrl: './record-grid-design.component.html',
  styleUrls: ['./record-grid-design.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RecordGridDesignComponent implements OnInit {
  @Input()
  model: RecordGridDesignModel;

  @ViewChild('filterTemplate', { static: true })
  filterTemplate: TemplateRef<any>;

  @ViewChild('visibleColumnsMenuTemplate', { static: true })
  visibleColumnsMenuTemplate: TemplateRef<any>;

  @Input()
  isReadOnly: boolean;

  adaptTableConfig$: Observable<IRecordGridDesignAdaptTableConfig>;
  hasRecordDefinitionName$: Observable<boolean>;

  constructor(
    private rxRecordGridConfigUtilsService: RxRecordGridConfigUtilsService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService
  ) {}

  ngOnInit(): void {
    this.adaptTableConfig$ = this.model.columns$.pipe(
      map((columns) => {
        const gridColumns = sortBy(columns, (column) => column.index);
        const initialSortGridColumn = gridColumns.find((gridColumn) => isObject(gridColumn.sortable));

        let sortMeta = {
          field: null,
          order: null
        };

        if (initialSortGridColumn) {
          const gridSortableObject = initialSortGridColumn.sortable as IRecordGridSortableObject;

          sortMeta = {
            field: String(initialSortGridColumn.fieldId),
            order: this.rxRecordGridConfigUtilsService.getColumnSortOrder(gridSortableObject.direction)
          };
        }

        return {
          columns: gridColumns.map(
            (gridColumn): IAdaptTableColumnConfig => ({
              field: gridColumn.fieldId,
              header: gridColumn.title,
              sortable: Boolean(gridColumn.sortable),
              hidden: !gridColumn.visible,
              testId: this.rxRecordGridUtilsService.getTestIdForGridColumn(gridColumn.fieldId)
            })
          ),
          sortMeta,
          toolbarConfig: {
            counter: false,
            quickFilter: true,
            filter: true,
            filterTemplate: this.filterTemplate,
            filterResults: false,
            visibleColumnsMenu: true,
            visibleColumnsMenuTemplate: this.visibleColumnsMenuTemplate,
            quickFilterTriggerable: true
          }
        };
      }),
      shareReplay(1)
    );

    this.hasRecordDefinitionName$ = this.model.recordDefinition$.pipe(map(Boolean), shareReplay(1));
  }

  onSort(event: SortEvent): void {
    this.adaptTableConfig$.pipe(take(1)).subscribe((adaptTableConfig) => {
      if (!isEqual(event, adaptTableConfig.sortMeta)) {
        let initialSortColumn: IRecordGridSortEditorModel;

        const column = this.model.currentGridDesignProperties.columns.find(
          (gridColumn) => gridColumn.fieldId === event.field
        );

        if (isObject(column.sortable)) {
          if (column.sortable.direction === ColumnSortDirection.Desc) {
            initialSortColumn = null;
          } else {
            initialSortColumn = { fieldId: column.fieldId, direction: ColumnSortDirection.Desc };
          }
        } else {
          initialSortColumn = { fieldId: column.fieldId, direction: ColumnSortDirection.Asc };
        }

        this.model.componentPropertiesChangeFromCanvas$.next({
          ...this.model.currentGridDesignProperties,
          initialSortColumn
        });
      }
    });
  }

  onBeforeViewComponentDrop(data: IViewComponentDropData): void {
    if (
      data.draggedViewComponentDescriptor.type === RxViewComponentType.ActionButton &&
      !data.draggedViewComponentGuid
    ) {
      data.initialPropertiesByName = {
        ...data.initialPropertiesByName,
        style: ActionButtonStyle.Tertiary
      } as IActionButtonProperties;
    }
  }
}
