import {
  IViewComponentDesignCommonDataDictionaryBranch,
  IViewComponentDesignValidationIssue,
  IViewDesignerInspectorConfig,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { map, take, takeUntil } from 'rxjs/operators';
import { IServiceListProperties } from '../service-list.types';
import {
  ExpressionInspectorControlComponent,
  IExpressionInspectorControlOptions
} from '@helix/platform/shared/components';

export class ServiceListDesignModel extends ViewDesignerComponentModel<IServiceListProperties> {
  static getInitialProperties(initialProperties?: IServiceListProperties): IServiceListProperties {
    return {
      serviceRequestId: null,
      serviceRequestName: null,
      serviceRequestGuid: null,
      ...initialProperties
    };
  }

  rxInit(): void {
    this.sandbox
      .getComponentPropertyValue('serviceRequestId')
      .pipe(
        map((value) =>
          value ? [] : [this.sandbox.createError('Selected service request ID cannot be blank.', 'serviceRequestId')]
        ),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe((validationIssues: IViewComponentDesignValidationIssue[]) =>
        this.sandbox.setValidationIssues(validationIssues)
      );

    this.sandbox.componentProperties$.pipe(take(1)).subscribe(() => {
      this.sandbox.updateInspectorConfig(this.getInspector());
      this.sandbox.setCommonDataDictionary(this.getCommonProps());
    });
  }

  private getCommonProps(): IViewComponentDesignCommonDataDictionaryBranch {
    return {
      label: this.sandbox.descriptor.name,
      expression: this.getExpressionForProperty('api'),
      children: [
        {
          label: 'Service request ID',
          expression: this.getExpressionForProperty('serviceRequestId')
        },
        {
          label: 'Service request name',
          expression: this.getExpressionForProperty('serviceRequestName')
        },
        {
          label: 'Service request GUID',
          expression: this.getExpressionForProperty('serviceRequestGuid')
        }
      ]
    };
  }

  private getInspector(): IViewDesignerInspectorConfig {
    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls: [
            {
              name: 'serviceRequestId',
              component: ExpressionInspectorControlComponent,
              options: {
                label: 'Service request ID',
                isRequired: true
              } as IExpressionInspectorControlOptions
            },

            {
              name: 'serviceRequestName',
              component: ExpressionInspectorControlComponent,
              options: {
                label: 'Service request name'
              } as IExpressionInspectorControlOptions
            },

            {
              name: 'serviceRequestGuid',
              component: ExpressionInspectorControlComponent,
              options: {
                label: 'Service request GUID'
              } as IExpressionInspectorControlOptions
            }
          ]
        }
      ]
    };
  }
}
