import { Injectable } from '@angular/core';
import { forEach, includes, isEmpty, isEqual, isString, keys, reduce } from 'lodash';
import { IPlainObject } from '@helix/platform/shared/api';
import { RxJsonParserService } from '@helix/platform/utils';
import { IFilterExpression, IRxRecordGridFilter } from '../types/record-grid-filter.types';
import { IRecordGridFilterData, RecordGridFilterOperator } from '@helix/platform/view/api';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridFilterHelperService {
  filtersKeywords = ['$NULL$', '$TIME$', '$DATE$', '$TIMESTAMP$', '$USER$'];

  constructor(private rxJsonParserService: RxJsonParserService) {}

  // Convert predefined filter data from:
  // "{
  //   and: [
  //     "or": [
  //       {
  //         "eq": "36dfbd6f-7a19-4b4a-81bf-431fe41f6862"
  //       }
  //     ]
  //   ]
  // }"
  //
  //
  // to:
  // {
  //   and: [
  //     "or": [
  //       {
  //         "eq": {
  //           "1": "bar"
  //         }
  //       }
  //     ]
  //   ]
  // }
  //
  getRecordGridFilterDataFromPredefinedFilter(
    filterJson: string,
    recordGridFilters: IRxRecordGridFilter[]
  ): IRecordGridFilterData {
    if (!isEmpty(recordGridFilters) && filterJson) {
      filterJson = this.normalizeFilterString(filterJson, recordGridFilters);
    }

    return this.clearFilterData(this.rxJsonParserService.tryParseJson<IRecordGridFilterData>(filterJson));
  }

  // Convert predefined filter string from:
  // "{
  //   and: [
  //     "or": [
  //       {
  //         "eq": "36dfbd6f-7a19-4b4a-81bf-431fe41f6862"
  //       }
  //     ]
  //   ]
  // }"
  //
  // to:
  // "{
  //   and: [
  //     "or": [
  //       {
  //         "eq": {
  //           "1": "bar"
  //         }
  //       }
  //     ]
  //   ]
  // }"
  //
  normalizeFilterString(filterString: string, filterComponents: IRxRecordGridFilter[]): string {
    forEach(filterComponents, (filterComponent: IRxRecordGridFilter) => {
      const filterExpression: IFilterExpression = {
        [filterComponent.fieldId]: filterComponent.value,
        ...(filterComponent.$DISPLAYVALUE$ && { $DISPLAYVALUE$: filterComponent.$DISPLAYVALUE$ })
      };

      filterString = filterString.replace(`"${filterComponent.guid}"`, JSON.stringify(filterExpression));
    });

    return filterString;
  }

  // Convert predefined filter string from:
  // "{
  //   and: [
  //     "or": [
  //       {
  //         "eq": {
  //           "1": "bar"
  //         }
  //       }
  //     ]
  //   ]
  // }"
  //
  //
  // to:
  // "{
  //   and: [
  //     "or": [
  //       {
  //         "eq": "36dfbd6f-7a19-4b4a-81bf-431fe41f6862"
  //       }
  //     ]
  //   ]
  // }"
  //
  denormalizeFilterString(filterString: string, filterComponents: IRxRecordGridFilter[]): string {
    forEach(filterComponents, (filterComponent: IRxRecordGridFilter) => {
      const filterExpression: IFilterExpression = {
        [filterComponent.fieldId]: filterComponent.value,
        ...(filterComponent.$DISPLAYVALUE$ && { $DISPLAYVALUE$: filterComponent.$DISPLAYVALUE$ })
      };

      if (filterExpression.$DISPLAYVALUE$) {
        filterString = JSON.stringify(
          JSON.parse(filterString),
          this.replacer(this.denormalize(filterComponent.guid, filterExpression))
        );
      } else {
        filterString = filterString.replace(JSON.stringify(filterExpression), `"${filterComponent.guid}"`);
      }
    });

    return filterString;
  }

  // e.g.
  // fn = denormalize(guid, {"536870913":"AGGADG1AAXPMRARKA0FGRKA0FG6I01","$DISPLAYVALUE$":"foo"});
  //
  // var filterExpression = {
  //   "and": [
  //     {
  //       "or": [
  //         {
  //           "eq": {
  //             "536870913": "AGGADG1AAXPMRARKA0FGRKA0FG6I01",
  //             "$DISPLAYVALUE$": "foo"
  //           }
  //         }
  //       ]
  //     }
  //   ]
  // };
  //
  // JSON.stringify(filterExpression,replacer(fn));
  // Here the filterExpression's keys are "add", 0, or, 0, eq
  //
  private replacer(fn: (guid: string, filterExpression: IFilterExpression) => any): any {
    return (key: any, value: any) => (typeof key === 'string' && fn[key] ? fn[key](value) : value);
  }

  // This method declares, how object's value of an operator key should denormalize.
  //
  // Converts the Enum RecordGridFilterOperator
  // to
  // "
  // {
  //   "eq": fn(),
  //   "gte": fn(),
  //   "lte": fn(),
  //   "like": fn(),
  //   "ne": fn(),
  //   "lt": fn(),
  //   "gt": fn(),
  //   "in": fn()
  // }
  // "
  //  Note : fn() defines, how the value should be updated.
  //
  // e.g.
  // Converts predefined filter string from:
  //  {"and":[{"or":[{"eq":{"Foo":"Bar","$DISPLAYVALUE$":"Baz"}}]}]}
  // to:
  //  {and":[{"or":[{"eq":"aa88747c-8b8e-4dee-abf2-556b84b7d28e"}]}]}
  private denormalize(guid: string, filterExpression: IFilterExpression): any {
    return reduce(
      RecordGridFilterOperator,
      (result, operator) => {
        result[operator] = (value) => (isEqual(value, filterExpression) ? guid : value);

        return result;
      },
      {}
    );
  }

  clearFilterData(node: IPlainObject): IPlainObject {
    const nodeKeys = keys(node);
    let result = node;

    if (!isEmpty(nodeKeys)) {
      const operator = nodeKeys[0];

      switch (true) {
        case includes(['or', 'and'], operator): {
          const children = node[operator].map(this.clearFilterData.bind(this)).filter((child) => Boolean(child));

          if (!isEmpty(children)) {
            node[operator] = children;
          } else {
            result = null;
          }

          break;
        }
        case isString(node[operator]): {
          result = null;

          break;
        }
      }
    }

    return result;
  }

  replaceKeywords(query: string): string {
    const singleQuoteRegexTemplate = '([^|^"])"(' + this.filtersKeywords.join('|').replace(/\$/g, '\\$') + ')"(?!")';

    const tripleQuoteRegexTemplate =
      '([^|^"])"""(' + this.filtersKeywords.join('|').replace(/\$/g, '\\$') + ')"""(?!")';

    const singleQuoteRegex = new RegExp(singleQuoteRegexTemplate, 'g');
    const tripleQuoteRegex = new RegExp(tripleQuoteRegexTemplate, 'g');

    return query.replace(singleQuoteRegex, '$1$2').replace(tripleQuoteRegex, '$1"$2"');
  }
}
