import { Component, Input } from '@angular/core';
import {
  AdaptFilterControlsComponent,
  AdvancedFilterBooleanDataType,
  AdvancedFilterOption
} from '@bmc-ux/adapt-angular';
import { SelectOption } from '@bmc-ux/obsolete';
import { find } from 'lodash';
import { IRecordGridColumnNamedFilterOption } from '../../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';

@Component({
  selector: 'rx-boolean-filter',
  templateUrl: './boolean-filter.component.html'
})
export class RxBooleanFilterComponent {
  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  set filterValue(values: RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>) {
    const selectOptions = this.getSelectOptions();

    this._filterValue = values;

    this.selectedValues = values?.filterValue
      ? values.filterValue.map((value) => find(selectOptions, { id: value }))
      : [];
  }

  get filterValue(): RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]> {
    return this._filterValue;
  }

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[] = [];

  @Input()
  isRequired = false;

  selectedValues: SelectOption[];
  private _filterValue: RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>;

  selectOptionFormatter(option: SelectOption): string {
    return option.name;
  }

  onValueChange(values: SelectOption[]): void {
    const filterValue = values.map((value) => value.id);

    this.filterControlsComponent.onFilterOptionModelChange(
      new RxRecordGridAdvancedFilterValue(filterValue, this.filterValue?.namedOptions)
    );
  }

  getSelectOptions(): SelectOption[] {
    return this.filterOption.data?.selectOptions || [];
  }

  onValueChangeRequired(model: AdvancedFilterBooleanDataType): void {
    const oldModelValue = Array.isArray(this.filterValue?.filterValue)
      ? this.filterValue.filterValue[0]
      : this.filterValue?.filterValue;

    this.filterControlsComponent.onFilterOptionModelChange(
      new RxRecordGridAdvancedFilterValue(model === oldModelValue ? null : [model], this.filterValue?.namedOptions)
    );
  }
}
