import { Injectable } from '@angular/core';
import { KeyValueObject } from '@bmc-ux/adapt-angular';
import { SortOrder } from '@bmc-ux/adapt-table';

import { BooleanLike } from '@helix/platform/shared/api';
import { RxJsonParserService } from '@helix/platform/utils';
import { assign, cloneDeep, includes, isBoolean, isObject, isString, map, omit } from 'lodash';
import { IRecordGridColumn } from '../types/record-grid-column.types';
import { IRecordGridConfig } from '../types/record-grid-config.interface';
import { ColumnSortDirection } from '../../common/types/record-grid.types';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridConfigUtilsService {
  constructor(private rxJsonParserService: RxJsonParserService) {}

  parseConfigString(str: BooleanLike | KeyValueObject | string): boolean | KeyValueObject {
    if (!str) {
      return false;
    }

    if (includes(['1', 'true'], str)) {
      return true;
    }

    if (includes(['0', 'false'], str)) {
      return false;
    }

    if (isString(str)) {
      return this.rxJsonParserService.tryParseJson(str, false);
    }

    if (isObject(str)) {
      return str;
    }

    return isBoolean(str) ? str : false;
  }

  getBooleanValue(str: BooleanLike): boolean {
    return this.parseConfigString(str) as boolean;
  }

  getColumnSortOrder(direction: ColumnSortDirection): SortOrder {
    return direction === ColumnSortDirection.Asc ? SortOrder.Asc : SortOrder.Desc;
  }

  getColumnSortDirection(sortOrder: SortOrder): ColumnSortDirection {
    return sortOrder === SortOrder.Asc ? ColumnSortDirection.Asc : ColumnSortDirection.Desc;
  }

  configDeepClone(configuration: IRecordGridConfig): IRecordGridConfig {
    const configClone = cloneDeep(omit(configuration, ['columns']));
    const columns = this.columnsDeepClone(configuration.columns);

    return assign(configClone, { columns });
  }

  columnsDeepClone(columns: IRecordGridColumn[]): IRecordGridColumn[] {
    return map(columns, this.columnDeepClone);
  }

  private columnDeepClone(column: IRecordGridColumn): IRecordGridColumn {
    const columnClone = cloneDeep(omit(column, ['cellTemplate']));

    return assign(columnClone, { cellTemplate: column.cellTemplate });
  }
}
