import {
  AfterViewInit,
  Component,
  ElementRef,
  Injector,
  QueryList,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { AbstractControl, NgForm, ValidationErrors } from '@angular/forms';
import { findIndex, isEmpty, map, some } from 'lodash';
import { ActiveModalRef, AdaptAccordionTabComponent, DismissReasons } from '@bmc-ux/adapt-angular';
import { IRecordDefinition } from '@helix/platform/record/api';
import { RX_MODAL, RxModalClass, RxModalService } from '@helix/platform/ui-kit';
import { RxGuidService } from '@helix/platform/utils';
import { IRecordGridPredefinedFilterPreset } from '../../../../runtime/types/record-grid-predefined-filter-preset.interface';
import {
  IPredefinedFilterPresetEditorPresetData,
  IRecordGridFilterPresetEditorModalParams,
  IRecordGridFilterPresetEditorModalResult
} from '../record-grid-filter-preset-editor.types';

@Component({
  selector: 'rx-record-grid-filter-preset-editor-modal',
  templateUrl: './record-grid-filter-preset-editor-modal.component.html',
  styleUrls: ['./record-grid-filter-preset-editor-modal.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RecordGridFilterPresetEditorModalComponent extends RxModalClass implements AfterViewInit {
  @ViewChildren(AdaptAccordionTabComponent, { read: ElementRef })
  private accordionTabEls: QueryList<ElementRef>;

  @ViewChild('filterPresetsForm', { read: NgForm, static: true })
  filterPresetsForm: NgForm;

  isReadOnly: boolean;
  recordDefinition: IRecordDefinition;
  selectedFieldIds: string[];
  filterPresets: IPredefinedFilterPresetEditorPresetData[] = [];
  params: IRecordGridFilterPresetEditorModalParams = this.activeModalRef.getData();

  constructor(
    private activeModalRef: ActiveModalRef,
    private rxModalService: RxModalService,
    private rxGuidService: RxGuidService,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);

    const activeFilterPreset = this.params.activeFilterPreset;

    this.recordDefinition = this.params.primaryRecordDefinition;
    this.selectedFieldIds = this.params.selectedFieldIds;
    this.isReadOnly = this.params.isReadOnly;

    this.filterPresets = map(
      this.params.filterPresets,
      (filterPreset): IPredefinedFilterPresetEditorPresetData =>
        this.createFilterPresetEditorData(filterPreset, filterPreset === activeFilterPreset)
    );
  }

  ngAfterViewInit(): void {
    const openFilterPresetIndex = findIndex(this.filterPresets, 'isOpen');

    if (openFilterPresetIndex !== -1) {
      this.accordionTabEls.toArray()[openFilterPresetIndex].nativeElement.scrollIntoView({
        block: 'nearest'
      });
    }
  }

  addFilterPreset(): void {
    this.markAsDirty();

    this.filterPresets.push(
      this.createFilterPresetEditorData(
        {
          guid: this.rxGuidService.generate(),
          title: 'New preset' + (this.filterPresets.length === 0 ? '' : ' ' + this.filterPresets.length),
          filters: '',
          recordGridFilters: []
        },
        true
      )
    );

    setTimeout(() => {
      this.accordionTabEls.last.nativeElement.scrollIntoView({
        block: 'nearest'
      });
    });
  }

  removeFilterPreset(index: number): void {
    this.markAsDirty();

    this.filterPresets.splice(index, 1);
  }

  createFilterPresetEditorData(
    filterPreset: IRecordGridPredefinedFilterPreset,
    isOpen: boolean
  ): IPredefinedFilterPresetEditorPresetData {
    return {
      ...filterPreset,
      isOpen,
      filterSelectOptions: {
        primaryRecordDefinition: this.recordDefinition,
        tagsLimit: 2,
        namedFilterOptions: this.params.namedFilterOptions,
        selectedFieldIds: this.selectedFieldIds
      },
      filterSelect: {
        basicFilters: filterPreset.recordGridFilters,
        filtersJson: filterPreset.filters
      }
    };
  }

  getCheckPresetTitleDuplicateValidator(
    currentFilterPreset: IPredefinedFilterPresetEditorPresetData
  ): (control: AbstractControl) => ValidationErrors | null {
    return (control: AbstractControl) => {
      const title = control.value;

      if (
        title &&
        this.filterPresets.some(
          (filterPreset) => filterPreset.title === title && filterPreset.guid !== currentFilterPreset.guid
        )
      ) {
        return { duplicatePresetName: { message: 'Duplicate Name' } };
      }

      return null;
    };
  }

  trackByForFilterPresets(index: number, filterPreset: IPredefinedFilterPresetEditorPresetData): string {
    return filterPreset.guid;
  }

  toggleOpen(expandAll: boolean): void {
    this.filterPresets.forEach((action) => (action.isOpen = expandAll));
  }

  onSaveChangesClick(): void {
    const result: IRecordGridFilterPresetEditorModalResult = {
      filterPresets: this.filterPresets.map(
        (filterPreset): IRecordGridPredefinedFilterPreset => ({
          filters: filterPreset.filterSelect.filtersJson,
          recordGridFilters: filterPreset.filterSelect.basicFilters,
          guid: filterPreset.guid,
          title: filterPreset.title
        })
      )
    };

    const hasEmptyFilterPreset = some(result.filterPresets, (filterPreset) => isEmpty(filterPreset.recordGridFilters));

    if (hasEmptyFilterPreset) {
      this.rxModalService
        .confirm({
          title: 'Warning',
          modalStyle: RX_MODAL.modalStyles.warning,
          message:
            'The filter preset will not filter records because no filters have been specified. Do you want to continue?'
        })
        .then((confirmResult) => {
          if (confirmResult) {
            this.activeModalRef.close(result);
          }
        });
    } else {
      this.activeModalRef.close(result);
    }
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
