import { Injectable } from '@angular/core';
import { IExpressionEvaluator, RxDefaultExpressionEvaluatorService } from '@helix/platform/view/api';
import { RX_RICH_TEXT } from '@helix/platform/view/api';
import { isNil } from 'lodash';

@Injectable()
export class RxRichTextExpressionEvaluatorService implements IExpressionEvaluator {
  constructor(private rxDefaultExpressionEvaluatorService: RxDefaultExpressionEvaluatorService) {}

  evaluate(expression: string, data: object): string {
    const tempElement = document.createElement('div');
    tempElement.innerHTML = expression;

    tempElement.querySelectorAll(`span[${RX_RICH_TEXT.expressionAttributeName}]`).forEach((span) => {
      const evaluatedExpression = this.rxDefaultExpressionEvaluatorService.evaluate(
        span.getAttribute(RX_RICH_TEXT.expressionAttributeName),
        data
      );

      const textNode = document.createTextNode(isNil(evaluatedExpression) ? '' : evaluatedExpression);

      span.parentElement.replaceChild(textNode, span);
    });

    return tempElement.innerHTML;
  }
}
