import {
  ViewComponentAddComponentPayload,
  IViewComponentDesignCommonDataDictionary,
  IViewComponentDesignCommonDataDictionaryBranch,
  IViewComponentDropPredicateData,
  ViewDesignerComponentModel,
  IViewDesignerInspectorConfig
} from '@helix/platform/view/designer';
import { IDataDictionaryBranch, RxDefinitionNameService } from '@helix/platform/shared/api';
import {
  IViewDesignerComponentModel,
  RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE,
  RxViewComponentRegistryService,
  RxViewComponentType
} from '@helix/platform/view/api';
import { ISelectOption } from '@helix/platform/shared/components';
import {
  IFieldDefinition,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxFieldDefinitionService
} from '@helix/platform/record/api';
import { combineLatest, forkJoin, Observable, of, Subject } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  first,
  map,
  mergeMap,
  pairwise,
  shareReplay,
  skip,
  switchMap,
  take,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { flow, isEqual, map as _map, reject, uniq } from 'lodash';
import { RecordEditorMode, RecordEditorState } from '../common/record-editor.types';
import { RxRecordEditorDesignHelpersService } from './record-editor-design-helpers.service';
import { IBaseRecordEditorFieldProperties } from '../../field-components/base-record-editor-field/design';
import { IRecordEditorDesignProperties, IRecordEditorProperties } from './record-editor-design.types';
import { IRecordEditorSelectedFieldComponent } from './components/fields-inspector-widget/fields-inspector-widget.types';

export class RecordEditorDesignModel
  extends ViewDesignerComponentModel<IRecordEditorProperties, IRecordEditorDesignProperties>
  implements IViewDesignerComponentModel<IRecordEditorProperties, IRecordEditorDesignProperties>
{
  private rxFieldDefinitionService = this.injector.get<RxFieldDefinitionService>(RxFieldDefinitionService);

  private rxViewComponentRegistryService =
    this.injector.get<RxViewComponentRegistryService>(RxViewComponentRegistryService);

  private rxRecordEditorDesignHelpersService = this.injector.get<RxRecordEditorDesignHelpersService>(
    RxRecordEditorDesignHelpersService
  );

  private rxDefinitionNameService = this.injector.get<RxDefinitionNameService>(RxDefinitionNameService);
  private updateInspector$ = new Subject<IViewDesignerInspectorConfig>();

  private associationOptions$: Observable<ISelectOption[]>;
  private isAssociationRequired$: Observable<boolean>;

  private recordDefinitionName$ = this.sandbox.getComponentPropertyValue('recordDefinitionName');
  private mode$ = this.sandbox.getComponentPropertyValue('mode');
  private showReadOnlyState$ = this.sandbox.getComponentPropertyValue('showReadOnlyState');
  private targetExtensionContainerGuid$ = this.sandbox.getViewPropertyValue('targetExtensionContainerGuid');
  private targetViewDefinitionName$ = this.sandbox.getViewPropertyValue('targetViewDefinitionName');

  private recordDefinition$ = this.recordDefinitionName$.pipe(
    switchMap((name) => this.rxRecordEditorDesignHelpersService.getRecordDefinition(name)),
    shareReplay(1)
  );

  private childComponentGuids$ = this.sandbox.getChildComponentGuids().pipe(shareReplay(1));

  private allChildFieldComponentGuids$ = this.sandbox
    .getChildComponentGuids((component) => this.rxRecordEditorDesignHelpersService.isFieldComponent(component), true)
    .pipe(distinctUntilChanged<string[]>(isEqual), shareReplay(1));

  isExtensionView$ = this.targetViewDefinitionName$.pipe(map(Boolean), shareReplay(1));

  childFieldIds$: Observable<string[]> = this.allChildFieldComponentGuids$.pipe(
    switchMap((guids) =>
      guids.length
        ? combineLatest(
            guids.map((guid) =>
              this.sandbox.getComponentPropertyValue<'fieldId', IBaseRecordEditorFieldProperties>('fieldId', guid)
            )
          ).pipe(map(uniq))
        : of([])
    ),
    shareReplay(1)
  );

  selectedFieldComponents$: Observable<IRecordEditorSelectedFieldComponent[]> = this.allChildFieldComponentGuids$.pipe(
    switchMap((guids) =>
      guids.length
        ? combineLatest(
            guids.map((guid) =>
              combineLatest([
                this.sandbox.getComponent(guid).pipe(
                  take(1),
                  map((component) => this.rxViewComponentRegistryService.get(component.type).name)
                ),
                this.sandbox.getComponentPropertyValue<'fieldId', IBaseRecordEditorFieldProperties>('fieldId', guid),
                this.sandbox.getComponentPropertyValue<'label', IBaseRecordEditorFieldProperties>('label', guid)
              ]).pipe(
                map(
                  ([componentName, fieldId, label]): IRecordEditorSelectedFieldComponent => ({
                    fieldId,
                    label: label || '<No field selected>',
                    componentName,
                    guid
                  })
                )
              )
            )
          )
        : of([])
    ),
    shareReplay(1)
  );

  associationComponentGuids$ = this.sandbox
    .getChildComponentGuids((component) => component.type === RxViewComponentType.Association, true)
    .pipe(shareReplay(1), takeUntil(this.sandbox.destroyed$));

  associationDefinitionName$ = this.sandbox.getComponentPropertyValue('associationDefinitionName');

  hasChildren$ = this.childComponentGuids$.pipe(
    map((guids) => Boolean(guids.length)),
    distinctUntilChanged(),
    shareReplay(1)
  );

  readonly systemFieldComponentGuids$ = this.recordDefinition$.pipe(
    mergeMap((recordDefinition) =>
      recordDefinition
        ? this.selectedFieldComponents$.pipe(
            map((components) =>
              components
                .filter((component) => {
                  const fieldDefinition = recordDefinition.fieldDefinitions.find(
                    (definition) => String(definition.id) === component.fieldId
                  );

                  return this.rxFieldDefinitionService.isSystemField(fieldDefinition);
                })
                .map((component) => component.guid)
            ),
            take(1)
          )
        : of([])
    )
  );

  recordFieldDefinitionItems$ = this.recordDefinition$.pipe(
    map((recordDefinition) =>
      recordDefinition ? this.rxRecordEditorDesignHelpersService.getRecordFieldDefinitionItems(recordDefinition) : []
    ),
    shareReplay(1)
  );

  static getInitialProperties(initialProps?: IRecordEditorProperties): IRecordEditorDesignProperties {
    const result: IRecordEditorProperties = {
      name: null,
      label: null,
      mode: RecordEditorMode.Edit,
      recordInstanceId: null,
      recordDefinitionName: null,
      associationDefinitionName: null,
      defaultState: initialProps.defaultState || null,
      allowEdit: '1',
      styles: null,
      ...RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE,
      ...initialProps
    };

    return {
      ...result,
      showReadOnlyState: Boolean(result.defaultState),
      allowEdit: result.mode === RecordEditorMode.Edit ? result.allowEdit : null
    };
  }

  rxInit(): void {
    const targetRecordEditorProperties$ = combineLatest([
      this.targetViewDefinitionName$.pipe(
        switchMap((targetViewDefinitionName) =>
          this.rxRecordEditorDesignHelpersService.getViewDefinition(targetViewDefinitionName)
        )
      ),
      this.targetExtensionContainerGuid$
    ]).pipe(
      map(([viewDefinition, targetExtensionContainerGuid]) =>
        this.rxRecordEditorDesignHelpersService.getTargetRecordEditorProperties(
          viewDefinition,
          targetExtensionContainerGuid
        )
      )
    );

    this.associationOptions$ = combineLatest([targetRecordEditorProperties$, this.recordDefinitionName$]).pipe(
      switchMap(([targetRecordEditorProperties, extensionRecordDefinitionName]) =>
        this.rxRecordEditorDesignHelpersService.getAssociationOptions(
          targetRecordEditorProperties.recordDefinitionName,
          extensionRecordDefinitionName
        )
      )
    );

    this.isAssociationRequired$ = combineLatest([
      this.isExtensionView$,
      this.recordDefinitionName$,
      targetRecordEditorProperties$
    ]).pipe(
      map(
        ([isExtensionView, recordDefinitionName, targetRecordEditorProperties]) =>
          isExtensionView && recordDefinitionName !== targetRecordEditorProperties.recordDefinitionName
      )
    );

    this.updateInspector$.pipe(takeUntil(this.sandbox.destroyed$)).subscribe((inspector) => {
      this.sandbox.updateInspectorConfig(inspector);
    });

    // initial set of inspector
    combineLatest([
      this.sandbox.componentProperties$,
      this.isExtensionView$,
      this.associationOptions$,
      this.isAssociationRequired$,
      targetRecordEditorProperties$
    ])
      .pipe(first(), takeUntil(this.sandbox.destroyed$))
      .subscribe(
        ([
          { recordDefinitionName, mode, showReadOnlyState },
          isExtensionView,
          associationOptions,
          isAssociationRequired,
          targetRecordEditorProperties
        ]) => {
          if (isExtensionView && !recordDefinitionName) {
            recordDefinitionName = targetRecordEditorProperties.recordDefinitionName;
            mode = targetRecordEditorProperties.mode;

            this.sandbox.updateComponentProperties({ mode, recordDefinitionName });
          }

          this.updateInspector$.next(
            this.rxRecordEditorDesignHelpersService.getInspector(
              recordDefinitionName,
              mode,
              showReadOnlyState,
              [],
              isExtensionView,
              associationOptions,
              isAssociationRequired,
              this._onBeforeModeChange.bind(this)
            )
          );
        }
      );

    this.mode$
      .pipe(skip(1), withLatestFrom(this.sandbox.componentProperties$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([mode, props]) => {
        this.sandbox.updateComponentProperties(
          mode === RecordEditorMode.Create || mode === RecordEditorMode.Temporary
            ? {
                ...props,
                recordInstanceId: null,
                defaultState: null,
                allowEdit: null,
                showReadOnlyState: null
              }
            : {
                ...props,
                defaultState: null,
                allowEdit: '1',
                showReadOnlyState: false
              }
        );
      });

    this.showReadOnlyState$
      .pipe(skip(1), withLatestFrom(this.sandbox.componentProperties$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([showReadOnlyState, props]) => {
        this.sandbox.updateComponentProperties({
          ...props,
          defaultState: showReadOnlyState ? RecordEditorState.Read : null
        });
      });

    combineLatest([
      combineLatest([
        this.sandbox.getComponentPropertyValue('recordDefinitionName'),
        this.sandbox.getComponentPropertyValue('mode'),
        this.showReadOnlyState$
      ]).pipe(skip(1)),
      this.recordFieldDefinitionItems$,
      this.isExtensionView$,
      this.associationOptions$,
      this.isAssociationRequired$
    ])
      .pipe(
        map(
          ([
            [recordDefinitionName, mode, showReadOnlyState],
            recordFieldDefinitionItems,
            isExtensionView,
            associationOptions,
            isAssociationRequired
          ]) =>
            this.rxRecordEditorDesignHelpersService.getInspector(
              recordDefinitionName,
              mode,
              showReadOnlyState,
              recordFieldDefinitionItems,
              isExtensionView,
              associationOptions,
              isAssociationRequired,
              this._onBeforeModeChange.bind(this)
            )
        ),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(this.updateInspector$);

    // clear child components after changing of record definition
    this.recordDefinitionName$
      .pipe(pairwise(), takeUntil(this.sandbox.destroyed$))
      .subscribe(([prevName, currentName]) => {
        if (prevName) {
          this.sandbox.setChildren([]);
          this.sandbox.updateComponentProperties({ associationDefinitionName: null });
        }
      });

    this.associationOptions$
      .pipe(
        skip(1),
        filter((associationOptions) => associationOptions.length === 1),
        withLatestFrom(this.isAssociationRequired$),
        filter(([, isAssociationRequired]) => isAssociationRequired),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(([[{ id: associationDefinitionName }]]) =>
        this.sandbox.updateComponentProperties({ associationDefinitionName })
      );

    // TODO: performance
    // validation
    combineLatest([
      this.sandbox.componentProperties$,
      this.recordDefinition$,
      this.childFieldIds$,
      this.isAssociationRequired$,
      this.isExtensionView$
    ])
      .pipe(
        map(([componentProperties, recordDefinition, childFieldIds, isAssociationRequired, isExtensionView]) => {
          return this.rxRecordEditorDesignHelpersService.validate(
            this.sandbox,
            componentProperties,
            recordDefinition,
            childFieldIds,
            isAssociationRequired,
            isExtensionView
          );
        }),
        distinctUntilChanged(isEqual),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe((validationIssues) => {
        this.sandbox.setValidationIssues(validationIssues);
      });

    combineLatest([
      this.sandbox.getComponentPropertyValue('name').pipe(
        map((name) => name || null),
        distinctUntilChanged()
      ),
      this.recordDefinition$
    ])
      .pipe(takeUntil(this.sandbox.destroyed$))
      .subscribe(([name, recordDefinition]) => {
        this._setSettableProperties(name, recordDefinition);
        this._setCommonDataDictionary(name, recordDefinition);
      });
  }

  removeComponent(guids: string[]): void {
    this.sandbox.removeComponents(guids);
  }

  updateSelectedFieldComponents(fieldIds: string[]): void {
    this.selectedFieldComponents$
      .pipe(
        take(1),
        mergeMap((components) => {
          const fieldIdsToAdd = fieldIds.filter(
            (fieldId) => !components.find((component) => component.fieldId === fieldId)
          );

          const fieldComponentsToAdd$: Observable<ViewComponentAddComponentPayload[]> = fieldIdsToAdd.length
            ? this.recordFieldDefinitionItems$.pipe(
                map((items) =>
                  fieldIdsToAdd.map((fieldId) => {
                    const { viewComponentType: type, name: label } = items.find((item) => item.id === fieldId);

                    return {
                      type,
                      propertiesByName: {
                        fieldId,
                        label
                      }
                    };
                  })
                ),
                take(1)
              )
            : of([]);

          const fieldIdsSet = new Set(fieldIds);

          const componentGuidsToRemove = components
            .filter((component) => component.fieldId)
            .filter((component) => !fieldIdsSet.has(component.fieldId))
            .map((component) => component.guid);

          return forkJoin([fieldComponentsToAdd$, of(componentGuidsToRemove)]);
        }),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(([fieldComponentsToAdd, fieldGuidsToRemove]) => {
        if (fieldComponentsToAdd.length) {
          this.sandbox.addComponent(fieldComponentsToAdd);
        }

        if (fieldGuidsToRemove.length) {
          this.sandbox.removeComponents(fieldGuidsToRemove);
        }
      });
  }

  getPropertiesByName(properties: IRecordEditorDesignProperties): IRecordEditorProperties {
    return this.rxRecordEditorDesignHelpersService.getComponentProperties(properties);
  }

  _dropPredicate(data: IViewComponentDropPredicateData): boolean {
    return data.draggedViewComponentDescriptor.options?.canBeEmbeddedInRecordEditor;
  }

  _onBeforeModeChange(prevMode: string, currentMode: string): Promise<boolean> {
    if (prevMode !== RecordEditorMode.Create && currentMode === RecordEditorMode.Create) {
      return this.systemFieldComponentGuids$
        .pipe(
          mergeMap((systemFieldComponentGuids) =>
            systemFieldComponentGuids.length
              ? this.rxRecordEditorDesignHelpersService.confirmSystemFieldRemoval().pipe(
                  tap((isConfirmed) => {
                    if (isConfirmed) {
                      this.sandbox.removeComponents(systemFieldComponentGuids);
                    }
                  })
                )
              : of(true)
          ),
          take(1)
        )
        .toPromise();
    } else {
      return Promise.resolve(true);
    }
  }

  dropPredicate(data: IViewComponentDropPredicateData): boolean {
    return data.draggedViewComponentDescriptor.options?.canBeEmbeddedInRecordEditor;
  }

  private _setCommonDataDictionary(name: string, recordDefinition: IRecordDefinition): void {
    const componentName = this._getComponentName(name, recordDefinition);

    this.sandbox.setCommonDataDictionary(this._getCommonProps(componentName, recordDefinition));
  }

  private _setSettableProperties(name: string, recordDefinition: IRecordDefinition): void {
    const componentName = this._getComponentName(name, recordDefinition);

    this.sandbox.setSettablePropertiesDataDictionary(componentName, [
      {
        label: 'Edit state',
        expression: this.getExpressionForProperty('editState')
      },
      {
        label: 'Read state',
        expression: this.getExpressionForProperty('readState')
      },
      ...(recordDefinition
        ? [
            {
              label: 'Record instance',
              children: recordDefinition.fieldDefinitions.map((definition) => ({
                label: definition.name,
                expression: this.getExpressionForProperty(`recordInstance.fieldInstances.${definition.id}.value`)
              }))
            }
          ]
        : [])
    ]);
  }

  private _getCommonProps(
    componentName: string,
    recordDefinition: IRecordDefinition
  ): IViewComponentDesignCommonDataDictionaryBranch {
    return {
      label: componentName,
      expression: this.getExpressionForProperty('api'),
      children: [
        {
          label: 'Can save',
          expression: this.getExpressionForProperty('canSave')
        },
        {
          label: 'In read state',
          expression: this.getExpressionForProperty('inReadState')
        },
        {
          label: 'Is dirty',
          expression: this.getExpressionForProperty('isDirty')
        },
        {
          label: 'Is valid',
          expression: this.getExpressionForProperty('isValid')
        },
        {
          label: 'Record definition name',
          expression: this.getExpressionForProperty('recordDefinitionName')
        },
        {
          label: 'Record instance',
          expression: this.getExpressionForProperty('recordInstance'),
          children: recordDefinition ? this._getRecordInstanceChildProps(recordDefinition) : []
        },
        {
          label: 'Record instance ID',
          expression: this.getExpressionForProperty('recordInstanceId')
        }
      ]
    };
  }

  private _getRecordInstanceChildProps(recordDefinition: IRecordDefinition): IViewComponentDesignCommonDataDictionary {
    return flow(
      (fieldDefinitions) => reject(fieldDefinitions, { resourceType: RX_RECORD_DEFINITION.resourceTypes.attachment }),
      (fieldDefinitions) =>
        _map(fieldDefinitions, (fieldDefinition: IFieldDefinition) => {
          const child: IDataDictionaryBranch = {
            label: fieldDefinition.name,
            expression: this.getExpressionForProperty(`recordInstance.fieldInstances.${fieldDefinition.id}.value`)
          };

          if (fieldDefinition.resourceType === RX_RECORD_DEFINITION.resourceTypes.selection) {
            const children = (child.autocompleteOptions = _map(
              fieldDefinition.optionNamesById,
              (optionName, optionValue) => ({
                label: optionName,
                expression: this.getExpressionForProperty(
                  `recordDefinition.fieldDefinitionsById[${fieldDefinition.id}].optionsById[${optionValue}].id`
                )
              })
            ));

            child.children = [
              {
                label: `${fieldDefinition.name} (Option name)`,
                expression: this.getExpressionForProperty(`selectionFieldOptionNamesById.${fieldDefinition.id}`)
              },
              {
                label: 'Options',
                children
              }
            ];
          }

          return child;
        })
    )(recordDefinition.fieldDefinitions);
  }

  private _getComponentName(name: string, recordDefinition: IRecordDefinition): string {
    let componentName = this.sandbox.descriptor.name;

    const recordDefinitionName = recordDefinition
      ? this.rxDefinitionNameService.getDisplayName(recordDefinition.name)
      : null;

    if (recordDefinitionName && name) {
      componentName += ` (${recordDefinitionName}: ${name})`;
    } else if (recordDefinitionName) {
      componentName += ` (${recordDefinitionName})`;
    } else if (name) {
      componentName += ` (${name})`;
    }

    return componentName;
  }
}
