import { Injectable } from '@angular/core';
import {
  IAssociationDefinition,
  RX_ASSOCIATED_RECORD_NODE_SIDES,
  RX_ASSOCIATION_DEFINITION,
  RxAssociationDefinitionDataPageService
} from '@helix/platform/association/api';
import {
  IFieldDefinition,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxRecordDefinitionCacheService
} from '@helix/platform/record/api';
import {
  IExpressionConfigurator,
  IFormControlBuilderConfig,
  RxDefinitionNameService
} from '@helix/platform/shared/api';
import {
  ExpressionInspectorControlComponent,
  IExpressionInspectorControlOptions,
  ISelectFormControlOptions,
  RxDefinitionPickerComponent,
  RxDefinitionPickerType,
  SelectFormControlComponent,
  SwitchFormControlComponent,
  TextFormControlComponent
} from '@helix/platform/shared/components';
import { RxGuidService } from '@helix/platform/utils';
import {
  RX_DISABLED_PROP_DEFAULT_VALUE,
  RX_STANDARD_PROPS_DEFAULT_VALUES,
  RxViewComponentType
} from '@helix/platform/view/api';
import {
  getDisabledFieldInspectorConfig,
  getStandardPropsInspectorConfigs,
  IViewComponentDesignSandbox,
  IViewComponentDesignValidationIssue,
  validateStandardProps,
  ViewComponentAddComponentPayload
} from '@helix/platform/view/designer';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RX_ASSOCIATION, RxAssociationEditingMode } from '../association.types';
import { IAssociationChildComponents, IAssociationDesignProperties } from './association-design.types';
import {
  AssociationRecordFieldSelectorFormControlComponent,
  IAssociatedRecordField
} from './association-record-field-selector-field';
import { RxSelectOption } from '@bmc-ux/adapt-angular';

@Injectable()
export class AssociationDesignHelperService {
  constructor(
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private rxAssociationDefinitionDataPageService: RxAssociationDefinitionDataPageService,
    private rxGuidService: RxGuidService,
    private rxDefinitionNameService: RxDefinitionNameService
  ) {}

  getDropdownModeInspectorControls(
    recordDefinitionField: IFieldDefinition[],
    associationDefinitionFilters: IAssociationDefinition[]
  ): IFormControlBuilderConfig[] {
    return [
      {
        name: 'fieldId',
        component: SelectFormControlComponent,
        options: {
          label: 'Field to display',
          options: recordDefinitionField
            ? recordDefinitionField
                .filter((associationDefinitionField) =>
                  [
                    RX_RECORD_DEFINITION.resourceTypes.character,
                    RX_RECORD_DEFINITION.resourceTypes.localizedCharacter
                  ].includes(associationDefinitionField.resourceType)
                )
                .map((associationDefinitionField) => ({
                  id: String(associationDefinitionField.id),
                  name: associationDefinitionField.name
                }))
            : [],
          emptyOption: true,
          required: true
        } as ISelectFormControlOptions
      },
      {
        name: 'filterByAssociation',
        component: SelectFormControlComponent,
        options: {
          label: 'Filter by association',
          options: associationDefinitionFilters
            ? associationDefinitionFilters.map((associationDefinition) => ({
                id: associationDefinition.name,
                name: this.rxDefinitionNameService.getDisplayName(associationDefinition.name)
              }))
            : [],
          emptyOption: true
        } as ISelectFormControlOptions
      }
    ];
  }

  getDisplayedFieldsControl(recordDefinitionFields: IFieldDefinition[]): IFormControlBuilderConfig {
    return {
      name: 'displayedFields',
      component: AssociationRecordFieldSelectorFormControlComponent,
      options: {
        label: 'Show/Hide fields',
        fields: recordDefinitionFields,
        required: true
      }
    };
  }

  getRecordIdControl(): IFormControlBuilderConfig {
    return {
      name: 'recordId',
      component: ExpressionInspectorControlComponent,
      options: {
        label: 'Record ID',
        isRequired: true
      } as IExpressionInspectorControlOptions
    };
  }

  getUseDefaultRolesControl(): IFormControlBuilderConfig {
    return {
      name: 'useDefaultRoles',
      component: SwitchFormControlComponent,
      options: {
        label: 'Use default roles'
      }
    };
  }

  getNodeABRoleControls(): IFormControlBuilderConfig[] {
    return [
      {
        name: 'nodeARole',
        component: ExpressionInspectorControlComponent,
        options: {
          label: 'First record role'
        } as IExpressionInspectorControlOptions
      },
      {
        name: 'nodeBRole',
        component: ExpressionInspectorControlComponent,
        options: {
          label: 'Second record role'
        } as IExpressionInspectorControlOptions
      }
    ];
  }

  getViewModeInspectorControls(): IFormControlBuilderConfig[] {
    return [
      {
        name: 'viewDefinitionNameForSelect',
        component: RxDefinitionPickerComponent,
        options: {
          label: 'View for selecting associated records',
          definitionType: RxDefinitionPickerType.View,
          required: true
        }
      },
      {
        name: 'viewDefinitionNameForCreate',
        component: RxDefinitionPickerComponent,
        options: {
          label: 'View for creating associated records',
          definitionType: RxDefinitionPickerType.View
        }
      }
    ];
  }

  getInitialInspectorControls(expressionConfigurator: IExpressionConfigurator): IFormControlBuilderConfig[] {
    return [
      {
        name: 'label',
        component: TextFormControlComponent,
        options: {
          label: 'Display label'
        }
      },
      getDisabledFieldInspectorConfig(),
      ...getStandardPropsInspectorConfigs()
    ];
  }

  getInitialProperties(): IAssociationDesignProperties {
    return {
      associatedRecordNodeSide: '',
      associationDefinitionName: '',
      displayedFields: [],
      editingMode: RxAssociationEditingMode.Views,
      fieldId: null,
      filterByAssociation: '',
      label: null,
      nodeARole: '',
      nodeBRole: '',
      recordDefinition: '',
      recordDefinitionName: '',
      recordId: '',
      recordInstance: '',
      useDefaultRoles: true,
      viewDefinitionNameForCreate: '',
      viewDefinitionNameForSelect: '',
      ...RX_DISABLED_PROP_DEFAULT_VALUE,
      ...RX_STANDARD_PROPS_DEFAULT_VALUES
    };
  }

  getActionButtonDefinition(
    componentProperties: IAssociationDesignProperties,
    action: string,
    parentGuid: string
  ): ViewComponentAddComponentPayload {
    return {
      type: RxViewComponentType.ActionButton,
      guid: this.rxGuidService.generate(),
      propertiesByName: {
        labelKey:
          action === RX_ASSOCIATION.actions.associate
            ? 'com.bmc.arsys.rx.client.view-components.record-editor.association.button.associate.label'
            : 'com.bmc.arsys.rx.client.view-components.record-editor.association.button.create-new.label',
        style: 'secondary',
        action
      },
      children: [
        {
          guid: this.rxGuidService.generate(),
          type: RxViewComponentType.Action,
          propertiesByName: {
            name: 'rxAvcAssociate',
            associationViewComponent: `\${view.components.${parentGuid}.api}`,
            viewDefinitionName:
              action === RX_ASSOCIATION.actions.associate
                ? componentProperties.viewDefinitionNameForSelect
                : componentProperties.viewDefinitionNameForCreate
          }
        }
      ],
      insertIndex: action === RX_ASSOCIATION.actions.associate ? 0 : 1
    };
  }

  isCardinalityOne(
    componentProperties: IAssociationDesignProperties,
    associationDefinitions: IAssociationDefinition[]
  ): boolean {
    const associationDefinition: IAssociationDefinition = associationDefinitions.find(
      (association) => association.name === componentProperties.associationDefinitionName
    );

    return Boolean(
      associationDefinition &&
        (associationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.oneToOne.value ||
          (associationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.oneToMany.value &&
            componentProperties.associatedRecordNodeSide === RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.value))
    );
  }

  getAvailableAssociations(
    firstRecordDefinitionName: string,
    secondRecordDefinitionName: string
  ): Observable<IAssociationDefinition[]> {
    const getParametersForAssociation = {
      headers: { 'default-bundle-scope': '' },
      params: { firstRecordDefinitionName, secondRecordDefinitionName, requireDependent: true }
    };

    return this.rxAssociationDefinitionDataPageService
      .get(getParametersForAssociation)
      .pipe(map((response) => response.data));
  }

  getRecordDefinition(recordDefinitionToAssociateName: string): Observable<IRecordDefinition> {
    return this.rxRecordDefinitionCacheService.getRecordDefinition(recordDefinitionToAssociateName);
  }

  getFieldsInChildComponentsWithNames(
    components: IAssociatedRecordField[],
    availableFields: IFieldDefinition[]
  ): IAssociatedRecordField[] {
    return components.reduce((acc: IAssociatedRecordField[], definition: IAssociatedRecordField) => {
      const fieldDescriptor = availableFields.find(
        (fieldDescriptorItem) => fieldDescriptorItem.id === Number(definition.data.fieldId)
      );
      acc.push({
        ...definition,
        data: {
          ...definition.data,
          name: fieldDescriptor ? fieldDescriptor.name : definition.data.label
        }
      });

      return acc;
    }, []);
  }

  cleanEditingModeDependentData(componentProperties: IAssociationDesignProperties): void {
    if (componentProperties.editingMode === RxAssociationEditingMode.Dropdown) {
      componentProperties.viewDefinitionNameForSelect = null;
      componentProperties.viewDefinitionNameForCreate = null;
      componentProperties.displayedFields = [];
    } else {
      componentProperties.filterByAssociation = null;
      componentProperties.recordId = null;
      componentProperties.fieldId = null;
    }
  }

  getNodeSideOptions(associationDefinition: IAssociationDefinition): RxSelectOption[] {
    return associationDefinition.nodeAId === associationDefinition.nodeBId
      ? [
          {
            id: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.value,
            name: associationDefinition.nodeAName || RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.defaultName
          },
          {
            id: RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.value,
            name: associationDefinition.nodeBName || RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.defaultName
          }
        ]
      : [];
  }

  resetRolesData(componentProperties: IAssociationDesignProperties): void {
    componentProperties.useDefaultRoles = false;
    componentProperties.nodeARole = null;
    componentProperties.nodeBRole = null;
  }

  validate(
    sandbox: IViewComponentDesignSandbox,
    properties: IAssociationDesignProperties,
    associationDefinition: IAssociationDefinition,
    childComponents: IAssociationChildComponents
  ): IViewComponentDesignValidationIssue[] {
    const validationIssues = [];

    if (!properties.recordDefinitionName) {
      validationIssues.push(
        sandbox.createError('Record definition to associate cannot be blank.', 'recordDefinitionName')
      );
    }

    if (properties.recordDefinitionName && !properties.associationDefinitionName) {
      validationIssues.push(sandbox.createError('Association to Use cannot be blank.', 'associationDefinitionName'));
    }

    if (properties.associationDefinitionName) {
      if (associationDefinition.nodeAId === associationDefinition.nodeBId && !properties.associatedRecordNodeSide) {
        validationIssues.push(sandbox.createError('Associated Record Node Side cannot be blank.', 'fieldId'));
      }
    }

    if (properties.editingMode === RxAssociationEditingMode.Views || !properties.editingMode) {
      if (properties.recordDefinitionName && childComponents.displayedFields.length === 0) {
        validationIssues.push(sandbox.createError('Preview Fields cannot be empty.', 'displayedFields'));
      }

      if (!properties.viewDefinitionNameForSelect) {
        validationIssues.push(
          sandbox.createError('View for selecting associated records cannot be blank.', 'viewDefinitionNameForSelect')
        );
      }
    } else if (properties.editingMode === RxAssociationEditingMode.Dropdown) {
      if (properties.filterByAssociation && !childComponents.recordId) {
        validationIssues.push(sandbox.createError('Record ID cannot be blank.', 'recordId'));
      }

      if (!properties.fieldId) {
        validationIssues.push(sandbox.createError('Field to display cannot be blank.', 'fieldId'));
      }
    }

    validationIssues.push(...validateStandardProps(properties));

    return validationIssues;
  }
}
