import { Component, Injector, Input, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup } from '@angular/forms';
import { IFormWidgetComponent } from '@helix/platform/shared/api';
import { InspectorWidgetBase } from '@helix/platform/shared/components';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import { RxGuidService } from '@helix/platform/utils';
import { omit } from 'lodash';
import { ReplaySubject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ISelectGroupFieldProperties, ISelectGroupFormControlOptions } from './select-group-design.types';
import { SelectGroupDesignModel } from './select-group-design.model';

interface IFieldFormGroup extends ISelectGroupFieldProperties {
  optionFilterFieldId: string;
  sourceRecordDefinitionName: string;
}

@Component({
  selector: 'rx-field-set-form-control',
  templateUrl: './field-set-form-control.component.html',
  styleUrls: ['./field-set-form-control.component.scss']
})
export class FieldSetFormControlComponent
  extends InspectorWidgetBase<ISelectGroupFormControlOptions, SelectGroupDesignModel>
  implements OnDestroy, OnInit, IFormWidgetComponent
{
  @Input()
  options: ISelectGroupFormControlOptions;

  @Input()
  isDisabled: boolean;

  formArray = this.formBuilder.array([]);
  valueChangeSubscription: Subscription;

  private maxSelectGroupFieldCount = 6;
  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(
    private formBuilder: FormBuilder,
    private rxGuidService: RxGuidService,
    private rxModalService: RxModalService,
    injector: Injector
  ) {
    super(injector);
  }

  ngOnInit(): void {
    this.designerItemModel.fieldProperties$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((fieldModels) => this.onWriteValue(fieldModels));

    this.setDisabledState(this.isDisabled);
  }

  onWriteValue(fieldModels: ISelectGroupFieldProperties[]): void {
    this.valueChangeSubscription?.unsubscribe();

    const formGroups = fieldModels
      .sort((a, b) => Number(a.index) - Number(b.index))
      .map((model) =>
        this.formBuilder.group({
          ...omit(model, 'index'),
          sourceRecordDefinitionName: { value: null, disabled: true }
        })
      );

    this.formArray = this.formBuilder.array(formGroups);

    this.valueChangeSubscription = this.formArray.valueChanges
      .pipe(takeUntil(this.destroyed$))
      .subscribe(this.onFormValueChange.bind(this));
  }

  private onFormValueChange(formValue: IFieldFormGroup[]): void {
    const value = formValue.map((formGroup, index) => {
      const inspectorFieldModel: ISelectGroupFieldProperties = {
        ...omit(formGroup, ['sourceRecordDefinitionName', 'optionFilterFieldId']),
        index: String(index),
        fieldId: formGroup.namedListDefinitionName ? formGroup.fieldId : null
      };

      if (index > 0) {
        inspectorFieldModel.optionFilterFieldId = formGroup.namedListDefinitionName
          ? formGroup.optionFilterFieldId
          : null;
      }

      return inspectorFieldModel;
    });

    this.designerItemModel.onFieldPropertiesChange(value);
  }

  addField(): void {
    this.formArray.push(this.getFieldFormGroup());
  }

  removeField(index: number): void {
    this.rxModalService
      .confirm({
        title: 'Warning',
        modalStyle: RX_MODAL.modalStyles.warning,
        message: 'Are you sure you want to delete this field?'
      })
      .then((response) => {
        if (response) {
          this.formArray.removeAt(index);
        }
      });
  }

  isAddFieldButtonDisabled(): boolean {
    return this.isDisabled || this.formArray.length >= this.maxSelectGroupFieldCount;
  }

  trackByFn(index: number, control: AbstractControl): string {
    return control.get('guid').value;
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  private getFieldFormGroup(): FormGroup {
    return this.formBuilder.group({
      guid: this.rxGuidService.generate(),
      label: null,
      namedListDefinitionName: null,
      fieldId: null,
      sourceRecordDefinitionName: null,
      optionFilterFieldId: null
    });
  }

  setDisabledState(isDisabled: boolean): void {
    isDisabled ? this.formArray.disable() : this.formArray.enable();
  }
}
