import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { RxRecordInstanceService, RxRecordInstanceUtilsService } from '@helix/platform/record/api';
import { RxLogService } from '@helix/platform/shared/api';
import { BaseViewComponent } from '@helix/platform/view/runtime';
import { TranslateService } from '@ngx-translate/core';
import { isEqual } from 'lodash';
import { Observable, throwError } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay, takeUntil, tap } from 'rxjs/operators';
import { ImageAlignment, RX_IMAGE_ALIGNMENT_STYLES } from '../image.types';
import { IImageConfig } from './image.types';

@Component({
  selector: 'rx-image',
  templateUrl: './image.component.html',
  styleUrls: ['./image.component.scss']
})
export class ImageComponent extends BaseViewComponent implements OnInit, OnDestroy {
  @Input()
  config: Observable<IImageConfig>;

  state = {
    recordDefinitionName: '',
    recordInstanceId: '',
    fieldId: '',
    maxWidth: '',
    imageUrl: null,
    isImageBroken: false,
    alignmentStyle: '',
    altText: ''
  };

  api = {
    setProperty: this.setProperty.bind(this)
  };

  fileName = '';

  constructor(
    private rxRecordInstanceService: RxRecordInstanceService,
    private rxLogService: RxLogService,
    private httpClient: HttpClient,
    private domSanitizer: DomSanitizer,
    private translateService: TranslateService,
    private rxRecordInstanceUtilsService: RxRecordInstanceUtilsService
  ) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.notifyPropertyChanged('api', this.api);

    const config$ = this.config.pipe(
      distinctUntilChanged(isEqual),
      tap((config) => {
        const newState = {
          ...config
        };

        this.isHidden = newState.hidden;

        if (!this.state.alignmentStyle && newState.alignment) {
          newState.alignmentStyle = this.getAlignmentStyle(newState.alignment);
        }

        delete newState.alignment;

        this.state = { ...this.state, ...newState };
      }),
      shareReplay(1)
    );

    config$
      .pipe(
        map(() => (this.state.recordInstanceId ? this.state.recordInstanceId : null)),
        filter((recordInstanceId) => Boolean(recordInstanceId)),
        distinctUntilChanged(),
        takeUntil(this.destroyed$)
      )
      .subscribe((recordInstanceId) => {
        const imageUrl = this.rxRecordInstanceService.getAttachmentDownloadUrl(
          this.state.recordDefinitionName,
          Number(this.state.fieldId),
          recordInstanceId
        );

        this.httpClient.get(imageUrl, { responseType: 'blob', observe: 'response' }).subscribe({
          next: (response) => {
            const fileStream = response.body;

            this.fileName = this.rxRecordInstanceUtilsService.tryParseContentDisposition(
              response.headers.get('content-disposition')
            );

            if (fileStream && fileStream.type.split('/')[0] === 'image') {
              const fileReader = new FileReader();

              fileReader.onloadend = () => {
                this.state.imageUrl = this.domSanitizer.bypassSecurityTrustResourceUrl(fileReader.result as string);
                this.state.isImageBroken = false;
                this.state.altText = this.fileName;
              };

              fileReader.onerror = this.onImageLoadFailed.bind(this);

              fileReader.readAsDataURL(fileStream);
            } else {
              this.rxLogService.warning(`rx-image: attachment is not a valid image.`);
              this.onImageLoadFailed();
            }
          },
          error: (err: HttpErrorResponse) => {
            this.state.imageUrl = null;
            this.state.isImageBroken = false;
            this.state.altText = '';
          }
        });
      });
  }

  private onImageLoadFailed(): void {
    this.state.imageUrl = null;
    this.state.isImageBroken = true;

    this.state.altText = this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.image.invalid-image.label',
      { imageName: this.fileName }
    );
  }

  private getAlignmentStyle(alignment: ImageAlignment): string {
    return RX_IMAGE_ALIGNMENT_STYLES[alignment];
  }

  private setProperty(propertyPath: string, value: any): void | Observable<never> {
    if (propertyPath === 'hidden') {
      this.isHidden = value;

      this.notifyPropertyChanged(propertyPath, this.isHidden);
    } else {
      return throwError(`Image: property ${propertyPath} is not settable.`);
    }
  }
}
