import { Injector } from '@angular/core';
import { RxDefinitionNameService } from '@helix/platform/shared/api';
import {
  IViewComponentDesignCommonDataDictionaryBranch,
  RxViewComponentExpressionConfigurator
} from '@helix/platform/view/designer';
import { concatMap, switchMap, map } from 'rxjs/operators';
import { AssociationDesignModel } from './association-design.model';
import { combineLatest, Observable, of } from 'rxjs';
import { IAssociationDesignProperties } from './association-design.types';

export class AssociationExpressionConfigurator extends RxViewComponentExpressionConfigurator {
  private rxDefinitionNameService = this.injector.get<RxDefinitionNameService>(RxDefinitionNameService);

  constructor(
    protected injector: Injector,
    protected componentGuid: string,
    protected componentModel: AssociationDesignModel
  ) {
    super(injector, componentGuid, componentModel);

    this.configureForProperty({
      propertyPath: 'recordId',
      dataDictionary$: componentModel.recordEditorGuid$.pipe(
        concatMap((recordEditorGuid) =>
          this.getCommonDataDictionary(this.getRecordEditorBranch(recordEditorGuid), recordEditorGuid)
        )
      )
    });
  }

  private getRecordEditorBranch(recordEditorGuid: string): Observable<IViewComponentDesignCommonDataDictionaryBranch> {
    const associationsBranch$: Observable<IViewComponentDesignCommonDataDictionaryBranch> =
      this.componentModel.otherAssociationRecordEditorComponentGuids$.pipe(
        switchMap((guids) =>
          guids.length
            ? combineLatest(
                guids.map((guid) =>
                  this.componentModel.sandbox
                    .getComponentPropertyValue<'associationDefinitionName', IAssociationDesignProperties>(
                      'associationDefinitionName',
                      guid
                    )
                    .pipe(
                      map(
                        (associationDefinitionName): IViewComponentDesignCommonDataDictionaryBranch => ({
                          label: this.rxDefinitionNameService.getDisplayName(associationDefinitionName),
                          children: [
                            {
                              label: 'First associated record',
                              children: [
                                {
                                  label: 'ID',
                                  expression: `\${view.components.${guid}.firstAssociatedRecord.id}`
                                }
                              ]
                            }
                          ]
                        })
                      )
                    )
                )
              ).pipe(
                map((children) => ({
                  label: 'Associations',
                  children
                }))
              )
            : of(null)
        )
      );

    return this.getComponentCommonDataDictionary(recordEditorGuid).pipe(
      switchMap((recordEditorBranch) =>
        associationsBranch$.pipe(
          map(
            (associationsBranch): IViewComponentDesignCommonDataDictionaryBranch =>
              associationsBranch
                ? {
                    ...recordEditorBranch,
                    children: [associationsBranch, ...recordEditorBranch.children]
                  }
                : recordEditorBranch
          )
        )
      )
    );
  }
}
