import { Component, Injector, OnInit } from '@angular/core';
import { RxModalClass } from '@helix/platform/ui-kit';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import {
  IRecordGridViewPresetColumn,
  IRecordGridViewPresetColumnsModalOptions,
  IRecordGridViewPresetColumnsModalResult
} from './record-grid-view-preset-columns-modal.types';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { cloneDeep, omit } from 'lodash';
import { IStepperWithUnitsFormControlOptions } from '@helix/platform/shared/components';
import { RX_RECORD_GRID } from '../../../../record-grid.constant';

@Component({
  selector: 'rx-record-grid-view-preset-columns-modal',
  templateUrl: './record-grid-view-preset-columns-modal.component.html',
  styleUrls: ['./record-grid-view-preset-columns-modal.component.scss']
})
export class RecordGridViewPresetColumnsModalComponent extends RxModalClass implements OnInit {
  params: IRecordGridViewPresetColumnsModalOptions = this.activeModalRef.getData();

  presetColumns: IRecordGridViewPresetColumn[] = this.params.presetColumns.map((column, index) => {
    const gridColumn = this.params.gridColumns.find((col) => col.fieldId === column.data.fieldId);

    return {
      ...cloneDeep(column),
      isOpen: index === 0,
      title: gridColumn.title,
      fallbackTitle: gridColumn.fallbackTitle
    };
  });

  isReadOnly = this.params.isReadOnly;

  columnWidthPropertyOptions: IStepperWithUnitsFormControlOptions = RX_RECORD_GRID.columnProperties.find(
    ({ name }) => name === 'width'
  ).options as IStepperWithUnitsFormControlOptions;

  constructor(private activeModalRef: ActiveModalRef, protected injector: Injector) {
    super(activeModalRef, injector);

    this.presetColumns.sort((a, b) => a.data.index - b.data.index);
  }

  expandAllColumns(event: MouseEvent): void {
    event.stopPropagation();

    this.presetColumns.forEach((column) => (column.isOpen = true));
  }

  collapseAllColumns(event: MouseEvent): void {
    event.stopPropagation();

    this.presetColumns.forEach((column) => (column.isOpen = false));
  }

  saveChanges(): void {
    this.activeModalRef.close({
      presetColumns: this.presetColumns.map((column) => omit(column, ['isOpen', 'title']))
    } as IRecordGridViewPresetColumnsModalResult);
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  moveColumn(fromIndex: number, toIndex: number): void {
    this.markAsDirty();
    moveItemInArray(this.presetColumns, fromIndex, toIndex);
    this.updateColumnIndexes();
  }

  onDropInSelectedColumnsContainer(event: CdkDragDrop<IRecordGridViewPresetColumn[]>) {
    this.moveColumn(event.previousIndex, event.currentIndex);
  }

  trackByGuid(index: number, column: IRecordGridViewPresetColumn): string {
    return column.guid;
  }

  private updateColumnIndexes(): void {
    this.presetColumns.forEach((column, index) => {
      column.data.index = index;
    });
  }
}
