import { Component, OnInit } from '@angular/core';
import {
  BaseViewComponent,
  IViewComponentSetProperty,
  RuntimeViewCanvasItemComponent
} from '@helix/platform/view/runtime';
import { distinctUntilChanged, pluck, skip, takeUntil, tap } from 'rxjs/operators';
import {
  ITabPanelApi,
  ITabPanelChildComponentData,
  ITabPanelComponentData,
  ITabPanelConfig,
  ITabPanelState
} from './tab-panel.types';
import { every, findIndex, get, isNil, isNull } from 'lodash';
import { RX_VIEW_DEFINITION } from '@helix/platform/view/api';
import { Observable, throwError } from 'rxjs';

@Component({
  selector: 'rx-tab-panel',
  templateUrl: './tab-panel.component.html'
})
export class RxTabPanelComponent extends BaseViewComponent implements OnInit, IViewComponentSetProperty {
  childLayouts: ITabPanelChildComponentData[] = [];

  state: ITabPanelState = {
    activeTabIndex: null
  };

  api: ITabPanelApi = {
    setProperty: this.setProperty.bind(this)
  };

  constructor(private runtimeCanvasItemComponent: RuntimeViewCanvasItemComponent) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.notifyPropertyChanged('api', this.api);

    this.config.pipe(takeUntil(this.destroyed$)).subscribe((config: ITabPanelConfig) => {
      this.state = { ...this.state, ...config };
      this.isHidden = Boolean(this.state.hidden);
    });

    this.childLayouts = get(
      this.runtimeCanvasItemComponent.getChildren(RX_VIEW_DEFINITION.defaultOutletName),
      '[0].children'
    );

    this.childLayouts.forEach((childLayout) => {
      childLayout.state = {
        enableLazyLoading: false,
        label: '',
        isRendered: false,
        hidden: 0
      };

      childLayout.config
        .pipe(
          tap((childLayoutConfig) => {
            childLayout.state = { ...childLayout.state, ...childLayoutConfig };
          }),
          pluck('hidden'),
          distinctUntilChanged(),
          skip(1),
          takeUntil(this.destroyed$)
        )
        .subscribe(() => {
          this.ensureActiveTabIsSet();
        });
    });

    this.trySetFirstVisibleTab();
  }

  setProperty(propertyPath: string, propertyValue: any): void | Observable<never> {
    if (propertyPath === 'hidden') {
      this.state.hidden = propertyValue;
      this.notifyPropertyChanged(propertyPath, propertyValue);
    } else if (propertyPath === 'activeTabIndex') {
      this.setActiveTabIndex(parseInt(propertyValue, 10));
    } else {
      return throwError(`Tab panel: property ${propertyPath} is not settable.`);
    }
  }

  trackByForTabs(index: number, item: ITabPanelChildComponentData): string {
    return item.guid;
  }

  canRenderContentForTab(tab: ITabPanelChildComponentData): boolean {
    return !tab.state.enableLazyLoading || tab.state.isRendered;
  }

  private getAllTabsData(): ITabPanelComponentData[] {
    let adaptTabsetIndex = 0;

    return this.childLayouts.map((tab): ITabPanelComponentData => {
      return {
        adaptTabsetIndex: this.isHiddenTab(tab) ? null : adaptTabsetIndex++,
        tab
      };
    });
  }

  isHiddenTab(tab: ITabPanelChildComponentData): boolean {
    return Boolean(tab.state.hidden);
  }

  getActiveTabIndexForAdaptTabset() {
    return isNull(this.state.activeTabIndex) ? 0 : this.getAllTabsData()[this.state.activeTabIndex].adaptTabsetIndex;
  }

  onActiveTabChanged(adaptTabsetIndex: number): void {
    const activeTabIndexForAllTabs = findIndex(
      this.getAllTabsData(),
      (tab) => tab.adaptTabsetIndex === adaptTabsetIndex
    );

    this.activateTab(activeTabIndexForAllTabs);
  }

  private setActiveTabIndex(index: number): void {
    const tab = this.childLayouts[index];

    if (tab && !this.isHiddenTab(tab)) {
      this.activateTab(index);
    }
  }

  private activateTab(index: number | null): void {
    this.state.activeTabIndex = index;

    if (!isNil(index)) {
      this.childLayouts[index].state.isRendered = true;
    }

    this.notifyPropertyChanged('activeTabIndex', index);
  }

  private ensureActiveTabIsSet(): void {
    const allTabs = this.childLayouts;
    const isAllTabsHidden = every(allTabs, (tab) => this.isHiddenTab(tab));

    if (isAllTabsHidden) {
      this.activateTab(null);
    } else if (!isNull(this.state.activeTabIndex) && this.isHiddenTab(allTabs[this.state.activeTabIndex])) {
      this.trySetFirstVisibleTab();
    } else if (isNull(this.state.activeTabIndex)) {
      this.trySetFirstVisibleTab();
    }
  }

  private trySetFirstVisibleTab() {
    const firstVisibleTab = findIndex(this.childLayouts, (tab) => !this.isHiddenTab(tab));

    if (firstVisibleTab !== -1) {
      this.activateTab(firstVisibleTab);
    }
  }
}
