import { Injectable } from '@angular/core';
import { chain, filter, without } from 'lodash';

import { IDefinitionAdapter, IPlainObject } from '@helix/platform/shared/api';
import {
  AnyViewComponentDefinition,
  IContainerViewComponentDefinition,
  IViewDefinition,
  IViewLayout,
  RxViewComponentType,
  RxViewDefinitionParserService
} from '@helix/platform/view/api';
import { RxJsonParserService } from '@helix/platform/utils';
import { RX_RECORD_GRID } from '../record-grid.constant';
import { ActionButtonSize, ActionButtonStyle } from '../../action-button/action-button.types';
import { RxRecordGridConfigUtilsService } from '../runtime/services/record-grid-config-utils.service';

@Injectable()
export class RxRecordGridDefinitionAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition>
{
  constructor(
    private viewDefinitionParserService: RxViewDefinitionParserService,
    private rxJsonParserService: RxJsonParserService,
    private rxRecordGridConfigUtilsService: RxRecordGridConfigUtilsService
  ) {}

  adaptDefinition(
    recordGridContainerViewComponentDefinition: IContainerViewComponentDefinition,
    viewDefinition: IViewDefinition
  ): void {
    const cellDisplayPropertiesList = recordGridContainerViewComponentDefinition.componentDefinitions
      .filter(({ type }) => type === RX_RECORD_GRID.components.column)
      .map(({ propertiesByName }) => propertiesByName.cellDisplayProperties)
      .filter(Boolean);

    this.viewDefinitionParserService
      .getComponents(recordGridContainerViewComponentDefinition)
      .map((componentDefinition) => componentDefinition.componentDefinition as IContainerViewComponentDefinition)
      .forEach((componentDefinition) => {
        switch (componentDefinition.type) {
          case RX_RECORD_GRID.type: {
            componentDefinition.propertiesByName.enableFilterPresets =
              componentDefinition.propertiesByName.enableFilterPresets ?? 'true';

            componentDefinition.propertiesByName.requiredFilters =
              componentDefinition.propertiesByName.requiredFilters ??
              (componentDefinition.propertiesByName['requireFiltering'] === 'true' ? 1 : 0);

            const layout = this.rxJsonParserService.tryParseJson<IViewLayout>(componentDefinition.layout);

            const actionButtons = filter(componentDefinition.componentDefinitions, {
              type: RxViewComponentType.ActionButton
            });

            const rowActionButtons = this.getActionButtonsForOutlet(
              layout,
              RX_RECORD_GRID.rowActionsOutletName,
              actionButtons
            );

            rowActionButtons.forEach((action) => {
              action.propertiesByName = { ...action.propertiesByName, cls: 'dropdown-item', styles: 'p-0' };
            });

            without(actionButtons, ...rowActionButtons).forEach((actionButton) => {
              if (
                [ActionButtonStyle.Primary, ActionButtonStyle.Secondary].includes(actionButton.propertiesByName.style)
              ) {
                actionButton.propertiesByName.size = ActionButtonSize.Small;
              }
            });

            break;
          }
          case RX_RECORD_GRID.components.column: {
            let isReferenced = null;

            if (
              !this.rxRecordGridConfigUtilsService.getBooleanValue(
                recordGridContainerViewComponentDefinition.propertiesByName.getDataForHiddenColumns
              )
            ) {
              const recordGridGuid = recordGridContainerViewComponentDefinition.guid;
              const columnFieldId = componentDefinition.propertiesByName.fieldId;

              const isReferencedInExpression = viewDefinition.viewComponentExpressions.some(
                (expression) =>
                  expression.includes(`\$\{view.components.${recordGridGuid}.firstSelectedRow.${columnFieldId}`) ||
                  expression.includes(`\$\{view.components.${recordGridGuid}.clickableRow.${columnFieldId}`)
              );

              const isReferencedInCellDisplayProperties = cellDisplayPropertiesList.some((cellDisplayProperties) =>
                cellDisplayProperties.includes(`\$\{view.components.grid.clickableRow.${columnFieldId}`)
              );

              isReferenced = isReferencedInExpression || isReferencedInCellDisplayProperties;
            }

            const adaptedProperties = {
              actions: [],
              clickable: false,
              guid: componentDefinition.guid,
              wrapText: componentDefinition.propertiesByName.wrapText ?? false,
              referenced: isReferenced,
              typeaheadKeystrokeCount:
                componentDefinition.propertiesByName.typeaheadKeystrokeCount ??
                RX_RECORD_GRID.defaultTypeaheadKeystrokeCount
            };

            const actions = filter(componentDefinition.componentDefinitions, {
              type: 'rx-action'
            }).map((action) => action.propertiesByName);

            if (actions.length) {
              adaptedProperties.clickable = true;
              adaptedProperties.actions = actions;
            }

            Object.assign(componentDefinition.propertiesByName, adaptedProperties);
            break;
          }
          case RX_RECORD_GRID.components.filterPreset:
          case RX_RECORD_GRID.components.filter: {
            Object.assign(componentDefinition.propertiesByName, {
              guid: componentDefinition.guid
            });
            break;
          }
        }
      });
  }

  private getActionButtonsForOutlet(
    layout: IViewLayout,
    outletName: string,
    actionButtons: AnyViewComponentDefinition<IPlainObject>[]
  ): AnyViewComponentDefinition<IPlainObject>[] {
    const actionButtonsGuids: string[] = chain(layout.outlets)
      .filter({
        name: outletName
      })
      .head()
      .get('columns[0].children', [])
      .value();

    return filter(actionButtons, (actionButton) => actionButtonsGuids.includes(actionButton.guid));
  }
}
