import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  HostBinding,
  Injector,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { AdaptModalService } from '@bmc-ux/adapt-angular';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IFormFocusable } from '@helix/platform/shared/api';
import { InspectorWidgetBase } from '@helix/platform/shared/components';
import { combineLatest } from 'rxjs';
import { concatMap, take } from 'rxjs/operators';
import { RecordEditorDesignModel } from '../../record-editor-design.model';
import { FieldSelectorComponent } from './field-selector/field-selector.component';
import { IFieldSelectorConfig } from './field-selector/field-selector.types';
import { IRecordEditorFieldsControlOptions } from './fields-inspector-widget.types';

@Component({
  selector: 'rx-fields-inspector-widget',
  templateUrl: './fields-inspector-widget.component.html',
  styleUrls: ['./fields-inspector-widget.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FieldsInspectorWidgetComponent
  extends InspectorWidgetBase<IRecordEditorFieldsControlOptions, RecordEditorDesignModel>
  implements IFormFocusable
{
  @HostBinding('class.rx-record-definition-fields')
  encapsulateClass = true;

  @ViewChild('openModalButton', { read: ElementRef, static: true })
  openModalButton: ElementRef;

  constructor(injector: Injector, private adaptModalService: AdaptModalService, private renderer: Renderer2) {
    super(injector);
  }

  focus(): void {
    this.renderer.selectRootElement(this.openModalButton.nativeElement, true).click();
  }

  openModal(): void {
    combineLatest([
      this.designerItemModel.childFieldIds$,
      this.designerItemModel.recordFieldDefinitionItems$,
      this.designerItemModel.isExtensionView$,
      this.designerItemModel.associationDefinitionName$
    ])
      .pipe(
        take(1),
        concatMap(([selectedFieldIds, availableFields, isExtensionView, associationDefinitionName]) =>
          this.adaptModalService.open({
            content: FieldSelectorComponent,
            data: {
              selectedFieldIds,
              availableFields:
                isExtensionView && associationDefinitionName
                  ? availableFields.filter(
                      (field) => field.resourceType !== RX_RECORD_DEFINITION.resourceTypes.attachment
                    )
                  : availableFields,
              hideSystemFields: this.options.hideSystemFields
            } as IFieldSelectorConfig,
            size: 'sm',
            hideBackdrop: false,
            customClass: 'rx-record-editor-field-selector-modal'
          })
        )
      )
      .subscribe((selectedFieldIds) => {
        this.designerItemModel.updateSelectedFieldComponents(selectedFieldIds);
      });
  }

  removeField(guid: string): void {
    this.designerItemModel.removeComponent([guid]);
  }

  trackByGuid<T extends { guid: string }>(index: number, item: T): string {
    return item.guid;
  }
}
