import { Component, Input, OnInit } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { Observable, throwError } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { BaseViewComponent, IViewComponentSetProperty } from '@helix/platform/view/runtime';
import { IRichTextConfig } from './rich-text.types';
import { RxCkEditorConfiguratorService } from '@helix/platform/view/api';

@Component({
  selector: 'rx-rich-text',
  template: `
    <div
      class="text-container focusable"
      [hidden]="isHidden"
      tabindex="0"
      [innerHTML]="html"
      [attr.aria-hidden]="isHidden"
    ></div>
  `,
  styleUrls: ['./rich-text.component.scss']
})
export class RichTextComponent extends BaseViewComponent implements OnInit, IViewComponentSetProperty {
  @Input()
  config: Observable<IRichTextConfig>;

  html: SafeHtml;
  filter = new CKEDITOR.filter('');

  api = {
    setProperty: this.setProperty.bind(this)
  };

  constructor(private sanitizer: DomSanitizer, private rxCkEditorConfiguratorService: RxCkEditorConfiguratorService) {
    super();

    this.filter.allow(this.rxCkEditorConfiguratorService.getContentRules());
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.notifyPropertyChanged('api', this.api);

    this.config.pipe(takeUntil(this.destroyed$)).subscribe((config) => {
      this.isHidden = Boolean(config.hidden);
      this.html = this.updateHtml(config.html);
    });
  }

  updateHtml(value: string): SafeHtml {
    if (value) {
      value = value.replace(/\n/g, '<br>');

      const fragment = CKEDITOR.htmlParser.fragment.fromHtml(value);
      const writer = new CKEDITOR.htmlParser.basicWriter();

      this.filter.applyTo(fragment);
      fragment.writeHtml(writer);
      value = writer.getHtml(true);
    } else {
      value = '';
    }

    return this.sanitizer.bypassSecurityTrustHtml(value);
  }

  setProperty(propertyPath: string, value: any): void | Observable<never> {
    if (propertyPath === 'hidden') {
      this.isHidden = value;

      this.notifyPropertyChanged(propertyPath, this.isHidden);
    } else {
      return throwError(`Rich text: property ${propertyPath} is not settable.`);
    }
  }
}
