import { Component, ElementRef, Injector, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { ActiveModalRef, AdaptAccordionTabComponent, DismissReasons, RxSelectOption } from '@bmc-ux/adapt-angular';
import {
  ICellDisplayProperties,
  ICellDisplayPropertiesEditorConfig,
  RX_CELL_PROPERTIES,
  RxGridCellBgColorCssMap,
  RxGridCellColor,
  RxGridCellIconPosition
} from '../../../../../common/types/cell-display-properties.types';
import {
  IExpressionEditorPropertyChange,
  IExpressionFormControlOptions,
  IIconPickerFormControlOptions,
  RxExpressionEditorService
} from '@helix/platform/shared/components';
import { ExpressionOperatorGroup, IPlainObject } from '@helix/platform/shared/api';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { forEach, get, omit } from 'lodash';
import { moveItemInArray } from '@angular/cdk/drag-drop';
import { CdkDragDrop } from '@angular/cdk/drag-drop/';
import { ReplaySubject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { RecordGridCellDisplayPropertiesExpressionConfigurator } from './cell-display-properties-expression-configurator.class';
import { RxModalClass } from '@helix/platform/ui-kit';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'rx-cell-display-properties-editor',
  templateUrl: './cell-display-properties-editor.component.html',
  styleUrls: ['./cell-display-properties-editor.component.scss']
})
export class RecordGridCellDisplayPropertiesEditorComponent extends RxModalClass implements OnInit, OnDestroy {
  @ViewChildren(AdaptAccordionTabComponent, { read: ElementRef })
  private accordionTabEls: QueryList<ElementRef>;
  private destroyed$ = new ReplaySubject<boolean>(1);

  private expressionConfigurator: RecordGridCellDisplayPropertiesExpressionConfigurator;

  config: ICellDisplayPropertiesEditorConfig = this.activeModalRef.getData();
  queryExpressionOptions: IExpressionFormControlOptions;
  propsFormArray: FormArray = this.formBuilder.array([]);
  iconPositions: RxSelectOption[] = RX_CELL_PROPERTIES.iconPositions;
  textColors: RxSelectOption[] = RX_CELL_PROPERTIES.colorsList;
  fontSizes: RxSelectOption[] = RX_CELL_PROPERTIES.fontSizes;
  colorsList: IPlainObject = RxGridCellBgColorCssMap;

  backgroundColors: RxSelectOption[] = RX_CELL_PROPERTIES.colorsList.filter(
    (color) => color.value !== RxGridCellColor.Light
  );

  badgeColors: RxSelectOption[] = RX_CELL_PROPERTIES.colorsList.filter(
    (color) => ![RxGridCellColor.Light, RxGridCellColor.Active, null].includes(color.value)
  );

  iconPickerOptions: IIconPickerFormControlOptions = {
    label: 'Icon',
    appendToBody: true,
    required: false
  };

  constructor(
    public activeModalRef: ActiveModalRef,
    private rxExpressionEditorService: RxExpressionEditorService,
    private formBuilder: FormBuilder,
    private translateService: TranslateService,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.expressionConfigurator = new RecordGridCellDisplayPropertiesExpressionConfigurator();

    this.expressionConfigurator.configureForProperty({
      propertyPath: 'cellDisplayProperties',
      dataDictionary$: this.expressionConfigurator.cellDisplayExpressionDataDictionary(this.config.columns),
      operators: this.expressionConfigurator.getOperatorRowsByGroup(ExpressionOperatorGroup.AllClient)
    });

    this.queryExpressionOptions = {
      label: 'Field value condition',
      dataDictionary$: this.expressionConfigurator.getDataDictionary('cellDisplayProperties'),
      operators: this.expressionConfigurator.getOperators('cellDisplayProperties'),
      isRequired: true
    };

    forEach(this.config.cellDisplayProperties, (props) => this.propsFormArray.push(this.getFormGroup(props)));
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  addCellProperties(): void {
    this.markAsDirty();

    this.propsFormArray.push(
      this.getFormGroup({
        fieldValueCondition: '',
        displayAsBadge: false,
        badgeColor: null,
        icon: null,
        iconPosition: null,
        textColor: null,
        backgroundColor: null,
        fontSize: null,
        bold: false,
        italic: false,
        isOpen: true
      })
    );

    setTimeout(() => {
      this.accordionTabEls.last.nativeElement.scrollIntoView({
        block: 'nearest'
      });
    });
  }

  removeCellProperties(index: number): void {
    this.propsFormArray.removeAt(index);
    this.markAsDirty();
  }

  optionFormatter(selectOption: RxSelectOption): string {
    return selectOption.label;
  }

  private getFormGroup(properties: ICellDisplayProperties): FormGroup {
    const formGroup = this.formBuilder.group({
      fieldValueCondition: [properties.fieldValueCondition, Validators.required],
      displayAsBadge: [properties.displayAsBadge],
      badgeColor: {
        value: this.getSelectValue(this.badgeColors, 'value', properties.badgeColor),
        disabled: !properties.displayAsBadge
      },
      icon: {
        value: properties.icon,
        disabled: properties.displayAsBadge
      },
      iconPosition: {
        value: this.getSelectValue(this.iconPositions, 'value', properties.iconPosition),
        disabled: !properties.icon
      },
      textColor: {
        value: this.getSelectValue(this.textColors, 'value', properties.textColor),
        disabled: properties.displayAsBadge
      },
      backgroundColor: {
        value: this.getSelectValue(this.backgroundColors, 'value', properties.backgroundColor),
        disabled: properties.displayAsBadge
      },
      fontSize: {
        value: this.getSelectValue(this.fontSizes, 'value', properties.fontSize),
        disabled: properties.displayAsBadge
      },
      bold: [properties.bold],
      italic: [properties.italic],
      isOpen: [properties.isOpen ?? false]
    });

    formGroup.controls.displayAsBadge.valueChanges
      .pipe(takeUntil(this.destroyed$))
      .subscribe((displayAsBadge: boolean) => {
        if (displayAsBadge && formGroup.controls.badgeColor.disabled) {
          formGroup.controls.badgeColor.reset({ value: [this.badgeColors[0]], disabled: false });
        }

        if (!displayAsBadge && formGroup.controls.badgeColor.enabled) {
          formGroup.controls.badgeColor.reset({ value: [], disabled: true });
        }
      });

    formGroup.controls.icon.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((iconName: string) => {
      if (iconName && formGroup.controls.iconPosition.disabled) {
        formGroup.controls.iconPosition.reset({ value: [this.iconPositions[0]], disabled: false });
      }

      if (!iconName && formGroup.controls.iconPosition.enabled) {
        formGroup.controls.iconPosition.reset({ value: [], disabled: true });
      }
    });

    return formGroup;
  }

  private getSelectValue(optionsList: RxSelectOption[], key: string, value: string): RxSelectOption[] {
    const item = optionsList.find((option) => option[key] === value);

    return item ? [item] : [];
  }

  openExpressionEditor(control: AbstractControl): void {
    this.rxExpressionEditorService
      .openEditor({
        property: {
          path: 'cellDisplayProperties',
          value: control.value,
          label: 'Cell display properties'
        },
        isReadOnly: false,
        expressionConfigurator: this.expressionConfigurator,
        legend: [
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.column.label'),
            icon: 'd-icon-field_text'
          }
        ]
      })
      .subscribe((expression: IExpressionEditorPropertyChange) => {
        control.setValue(expression.value);
        this.markAsDirty();
      });
  }

  moveCellProperties(fromIndex: number, toIndex: number): void {
    moveItemInArray(this.propsFormArray.controls, fromIndex, toIndex);
    this.markAsDirty();
  }

  onSelectedCellPropertyDrop(event: CdkDragDrop<any[], any>): void {
    moveItemInArray(this.propsFormArray.controls, event.previousIndex, event.currentIndex);

    this.markAsDirty();
  }

  toggleProp(control: AbstractControl): void {
    control.setValue(!control.value);
    this.markAsDirty();
  }

  toggleOpen(expandAll: boolean): void {
    this.propsFormArray.controls.forEach((control) => {
      control.get('isOpen').setValue(expandAll);
    });
  }

  resetIconAndTextProps(props: AbstractControl): void {
    const isDisplayAsBadge: boolean = props.get('displayAsBadge').value;

    props.get('icon').reset({ value: null, disabled: isDisplayAsBadge });
    props.get('textColor').reset({ value: [this.textColors[0]], disabled: isDisplayAsBadge });
    props.get('backgroundColor').reset({ value: [this.backgroundColors[0]], disabled: isDisplayAsBadge });
    props.get('fontSize').reset({ value: [this.fontSizes[0]], disabled: isDisplayAsBadge });
    props.get('bold').reset({ value: false, disabled: isDisplayAsBadge });
    props.get('italic').reset({ value: false, disabled: isDisplayAsBadge });
  }

  onSave(): void {
    const formValues: ICellDisplayProperties[] = this.propsFormArray.getRawValue().map((value) => ({
      ...value,
      icon: get(value, 'icon', null),
      badgeColor: get(value, 'badgeColor[0].value', null),
      iconPosition: get(value, 'iconPosition[0].value', null),
      textColor: get(value, 'textColor[0].value', null),
      backgroundColor: get(value, 'backgroundColor[0].value', null),
      fontSize: get(value, 'fontSize[0].value', null)
    }));

    const cellProps: ICellDisplayProperties[] = formValues.map((value) => ({
      ...omit(value, 'isOpen')
    }));

    this.activeModalRef.close(cellProps);
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
