import { Injectable } from '@angular/core';
import { AdaptRxDatetimeAdapter, RxDatetimeStruct } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';
// Adapt dropped moment and changed the signature of AdvancedFilterTimeDataType from [moment.Moment, moment.Moment] to [RxDatetimeStruct, RxDatetimeStruct].
// We need to extend the AdaptRxDatetimeAdapter class to implement the missing moment methods that we rely on.
@Injectable()
export class RxAdaptDatetimeMomentAdapter extends AdaptRxDatetimeAdapter<moment.Moment> {
  /**
   * Converts moment.Moment => RxDatetimeStruct
   */
  fromModel(dateValue: moment.Moment | null): RxDatetimeStruct | null {
    let adaptedValue: RxDatetimeStruct = null;
    const date = moment(dateValue);

    if (date && date.isValid()) {
      adaptedValue = {
        year: date.year(),
        month: date.month(),
        date: date.date(),
        hours: date.hours(),
        minutes: date.minutes(),
        seconds: date.seconds()
      };
    }

    return adaptedValue;
  }

  /**
   * Converts RxDatetimeStruct => moment.Moment
   */
  toModel(date: RxDatetimeStruct | null): moment.Moment | null {
    let adaptedValue: moment.Moment = null;

    if (date) {
      const currentDateTime = moment();

      adaptedValue = date.parseError
        ? moment.invalid({
            invalidFormat: true,
            parsedDateParts: [date.parseError.query],
            meridiem: date.parseError.use12HoursTime ? '12' : '24'
          })
        : moment([
            date.year ?? currentDateTime.year(),
            date.month ?? currentDateTime.month(),
            date.date ?? currentDateTime.date(),
            date.hours ?? 0,
            date.minutes ?? 0,
            date.seconds ?? 0
          ]);
    }

    return adaptedValue;
  }
}
