import { Component, Injector, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { combineLatest, Observable, ReplaySubject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  first,
  map,
  shareReplay,
  skip,
  switchMap,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { cloneDeep, isEqual, noop } from 'lodash';
import { IFormWidgetComponent, IPlainObject } from '@helix/platform/shared/api';
import { InspectorWidgetBase, ISelectFormControlOptions, ISelectOption } from '@helix/platform/shared/components';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import { RxGuidService, RxJsonParserService } from '@helix/platform/utils';
import { OpenViewActionModalSize, RecordGridNamedFilterOptionKey, RxViewComponentType } from '@helix/platform/view/api';
import {
  IViewComponentDesignData,
  IViewComponentDesignSetPayload,
  ViewDesignerFacade
} from '@helix/platform/view/designer';
import { RecordGridDesignModel } from '../../record-grid-design.model';
import { RX_RECORD_GRID } from '../../../record-grid.constant';
import { IRecordGridDesignInspectorColumnConfig } from '../../editors/record-grid-column-editor-control/record-grid-column-editor.types';
import { RecordGridEditViewPresetsModalComponent } from './record-grid-edit-view-presets-modal/record-grid-edit-view-presets-modal.component';
import {
  IPresetItem,
  IRecordGridViewPresetColumnData,
  IRecordGridViewPresetsWidgetOptions
} from './record-grid-view-presets-widget.types';
import {
  IRecordGridEditViewPresetsModalOptions,
  IRecordGridEditViewPresetsModalResult
} from './record-grid-edit-view-presets-modal/record-grid-edit-view-presets-modal.types';
import { RxRecordGridDesignUtilsService } from '../../record-grid-design-utils.service';
import { IViewPresetSelectorDesignProperties } from '../../../../view-preset-selector/design/view-preset-selector-design.types';
import { IRecordGridFilterDescriptorModel } from '../record-grid-filter-select-control/record-grid-filter-select-control.types';
import { RxRecordGridFilterHelperService } from '../../../common/services/record-grid-filter-helper.service';
import { IGridViewPresetProperties } from '../../../common/types/record-grid.types';
import { IRxRecordGridFilter } from '../../../common/types/record-grid-filter.types';

@Component({
  selector: 'rx-record-grid-view-presets-widget',
  templateUrl: './record-grid-view-presets-widget.component.html',
  styleUrls: ['./record-grid-view-presets-widget.component.scss']
})
export class RecordGridViewPresetsWidgetComponent
  extends InspectorWidgetBase<IPlainObject, RecordGridDesignModel>
  implements OnInit, OnChanges, OnDestroy, IFormWidgetComponent
{
  @Input()
  options: IRecordGridViewPresetsWidgetOptions;

  currentPresetSelectorGuid: string;
  currentPresetSelectorGuid$ = new ReplaySubject<string>(1);
  isDisabled: boolean;

  presetSelectors$: Observable<IViewComponentDesignData<IViewPresetSelectorDesignProperties>[]> =
    this.viewDesignerFacade.getComponentsByType<IViewPresetSelectorDesignProperties>(
      RxViewComponentType.ViewPresetSelector
    );

  presetSelectorOptions$: Observable<ISelectOption[]> = this.presetSelectors$.pipe(
    map((items) => items.map(({ guid, data }) => ({ id: guid, name: data.name })))
  );

  private destroyed$ = new ReplaySubject<boolean>(1);

  selectedPresetsList$: Observable<IPresetItem[]> = this.currentPresetSelectorGuid$.pipe(
    tap((guid) => this.updateViewPresetSelectorProp(guid)),
    switchMap((guid) => this.rxRecordGridDesignUtilsService.getPresetsList(guid)),
    shareReplay({ refCount: true, bufferSize: 1 }),
    takeUntil(this.destroyed$)
  );

  viewPresetSelectorProp$ = this.designerItemModel.sandbox.getComponentPropertyValue('viewPresetSelector');

  gridColumnGuids$ = this.designerItemModel.sandbox.getChildComponentGuids(
    (component) => component.type === RX_RECORD_GRID.components.column
  );

  selectOptions: ISelectFormControlOptions = {
    options: [],
    emptyOption: true,
    label: 'View preset selector',
    beforeValueChange: (oldValue, newValue) => {
      if (oldValue && oldValue !== newValue) {
        return this.rxModalService.confirm({
          title: 'Warning',
          modalStyle: RX_MODAL.modalStyles.warning,
          message: 'All record grid view presets will be cleared. Do you want to continue?'
        });
      } else {
        return Promise.resolve(true);
      }
    }
  };

  constructor(
    protected injector: Injector,
    private rxModalService: RxModalService,
    private viewDesignerFacade: ViewDesignerFacade,
    private rxGuidService: RxGuidService,
    private rxRecordGridDesignUtilsService: RxRecordGridDesignUtilsService,
    private rxRecordGridFilterHelperService: RxRecordGridFilterHelperService,
    private rxJsonParserService: RxJsonParserService
  ) {
    super(injector);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.options.currentValue.recordDefinition?.name !== changes.options.previousValue.recordDefinition?.name) {
      this.currentPresetSelectorGuid = null;
      this.currentPresetSelectorGuid$.next(null);
    }
  }

  ngOnInit(): void {
    this.presetSelectorOptions$.pipe(takeUntil(this.destroyed$)).subscribe((options) => {
      this.selectOptions.options = options;
    });

    // select current preset selector item
    this.viewPresetSelectorProp$
      .pipe(
        first(),
        map((val) => this.designerItemModel.extractViewPresetSelectorGuid(val)),
        withLatestFrom(this.presetSelectorOptions$),
        takeUntil(this.destroyed$)
      )
      .subscribe(([guid, presetSelectorOptions]) => {
        const selectedOption = presetSelectorOptions.find((item) => item.id === guid)?.id;

        if (selectedOption) {
          this.currentPresetSelectorGuid = selectedOption;
          this.currentPresetSelectorGuid$.next(this.currentPresetSelectorGuid);
        }
      });

    this.gridColumnGuids$
      .pipe(
        filter(() => !!this.currentPresetSelectorGuid),
        switchMap((guids) =>
          combineLatest(
            guids.map((guid) =>
              combineLatest([
                this.designerItemModel.sandbox.getComponentPropertyValue('sortable', guid),
                this.designerItemModel.sandbox.getComponentPropertyValue('filterable', guid),
                this.viewDesignerFacade.getChildComponents(
                  guid,
                  (c) => c.type === RX_RECORD_GRID.components.filterPreset
                )
              ])
            )
          )
        ),
        distinctUntilChanged(isEqual),
        // in case of removing and adding columns at a same time from columns modal
        // stream updated several times with according to adding/removing columns
        // we need it to be stabilized to proceed with column presets updating
        debounceTime(100),
        switchMap(() => this.designerItemModel.columns$.pipe(first())),
        withLatestFrom(this.designerItemModel.childComponentsTree$.pipe(map((components) => cloneDeep(components)))),
        skip(1),
        takeUntil(this.destroyed$)
      )
      .subscribe(([gridColumns, allComponents]) => {
        this.updateColumnPresets(allComponents, gridColumns);
      });
  }

  private updateColumnPresets(
    allComponents: IViewComponentDesignData[],
    gridColumns: IRecordGridDesignInspectorColumnConfig[]
  ) {
    const viewPresetsPayload = allComponents
      .filter((component) => component.type === RX_RECORD_GRID.components.viewPreset)
      .map((component) => {
        const presetColumnComponents: IRecordGridViewPresetColumnData[] = component.children.filter(
          (item) => item.type === RX_RECORD_GRID.components.columnViewPreset
        );

        const presetFilterComponents = this.getPresetFilterComponentsForColumns(
          component as IViewComponentDesignData<IGridViewPresetProperties>,
          gridColumns
        );

        const updatedPresetColumns = gridColumns
          .map((gridColumn) => {
            const existingPresetColumn = presetColumnComponents.find(
              (presetColumn) => presetColumn.data.fieldId === gridColumn.fieldId
            );

            if (existingPresetColumn) {
              if (!gridColumn.sortable) {
                existingPresetColumn.data.sortable = null;
              }

              return existingPresetColumn;
            } else {
              const columnPresetPayload = this.rxRecordGridDesignUtilsService.getColumnPresetPayload(gridColumn);
              columnPresetPayload.data.visible = false;
              columnPresetPayload.data.index = null;
              columnPresetPayload.data.width = null;

              return columnPresetPayload;
            }
          })
          .map((presetCol, i, array) => {
            if (presetCol.data.index === null) {
              presetCol.data.index = Math.max(...array.map((col) => col.data.index)) + 1;
            }

            return presetCol;
          });

        (component as IViewComponentDesignSetPayload).children = [...updatedPresetColumns, ...presetFilterComponents];

        return component;
      });

    this.designerItemModel.sandbox.setChildrenByType(viewPresetsPayload, [RX_RECORD_GRID.components.viewPreset]);
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  private getPresetFilterComponentsForColumns(
    presetComponent: IViewComponentDesignData<IGridViewPresetProperties>,
    gridColumns: IRecordGridDesignInspectorColumnConfig[]
  ): IViewComponentDesignSetPayload[] {
    const gridColumnsIds = gridColumns.filter((col) => col.filterable).map((col) => col.fieldId);

    const presetFilterComponents: IViewComponentDesignData<IRecordGridFilterDescriptorModel>[] =
      presetComponent.children.filter((item) => item.type !== RX_RECORD_GRID.components.columnViewPreset);

    // removing filter presets for removed columns or ones that have removed named filter option reference
    const filteredPresetFilterComponents = presetFilterComponents.reduce((result, presetFilterComponent) => {
      // checking if column still can be used for filtering
      if (gridColumnsIds.includes(presetFilterComponent.data.fieldId)) {
        const namedFilterOptions =
          gridColumns.find((col) => col.fieldId === presetFilterComponent.data.fieldId)?.namedFilterOptions ?? [];
        const namedFilterOptionsGuids = namedFilterOptions.map((option) => option.guid);

        let presetFiltersObj = this.rxRecordGridDesignUtilsService.getBasicRecordGridFiltersFromChildData([
          presetFilterComponent
        ]);

        presetFiltersObj = this.deserializeNamedOptions(presetFiltersObj);
        const namedFilterOptionGuid = presetFiltersObj[0].value?.[RecordGridNamedFilterOptionKey];

        // if named filter option selected then check if it's still available
        if (namedFilterOptionGuid) {
          if (namedFilterOptionsGuids.includes(namedFilterOptionGuid)) {
            result.push(presetFilterComponent);
          }
        } else {
          result.push(presetFilterComponent);
        }
      }

      return result;
    }, []);

    if (presetFilterComponents.length) {
      if (filteredPresetFilterComponents.length < presetFilterComponents.length) {
        const presetFilters =
          this.rxRecordGridDesignUtilsService.getBasicRecordGridFiltersFromChildData(filteredPresetFilterComponents);

        const newFilterData = this.rxRecordGridFilterHelperService.getRecordGridFilterDataFromPredefinedFilter(
          presetComponent.data.filters,
          presetFilters
        );

        // updating filters JSON after column remove
        presetComponent.data.filters = newFilterData
          ? this.rxRecordGridFilterHelperService.denormalizeFilterString(JSON.stringify(newFilterData), presetFilters)
          : null;
      }

      return filteredPresetFilterComponents;
    }

    return presetFilterComponents;
  }

  deserializeNamedOptions(recordGridFilters: IRxRecordGridFilter[]): IRxRecordGridFilter[] {
    return recordGridFilters.map((filter) => {
      const value = this.rxJsonParserService.tryParseJson(filter.value);

      if (value && value[RecordGridNamedFilterOptionKey]) {
        return {
          ...filter,
          value
        };
      }

      return filter;
    });
  }

  openEditor(preset?: IPresetItem) {
    combineLatest([
      this.designerItemModel.gridViewPresets$,
      this.selectedPresetsList$,
      this.designerItemModel.columns$,
      this.designerItemModel.recordDefinition$
    ])
      .pipe(first(), takeUntil(this.destroyed$))
      .subscribe(([viewPresets, presetsList, columns, recordDefinition]) => {
        this.rxModalService
          .openModal({
            title: 'Edit view presets',
            content: RecordGridEditViewPresetsModalComponent,
            size: OpenViewActionModalSize.Large as any,
            data: {
              viewPresets,
              presetsList,
              gridColumns: columns,
              recordDefinition,
              activePreset: preset?.guid,
              isReadOnly: this.isDisabled
            } as IRecordGridEditViewPresetsModalOptions
          })
          .then((result: IRecordGridEditViewPresetsModalResult) => {
            this.onPresetModalClose(result);
          })
          .catch(noop);
      });
  }

  trackByGuid(index: number, item: IPresetItem): string {
    return item.name;
  }

  private onPresetModalClose(result: IRecordGridEditViewPresetsModalResult): void {
    if (result && result.presets) {
      this.designerItemModel.childComponentsTree$
        .pipe(
          first(),
          map((components) => cloneDeep(components) as IViewComponentDesignSetPayload[]),
          takeUntil(this.destroyed$)
        )
        .subscribe((components) => {
          result.presets.forEach((preset) => {
            const presetComponent = components.find(
              (c) => c.type === RX_RECORD_GRID.components.viewPreset && c.data.viewPresetGuid === preset.guid
            );

            if (presetComponent) {
              const columnPayloads = preset.presetColumns.map((column) =>
                this.rxRecordGridDesignUtilsService.getColumnPresetPayload(column.data)
              );

              const filterPayloads = this.rxRecordGridDesignUtilsService.getGridFilterComponentPayloads(
                preset.filters.basicFilters
              );

              presetComponent.data = {
                viewPresetGuid: preset.guid,
                filters: preset.filters.filtersJson
              };

              presetComponent.children = [...columnPayloads, ...filterPayloads];
            }
          });

          this.designerItemModel.sandbox.setChildren(components);
        });
    }
  }

  private updateViewPresetSelectorProp(guid: string): void {
    this.designerItemModel.sandbox.updateComponentProperties({
      viewPresetSelector: guid ? `\${view.components.${guid}.api}` : null
    });
  }
}
