import { Injectable } from '@angular/core';
import { IRecordDefinition, RX_RECORD_DEFINITION, RxFieldDefinitionService } from '@helix/platform/record/api';
import { IPlainObject } from '@helix/platform/shared/api';
import { RxError, RxGuidService } from '@helix/platform/utils';
import {
  IViewInputParams,
  OpenViewActionLaunchBehavior,
  OpenViewActionType,
  RX_VIEW_ACTION
} from '@helix/platform/view/api';
import { TranslateService } from '@ngx-translate/core';
import { defaults, every, has, isUndefined, reduce } from 'lodash';
import { forkJoin, Observable, throwError } from 'rxjs';
import { map } from 'rxjs/operators';
import { RxRecordGridUtilsService } from '../../common/services/record-grid-utils.service';
import { RX_RECORD_GRID } from '../../record-grid.constant';
import { IColumnSortInfo, IRecordGridColumn, IRecordGridColumnWithMetadata } from '../types/record-grid-column.types';
import { IRecordGridConfig } from '../types/record-grid-config.interface';
import { RxRecordGridConfigUtilsService } from './record-grid-config-utils.service';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridConfiguratorService {
  private allowedOpenViewActionExpressionsForHrefRegex: RegExp;

  constructor(
    private rxGuidService: RxGuidService,
    private rxFieldDefinitionService: RxFieldDefinitionService,
    private rxRecordGridConfigUtilsService: RxRecordGridConfigUtilsService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private translateService: TranslateService
  ) {}

  getGridConfig(rxConfiguration: IRecordGridConfig): IRecordGridConfig {
    const config = defaults(this.rxRecordGridConfigUtilsService.configDeepClone(rxConfiguration), {
      associatedRecordId: null,
      associatedRoleName: null,
      enableColumnSelection: true,
      enableFiltering: true,
      expandable: false,
      useExternalFiltering: true,
      enableRowSelection: RX_RECORD_GRID.selectionTypes.multiple,
      filterExpression: null,
      filters: RX_RECORD_GRID.defaultFilter,
      filterTagsLimit: 3,
      recordIdField: RX_RECORD_DEFINITION.coreFieldIds.id.toString(),
      emptyStateLabelText: this.translateService.instant(
        'com.bmc.arsys.rx.client.empty-state.no-items-to-display.label'
      ),
      emptyStateWithFilterLabelText: this.translateService.instant(
        'com.bmc.arsys.rx.client.empty-state.no-items-to-display.label'
      ),
      searchFieldPlaceholderText: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label'),
      styles: ''
    } as IRecordGridConfig);

    if (!config.recordDefinitionName) {
      if (!rxConfiguration.getRecordDefinition) {
        throwError(new RxError('getRecordDefinition function must be defined if recordDefinitionName is not set.'));
      }
      if (!rxConfiguration.getData) {
        throwError(new RxError('getData function must be defined if recordDefinitionName is not set.'));
      }
    }

    config.enableFiltering = Boolean(config.enableFiltering);
    config.cardLayoutWidth = parseFloat(config.cardLayoutWidth as string) || null;

    return config;
  }

  getColumnsWithMetadata(
    recordGridGuid: string,
    columns: IRecordGridColumn[],
    recordDefinition: IRecordDefinition
  ): Observable<IRecordGridColumnWithMetadata[]> {
    const columnsClone = this.rxRecordGridConfigUtilsService.columnsDeepClone(columns);

    this.allowedOpenViewActionExpressionsForHrefRegex = new RegExp(
      `^\\$\\{view\\.components\\.${recordGridGuid}\\.(clickableRow|recordDefinition)\\.|^\\$\\{view\\.inputParams\\.`
    );

    const columnsDefinitions$ = columnsClone.map((column) => {
      return this.rxRecordGridUtilsService.getFieldDefinition(column.fieldId, recordDefinition).pipe(
        map((fieldDefinition) => {
          let filterType = fieldDefinition.resourceType
            ? this.getFieldDefinitionResourceTypeShortName(fieldDefinition.resourceType)
            : null;

          if (filterType === RX_RECORD_DEFINITION.dataTypes.attachment.shortName) {
            filterType = RX_RECORD_DEFINITION.dataTypes.character.shortName;
          }

          const filterable =
            this.rxRecordGridConfigUtilsService.getBooleanValue(column.filterable) || isUndefined(column.filterable);

          const searchable = has(column, 'searchable')
            ? this.rxRecordGridConfigUtilsService.getBooleanValue(column.searchable)
            : filterable && this.rxRecordGridUtilsService.isSearchable(fieldDefinition, recordDefinition);

          const columnWithMetadata: IRecordGridColumnWithMetadata = {
            ...column,
            clickable: this.rxRecordGridConfigUtilsService.getBooleanValue(column.clickable),
            filterable,
            sortable:
              (this.rxRecordGridConfigUtilsService.parseConfigString(column.sortable) as IColumnSortInfo | boolean) ||
              isUndefined(column.sortable),
            visible: this.rxRecordGridConfigUtilsService.getBooleanValue(column.visible) || isUndefined(column.visible),
            searchable,
            fieldDefinition,
            filterType,
            clickableWithHref: this.isColumnClickableWithHref(column),
            title: column.title?.trim(),
            fallbackTitle: `[${fieldDefinition.name}]`
          };

          return columnWithMetadata;
        })
      );
    });

    return forkJoin(columnsDefinitions$);
  }

  getSelectionFieldOptionLabelsByFieldId(
    columns: IRecordGridColumn[],
    recordDefinition: IRecordDefinition
  ): IPlainObject {
    return reduce(
      columns,
      (result, column) => {
        const fieldDefinition = recordDefinition.fieldDefinitionsById[column.fieldId];

        if (fieldDefinition.resourceType === RX_RECORD_DEFINITION.dataTypes.selection.resourceType) {
          result[column.fieldId] = fieldDefinition.optionLabelsById;
        }

        return result;
      },
      {}
    );
  }

  private getFieldDefinitionResourceTypeShortName(resourceType) {
    return resourceType
      .split('.')
      .pop()
      .replace(/fieldDefinition/i, '');
  }

  private isColumnClickableWithHref(column: IRecordGridColumn): boolean {
    let clickableWithHref = false;

    if (this.rxRecordGridConfigUtilsService.getBooleanValue(column.clickable)) {
      clickableWithHref = column.actions.every((action) => {
        return (
          (action.name === RX_VIEW_ACTION.viewActionNames.launchUrl ||
            (action.name === RX_VIEW_ACTION.viewActionNames.openView &&
              action.presentation.launchBehavior === OpenViewActionLaunchBehavior.SameWindow &&
              action.presentation.type === OpenViewActionType.FullWidth &&
              this.canViewParamsBeEvaluatedForHref(action.viewParams))) &&
          this.canExpressionBeEvaluatedForHref(action.$condition$)
        );
      });
    }

    return clickableWithHref;
  }

  private canViewParamsBeEvaluatedForHref(viewParams: IViewInputParams): boolean {
    return every(viewParams, (viewParamValue) => this.canExpressionBeEvaluatedForHref(viewParamValue));
  }

  private canExpressionBeEvaluatedForHref(expression: string): boolean {
    return every(expression?.match(/\$\{[^{]*\}/g), (expressionToken) =>
      this.allowedOpenViewActionExpressionsForHrefRegex.test(expressionToken)
    );
  }
}
