import { Component, Input, OnInit } from '@angular/core';
import { AdaptFilterControlsComponent, AdvancedFilterOption } from '@bmc-ux/adapt-angular';
import {
  INamedListDefinition,
  INamedListOption,
  INamedListOptionPage,
  RxNamedListDefinitionService,
  RxNamedListService,
  NamedListTypeAheadOption
} from '@helix/platform/named-list/api';
import { RxFeatureService } from '@helix/platform/shared/api';
import { Observable, of, OperatorFunction } from 'rxjs';
import { debounceTime, distinctUntilChanged, finalize, map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { RX_RECORD_GRID } from '../../../record-grid.constant';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';
import { IRecordGridColumnNamedFilterOption } from '../../types/record-grid.types';

@Component({
  selector: 'rx-string-with-type-ahead-filter',
  templateUrl: './string-with-type-ahead-filter.component.html',
  styleUrls: ['./string-with-type-ahead-filter.component.scss']
})
export class RxStringWithTypeAheadFilterComponent implements OnInit {
  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  set filterValue(values: RxRecordGridAdvancedFilterValue<INamedListOption[]>) {
    this._filterValue = values;
    this.selectedValues = values?.filterValue || [];
  }

  get filterValue(): RxRecordGridAdvancedFilterValue<INamedListOption[]> {
    return this._filterValue;
  }

  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[] = [];

  selectedValues: INamedListOption[] = [];
  isSearchInProgress = false;
  typeaheadKeystrokeCount: number;
  private namedListDefinition$: Observable<INamedListDefinition>;
  private _filterValue: RxRecordGridAdvancedFilterValue<INamedListOption[]>;

  search: OperatorFunction<string, INamedListOption[]> = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(250),
      distinctUntilChanged(),
      tap(() => {
        this.isSearchInProgress = true;
      }),
      switchMap((term) => this.getSelectOptions(term)),
      finalize(() => {
        this.isSearchInProgress = false;
      })
    );

  constructor(
    private rxNamedListService: RxNamedListService,
    private rxNamedListDefinitionService: RxNamedListDefinitionService,
    private rxFeatureService: RxFeatureService
  ) {}

  ngOnInit(): void {
    this.namedListDefinition$ = this.rxNamedListDefinitionService
      .get(this.filterOption.data.namedListDefinition)
      .pipe(shareReplay(1));

    this.typeaheadKeystrokeCount =
      this.filterOption.data.typeaheadKeystrokeCount ?? RX_RECORD_GRID.defaultTypeaheadKeystrokeCount;
  }

  onValueChange(selectedValues: NamedListTypeAheadOption[]): void {
    this.filterControlsComponent.onFilterOptionModelChange(
      new RxRecordGridAdvancedFilterValue(
        selectedValues.map(
          (selectedValue: NamedListTypeAheadOption): INamedListOption =>
            this.rxNamedListService.isNamedListOption(selectedValue)
              ? (selectedValue as INamedListOption)
              : ({ displayValue: undefined, value: selectedValue, title: '', contextualFields: [] } as INamedListOption)
        ),
        this.filterValue?.namedOptions
      )
    );
  }

  private getSelectOptions(term: string): Observable<INamedListOption[]> {
    return term.length >= this.typeaheadKeystrokeCount
      ? this.namedListDefinition$.pipe(
          switchMap((namedListDefinition: INamedListDefinition) =>
            this.rxNamedListService
              .getOptionPageByLabelOrValue(namedListDefinition, term, this.filterOption.data.additionalQueryCriteria)
              .pipe(map((optionPage: INamedListOptionPage): INamedListOption[] => optionPage.options))
          )
        )
      : of([]);
  }
}
