import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import {
  IContainerViewComponentDefinition,
  IViewComponentDefinition,
  IViewDefinition,
  IViewLayoutOutlet,
  RX_VIEW_DEFINITION,
  RxViewComponentType
} from '@helix/platform/view/api';
import { IActionButtonProperties } from '../../../action-button/action-button.types';
import { RxAssociationEditingMode } from '../association.types';
import { IAssociationProperties } from './association-design.types';

@Injectable()
export class AssociationDesignAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition<IAssociationProperties>, IViewDefinition>
{
  adaptDefinition(componentDefinition: IContainerViewComponentDefinition<IAssociationProperties>): void {
    // If a view from old view designer gets opened we have to add a layout with
    // action button guids, so they can be rendered inside association view component.
    if (!componentDefinition.layout) {
      const actionButtonGuids = componentDefinition.componentDefinitions
        .filter((definition) => definition.type === RxViewComponentType.ActionButton)
        .map((definition: IViewComponentDefinition<IActionButtonProperties>) => definition.guid);

      componentDefinition.layout = JSON.stringify({
        outlets: [
          {
            name: RX_VIEW_DEFINITION.defaultOutletName,
            columns: [
              {
                children: actionButtonGuids
              }
            ]
          }
        ] as IViewLayoutOutlet[]
      });
    }

    const adaptedProperties: Partial<IAssociationProperties> = {
      editingMode: componentDefinition.propertiesByName.editingMode ?? RxAssociationEditingMode.Views,
      nodeARole: componentDefinition.propertiesByName.nodeARole ?? null,
      nodeBRole: componentDefinition.propertiesByName.nodeBRole ?? null,
      styles: componentDefinition.propertiesByName.styles ?? null,
      useDefaultRoles: componentDefinition.propertiesByName.useDefaultRoles ?? false
    };

    Object.assign(componentDefinition.propertiesByName, adaptedProperties);
  }
}
