import { Component, Injector, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Observable, ReplaySubject } from 'rxjs';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { RxModalClass } from '@helix/platform/ui-kit';
import { TranslateService } from '@ngx-translate/core';
import { RuntimeViewModelApi } from '@helix/platform/view/runtime';
import {
  IShareViewPresetModalData,
  IShareViewPresetUserPayload,
  ITagUserAutocompleteValue
} from './share-view-preset.types';
import { RxCurrentUserService, RxNotificationService } from '@helix/platform/shared/api';
import { RxShareViewPresetService } from './share-view-preset.service';
import { map } from 'rxjs/operators';

@Component({
  selector: 'rx-share-view-preset',
  templateUrl: './share-view-preset.component.html',
  styleUrls: ['./share-view-preset.component.scss']
})
export class ShareViewPresetComponent extends RxModalClass implements OnInit, OnDestroy {
  private maxRecipients = 100;

  strings = {
    users: {
      label: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.users-control.label'
      ),
      placeholder: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.users-control.placeholder'
      )
    },
    addButton: this.translateService.instant('com.bmc.arsys.rx.client.common.add.label'),
    notifyRecipients: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.notify-recipients.label'
    ),
    recipients: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.recipients.label'
    ),
    savedNotification: {
      title: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.saved-notification.title'
      ),
      message: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.saved-notification.message'
      )
    },
    removedNotification: {
      title: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.removed-notification.title'
      ),
      message: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.removed-notification.message'
      )
    },
    maxRecipientsWarning: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.view-preset-selector.share-view-preset.max-users-warning.message',
      {
        maxUserCount: this.maxRecipients
      }
    )
  };

  form = this.formBuilder.group({
    users: this.formBuilder.control([]),
    notifyRecipients: this.formBuilder.control(false)
  });

  modalData: IShareViewPresetModalData = this.activeModalRef.getData();
  runtimeViewModelApi: RuntimeViewModelApi = this.modalData.runtimeViewModelApi;
  selectedUsers: IShareViewPresetUserPayload[] = [];

  get isRecipientsLimitReached(): boolean {
    return this.form.get('users').value.length + this.selectedUsers.length > this.maxRecipients;
  }

  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  private currentUserLoginName = this.rxCurrentUserService.getName();
  private sharedViewPresetInstanceGuid: string;
  private isSelectedUsersChanged = false;

  search: (text$: Observable<string>) => Observable<ITagUserAutocompleteValue[]> = (text$: Observable<string>) => {
    return this.rxShareViewPresetService.getAutocompleteSearch(text$).pipe(
      map((tags) => {
        const selectedUsersLoginIDs = this.selectedUsers.map((user) => user.loginId);
        const tagsInputLoginIDs = this.form.get('users').value.map(({ data }) => data.loginId);

        // exclude current user and users that have already been added
        return tags.filter(
          (item) =>
            item.data.loginId !== this.currentUserLoginName &&
            !selectedUsersLoginIDs.includes(item.data.loginId) &&
            !tagsInputLoginIDs.includes(item.data.loginId)
        );
      })
    );
  };

  constructor(
    private activeModalRef: ActiveModalRef,
    protected injector: Injector,
    private formBuilder: FormBuilder,
    private translateService: TranslateService,
    private rxShareViewPresetService: RxShareViewPresetService,
    private rxCurrentUserService: RxCurrentUserService,
    private rxNotificationService: RxNotificationService
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.loadExistingSharedViewPresetUsers();
  }

  private loadExistingSharedViewPresetUsers(): void {
    if (this.modalData.currentViewPreset.userSharedViewPresetGuid) {
      this.sharedViewPresetInstanceGuid = this.modalData.currentViewPreset.userSharedViewPresetGuid;

      this.rxShareViewPresetService.getSharedViewPresetUsers(this.sharedViewPresetInstanceGuid).subscribe((users) => {
        this.selectedUsers = users;
      });
    }
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  save(): void {
    if (this.selectedUsers.length) {
      this.saveViewPreset();
    } else {
      this.removeViewPreset();
    }
  }

  private removeViewPreset(): void {
    this.rxShareViewPresetService.deleteSharedViewPreset(this.sharedViewPresetInstanceGuid).subscribe(() => {
      this.rxNotificationService.addSuccessMessage(
        this.strings.removedNotification.message,
        this.strings.removedNotification.title
      );

      this.activeModalRef.close(null);
    });
  }

  private saveViewPreset(): void {
    this.rxShareViewPresetService
      .saveSharedViewPreset(
        {
          runtimeViewModelApi: this.runtimeViewModelApi,
          currentViewPreset: this.modalData.currentViewPreset,
          presetSelectorGuid: this.modalData.presetSelectorGuid,
          sharedUsers: this.selectedUsers
        },
        this.sharedViewPresetInstanceGuid
      )
      .subscribe((sharedViewPresetInstanceGuid) => {
        this.rxNotificationService.addSuccessMessage(
          this.strings.savedNotification.message,
          this.strings.savedNotification.title
        );

        this.activeModalRef.close(sharedViewPresetInstanceGuid);
      });
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  onAdd(): void {
    const usersToAdd = this.form.get('users').value.map((tag: ITagUserAutocompleteValue) => ({
      ...tag.data
    }));

    this.selectedUsers = this.selectedUsers.concat(usersToAdd);
    this.form.get('users').setValue([]);
    this.isSelectedUsersChanged = true;
  }

  removeSelectedUsers(selectedUserLoginIds: string[]): void {
    this.selectedUsers = this.selectedUsers.filter((user) => !selectedUserLoginIds.includes(user.loginId));
    this.isSelectedUsersChanged = true;
    this.markAsDirty();
  }

  // saving is allowed when changing users of existing preset
  // or creating new preset with at least one user selected
  isSaveButtonDisabled(): boolean {
    return !(
      (this.sharedViewPresetInstanceGuid && this.isSelectedUsersChanged) ||
      (!this.sharedViewPresetInstanceGuid && this.selectedUsers.length > 0)
    );
  }
}
