import { Component, Input } from '@angular/core';
import {
  AdaptFilterControlsComponent,
  AdaptRxDatetimeAdapter,
  AdvancedFilterOption,
  RxDatetimePickerMode
} from '@bmc-ux/adapt-angular';
import { IRecordGridColumnNamedFilterOption, RxAdvancedFilterDateDataType } from '../../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';
import { RxAdaptDatetimeMomentAdapter } from './rx-adapt-datetime-moment-adapter.service';

@Component({
  selector: 'rx-date-time-range-filter',
  templateUrl: './date-time-range-filter.component.html',
  providers: [{ provide: AdaptRxDatetimeAdapter, useClass: RxAdaptDatetimeMomentAdapter }]
})
export class RxDateTimeRangeFilterComponent {
  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  filterValue: RxRecordGridAdvancedFilterValue;

  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[];

  @Input()
  mode: RxDatetimePickerMode;

  rxDatetimePickerMode = RxDatetimePickerMode;

  onTimeTypeFilterOptionModelChange(model: RxAdvancedFilterDateDataType): void {
    this.onDateTimeTypeFilterOptionModelChange(model);
  }

  onDateTypeFilterOptionModelChange(model: RxAdvancedFilterDateDataType): void {
    if (model[0] && model[0].isValid()) {
      model[0] = model[0].startOf('day');
    }

    if (model[1] && model[1].isValid()) {
      model[1] = model[1].startOf('day');
    }

    this.onDateTimeTypeFilterOptionModelChange(model);
  }

  onDateTimeTypeFilterOptionModelChange(newValue: RxAdvancedFilterDateDataType): void {
    this.filterControlsComponent.onFilterOptionModelChange(
      new RxRecordGridAdvancedFilterValue(newValue, this.filterValue?.namedOptions)
    );
  }
}
