import { Component, Input } from '@angular/core';
import {
  AdaptFilterControlsComponent,
  AdvancedFilterOption,
  AdvancedFilterSelectionTypeData
} from '@bmc-ux/adapt-angular';
import { SelectOption } from '@bmc-ux/obsolete';
import { IRecordGridColumnNamedFilterOption } from '../../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';

@Component({
  selector: 'rx-selection-filter',
  templateUrl: './selection-filter.component.html'
})
export class RxSelectionFilterComponent {
  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  set filterValue(values: RxRecordGridAdvancedFilterValue<string[]>) {
    this._filterValue = values;

    const options = this.getSelectionTypeOptions();

    this.selectionTypeModel =
      options.length && values?.filterValue
        ? values.filterValue.map((id) => options.find((option) => option.id === id))
        : null;
  }

  get filterValue(): RxRecordGridAdvancedFilterValue<string[]> {
    return this._filterValue;
  }

  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[];

  selectionTypeModel: any[];
  private _filterValue: RxRecordGridAdvancedFilterValue<string[]>;

  getSelectionTypeOptions(): SelectOption[] {
    const data = this.filterOption.data as AdvancedFilterSelectionTypeData;

    return data?.selectOptions ?? [];
  }

  onSelectionTypeFilterOptionModelChange(value: SelectOption[]): void {
    this.filterControlsComponent.onFilterOptionModelChange(
      new RxRecordGridAdvancedFilterValue(
        value.map((val) => val.id),
        this.filterValue?.namedOptions
      )
    );
  }
}
