import { ChangeDetectionStrategy, Component, Injector, OnInit, ViewChild } from '@angular/core';
import { RxModalClass } from '@helix/platform/ui-kit';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { Observable, of } from 'rxjs';
import { IRecordGridConfig } from '../../../record-grid/runtime/types/record-grid-config.interface';
import { RecordGridComponent } from '../../../record-grid/runtime/record-grid.component';
import { RowSelectionMode } from '../../../record-grid/runtime/types/row-selection-mode.enum';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IDataPageResult } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { IAddSharedViewPresetsModalData, ISharedViewPresetDescriptor } from './add-shared-view-presets.types';
import { map } from 'rxjs/operators';
import { ISharedViewPresetItem } from '../share-view-preset/share-view-preset.types';
import { isEmpty } from 'lodash';

@Component({
  selector: 'rx-add-shared-view-presets',
  templateUrl: './add-shared-view-presets.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AddSharedViewPresetsComponent extends RxModalClass implements OnInit {
  @ViewChild(RecordGridComponent, { static: true })
  recordGrid: RecordGridComponent;

  recordGridConfig$: Observable<IRecordGridConfig> = of({
    columns: [
      {
        fieldId: 'label',
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.grid.column.preset-name.title'
        )
      },
      {
        fieldId: 'ownerFullName',
        title: this.translateService.instant(
          'com.bmc.arsys.rx.client.view-components.view-preset-selector.add-shared-view-preset.grid.column.shared-by.title'
        )
      }
    ],
    enableFiltering: false,
    enableRowSelection: RowSelectionMode.Multiple,
    recordIdField: 'guid',
    styles: 'flex-fill',
    toolbarConfig: {
      filter: false,
      visibleColumnsMenu: false
    },
    useExternalFiltering: false,
    getRecordDefinition: () =>
      of({
        fieldDefinitions: [
          {
            id: 'label',
            resourceType: RX_RECORD_DEFINITION.resourceTypes.character
          },
          {
            id: 'ownerFullName',
            resourceType: RX_RECORD_DEFINITION.resourceTypes.character
          }
        ]
      }),
    getData: (): Observable<IDataPageResult> =>
      of({
        data: this.presets,
        totalSize: this.presets.length
      })
  });

  modalData: IAddSharedViewPresetsModalData = this.activeModalRef.getData();
  private presets: ISharedViewPresetItem[] = this.modalData.newPresets;
  isAddButtonDisabled$: Observable<boolean>;

  constructor(
    private activeModalRef: ActiveModalRef,
    protected injector: Injector,
    private translateService: TranslateService
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.isAddButtonDisabled$ = this.recordGrid.rowSelectionChanged.pipe(map(isEmpty));
  }

  add(): void {
    const result: ISharedViewPresetDescriptor[] = this.recordGrid.api.getSelectedRows().map(({ label, guid }) => ({
      label,
      guid
    }));

    this.activeModalRef.close(result);
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
