import { Component, Input, OnChanges, OnDestroy, Renderer2, SimpleChanges, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { head, isEmpty, isEqual } from 'lodash';
import {
  IRecordGridSortEditorControlOptions,
  IRecordGridSortEditorModel
} from './record-grid-sort-editor-control.types';
import { AdaptRxSelectComponent } from '@bmc-ux/adapt-angular';
import { IFormFocusable, IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor, ISelectOption } from '@helix/platform/shared/components';
import { ColumnSortDirection } from '../../../common/types/record-grid.types';
import { BehaviorSubject, forkJoin, of } from 'rxjs';
import { RxRecordGridUtilsService } from '../../../common/services/record-grid-utils.service';
import { defaultIfEmpty, map, switchMap, take } from 'rxjs/operators';
import { RxStringService } from '@helix/platform/utils';

@Component({
  selector: 'rx-record-grid-sort-editor-control',
  templateUrl: './record-grid-sort-editor-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridSortEditorControlComponent,
      multi: true
    }
  ]
})
export class RecordGridSortEditorControlComponent
  extends ValueAccessor<IRecordGridSortEditorModel>
  implements IFormControlComponent, IFormFocusable, OnChanges, OnDestroy
{
  @Input()
  options: IRecordGridSortEditorControlOptions;

  @ViewChild('adaptRxSelect', { static: true })
  adaptRxSelect: AdaptRxSelectComponent;

  ColumnSortDirection = ColumnSortDirection;
  updateOptionsSubject = new BehaviorSubject<void>(null);

  columnOptions$ = this.updateOptionsSubject.pipe(
    switchMap(() =>
      this.options.gridColumns && this.options.recordDefinition
        ? forkJoin(
            this.options.gridColumns
              .filter((gridColumn) => gridColumn.sortable)
              .map((gridColumn) => {
                if (this.rxStringService.isEmptySafe(gridColumn.title)) {
                  return this.rxRecordGridUtilsService
                    .getFieldDefinition(gridColumn.fieldId, this.options.recordDefinition)
                    .pipe(map((fieldDefinition) => ({ id: gridColumn.fieldId, name: `[${fieldDefinition.name}]` })));
                }

                return of({ id: gridColumn.fieldId, name: gridColumn.title });
              })
          ).pipe(defaultIfEmpty([]))
        : of([])
    )
  );

  selectedColumnOption: ISelectOption[] = [];

  get selectedSortDirection(): ColumnSortDirection {
    return this.value ? this.value.direction : null;
  }

  constructor(
    private renderer: Renderer2,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxStringService: RxStringService
  ) {
    super();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.options?.previousValue &&
      (!isEqual(
        (changes.options.currentValue as IRecordGridSortEditorControlOptions).gridColumns,
        (changes.options.previousValue as IRecordGridSortEditorControlOptions).gridColumns
      ) ||
        (changes.options.currentValue as IRecordGridSortEditorControlOptions).recordDefinition?.name !==
          (changes.options.previousValue as IRecordGridSortEditorControlOptions).recordDefinition?.name)
    ) {
      this.updateOptionsSubject.next();
    }
  }

  onWriteValue(value: IRecordGridSortEditorModel) {
    this.columnOptions$.pipe(take(1)).subscribe((columnOptions) => {
      this.selectedColumnOption = value ? [columnOptions.find((option) => option.id === value.fieldId)] : [];
    });
  }

  focus(): void {
    this.renderer.selectRootElement(this.adaptRxSelect.selectButtonRef.nativeElement, true).focus();
  }

  onSelectModelChange(value: ISelectOption[]): void {
    this.selectedColumnOption = value;

    this.value = isEmpty(value) ? null : { fieldId: head(value).id, direction: ColumnSortDirection.Asc };
  }

  setSortDirection(sortDirection: ColumnSortDirection): void {
    this.value = { ...this.value, direction: sortDirection };
  }

  optionFormatter(option: ISelectOption): string {
    return option.name;
  }

  ngOnDestroy(): void {
    this.updateOptionsSubject.complete();
  }
}
