import { Injectable } from '@angular/core';
import { AbstractControl, AsyncValidatorFn, FormControl, FormGroup, ValidatorFn } from '@angular/forms';
import { RxGuidService } from '@helix/platform/utils';
import { forEach, forOwn, includes, reduce } from 'lodash';
import { RxRecordInstanceUtilsService } from '@helix/platform/record/api';
import { RecordEditorFormControl } from './record-editor-form-control.class';

export const RecordEditorFormDelimiter = '%';

const BASE_FORM_GROUP_KEY = 'baseFormGroup';

@Injectable()
export class RecordEditorForm extends FormGroup {
  constructor(
    private rxGuidService: RxGuidService,
    private rxRecordInstanceUtilsService: RxRecordInstanceUtilsService
  ) {
    super({});
  }

  addFieldControl(
    fieldId: string,
    guid: string,
    initialValue: any = null,
    validator?: ValidatorFn | ValidatorFn[],
    asyncValidator?: AsyncValidatorFn | AsyncValidatorFn[]
  ): FormControl {
    const formControl = new RecordEditorFormControl('', validator, asyncValidator);
    const formGroup = this.controls[BASE_FORM_GROUP_KEY] as FormGroup;

    formControl.setValue(initialValue);

    if (!formGroup) {
      this.addControl(
        BASE_FORM_GROUP_KEY,
        new FormGroup({
          [this.getControlId(fieldId, guid)]: formControl
        })
      );
    } else {
      formGroup.addControl(this.getControlId(fieldId, guid), formControl);
    }

    return formControl;
  }

  addAssociatedFieldControl(
    fieldId: string,
    guid: string,
    associatedRecordPath: string[],
    initialValue: any = null,
    validator?: ValidatorFn | ValidatorFn[],
    asyncValidator?: AsyncValidatorFn | AsyncValidatorFn[]
  ): FormControl {
    const associatedFormGroupName = associatedRecordPath.join(RecordEditorFormDelimiter);
    const associatedFormGroup: FormGroup = this.controls[associatedFormGroupName] as FormGroup;
    const formControl = new RecordEditorFormControl(initialValue, validator, asyncValidator);

    if (!associatedFormGroup) {
      this.addControl(
        associatedFormGroupName,
        new FormGroup({
          [this.getControlId(fieldId, guid)]: formControl
        })
      );
    } else {
      associatedFormGroup.addControl(this.getControlId(fieldId, guid), formControl);
    }

    return formControl;
  }

  resetFieldControls(formStateByFieldId?: { [fieldId: string]: any }, associatedRecordPath?: string[]): void {
    const formGroup: FormGroup = this.controls[
      associatedRecordPath ? associatedRecordPath.join(RecordEditorFormDelimiter) : BASE_FORM_GROUP_KEY
    ] as FormGroup;

    if (formStateByFieldId) {
      formGroup.reset(this.prepareFormGroupState(formStateByFieldId, formGroup));
    } else {
      formGroup.reset();
    }
  }

  syncFormControlValues(
    fieldId: string,
    value: any,
    associatedFromGroupName?: string,
    options: { markAsDirty?: boolean; markAsTouched?: boolean } = {}
  ): void {
    this.getControlsBoundToRecordField(String(fieldId), associatedFromGroupName).forEach((formControl) => {
      if (options.markAsDirty !== false) {
        formControl.markAsDirty();
      }

      if (options.markAsTouched !== false) {
        formControl.markAsTouched();
      }

      if (!this.rxRecordInstanceUtilsService.isFieldValueEqual(formControl.value, value)) {
        formControl.setValue(value);
      }
    });
  }

  getFieldIds(associatedRecordPath?: string[]): string[] {
    const formGroup: FormGroup = this.controls[
      associatedRecordPath ? associatedRecordPath.join(RecordEditorFormDelimiter) : BASE_FORM_GROUP_KEY
    ] as FormGroup;

    if (formGroup) {
      return reduce(
        formGroup.controls,
        (result: string[], control: AbstractControl, formControlId: string) => {
          const fieldId = this.getFieldId(formControlId);

          if (control instanceof FormControl && !includes(result, fieldId)) {
            result.push(fieldId);
          }

          return result;
        },
        []
      );
    } else {
      return [];
    }
  }

  clearAndReset(): void {
    forOwn(this.controls, (control, name) => this.removeControl(name));
    this.reset();
  }

  markInvalidControlsAsTouched(): void {
    forEach(this.controls, (formGroup: FormGroup) => {
      forEach(formGroup.controls, (control) => {
        if (control.invalid) {
          control.markAsTouched();
        }
      });
    });
  }

  getFormControl(fieldId: string, guid: string, associatedRecordPath?: string[]): RecordEditorFormControl {
    let control: FormControl;
    let formGroup: FormGroup;

    formGroup = this.controls[
      associatedRecordPath ? associatedRecordPath.join(RecordEditorFormDelimiter) : BASE_FORM_GROUP_KEY
    ] as FormGroup;

    if (formGroup) {
      control = formGroup.get(this.getControlId(fieldId, guid)) as FormControl;
    }

    return control as RecordEditorFormControl;
  }

  private getControlsBoundToRecordField(fieldId: string, associatedFormGroupName?: string): RecordEditorFormControl[] {
    const formGroup: FormGroup = this.controls[associatedFormGroupName || BASE_FORM_GROUP_KEY] as FormGroup;

    return reduce(
      formGroup.controls,
      (result, control, formControlId) => {
        if (control instanceof FormControl && this.getFieldId(formControlId) === fieldId) {
          result.push(control);
        }

        return result;
      },
      []
    );
  }

  private getControlId(fieldId: string, guid?: string): string {
    return `${fieldId}:${guid || this.rxGuidService.generate()}`;
  }

  private getFieldId(formControlId: string): string {
    return formControlId.split(':')[0];
  }

  private prepareFormGroupState(
    formStateByFieldId: { [fieldId: string]: any },
    formGroup: FormGroup
  ): { [formControlId: string]: any } {
    return reduce(
      formGroup.controls,
      (result, control, formControlId) => {
        result[formControlId] = formStateByFieldId[this.getFieldId(formControlId)];

        return result;
      },
      {}
    );
  }
}
