import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { RxJsonParserService } from '@helix/platform/utils';
import {
  IContainerViewComponentDefinition,
  IViewDefinition,
  RxViewComponentType,
  RxViewDefinitionParserService
} from '@helix/platform/view/api';

@Injectable()
export class RxTabPanelComponentDefinitionAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition> {
  constructor(
    private viewDefinitionParserService: RxViewDefinitionParserService,
    private rxJsonParserService: RxJsonParserService
  ) {}

  adaptDefinition(tabPanelViewComponentDefinition: IContainerViewComponentDefinition) {
    this.viewDefinitionParserService
      .getComponents(tabPanelViewComponentDefinition)
      .filter((componentPair) => componentPair.componentDefinition.type === RxViewComponentType.TabPanel)
      .map((componentPair) => componentPair.componentDefinition as IContainerViewComponentDefinition)
      .forEach((componentDefinition) => {
        if (componentDefinition.layout) {
          let layout = this.rxJsonParserService.tryParseJson(componentDefinition.layout);

          if (layout && layout.componentDefinitionId) {
            const columns = layout.columns.map((column) => {
              return {
                children: column.children.map((child) => child.columns[0].children[0].componentDefinitionId)
              };
            });

            layout = {
              outlets: [
                {
                  name: 'DEFAULT',
                  columns: columns
                }
              ]
            };

            componentDefinition.layout = JSON.stringify(layout);
          }
        }
      });
  }
}
