import { Directive, HostBinding, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { IPlainObject } from '@helix/platform/shared/api';
import {
  ICellDisplayProperties,
  RxGridCellBgColorCssMap,
  RxGridCellFontColorCssMap,
  RxGridCellFontSize,
  RxGridCellIconPosition
} from '../../common/types/cell-display-properties.types';
import { RxExpressionEvaluatorService } from '@helix/platform/view/api';
import { set } from 'lodash';
import { RxObjectUtilsService } from '@helix/platform/utils';

@Directive({
  selector: '[rxCellDisplayProps]'
})
export class CellDisplayPropertiesDirective implements OnInit, OnChanges {
  @Input()
  rxCellDisplayProps: ICellDisplayProperties[];

  @Input()
  rxCellDisplayPropsBadgeElem: HTMLElement;

  @Input()
  rxCellDisplayPropsDataItem: IPlainObject;

  @HostBinding('class')
  cssClasses: string;

  private evaluationData: IPlainObject = {};

  constructor(
    private rxExpressionEvaluatorService: RxExpressionEvaluatorService,
    private rxObjectUtilsService: RxObjectUtilsService
  ) {}

  ngOnInit(): void {
    this.updateCellClasses();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      !changes.rxCellDisplayPropsDataItem.firstChange &&
      changes.rxCellDisplayPropsDataItem.currentValue !== changes.rxCellDisplayPropsDataItem.previousValue
    ) {
      this.updateCellClasses();
    }
  }

  private getActiveProps(items: ICellDisplayProperties[]): ICellDisplayProperties {
    return (
      items.find((props) =>
        this.rxExpressionEvaluatorService.tryEvaluate(props.fieldValueCondition, this.evaluationData)
      ) || null
    );
  }

  private getCellCssClasses(props: ICellDisplayProperties): string[] {
    const result = [];

    if (!props.displayAsBadge) {
      if (props.bold) {
        result.push('font-weight-bold');
      }

      if (props.italic) {
        result.push('font-italic');
      }

      if (props.fontSize === RxGridCellFontSize.Small) {
        result.push('small');
      } else if (props.fontSize === RxGridCellFontSize.Large) {
        result.push('large');
      }

      if (props.textColor) {
        result.push(RxGridCellFontColorCssMap[props.textColor]);
      }

      if (props.backgroundColor) {
        result.push(RxGridCellBgColorCssMap[props.backgroundColor]);
      }

      if (props.icon) {
        if (props.iconPosition === RxGridCellIconPosition.ReplaceText) {
          result.push(`d-icon-${props.icon}`);
        } else {
          result.push(`d-icon-${props.iconPosition}-${props.icon}`);
        }
      }
    }

    return result;
  }

  private getSpanCssClasses(props: ICellDisplayProperties): string[] {
    if (props.displayAsBadge) {
      return ['badge', `badge-${props.badgeColor}`];
    } else if (props.iconPosition === RxGridCellIconPosition.ReplaceText) {
      return ['hidden'];
    } else {
      return [];
    }
  }

  private updateCellClasses(): void {
    if (this.rxCellDisplayProps?.length) {
      let cssClasses = '';

      const expandedRxCellDisplayPropsDataItem = {
        ...this.rxCellDisplayPropsDataItem,
        ...this.rxObjectUtilsService.expandProperties(this.rxCellDisplayPropsDataItem)
      };

      set(this.evaluationData, `view.components.grid.clickableRow`, expandedRxCellDisplayPropsDataItem);

      const activeProps = this.getActiveProps(this.rxCellDisplayProps);

      if (activeProps) {
        const cellClasses = this.getCellCssClasses(activeProps);

        cssClasses = cellClasses.join(' ');
      }

      if (this.rxCellDisplayPropsBadgeElem) {
        const spanClasses: string[] = activeProps ? this.getSpanCssClasses(activeProps) : [];

        this.rxCellDisplayPropsBadgeElem.classList.value = '';
        this.rxCellDisplayPropsBadgeElem.classList.add(...spanClasses);
      }

      this.cssClasses = cssClasses;
    }
  }
}
