import { RxSelectOption } from '@bmc-ux/adapt-angular';
import { IRecordDefinition, RX_RECORD_DEFINITION, RxRecordDefinitionCacheService } from '@helix/platform/record/api';
import { Tooltip } from '@helix/platform/shared/api';
import {
  ExpressionInspectorControlComponent,
  IDefinitionPickerComponentOptions,
  IExpressionInspectorControlOptions,
  ISelectFormControlOptions,
  IStepperWithUnitsFormControlOptions,
  RxDefinitionPickerComponent,
  RxDefinitionPickerType,
  SelectFormControlComponent,
  StepperWithUnitsFormControlComponent,
  TextFormControlComponent
} from '@helix/platform/shared/components';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import {
  getStandardPropsInspectorConfigs,
  IViewComponentDesignValidationIssue,
  validateAvailableOnDevicesProp,
  validateCssClassNames,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { combineLatest, Observable, of } from 'rxjs';
import { compact, flatten } from 'lodash';
import { map, skip, switchMap, take, takeUntil, withLatestFrom } from 'rxjs/operators';
import { ImageAlignment, RX_IMAGE_ALIGNMENT_OPTIONS } from '../image.types';
import { IImageProperties } from './image-design.types';
import {
  IViewDesignerComponentModel,
  RX_AVAILABLE_ON_DEVICES_PROP_NAME,
  RX_STANDARD_PROPS_DEFAULT_VALUES
} from '@helix/platform/view/api';

export class ImageDesignModel
  extends ViewDesignerComponentModel<IImageProperties>
  implements IViewDesignerComponentModel<IImageProperties>
{
  private recordDefinitionName$ = this.sandbox.getComponentPropertyValue('recordDefinitionName');

  private attachmentFields$ = this.recordDefinitionName$.pipe(
    switchMap((recordDefinitionName) =>
      recordDefinitionName ? this.getAttachmentFieldsFromRecordDefinition(recordDefinitionName) : of([])
    )
  );

  private rxModalService = this.injector.get(RxModalService);
  private rxRecordDefinitionCacheService = this.injector.get(RxRecordDefinitionCacheService);

  private maxWidthUnits = [
    {
      name: 'pixels',
      id: 'px'
    },
    {
      name: '%',
      id: '%'
    }
  ];

  static getInitialProperties(initialProperties?: IImageProperties): IImageProperties {
    return {
      name: null,
      recordDefinitionName: null,
      recordInstanceId: null,
      fieldId: null,
      maxWidth: null,
      alignment: ImageAlignment.Left,
      ...RX_STANDARD_PROPS_DEFAULT_VALUES,

      ...initialProperties
    };
  }

  rxInit(): void {
    this.sandbox.componentProperties$.pipe(take(1)).subscribe((componentProperties: IImageProperties) => {
      this.sandbox.updateInspectorConfig(this.getInspector(componentProperties));
    });

    this.recordDefinitionName$
      .pipe(skip(1), withLatestFrom(this.sandbox.componentProperties$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([recordDefinitionName, properties]) => {
        const newProperties = {
          ...properties,
          recordInstanceId: null,
          fieldId: null
        };

        this.sandbox.updateComponentProperties(newProperties);
        this.sandbox.updateInspectorConfig(this.getInspector(newProperties));
      });

    this.attachmentFields$
      .pipe(withLatestFrom(this.sandbox.componentProperties$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([attachmentFields, properties]) => {
        this.sandbox.updateInspectorConfig(this.getInspector(properties, attachmentFields));
      });

    combineLatest([
      this.validateEmptyProp('recordDefinitionName', 'Record definition name cannot be blank.'),
      this.validateEmptyProp('recordInstanceId', 'Record instance ID cannot be blank.'),
      this.validateEmptyProp('fieldId', 'Field ID cannot be blank.'),
      this.sandbox.getComponentPropertyValue('maxWidth').pipe(map((value) => this.validateMaxWidth(value))),
      this.sandbox.getComponentPropertyValue('styles').pipe(map(validateCssClassNames)),
      this.sandbox
        .getComponentPropertyValue(RX_AVAILABLE_ON_DEVICES_PROP_NAME)
        .pipe(map(validateAvailableOnDevicesProp))
    ])
      .pipe(map(flatten), map(compact), takeUntil(this.sandbox.destroyed$))
      .subscribe((validationIssues: IViewComponentDesignValidationIssue[]) =>
        this.sandbox.setValidationIssues(validationIssues)
      );

    this.sandbox.getComponentPropertyValue('name').subscribe((name) => {
      const componentName = name ? `${this.sandbox.descriptor.name} (${name})` : this.sandbox.descriptor.name;

      this.sandbox.setSettablePropertiesDataDictionary(componentName, [
        {
          label: 'Hidden',
          expression: this.getExpressionForProperty('hidden')
        }
      ]);
    });
  }

  private getAttachmentFieldsFromRecordDefinition(recordDefinitionName: string): Observable<RxSelectOption[]> {
    return this.rxRecordDefinitionCacheService.getRecordDefinition(recordDefinitionName).pipe(
      map((recordDefinition: IRecordDefinition) =>
        recordDefinition.fieldDefinitions
          .filter((definition) => definition.resourceType === RX_RECORD_DEFINITION.dataTypes.attachment.resourceType)
          .map(({ id, name }) => ({
            id: id.toString(),
            name
          }))
      )
    );
  }

  private getInspector(props: IImageProperties, attachmentFields: RxSelectOption[] = []) {
    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls: [
            {
              name: 'name',
              component: TextFormControlComponent,
              options: {
                label: 'Name',
                tooltip: new Tooltip('Enter a name to uniquely identify the Image view component.')
              }
            },
            {
              name: 'recordDefinitionName',
              component: RxDefinitionPickerComponent,
              options: {
                label: 'Record definition name',
                definitionType: RxDefinitionPickerType.RegularRecord,
                beforeValueChange: (oldValue: string, newValue: string): Promise<boolean> => {
                  if (Boolean(oldValue) && props.recordInstanceId && props.fieldId) {
                    return this.rxModalService.confirm({
                      title: 'Warning',
                      modalStyle: RX_MODAL.modalStyles.warning,
                      message: 'Record Instance ID and Field ID will be cleared. Do you want to continue?'
                    });
                  } else {
                    return Promise.resolve(true);
                  }
                },
                required: true
              } as IDefinitionPickerComponentOptions
            },
            {
              name: 'fieldId',
              component: SelectFormControlComponent,
              options: {
                label: 'Field name',
                options: attachmentFields,
                required: true
              } as ISelectFormControlOptions
            },
            {
              name: 'recordInstanceId',
              component: ExpressionInspectorControlComponent,
              options: {
                label: 'Record ID',
                isRequired: true
              } as IExpressionInspectorControlOptions
            },
            {
              name: 'alignment',
              component: SelectFormControlComponent,
              options: {
                label: 'Horizontal alignment',
                options: RX_IMAGE_ALIGNMENT_OPTIONS
              } as ISelectFormControlOptions
            },
            {
              name: 'maxWidth',
              component: StepperWithUnitsFormControlComponent,
              options: {
                label: 'Maximum width',
                defaultUnit: 'px',
                units: this.maxWidthUnits,
                stepperOptionByUnits: {
                  px: {
                    minValue: 0
                  },
                  '%': {
                    minValue: 0,
                    maxValue: 100,
                    defaultValue: 100
                  }
                }
              } as IStepperWithUnitsFormControlOptions
            },
            ...getStandardPropsInspectorConfigs()
          ]
        }
      ]
    };
  }

  private validateEmptyProp<K extends keyof IImageProperties>(
    propertyName: K,
    description: string
  ): Observable<IViewComponentDesignValidationIssue> {
    return this.sandbox
      .getComponentPropertyValue(propertyName)
      .pipe(map((value) => (value ? null : this.sandbox.createError(description, propertyName))));
  }

  private validateMaxWidth(maxWidth: string): IViewComponentDesignValidationIssue {
    if (maxWidth) {
      const matches = maxWidth.match(/^([+-]?\d+(?:\.\d+)?)(.*)/);

      if (matches) {
        const numberValue = Number(matches[1]);
        const unitValue = matches[2];

        if (unitValue === '%' && numberValue > 100) {
          return this.sandbox.createError('Maximum width cannot be greater than 100%.');
        } else if (numberValue < 0) {
          return this.sandbox.createError('Maximum width should be a positive number.');
        }
      }
    }
  }
}
