import { HttpClient } from '@angular/common/http';
import { Component, Input, OnInit } from '@angular/core';
import { BaseViewComponent } from '@helix/platform/view/runtime';
import { find, flow, forEach, groupBy, map } from 'lodash';
import { Observable } from 'rxjs';
import { distinctUntilChanged, finalize, pluck, takeUntil, withLatestFrom } from 'rxjs/operators';
import { RxSelectionChangeEvent, RxSelectOption, RxSelectOptionsGroup } from '@bmc-ux/adapt-angular';
import { ICatalog, ICatalogItem } from './service-list.interface';
import { IServiceListProperties } from '../service-list.types';

@Component({
  selector: 'rx-service-list',
  templateUrl: './service-list.component.html'
})
export class ServiceListComponent extends BaseViewComponent implements OnInit {
  @Input()
  config: Observable<IServiceListProperties>;

  isDisabled = true;
  serviceListOptions: RxSelectOptionsGroup[] = [];

  service: RxSelectOption = {
    id: null,
    name: null,
    guid: null
  };

  constructor(private httpClient: HttpClient) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();

    const serviceRequestId$ = this.config.pipe(
      pluck('serviceRequestId'),
      distinctUntilChanged(),
      takeUntil(this.destroyed$)
    );

    this.httpClient
      .get<ICatalog>('/api/com.bmc.dsm.chatbot/rx/application/chatbot/services')
      .pipe(
        withLatestFrom(serviceRequestId$),
        finalize(() => {
          this.isDisabled = false;
        }),
        takeUntil(this.destroyed$)
      )
      .subscribe(([chatbotServices, serviceRequestId]) => {
        this.serviceListOptions = flow(
          (catalogs: ICatalogItem[]) => groupBy(catalogs, 'groupName'),
          (catalogsByGroup) =>
            map(catalogsByGroup, (catalogs: ICatalogItem[], groupName: string) => ({
              name: groupName,
              children: map(catalogs, (catalog: ICatalogItem) => ({
                id: catalog.id,
                name: catalog.name,
                guid: catalog.guid
              }))
            }))
        )(chatbotServices?.catalogDetailsList || []);

        this.selectServiceRequest(serviceRequestId);
      });

    serviceRequestId$.pipe(takeUntil(this.destroyed$)).subscribe((requestId: string) => {
      this.selectServiceRequest(requestId);
    });
  }

  private selectServiceRequest(serviceRequestId): void {
    let service;

    forEach(this.serviceListOptions, (item: RxSelectOptionsGroup) => {
      if (service) {
        return false;
      }

      service = find(item.children, {
        id: serviceRequestId
      });
    });

    this.service = service
      ? [
          {
            id: service.id,
            name: service.name,
            guid: service.guid
          }
        ]
      : [];
  }

  public onSelectionChange(event: RxSelectionChangeEvent): void {
    const service: ICatalogItem = event.options[0];

    this.notifyPropertyChanged('serviceRequestId', service.id);
    this.notifyPropertyChanged('serviceRequestName', service.name);
    this.notifyPropertyChanged('serviceRequestGuid', service.guid);
  }

  public optionFormatter(option: RxSelectOption): string {
    return option.name;
  }
}
