import { Component, Input } from '@angular/core';
import {
  AdaptFilterControlsComponent,
  AdaptMetatagComponent,
  AdvancedFilterBooleanDataType,
  AdvancedFilterOption,
  getTagText,
  isEmptyOrWhitespace,
  TagType
} from '@bmc-ux/adapt-angular';
import { isEqual } from 'lodash';
import { IRecordGridColumnNamedFilterOption } from '../../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';

@Component({
  selector: 'rx-string-filter',
  templateUrl: './string-filter.component.html'
})
export class RxStringFilterComponent {
  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  filterValue: RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>;

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[] = [];

  onStringTypeFilterOptionModelChange(model: TagType[], tagField: AdaptMetatagComponent) {
    const newModel = model.filter((tag) => !isEmptyOrWhitespace(getTagText(tag)));

    if (newModel.length < model.length && isEqual(newModel, this.filterValue?.filterValue || [])) {
      tagField.writeValue(newModel);
    } else {
      this.filterControlsComponent.onFilterOptionModelChange(
        new RxRecordGridAdvancedFilterValue(newModel, this.filterValue?.namedOptions)
      );
    }
  }
}
