import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  NgZone,
  OnDestroy,
  OnInit,
  QueryList,
  Renderer2,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { RX_VIEW_DEFINITION } from '@helix/platform/view/api';
import { BaseViewComponent, IViewComponent, RuntimeViewCanvasItemComponent } from '@helix/platform/view/runtime';
import { get, isEmpty, isUndefined, last, throttle } from 'lodash';
import { merge, Observable, Subject, throwError } from 'rxjs';
import { IButtonBarChildComponentData, IButtonBarConfig } from './button-bar.types';
import { ResizeSensor } from 'css-element-queries';
import { map, takeUntil } from 'rxjs/operators';
import { RxButtonBarService } from '../button-bar.service';

@Component({
  selector: 'rx-button-bar',
  templateUrl: './button-bar.component.html',
  styleUrls: ['./button-bar.component.scss']
})
export class ButtonBarComponent extends BaseViewComponent implements OnInit, OnDestroy, IViewComponent, AfterViewInit {
  @ViewChildren('buttonItems', { read: ElementRef })
  private buttonItemsQueryList: QueryList<ElementRef>;

  @ViewChild('dropdownToggle', { read: ElementRef })
  private dropdownToggleButton: ElementRef;

  api = {
    setProperty: this.setProperty.bind(this)
  };

  config: Observable<IButtonBarConfig>;
  alignClass: string;
  childLayouts: IButtonBarChildComponentData[] = null;
  isDropdownVisible = false;

  private resizeSensor: ResizeSensor;
  private resize$ = new Subject();
  private onResizeThrottled = throttle(this.onResize.bind(this), 100);

  constructor(
    private elementRef: ElementRef<HTMLElement>,
    private runtimeCanvasItemComponent: RuntimeViewCanvasItemComponent,
    private changeDetector: ChangeDetectorRef,
    private ngZone: NgZone,
    private rxButtonBarService: RxButtonBarService,
    private renderer: Renderer2
  ) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.notifyPropertyChanged('api', this.api);

    this.config.pipe(takeUntil(this.destroyed$)).subscribe((config) => {
      this.isHidden = Boolean(config.hidden);
      this.alignClass = this.rxButtonBarService.getAlignClass(config.alignment);
    });

    this.childLayouts = get(
      this.runtimeCanvasItemComponent.getChildren(RX_VIEW_DEFINITION.defaultOutletName),
      '[0].children'
    );
  }

  ngAfterViewInit(): void {
    const el = this.renderer.selectRootElement(this.elementRef.nativeElement, true);

    if (!isEmpty(this.childLayouts)) {
      const buttonConfigs = this.childLayouts.map((layout) => layout.config);

      this.ngZone.runOutsideAngular(() => {
        this.resizeSensor = new ResizeSensor(el, (size) => {
          this.ngZone.run(() => this.resize$.next(size));
        });
      });

      merge(...buttonConfigs, this.resize$)
        .pipe(
          map((size: any) => size.width || el.offsetWidth),
          takeUntil(this.destroyed$)
        )
        .subscribe((width: number) => this.onResizeThrottled(width));
    }

    this.onResize(el.offsetWidth);
    this.changeDetector.detectChanges();
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();

    if (this.resizeSensor) {
      this.resizeSensor.detach();
    }
  }

  onResize(containerWidth: number): void {
    let totalButtonsWidth = 0;

    const toggleButtonOffsetWidth = this.dropdownToggleButton
      ? this.renderer.selectRootElement(this.dropdownToggleButton.nativeElement, true).offsetWidth
      : 0;

    this.buttonItemsQueryList.forEach((item: ElementRef, index: number) => {
      totalButtonsWidth += this.renderer.selectRootElement(item.nativeElement, true).offsetWidth;

      const containerFitsButton = Math.floor(containerWidth) >= Math.ceil(totalButtonsWidth) + toggleButtonOffsetWidth;

      this.childLayouts[index].showInDropdown = !containerFitsButton;

      if (
        !isUndefined(this.childLayouts[index - 1]) &&
        this.childLayouts.filter((button) => button.showInDropdown).length < 2
      ) {
        this.childLayouts[index - 1].showInDropdown = !containerFitsButton;
      }
    });

    this.isDropdownVisible = isEmpty(this.childLayouts) ? false : last(this.childLayouts).showInDropdown;
  }

  trackByFn(index: number, item: IButtonBarChildComponentData): string {
    return item.guid;
  }

  setProperty(propertyPath: string, propertyValue: any): void | Observable<never> {
    if (propertyPath === 'hidden') {
      this.isHidden = propertyValue;
      this.notifyPropertyChanged(propertyPath, this.isHidden);
    } else {
      return throwError(`Button Bar: property ${propertyPath} is not settable.`);
    }
  }
}
