import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BooleanFieldDesignModel } from './boolean-field-design.model';
import { BooleanFieldEditingMode } from '../boolean-field.types';
import { map, takeUntil } from 'rxjs/operators';
import { combineLatest, ReplaySubject } from 'rxjs';

@Component({
  selector: 'rx-boolean-field-design',
  templateUrl: './boolean-field-design.component.html'
})
export class BooleanFieldDesignComponent implements OnInit, OnDestroy {
  @Input()
  model: BooleanFieldDesignModel;

  booleanValue: boolean; // need this, because new adapt-switcher don't work without ngModel
  shouldDisplayAsCheckbox = false;

  private destroyed$ = new ReplaySubject<boolean>(1);

  ngOnInit(): void {
    const editingMode$ = this.model.componentProperties$.pipe(
      map((componentProperties) => componentProperties.editingMode || BooleanFieldEditingMode.Switch)
    );

    combineLatest([editingMode$, this.model.isRequired$])
      .pipe(takeUntil(this.destroyed$))
      .subscribe(([editingMode, isRequired]) => {
        this.shouldDisplayAsCheckbox = isRequired && editingMode === BooleanFieldEditingMode.Checkbox;
      });
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }
}
