import { Injector } from '@angular/core';
import { Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { IFieldDefinition, RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import {
  ExpressionFormControlComponent,
  IDefinitionPickerComponentOptions,
  IExpressionFormControlOptions,
  ISwitcherFormControlOptions,
  RxDefinitionPickerComponent,
  RX_DEFINITION_PICKER,
  SwitchFormControlComponent
} from '@helix/platform/shared/components';
import {
  IViewComponentDesignSandbox,
  IViewDesignerInspectorConfig,
  NamedListFilterExpressionConfigurator
} from '@helix/platform/view/designer';
import { BaseRecordEditorFieldDesign } from '../../base-record-editor-field/design';
import { ITextFieldProperties } from './text-field-design.types';
import { omit } from 'lodash';
import { Tooltip } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';

export class TextFieldDesignModel extends BaseRecordEditorFieldDesign {
  fieldResourceTypes = [RX_RECORD_DEFINITION.resourceTypes.character];

  private translateService = this.injector.get(TranslateService);

  static getInitialProperties(initialProperties?: ITextFieldProperties): ITextFieldProperties {
    return {
      ...BaseRecordEditorFieldDesign.getInitialProperties(),
      enableMultiSelection: null,
      additionalQueryCriteria: null,
      ...initialProperties
    };
  }

  constructor(public injector: Injector, public sandbox: IViewComponentDesignSandbox<ITextFieldProperties>) {
    super(injector, sandbox);
  }

  getInspectorConfig(): Observable<IViewDesignerInspectorConfig> {
    return this.selectedFieldDefinition$.pipe(
      switchMap((fieldDefinition) =>
        super
          .getInspectorConfig()
          .pipe(
            map((inspectorConfig) =>
              fieldDefinition?.namedListDefinition
                ? this.getNamedListInspectorConfig(inspectorConfig, fieldDefinition)
                : inspectorConfig
            )
          )
      )
    );
  }

  private getNamedListInspectorConfig(
    inspectorConfig: IViewDesignerInspectorConfig,
    fieldDefinition: IFieldDefinition
  ): IViewDesignerInspectorConfig {
    const namedListFilterExpressionConfigurator = new NamedListFilterExpressionConfigurator(
      fieldDefinition,
      this.injector
    );

    const enableMultiSelectionControl = {
      name: 'enableMultiSelection',
      component: SwitchFormControlComponent,
      options: {
        label: 'Enable multi-selection'
      } as ISwitcherFormControlOptions
    };

    const additionalQueryCriteriaControls = [
      {
        name: 'namedListDefinitionName',
        component: RxDefinitionPickerComponent,
        isDisabled: true,
        options: {
          label: this.translateService.instant('com.bmc.arsys.rx.client.definition-type.named-list.label'),
          definitionType: RX_DEFINITION_PICKER.definitionTypes.namedList.type
        } as IDefinitionPickerComponentOptions
      },
      {
        name: 'additionalQueryCriteria',
        component: ExpressionFormControlComponent,
        options: {
          label: 'Additional named list filter',
          tooltip: new Tooltip(
            'Build a filter to apply at runtime in addition to the filter, if any, specified in the named list definition.'
          ),
          dataDictionary$: namedListFilterExpressionConfigurator.getDataDictionary(),
          operators: namedListFilterExpressionConfigurator.getOperators()
        } as IExpressionFormControlOptions
      }
    ];

    inspectorConfig.inspectorSectionConfigs[0].controls.push(enableMultiSelectionControl);
    inspectorConfig.inspectorSectionConfigs[0].controls.push(...additionalQueryCriteriaControls);

    // update the named list name for named list inspector control
    this.sandbox.updateComponentProperties({ namedListDefinitionName: fieldDefinition.namedListDefinition });

    return inspectorConfig;
  }

  getPropertiesByName(properties: ITextFieldProperties): ITextFieldProperties {
    return omit(super.getPropertiesByName(properties) as ITextFieldProperties, 'namedListDefinitionName');
  }
}
