import { Injectable, TemplateRef } from '@angular/core';
import {
  AdaptTranslateService,
  AdvancedFilterBooleanDataType,
  AdvancedFilterDataTypesConfigsService,
  AdvancedFilterExpressionTip,
  AdvancedFilterOption,
  AdvancedFilterOptionConfig,
  AdvancedFilterOptionDataType,
  AdvancedFilterSelectionTypeData,
  formatAdvancedFilterTagText,
  isEmptyOrWhitespace,
  ParsedTagFieldExpressionResult,
  TagModel
} from '@bmc-ux/adapt-angular';
import { IAssociationDescriptor } from '@helix/platform/association/api';
import {
  IFieldDefinition,
  IFieldDefinitionsById,
  IRecordDefinition,
  RecordFieldOption,
  RxRecordDefinitionService
} from '@helix/platform/record/api';
import { RxObjectUtilsService } from '@helix/platform/utils';
import { get, isArray, isEmpty, isFunction, isNil, isString, uniq } from 'lodash';
import { IRecordGridFilterConfig } from '../types/record-grid-filter-config.interfaces';
import { RxRecordGridUtilsService } from './record-grid-utils.service';
import moment from 'moment-es6';
import { IRecordGridColumnNamedFilterOption } from '../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../types/record-grid-advanced-filter-value.class';
import { INamedListOption, RxNamedListService } from '@helix/platform/named-list/api';

@Injectable()
export class RxRecordGridAdvancedFilteringService {
  translation = this.adaptTranslateService.getCurrentLanguage();

  adaptTexts = {
    fromLabel: this.translation['adapt.advancedFilter.configsTexts.fromLabel'],
    toLabel: this.translation['adapt.advancedFilter.configsTexts.toLabel'],
    trueLabel: this.translation['adapt.advancedFilter.configsTexts.trueLabel'],
    falseLabel: this.translation['adapt.advancedFilter.configsTexts.falseLabel'],
    blankLabel: this.translation['adapt.advancedFilter.configsTexts.blankLabel'],
    validationErrors: {
      invalidRangeName: this.translation['adapt.advancedFilter.validationErrors.invalidRangeName'],
      invalidRangeText: this.translation['adapt.advancedFilter.validationErrors.invalidRangeText'],
      numberTypeTexts: {
        fromValueMustBeNumber:
          this.translation['adapt.advancedFilter.validationErrors.numberTypeTexts.fromValueMustBeNumber'],
        toValueMustBeNumber:
          this.translation['adapt.advancedFilter.validationErrors.numberTypeTexts.toValueMustBeNumber'],
        invalidBetweenMinAndMax:
          this.translation['adapt.advancedFilter.validationErrors.numberTypeTexts.invalidBetweenMinAndMax'],
        invalidMinValue: this.translation['adapt.advancedFilter.validationErrors.numberTypeTexts.invalidMinValue'],
        invalidMaxValue: this.translation['adapt.advancedFilter.validationErrors.numberTypeTexts.invalidMaxValue']
      }
    }
  };

  defaultConfig = this.advancedFilterDataTypesConfigsService.getConfigs(this.adaptTexts);

  constructor(
    private rxRecordDefinitionService: RxRecordDefinitionService,
    private rxObjectUtilsService: RxObjectUtilsService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxNamedListService: RxNamedListService,
    private advancedFilterDataTypesConfigsService: AdvancedFilterDataTypesConfigsService,
    private adaptTranslateService: AdaptTranslateService
  ) {}

  addAssociationFieldsToFieldDefinitionsById(
    fieldDefinitionsById: IFieldDefinitionsById,
    associationDescriptors: IAssociationDescriptor[],
    associatedRecordDefinitions: IRecordDefinition[]
  ): IFieldDefinitionsById {
    associationDescriptors.forEach((associationDescriptor) => {
      const associatedRecordDefinition = associatedRecordDefinitions.find(
        (recordDefinition) => recordDefinition.name === associationDescriptor.recordDefinitionName
      );

      associatedRecordDefinition.fieldDefinitions.forEach((fieldDefinition) => {
        const field = this.rxRecordGridUtilsService.getFieldIdForGridColumn(fieldDefinition.id, associationDescriptor);

        if (!fieldDefinitionsById[field]) {
          this.rxRecordDefinitionService.addFieldDefinitionToMap(fieldDefinition, fieldDefinitionsById, field);
        }
      });
    });

    fieldDefinitionsById = {
      ...fieldDefinitionsById,
      ...this.rxObjectUtilsService.expandProperties(fieldDefinitionsById)
    };

    return fieldDefinitionsById;
  }

  getAdvancedFilterOption(
    fieldId: string,
    header: string,
    fieldDefinition: IFieldDefinition,
    filterConfig: IRecordGridFilterConfig,
    isHidden?: boolean,
    customFilterTemplates?: { [key: string]: TemplateRef<any> },
    prefixLabel?: string,
    namedFilterOptions?: IRecordGridColumnNamedFilterOption[],
    typeaheadKeystrokeCount?: number,
    additionalQueryCriteria?: string
  ): AdvancedFilterOption {
    const config: AdvancedFilterOption = {
      id: fieldId,
      label: header,
      dataType: filterConfig.filterOptionDataType,
      isHidden,
      data: isFunction(filterConfig.getDataForAdvancedFilter)
        ? filterConfig.getDataForAdvancedFilter(fieldDefinition, fieldId)
        : null
    };

    namedFilterOptions = namedFilterOptions ?? [];

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.boolean) {
      config.inputsTemplate =
        fieldDefinition.fieldOption !== RecordFieldOption.Required
          ? customFilterTemplates.optionalBooleanFilter
          : customFilterTemplates.requiredBooleanFilter;

      config.customConfig = <
        AdvancedFilterOptionConfig<RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>>
      >{
        getExpressionFieldTips: (filterOption: AdvancedFilterOption): TagModel<AdvancedFilterExpressionTip>[] => {
          const data = filterOption.data as AdvancedFilterSelectionTypeData;
          const selectOptions = data?.selectOptions || [];

          const namedFilterOptionTips = this.getNamedFilterOptionTips(
            namedFilterOptions,
            filterOption,
            config.customConfig
          );

          return namedFilterOptionTips.concat(
            selectOptions.map((selectOption) => {
              return AdvancedFilterDataTypesConfigsService.createAdvancedFilterTag(
                new RxRecordGridAdvancedFilterValue([selectOption.id]),
                filterOption,
                config.customConfig,
                false,
                formatAdvancedFilterTagText(filterOption, selectOption.name)
              );
            })
          );
        },
        formatValueForTag: (value, option: AdvancedFilterOption): string => {
          const data = option.data as AdvancedFilterSelectionTypeData;
          const selectOptions = data?.selectOptions || [];

          const filterValueLabels = value.filterValue.map(
            (id) => selectOptions.find((selectOption) => selectOption.id === id).name
          );

          return this.buildTagLabel(filterValueLabels, this.getNamedOptionTitles(value, namedFilterOptions));
        },
        needRemoveTagValue: (value) => RxRecordGridAdvancedFilterValue.isEmpty(value),
        updateFilterValue: (
          newValue: RxRecordGridAdvancedFilterValue,
          oldValue: RxRecordGridAdvancedFilterValue,
          filterOption: AdvancedFilterOption
        ) => this.updateFilterValue(oldValue, newValue, filterOption.data.isRequired),
        parseTagFieldExpression: (
          filterOption: AdvancedFilterOption<RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>>,
          searchTerm: string
        ): ParsedTagFieldExpressionResult<RxRecordGridAdvancedFilterValue<AdvancedFilterBooleanDataType[]>> | null => {
          const rawValues = AdvancedFilterDataTypesConfigsService.getExpressionRawValues(filterOption, searchTerm);
          const value = this.getFilterValueFromString(namedFilterOptions, rawValues);
          const data = filterOption.data as AdvancedFilterSelectionTypeData;
          const selectOptions = data?.selectOptions || [];

          value.filterValue =
            value.filterValue
              .map((optionLabel: string) => {
                optionLabel = optionLabel.trim().toLowerCase();
                const selectOption = selectOptions.find((option) => option.name.toLowerCase() === optionLabel);

                return selectOption?.id ?? null;
              })
              .filter((val) => !isNil(val)) || [];

          return value.length ? { value } : null;
        }
      };
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.string) {
      if (fieldDefinition.namedListDefinition) {
        config.inputsTemplate = customFilterTemplates.inputsForStringWithTypeAheadTemplate;
        config.data = { ...config.data, typeaheadKeystrokeCount, additionalQueryCriteria };
      } else {
        config.inputsTemplate = customFilterTemplates.inputsForStringTypeTemplate;
      }

      config.customConfig = {
        needRemoveTagValue(value: RxRecordGridAdvancedFilterValue) {
          return RxRecordGridAdvancedFilterValue.isEmpty(value);
        },
        formatValueForTag: (value: RxRecordGridAdvancedFilterValue, filterOption: AdvancedFilterOption): string =>
          this.buildTagLabel(value.filterValue, this.getNamedOptionTitles(value, namedFilterOptions)),
        getExpressionFieldTips: (option, searchTerm: string) => {
          return [
            ...this.getNamedFilterOptionTips(namedFilterOptions, option, config.customConfig),
            AdvancedFilterDataTypesConfigsService.createAdvancedFilterTag(
              new RxRecordGridAdvancedFilterValue(),
              option,
              config.customConfig,
              true,
              formatAdvancedFilterTagText(option, '')
            )
          ];
        },
        updateFilterValue: (
          newValue: RxRecordGridAdvancedFilterValue,
          oldValue: RxRecordGridAdvancedFilterValue,
          filterOption: AdvancedFilterOption
        ) => this.updateFilterValue(oldValue, newValue),
        parseTagFieldExpression: (filterOption, searchTerm) => {
          return this.parseStringTagFieldExpression(filterOption, searchTerm, namedFilterOptions);
        }
      };
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.selection) {
      config.inputsTemplate = customFilterTemplates.inputsForSelectionTypeTemplate;

      config.customConfig = {
        needRemoveTagValue(value: RxRecordGridAdvancedFilterValue) {
          return RxRecordGridAdvancedFilterValue.isEmpty(value);
        },
        formatValueForTag: (value: RxRecordGridAdvancedFilterValue, option: AdvancedFilterOption): string => {
          const filterValueLabels = this.defaultConfig[filterConfig.filterOptionDataType].formatValueForTag(
            value.filterValue,
            option
          );

          return this.buildTagLabel(filterValueLabels, this.getNamedOptionTitles(value, namedFilterOptions));
        },
        getExpressionFieldTips: (filterOption, searchTerm: string) => {
          const data = filterOption.data as AdvancedFilterSelectionTypeData;
          const selectOptions = (data && data.selectOptions) || [];

          return this.getNamedFilterOptionTips(namedFilterOptions, filterOption, config.customConfig).concat(
            selectOptions.map((selectOption) => {
              return AdvancedFilterDataTypesConfigsService.createAdvancedFilterTag(
                new RxRecordGridAdvancedFilterValue([selectOption.id]),
                filterOption,
                config.customConfig,
                false,
                formatAdvancedFilterTagText(filterOption, selectOption.name)
              );
            })
          );
        },
        updateFilterValue: (
          newValue: RxRecordGridAdvancedFilterValue,
          oldValue: RxRecordGridAdvancedFilterValue,
          filterOption: AdvancedFilterOption
        ) => this.updateFilterValue(oldValue, newValue),
        parseTagFieldExpression: (filterOption, searchTerm) => {
          return this.parseStringTagFieldExpression(filterOption, searchTerm, namedFilterOptions);
        }
      };
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.datetime) {
      config.inputsTemplate = customFilterTemplates.inputsForDatetimeTypeTemplate;

      config.customConfig = this.getDatetimeCustomConfig(
        namedFilterOptions,
        config,
        AdvancedFilterOptionDataType.datetime,
        'll LTS'
      );
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.date) {
      config.inputsTemplate = customFilterTemplates.inputsForDateTypeTemplate;

      config.customConfig = this.getDatetimeCustomConfig(
        namedFilterOptions,
        config,
        AdvancedFilterOptionDataType.date,
        'll'
      );
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.time) {
      config.inputsTemplate = customFilterTemplates.inputsForTimeTypeTemplate;

      config.customConfig = this.getDatetimeCustomConfig(
        namedFilterOptions,
        config,
        AdvancedFilterOptionDataType.time,
        'LTS'
      );
    }

    if (filterConfig.filterOptionDataType === AdvancedFilterOptionDataType.number) {
      config.inputsTemplate = customFilterTemplates.inputsForNumberTypeTemplate;

      config.customConfig = {
        needRemoveTagValue(value: RxRecordGridAdvancedFilterValue) {
          return RxRecordGridAdvancedFilterValue.isEmptyWithRange(value);
        },
        getExpressionFieldTips: (option, searchTerm: string) => {
          return this.getNamedFilterOptionTips(namedFilterOptions, option, config.customConfig, [null, null]).concat(
            this.defaultConfig[filterConfig.filterOptionDataType].getExpressionFieldTips(option, searchTerm)
          );
        },
        updateFilterValue: (
          newValue: RxRecordGridAdvancedFilterValue,
          oldValue: RxRecordGridAdvancedFilterValue,
          filterOption: AdvancedFilterOption
        ) => {
          newValue.filterValue = this.defaultConfig[filterConfig.filterOptionDataType].updateFilterValue(
            newValue.filterValue,
            oldValue.filterValue,
            filterOption
          );

          newValue.namedOptions = uniq(oldValue.namedOptions.concat(newValue.namedOptions));

          return newValue;
        },
        formatValueForTag: (value: RxRecordGridAdvancedFilterValue) => {
          const formattedValue = value.filterValue.filter((val) => val != null).join(' - ');

          return this.buildTagLabel(formattedValue, this.getNamedOptionTitles(value, namedFilterOptions));
        },
        parseTagFieldExpression: (filterOption: AdvancedFilterOption, searchTerm: string) => {
          const { value: valuesStr, label } = this.getExpressionRawValues(filterOption, searchTerm);

          if (!valuesStr) {
            return null;
          }

          const value = this.getFilterValueFromString(namedFilterOptions, valuesStr);

          value.filterValue =
            this.defaultConfig[filterConfig.filterOptionDataType].parseTagFieldExpression(
              filterOption,
              `${label} ${value.filterValue[0]}`
            )?.value ?? [];

          return value.length ? { value } : null;
        },
        validateValue: (
          value: RxRecordGridAdvancedFilterValue,
          filterOption: AdvancedFilterOption<RxRecordGridAdvancedFilterValue>
        ) => this.defaultConfig[filterConfig.filterOptionDataType].validateValue(value.filterValue, filterOption)
      };
    }

    if (prefixLabel) {
      const prefixConfig = {
        getTagText(value: string, option: AdvancedFilterOption, formattedValue: string): string {
          return `${prefixLabel}: ${formattedValue}`;
        }
      };

      config.customConfig = { ...config.customConfig, ...prefixConfig };
    }

    return config;
  }

  private parseStringTagFieldExpression(
    filterOption: AdvancedFilterOption,
    searchTerm: string,
    namedFilterOptions: IRecordGridColumnNamedFilterOption[]
  ): ParsedTagFieldExpressionResult<RxRecordGridAdvancedFilterValue> {
    const valuesStr = AdvancedFilterDataTypesConfigsService.getExpressionRawValues(filterOption, searchTerm);

    if (!valuesStr) {
      return null;
    }

    const value = this.getFilterValueFromString(namedFilterOptions, valuesStr);

    return value.length ? { value } : null;
  }

  private updateFilterValue(
    oldValue: RxRecordGridAdvancedFilterValue,
    newValue: RxRecordGridAdvancedFilterValue,
    replaceFilterValue = false
  ): RxRecordGridAdvancedFilterValue {
    if (!replaceFilterValue) {
      newValue.filterValue = uniq(oldValue.filterValue.concat(newValue.filterValue));
    }

    newValue.namedOptions = uniq(oldValue.namedOptions.concat(newValue.namedOptions));

    return newValue;
  }

  private buildTagLabel(
    filterValueLabels: string | string[] | INamedListOption[],
    namedOptionTitles: string[],
    filterValueSeparator = ', ',
    namedOptionsSeparator = '; '
  ): string {
    return [
      isString(filterValueLabels)
        ? filterValueLabels
        : isArray(filterValueLabels) && !isEmpty(filterValueLabels)
        ? this.rxNamedListService.isNamedListOption(filterValueLabels[0])
          ? filterValueLabels.map((value) => value.displayValue ?? value.value).join(filterValueSeparator)
          : filterValueLabels.join(filterValueSeparator)
        : '',
      ...namedOptionTitles
    ]
      .filter(Boolean)
      .join(namedOptionsSeparator);
  }

  private getNamedOptionTitles(
    value: RxRecordGridAdvancedFilterValue,
    options: IRecordGridColumnNamedFilterOption[]
  ): string[] {
    const optionGuids = options.map((option) => option.guid);

    return value.namedOptions
      .sort((option1, option2) => optionGuids.indexOf(option1) - optionGuids.indexOf(option2))
      .map((guid) => options.find((option) => option.guid === guid)?.title);
  }

  private getDatetimeCustomConfig(
    namedFilterOptions: IRecordGridColumnNamedFilterOption[],
    config: AdvancedFilterOption,
    fieldType: AdvancedFilterOptionDataType,
    tagValueFormat: string
  ): AdvancedFilterOptionConfig {
    return {
      getExpressionFieldTips: (
        option: AdvancedFilterOption,
        searchTerm: string
      ): TagModel<AdvancedFilterExpressionTip>[] => {
        return this.getNamedFilterOptionTips(namedFilterOptions, option, config.customConfig).concat(
          this.defaultConfig[fieldType].getExpressionFieldTips(option, searchTerm)
        );
      },
      needRemoveTagValue(value: RxRecordGridAdvancedFilterValue) {
        return RxRecordGridAdvancedFilterValue.isEmptyWithRange(value);
      },
      formatValueForTag: (value: RxRecordGridAdvancedFilterValue): string => {
        const formattedValue = value.filterValue
          .filter((val) => val)
          .map((date) => moment(date).format(tagValueFormat))
          .join(' - ');

        return this.buildTagLabel(formattedValue, this.getNamedOptionTitles(value, namedFilterOptions));
      },
      updateFilterValue: (
        newValue: RxRecordGridAdvancedFilterValue,
        oldValue: RxRecordGridAdvancedFilterValue,
        filterOption: AdvancedFilterOption
      ) => {
        newValue.filterValue = this.defaultConfig[fieldType].updateFilterValue(
          newValue.filterValue,
          oldValue.filterValue,
          filterOption
        );

        newValue.namedOptions = uniq(oldValue.namedOptions.concat(newValue.namedOptions));

        return newValue;
      },
      parseTagFieldExpression: (filterOption: AdvancedFilterOption, searchTerm: string) => {
        const { value: valuesStr } = this.getExpressionRawValues(filterOption, searchTerm);

        if (!valuesStr) {
          return null;
        }

        const value = this.getFilterValueFromString(namedFilterOptions, valuesStr);
        const [dateStr] = searchTerm.split('; ');

        value.filterValue = this.defaultConfig[fieldType].parseTagFieldExpression(filterOption, dateStr)?.value ?? [];

        return value.length ? { value } : null;
      },
      validateValue: (
        value: RxRecordGridAdvancedFilterValue,
        filterOption: AdvancedFilterOption<RxRecordGridAdvancedFilterValue>
      ) => this.defaultConfig[fieldType].validateValue(value.filterValue, filterOption)
    };
  }

  private getExpressionRawValues(
    filterOption: AdvancedFilterOption,
    searchTerm: string
  ): { label: string; value: string } {
    return [
      `${filterOption.label}:`,
      `${filterOption.label} ${this.adaptTexts.fromLabel}`,
      `${filterOption.label} ${this.adaptTexts.toLabel}`
    ].reduce(
      (result, mask) => {
        if (searchTerm.startsWith(mask)) {
          result.label = mask;
          result.value = searchTerm.replace(mask, '').trim();
        }

        return result;
      },
      { label: null, value: null }
    );
  }

  private getFilterValueFromString(
    namedFilterOptions: IRecordGridColumnNamedFilterOption[],
    valuesStr: string
  ): RxRecordGridAdvancedFilterValue {
    const values = (valuesStr ?? '')
      .trim()
      .split(';')
      .filter((str) => !isEmptyOrWhitespace(str))
      .map((str) => str.trim());

    const value = new RxRecordGridAdvancedFilterValue([]);

    values.forEach((val) => {
      const namedOptionGuid = namedFilterOptions.find((item) => item.title === val)?.guid;

      if (namedOptionGuid) {
        value.namedOptions.push(namedOptionGuid);
      } else {
        value.filterValue.push(
          ...val
            .split(',')
            .map((str) => str.trim())
            .filter(Boolean)
        );
      }
    });

    value.namedOptions = uniq(value.namedOptions);

    return value;
  }

  private getNamedFilterOptionTips(
    namedFilterOptions: IRecordGridColumnNamedFilterOption[],
    option: AdvancedFilterOption,
    filterOptionConfig: AdvancedFilterOptionConfig,
    value = []
  ): TagModel<AdvancedFilterExpressionTip>[] {
    return (namedFilterOptions ?? []).map((item) =>
      AdvancedFilterDataTypesConfigsService.createAdvancedFilterTag(
        new RxRecordGridAdvancedFilterValue(value, [item.guid]),
        option,
        filterOptionConfig,
        false,
        formatAdvancedFilterTagText(option, item.title)
      )
    );
  }
}
