import { Tooltip } from '@helix/platform/shared/api';
import {
  ISelectFormControlOptions,
  ITagAutocompleteValue,
  SelectFormControlComponent,
  TextFormControlComponent
} from '@helix/platform/shared/components';
import {
  IViewDesignerComponentModel,
  RX_STANDARD_PROPS_DEFAULT_VALUES,
  RX_VIEW_DEFINITION
} from '@helix/platform/view/api';
import {
  getAvailableOnDevicesInspectorConfig,
  getHiddenFieldInspectorConfig,
  getStylesFieldInspectorConfig,
  IViewDesignerInspectorConfig,
  validateStandardProps,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { isEmpty, times, values } from 'lodash';
import { Observable } from 'rxjs';
import { map, skip, take, takeUntil } from 'rxjs/operators';
import { RX_CONTAINER } from '../container.constant';
import { ContainerRowWrap } from '../container.types';
import { IContainerProperties } from './container-design.types';

const cssClassesAutocompleteValues = RX_VIEW_DEFINITION.styles.map(
  (style): ITagAutocompleteValue => ({ text: style.name, data: { value: style.id } })
);

export class ContainerDesignModel
  extends ViewDesignerComponentModel<IContainerProperties>
  implements IViewDesignerComponentModel<IContainerProperties>
{
  componentProperties$ = this.sandbox.componentProperties$;
  hasChildren$: Observable<boolean> = this.sandbox.getChildComponentGuids().pipe(map((guids) => !isEmpty(guids)));

  static getInitialProperties(initialProperties?: IContainerProperties): IContainerProperties {
    return {
      name: null,
      rowWrap: ContainerRowWrap.Sm,
      columnCount: '1',
      ...RX_STANDARD_PROPS_DEFAULT_VALUES,
      ...initialProperties
    };
  }

  rxInit(): void {
    this.componentProperties$.pipe(take(1)).subscribe(() => {
      this.sandbox.updateInspectorConfig(this.getInspector());
    });

    this.componentProperties$.subscribe((componentProperties) => this.validate(componentProperties));

    this.sandbox
      .getComponentPropertyValue('columnCount')
      .pipe(skip(1), takeUntil(this.sandbox.destroyed$))
      .subscribe((columnCount) => {
        this.sandbox.setLayout(this.getDefaultColumnSpans(Number(columnCount)));
      });

    this.sandbox.getComponentPropertyValue('name').subscribe((name) => {
      const componentName = name ? `${this.sandbox.descriptor.name} (${name})` : this.sandbox.descriptor.name;

      this.sandbox.setSettablePropertiesDataDictionary(componentName, [
        {
          label: 'Hidden',
          expression: this.getExpressionForProperty('hidden')
        }
      ]);
    });
  }

  private validate(model: IContainerProperties): void {
    this.sandbox.setValidationIssues(validateStandardProps(model));
  }

  getPropertiesByName(properties: IContainerProperties): IContainerProperties {
    const result = {
      ...properties
    };

    if (!result.styles) {
      delete result.styles;
    }

    return result;
  }

  setContainerLayout(columnSizes: number[]): void {
    this.sandbox.setLayout(columnSizes);
  }

  private getDefaultColumnSpans(columnCount: number): number[] {
    const columnSpans: number[] = [];
    const span = Math.floor(RX_CONTAINER.maxColumnSpan / columnCount);

    times(columnCount, () => columnSpans.push(span));
    columnSpans[columnSpans.length - 1] = (RX_CONTAINER.maxColumnSpan % columnCount) + span;

    return columnSpans;
  }

  private getInspector(): IViewDesignerInspectorConfig {
    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls: [
            {
              name: 'name',
              component: TextFormControlComponent,
              options: {
                label: 'Name',
                tooltip: new Tooltip('Enter a name to uniquely identify the Container.')
              }
            },
            {
              name: 'columnCount',
              component: SelectFormControlComponent,
              options: {
                label: 'Number of columns',
                required: true,
                tooltip: new Tooltip(
                  'Resize container columns on the canvas by dragging the dashed column separator line.'
                ),
                options: times(6, (index: number) => ({ id: (++index).toString(), name: index.toString() }))
              } as ISelectFormControlOptions
            },
            {
              name: 'rowWrap',
              component: SelectFormControlComponent,
              options: {
                label: 'Row wrap',
                sortAlphabetically: false,
                options: values(RX_CONTAINER.rowWrapOptions),
                tooltip: new Tooltip(
                  `The Row wrap property controls the layout of multi-column containers based on
                    the width of the view. When the width of the view is decreased to a width less
                    than the selected Row wrap value, each container column after the first one
                    wraps onto a new line.`
                )
              } as ISelectFormControlOptions
            },
            getHiddenFieldInspectorConfig(),
            getAvailableOnDevicesInspectorConfig(),
            getStylesFieldInspectorConfig(cssClassesAutocompleteValues)
          ]
        }
      ]
    };
  }
}
