import { Injectable } from '@angular/core';
import { AdvancedFilterOption, SelectedAdvancedFilter } from '@bmc-ux/adapt-angular';
import { RowDataItem } from '@bmc-ux/adapt-table';
import { IAssociationDescriptor } from '@helix/platform/association/api';
import {
  IFieldDefinition,
  IFieldDefinitionsById,
  IRecordDefinition,
  RX_RECORD_DEFINITION,
  RxRecordDefinitionService
} from '@helix/platform/record/api';
import { RxJsonParserService, RxObjectUtilsService, RxStringService } from '@helix/platform/utils';
import {
  IRecordGridFilterColumnValue,
  IRecordGridFilterData,
  IRecordGridFilterDataOrOperator,
  IRecordGridFilterOperator,
  IRowDataItem,
  RecordGridFilterDataLogic,
  RecordGridFilterOperator,
  RecordGridNamedFilterOptionKey
} from '@helix/platform/view/api';
import {
  every,
  filter as _filter,
  find,
  findKey,
  get,
  has,
  includes,
  intersection,
  isEmpty,
  isString,
  keys,
  map as _map,
  reduce,
  some,
  values
} from 'lodash';
import moment from 'moment-es6';
import { forkJoin, Observable, of } from 'rxjs';
import { defaultIfEmpty, filter, map } from 'rxjs/operators';
import { RxRecordGridAdvancedFilteringService } from '../../common/services/record-grid-advanced-filtering.service';
import { RxRecordGridUtilsService } from '../../common/services/record-grid-utils.service';
import { QueryFiltersLogic } from '../types/query-filters-logic.enum';
import { IAdaptTableColumnConfig, IRecordGridColumnWithMetadata } from '../types/record-grid-column.types';
import { IRecordGridFilterConfigs } from '../../common/types/record-grid-filter-config.interfaces';
import { IRxRecordGridFilter } from '../../common/types/record-grid-filter.types';
import { IRecordGridNamedFilterOptionsMap } from '../../common/types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../common/types/record-grid-advanced-filter-value.class';
import { RxRecordGridFilterHelperService } from '../../common/services/record-grid-filter-helper.service';
import { IPlainObject } from '@helix/platform/shared/api';

@Injectable()
export class RxRecordGridFilterService {
  filtersKeywords = ['$NULL$', '$TIME$', '$DATE$', '$TIMESTAMP$', '$USER$'];

  constructor(
    public rxStringService: RxStringService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxJsonParserService: RxJsonParserService,
    private rxRecordGridAdvancedFilteringService: RxRecordGridAdvancedFilteringService,
    private rxRecordDefinitionService: RxRecordDefinitionService,
    private rxObjectUtilsService: RxObjectUtilsService,
    private rxRecordGridFilterHelperService: RxRecordGridFilterHelperService
  ) {}

  clearFilterData(node: IPlainObject): IPlainObject {
    const nodeKeys = keys(node);
    let result = node;

    if (!isEmpty(nodeKeys)) {
      const operator = nodeKeys[0];

      switch (true) {
        case includes(['or', 'and'], operator): {
          const children = node[operator].map(this.clearFilterData.bind(this)).filter((child) => Boolean(child));

          if (!isEmpty(children)) {
            node[operator] = children;
          } else {
            result = null;
          }

          break;
        }
        case isString(node[operator]): {
          result = null;

          break;
        }
      }
    }

    return result;
  }

  getFilterForAvailableColumns(node: IPlainObject, filterableColumns: IRecordGridColumnWithMetadata[]): IPlainObject {
    const nodeKeys = keys(node);
    let result = node;

    if (!isEmpty(nodeKeys)) {
      const operator = nodeKeys[0];
      const fieldId = Object.keys(node[operator])[0];

      switch (true) {
        case includes(['or', 'and'], operator): {
          const children = node[operator]
            .map((nodeChild) => this.getFilterForAvailableColumns(nodeChild, filterableColumns))
            .filter((child) => Boolean(child));

          if (!isEmpty(children)) {
            node[operator] = children;
          } else {
            result = null;
          }

          break;
        }
        case !some(filterableColumns, { fieldId }): {
          result = null;

          break;
        }
        case includes(['eq'], operator): {
          const fieldDefinition = find(filterableColumns, { fieldId }).fieldDefinition;

          // remove selection field filter option that is not available
          if (
            fieldDefinition.resourceType === RX_RECORD_DEFINITION.resourceTypes.selection &&
            !has(fieldDefinition.optionNamesById, node[operator][fieldId])
          ) {
            result = null;
          }

          break;
        }
      }
    }

    return result;
  }

  replaceKeywords(query: string): string {
    const singleQuoteRegexTemplate = '([^|^"])"(' + this.filtersKeywords.join('|').replace(/\$/g, '\\$') + ')"(?!")';

    const tripleQuoteRegexTemplate =
      '([^|^"])"""(' + this.filtersKeywords.join('|').replace(/\$/g, '\\$') + ')"""(?!")';

    const singleQuoteRegex = new RegExp(singleQuoteRegexTemplate, 'g');
    const tripleQuoteRegex = new RegExp(tripleQuoteRegexTemplate, 'g');

    return query.replace(singleQuoteRegex, '$1$2').replace(tripleQuoteRegex, '$1"$2"');
  }

  generateTextFilterQuery(text: string, columns: IAdaptTableColumnConfig[]): string {
    let filterQuery = null;

    if (text) {
      const query = columns
        .filter((column) => !column.hidden && column.searchable)
        .map((column) => {
          const fieldId = this.getQueryFilterField(column.field);
          let queryString = '';

          if (
            column.filterType === RX_RECORD_DEFINITION.dataTypes.character.shortName ||
            column.filterType === RX_RECORD_DEFINITION.dataTypes.localizedCharacter.shortName
          ) {
            queryString = `${fieldId} LIKE "%${this.escapeDoubleQuotes(text).replace(/[%_]/g, '\\$&')}%"`;
          } else {
            if (!isNaN(text as any)) {
              queryString = `${fieldId} = "${this.escapeDoubleQuotes(text)}"`;
            }
          }

          return queryString;
        })
        .filter(Boolean)
        .join(' OR ');

      filterQuery = query ? `(${query})` : '';
    }

    return filterQuery;
  }

  addQueries(...queries: string[]): string {
    return _filter(queries, Boolean).join(' AND ');
  }

  joinQueryFilters(queries: string[], logic: QueryFiltersLogic): string {
    const query = queries.join(logic === QueryFiltersLogic.And ? ' AND ' : ' OR ');

    return query ? `(${query})` : '';
  }

  /**
   * Convert saved filter preset data from:
   * {
   *   and: {
   *     "or": [
   *       {
   *         "eq": {
   *           "1": "bar"
   *         }
   *       },
   *       {
   *         "eq": {
   *           "1": "foo"
   *         }
   *       }
   *     ]
   *   }
   * }
   *
   *
   * to:
   * [{
   *   filterOptionId: '1',
   *   value: ['bar', 'foo']
   * }]
   */
  getAdvancedFilterData(
    filterData: IRecordGridFilterData,
    fieldDefinitionsById: IFieldDefinitionsById,
    recordGridFilterConfigs: IRecordGridFilterConfigs,
    primaryRecordDefinition: IRecordDefinition,
    filterOptions: AdvancedFilterOption[],
    associationDescriptors: IAssociationDescriptor[]
  ): Observable<SelectedAdvancedFilter[]> {
    return forkJoin(
      filterData && filterData.and
        ? filterData.and.map((gridFilterData: IRecordGridFilterData): Observable<SelectedAdvancedFilter> => {
            const filterLogic = Object.keys(gridFilterData)[0] as RecordGridFilterDataLogic;
            const filterOperators = gridFilterData[filterLogic] as IRecordGridFilterOperator[];
            const firstFilterOperator = values(filterOperators[0])[0];
            const columnField = String(Object.keys(firstFilterOperator)[0]);
            let fieldDefinition = fieldDefinitionsById[columnField];

            const fieldDefinition$ = fieldDefinition
              ? of(fieldDefinition)
              : this.rxRecordGridUtilsService.getFieldDefinition(columnField, primaryRecordDefinition);

            return fieldDefinition$.pipe(
              filter<IFieldDefinition>(Boolean),
              map((newFieldDefinition) => {
                fieldDefinition = newFieldDefinition;

                const filterConfig = recordGridFilterConfigs[fieldDefinition.resourceType];

                if (!fieldDefinitionsById[columnField]) {
                  this.rxRecordDefinitionService.addFieldDefinitionToMap(
                    fieldDefinition,
                    fieldDefinitionsById,
                    columnField
                  );

                  fieldDefinitionsById = primaryRecordDefinition.fieldDefinitionsById = {
                    ...fieldDefinitionsById,
                    ...this.rxObjectUtilsService.expandProperties(fieldDefinitionsById)
                  };

                  const associationDescriptor = this.rxRecordGridUtilsService.findAssociationDescriptor(
                    associationDescriptors,
                    this.rxRecordGridUtilsService.getFieldLocator(columnField)
                  );

                  filterOptions.push(
                    this.rxRecordGridAdvancedFilteringService.getAdvancedFilterOption(
                      columnField,
                      this.rxRecordGridUtilsService.getColumnLabel(fieldDefinition, associationDescriptor),
                      fieldDefinition,
                      filterConfig,
                      true
                    )
                  );
                }

                return filterConfig.getSelectedAdvancedFilterData(
                  filterLogic,
                  filterOperators,
                  fieldDefinition,
                  columnField
                );
              })
            );
          })
        : []
    ).pipe(
      defaultIfEmpty([]),
      map((advancedFiltersData) => advancedFiltersData.filter(Boolean))
    );
  }

  getQueryFromRecordGridFilterData(
    filterData: IRecordGridFilterData,
    fieldDefinitionsById: IFieldDefinitionsById
  ): string {
    const primitiveOperators = [
      RecordGridFilterOperator.Eq,
      RecordGridFilterOperator.Like,
      RecordGridFilterOperator.Ne,
      RecordGridFilterOperator.Lt,
      RecordGridFilterOperator.Lte,
      RecordGridFilterOperator.Gt,
      RecordGridFilterOperator.Gte,
      RecordGridFilterOperator.In,
      'regex'
    ];

    const isPrimitive = (expression: IRecordGridFilterDataOrOperator): expression is IRecordGridFilterOperator => {
      return intersection(primitiveOperators.slice(), keys(expression)).length > 0;
    };

    const evaluatePrimitive = (expression: IRecordGridFilterOperator) => {
      return reduce(
        expression,
        (operatorsResult, condition, operator: RecordGridFilterOperator) => {
          return (
            operatorsResult +
            reduce(
              condition as IRecordGridFilterColumnValue,
              (operatorValuesResult, conditionValue, conditionId) => {
                const fieldDefinition = fieldDefinitionsById[conditionId];
                let filterOperator = operator;
                let value = conditionValue;

                const isTextField =
                  !!fieldDefinition &&
                  includes(
                    [
                      RX_RECORD_DEFINITION.dataTypes.character.resourceType,
                      RX_RECORD_DEFINITION.dataTypes.localizedCharacter.resourceType
                    ],
                    fieldDefinition.resourceType
                  );

                if (isTextField) {
                  const { isLikeOperation, filterValue } = this.getStringDataTypeFilterData(
                    conditionValue,
                    fieldDefinition
                  );

                  filterOperator = isLikeOperation ? RecordGridFilterOperator.Like : filterOperator;
                  value = filterValue;
                }
                // following code removes the $DISPLAYVALUE$ from the filter expression
                // e.g.  {"and":[{"or":[{"eq":{"536870913":"AGGADG1AAXPMRARKA0FGRKA0FG6I01","$DISPLAYVALUE$":"Alex"}}]}]}
                if (conditionId === '$DISPLAYVALUE$') {
                  return operatorValuesResult;
                }

                return operatorValuesResult + this.getQueryExpression(value, conditionId, filterOperator);
              },
              ''
            )
          );
        },
        ''
      );
    };

    let evaluateExpression = (expression: IRecordGridFilterDataOrOperator): string => {
      if (isPrimitive(expression)) {
        return evaluatePrimitive(expression);
      }

      return reduce(
        expression,
        (result, childExpression, operator) => {
          const childExpressionResult = _map(childExpression, evaluateExpression);

          switch (operator) {
            case RecordGridFilterDataLogic.And:
              return result + `(${childExpressionResult.join(' AND ')})`;
            case RecordGridFilterDataLogic.Or:
              return result + `(${childExpressionResult.join(' OR ')})`;
            default:
              return result + `(${childExpressionResult.join(' AND ')})`;
          }
        },
        ''
      );
    };

    evaluateExpression = evaluateExpression.bind(this);

    return evaluateExpression(filterData);
  }

  getSelectedFiltersFromPredefinedFilter(
    filters: string,
    recordGridFilters: IRxRecordGridFilter[],
    fieldDefinitionsById: IFieldDefinitionsById,
    recordGridFilterConfigs: IRecordGridFilterConfigs,
    primaryRecordDefinition: IRecordDefinition,
    filterOptions: AdvancedFilterOption[],
    associationDescriptors: IAssociationDescriptor[]
  ): Observable<SelectedAdvancedFilter[]> {
    const recordGridFilterData = this.rxRecordGridFilterHelperService.getRecordGridFilterDataFromPredefinedFilter(
      filters,
      this.deserializeNamedOptions(recordGridFilters)
    );

    const selectedFilters$ = this.getAdvancedFilterData(
      recordGridFilterData,
      fieldDefinitionsById,
      recordGridFilterConfigs,
      primaryRecordDefinition,
      filterOptions,
      associationDescriptors
    );

    return selectedFilters$;
  }

  getRecordGridFilterDataFromAdvancedFilter(
    filters: SelectedAdvancedFilter[],
    fieldDefinitionsById: IFieldDefinitionsById,
    recordGridFilterConfigs: IRecordGridFilterConfigs,
    namedFilterOptions?: IRecordGridNamedFilterOptionsMap
  ): IRecordGridFilterData {
    const mappedFilters = filters.map((filterData): IRecordGridFilterDataOrOperator => {
      let mappedFilter = null;
      const fieldId = String(filterData.filterOptionId);
      const fieldDefinition = fieldDefinitionsById[fieldId];

      if (fieldDefinition) {
        const filterConfig = recordGridFilterConfigs[fieldDefinition.resourceType];

        mappedFilter = filterConfig.getRecordGridFilterData(
          filterData.value,
          fieldDefinition,
          fieldId,
          namedFilterOptions && namedFilterOptions[fieldId]
        );
      }

      return mappedFilter;
    });

    return mappedFilters.length
      ? {
          and: mappedFilters
        }
      : {};
  }

  getQueryFilterField(fieldId: string): string {
    if (this.rxRecordGridUtilsService.isAssociatedRecordFieldId(fieldId)) {
      return `\${${fieldId}}`;
    } else {
      return `'${fieldId}'`;
    }
  }

  buildQueryFilter(fieldId: string, operator: string, value: string | number): string {
    const needQuotes = isString(value) && !this.filtersKeywords.includes(value);
    const result = isString(value) ? (needQuotes ? `"${this.escapeDoubleQuotes(value)}"` : value) : String(value);

    return `${this.getQueryFilterField(fieldId)} ${operator} ${result}`;
  }

  filterRows(
    rows: IRowDataItem[],
    filters: SelectedAdvancedFilter[],
    recordDefinition: IRecordDefinition,
    filterConfigs: IRecordGridFilterConfigs
  ): IRowDataItem[] {
    const filterData = this.getRecordGridFilterDataFromAdvancedFilter(
      filters,
      recordDefinition.fieldDefinitionsById,
      filterConfigs
    );

    return rows.filter((row: IRowDataItem) => {
      if (row.groupField) {
        row.items = this.getFilteredItems(row.items, filterData, recordDefinition);

        return row.items.length;
      } else {
        return every(filterData.and, (filterExpression) => this.matchRow(row, filterExpression, recordDefinition));
      }
    });
  }

  private getFilteredItems(
    rowDataItems: RowDataItem[],
    filterData: IRecordGridFilterData,
    recordDefinition: IRecordDefinition
  ): RowDataItem[] {
    return reduce(
      rowDataItems,
      (filteredItems, itemsRow) => {
        let { items = [] } = itemsRow;

        if (
          !itemsRow.items &&
          every(filterData.and, (filterExpression) => this.matchRow(itemsRow, filterExpression, recordDefinition))
        ) {
          filteredItems.push(itemsRow);
        } else {
          items = this.getFilteredItems(items, filterData, recordDefinition);

          if (items.length) {
            filteredItems.push({ ...itemsRow, items });
          }
        }

        return filteredItems;
      },
      []
    );
  }

  private matchRow(
    row: IRowDataItem | RowDataItem,
    filterExpression: IRecordGridFilterData,
    recordDefinition: IRecordDefinition
  ): boolean {
    const operator = findKey(filterExpression);

    switch (operator) {
      case 'and':
        return every(filterExpression[operator], (childExpression) =>
          this.checkCondition(row, childExpression, recordDefinition)
        );

      case 'or':
        return some(filterExpression[operator], (childExpression) =>
          this.checkCondition(row, childExpression, recordDefinition)
        );

      default:
        return false;
    }
  }

  private checkCondition(
    row: IRowDataItem | RowDataItem,
    childExpression: IRecordGridFilterDataOrOperator,
    recordDefinition: IRecordDefinition
  ): boolean {
    const operator = findKey(childExpression);
    const fieldId = findKey(childExpression[operator]);
    const filterValue = childExpression[operator][fieldId];
    const rowValue = row[fieldId];

    if (
      recordDefinition.fieldDefinitionsById[fieldId].resourceType ===
      RX_RECORD_DEFINITION.dataTypes.dateTime.resourceType
    ) {
      switch (operator) {
        case 'eq':
          return moment(rowValue).isSame(filterValue);
        case 'lte':
          return moment(rowValue).isSameOrBefore(filterValue);
        case 'lt':
          return moment(rowValue).isBefore(filterValue);
        case 'gte':
          return moment(rowValue).isSameOrAfter(filterValue);
        case 'gt':
          return moment(rowValue).isAfter(filterValue);

        default:
          return false;
      }
    } else {
      return operator === 'eq' ? this.rxStringService.caseInsensitiveIsEqual(rowValue, filterValue) : false;
    }
  }

  private escapeDoubleQuotes(text: string): string {
    return text.replace(/"/g, '""');
  }

  private getQueryExpression(conditionValue: any, conditionId: string, operator: RecordGridFilterOperator): string {
    const value = isString(conditionValue) ? '"' + conditionValue + '"' : conditionValue;

    conditionId = this.getQueryFilterField(conditionId);

    switch (operator) {
      case RecordGridFilterOperator.Eq:
        return conditionId + '=' + value;
      case RecordGridFilterOperator.Like:
        return conditionId + ' LIKE ' + value;
      case RecordGridFilterOperator.Ne:
        return conditionId + '!=' + value;
      case RecordGridFilterOperator.Lt:
        return conditionId + '<' + value;
      case RecordGridFilterOperator.Lte:
        return conditionId + '<=' + value;
      case RecordGridFilterOperator.Gt:
        return conditionId + '>' + value;
      case RecordGridFilterOperator.Gte:
        return conditionId + '>=' + value;
      case RecordGridFilterOperator.In:
        return conditionId + ' IN ' + value;
      default:
        return conditionId + '=' + value;
    }
  }

  getStringDataTypeFilterData(
    filterValue: string,
    fieldDefinition: IFieldDefinition
  ): { isLikeOperation: boolean; filterValue: string } {
    let isLikeOperation = false;

    if (!get(fieldDefinition.searchDefinition, 'enableFTSSearch')) {
      if (includes(filterValue, '%')) {
        const filterValueSplitData = filterValue.split(/\\\\/);

        isLikeOperation = some(filterValueSplitData, (value) => value.replace(/\\%/g, '').match('%'));

        if (!isLikeOperation) {
          filterValue = _map(filterValueSplitData, (value) => value.replace(/\\%/g, '%')).join('\\');
        }
      } else {
        filterValue = filterValue.replace('\\\\', '\\');
      }
    }

    return { isLikeOperation, filterValue };
  }

  clearRemovedNamedFilterOptions(
    selectedFilters: SelectedAdvancedFilter<RxRecordGridAdvancedFilterValue>[],
    namedFilterOptions: IRecordGridNamedFilterOptionsMap
  ): SelectedAdvancedFilter[] {
    return selectedFilters.reduce((result, filter) => {
      const namedFilterOptionsGuids = namedFilterOptions[filter.filterOptionId]?.map((option) => option.guid) ?? [];

      filter.value.namedOptions = filter.value.namedOptions.filter((option) =>
        namedFilterOptionsGuids.includes(option)
      );

      if (!RxRecordGridAdvancedFilterValue.isEmptyWithRange(filter.value)) {
        result.push(filter);
      }

      return result;
    }, []);
  }

  deserializeNamedOptions(recordGridFilters: IRxRecordGridFilter[]): IRxRecordGridFilter[] {
    return recordGridFilters.map((filter) => {
      const value = this.rxJsonParserService.tryParseJson(filter.value);

      if (value && value[RecordGridNamedFilterOptionKey]) {
        return {
          ...filter,
          value
        };
      }

      return filter;
    });
  }
}
