import { Injectable } from '@angular/core';
import { combineLatest, Observable, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, switchMap } from 'rxjs/operators';
import { RxShareViewPresetDataService } from './share-view-preset-data.service';
import { RxJsonParserService, RxStringService } from '@helix/platform/utils';
import { RX_VIEW_PRESET } from '../../view-preset-selector.types';
import { RxCurrentUserService } from '@helix/platform/shared/api';
import {
  ISaveSharedViewPresetPayload,
  ISharedViewPresetItem,
  IShareViewPresetPayload,
  IShareViewPresetUserPayload,
  ITagUserAutocompleteValue
} from './share-view-preset.types';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IViewPresetsByViewComponentGuid } from '@helix/platform/view/api';

@Injectable({
  providedIn: 'root'
})
export class RxShareViewPresetService {
  constructor(
    private rxShareViewPresetDataService: RxShareViewPresetDataService,
    private rxStringService: RxStringService,
    private rxJsonParserService: RxJsonParserService,
    private rxCurrentUserService: RxCurrentUserService
  ) {}

  getAutocompleteSearch(text$: Observable<string>): Observable<ITagUserAutocompleteValue[]> {
    return text$.pipe(
      debounceTime(250),
      distinctUntilChanged(),
      switchMap((searchTerm) => {
        const trimmedValue = searchTerm.trim();

        if (trimmedValue.length > 0) {
          return this.rxShareViewPresetDataService.searchUsers(trimmedValue);
        } else {
          return of([]);
        }
      }),
      map((users) =>
        users.map((user) => ({
          text: user[RX_VIEW_PRESET.user.fields.fullName],
          data: {
            loginId: user[RX_VIEW_PRESET.user.fields.loginId],
            fullName: user[RX_VIEW_PRESET.user.fields.fullName],
            emailAddress: user[RX_VIEW_PRESET.user.fields.email]
          }
        }))
      )
    );
  }

  getSharedViewPresetUsers(guid: string): Observable<IShareViewPresetUserPayload[]> {
    return this.rxShareViewPresetDataService
      .getSharedViewPreset(guid)
      .pipe(
        map((sharedViewPreset) =>
          this.rxJsonParserService.tryParseJson(
            sharedViewPreset?.fieldInstances[RX_VIEW_PRESET.sharedViewPreset.fields.sharedUsers].value,
            []
          )
        )
      );
  }

  getSharedViewPresetData(
    guid: string
  ): Observable<{ viewPresetName: string; sharedViewPresets: IViewPresetsByViewComponentGuid }> {
    return this.rxShareViewPresetDataService.getSharedViewPreset(guid).pipe(
      map((sharedViewPreset) => ({
        viewPresetName: sharedViewPreset?.fieldInstances[RX_VIEW_PRESET.sharedViewPreset.fields.viewPresetName].value,
        sharedViewPresets: this.rxJsonParserService.tryParseJson(
          sharedViewPreset?.fieldInstances[RX_VIEW_PRESET.sharedViewPreset.fields.viewPresetInfo].value,
          null
        )
      }))
    );
  }

  private createUpdateSharedViewPreset(payload: IShareViewPresetPayload, presetGuid?: string): Observable<string> {
    if (presetGuid) {
      return this.rxShareViewPresetDataService.updateSharedViewPreset(presetGuid, payload).pipe(map(() => presetGuid));
    } else {
      return this.rxShareViewPresetDataService.createSharedViewPreset(payload);
    }
  }

  saveSharedViewPreset(data: ISaveSharedViewPresetPayload, presetGuid?: string): Observable<string> {
    const sharedUsers$ = data.sharedUsers ? of(data.sharedUsers) : this.getSharedViewPresetUsers(presetGuid);

    return combineLatest([data.runtimeViewModelApi.shareViewPreset(data.presetSelectorGuid), sharedUsers$]).pipe(
      map(([sharedViewPresets, sharedUsers]: [IViewPresetsByViewComponentGuid, IShareViewPresetUserPayload[]]) => ({
        viewPresetName: data.currentViewPreset.label,
        viewName: '',
        viewPresetGuid: data.presetSelectorGuid,
        submitter: this.rxCurrentUserService.get().fullName,
        sharedUsers,
        presetInformation: JSON.stringify(sharedViewPresets)
      })),
      switchMap((payload: IShareViewPresetPayload) => this.createUpdateSharedViewPreset(payload, presetGuid))
    );
  }

  deleteSharedViewPreset(sharedViewPresetInstanceGuid: string): Observable<any> {
    return this.rxShareViewPresetDataService.deleteSharedViewPreset(sharedViewPresetInstanceGuid);
  }

  getSharedViewPresetsForCurrentUser(presetSelectorGuid: string): Observable<ISharedViewPresetItem[]> {
    return this.rxShareViewPresetDataService
      .getSharedViewPresets(presetSelectorGuid, this.rxCurrentUserService.getName())
      .pipe(
        map((presets) =>
          presets.map((preset) => ({
            label: preset[RX_VIEW_PRESET.sharedViewPreset.fields.viewPresetName],
            guid: preset[RX_VIEW_PRESET.sharedViewPreset.fields.id],
            ownerFullName: preset[RX_VIEW_PRESET.sharedViewPreset.fields.ownerFullName]
          }))
        )
      );
  }
}
