import { Component, Input, HostBinding, TemplateRef, ViewChild, Output, EventEmitter } from '@angular/core';
import { AdaptModalService } from '@bmc-ux/adapt-angular';
import { ColumnConfig, AdaptTableComponent, SortOrder } from '@bmc-ux/adapt-table';
import { SortMeta } from 'primeng/api';
import { IPlainObject } from '@helix/platform/shared/api';
import { ISortButtonClass } from './sort-button-class.interface';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'rx-card-layout-cell-header',
  templateUrl: './card-layout-cell-header.component.html',
  styleUrls: ['card-layout-cell-header.component.scss']
})
export class CardLayoutCellHeaderComponent {
  @Input() title: string;
  @Input() columns: ColumnConfig[];
  @Input() adaptTable: AdaptTableComponent;
  @Input() openDialog: (config: any) => Promise<any>;

  @Output() sort = new EventEmitter<SortMeta[]>();

  @ViewChild('cardLayoutSortModalTemplate') cardLayoutSortModalTemplate: TemplateRef<any>;

  multiSortMeta: SortMeta[];

  constructor(private modalService: AdaptModalService, private translateService: TranslateService) {}

  onSortMenuItemClick(): void {
    this.multiSortMeta = this.adaptTable.multiSortMeta || [];

    this.openDialog({
      title: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid.cards.sort-by.dialog.title'
      ),
      content: this.cardLayoutSortModalTemplate
    }).catch(() => {});
  }

  getSortableColumns() {
    return this.columns
      .filter((column) => this.adaptTable.isSortableColumn(column))
      .sort((column1, column2) => column1.header.localeCompare(column2.header));
  }

  onSortAscClick(column: ColumnConfig): void {
    this.changeSort(column, SortOrder.Asc);
  }

  onSortDescClick(column: ColumnConfig): void {
    this.changeSort(column, SortOrder.Desc);
  }

  changeSort(column: ColumnConfig, order: SortOrder): void {
    const sortIndex = this.getColumnSortIndex(column);
    const sort = this.multiSortMeta[sortIndex];

    if (sort) {
      if (sort.order === order) {
        this.multiSortMeta.splice(sortIndex, 1);
      } else {
        sort.order = order;
      }
    } else {
      this.multiSortMeta.push({
        field: column.field,
        order
      });
    }
  }

  getColumnSort(column: ColumnConfig): SortMeta {
    return this.multiSortMeta[this.getColumnSortIndex(column)];
  }

  getColumnSortIndex(column: ColumnConfig): number {
    return this.multiSortMeta.findIndex((sort) => sort.field === column.field);
  }

  hasColumnSortOrder(column: ColumnConfig, order: SortOrder): boolean {
    const sort = this.getColumnSort(column);

    return sort ? sort.order === order : false;
  }

  getSortAscBtnClass(column: ColumnConfig): IPlainObject {
    return this.getSortBtnClass(column, SortOrder.Asc);
  }

  getSortDescBtnClass(column: ColumnConfig): IPlainObject {
    return this.getSortBtnClass(column, SortOrder.Desc);
  }

  getSortBtnClass(column: ColumnConfig, order: SortOrder): ISortButtonClass {
    const isActiveSortDesc = this.hasColumnSortOrder(column, order);

    return { 'btn-success': isActiveSortDesc, 'btn-secondary': !isActiveSortDesc };
  }

  clearAllSorting(): void {
    this.multiSortMeta = [];
  }

  applySorting(): void {
    this.sort.emit(this.multiSortMeta);
  }

  getCurrentSortingSummary(): string {
    const ordersTitles = {
      [SortOrder.Asc]: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid.cards.sort-by.sort.ascending'
      ),
      [SortOrder.Desc]: this.translateService.instant(
        'com.bmc.arsys.rx.client.view-components.record-grid.cards.sort-by.sort.descending'
      )
    };

    return this.multiSortMeta
      .map((sortMeta, index): string => {
        const column = this.columns.find((c) => c.field === sortMeta.field);

        return `${index + 1}. ${column.header} (${ordersTitles[sortMeta.order]})`;
      })
      .join('\n');
  }
}
