import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IViewComponentDesignSandbox, IViewDesignerInspectorConfig } from '@helix/platform/view/designer';
import { ISelectFormControlOptions, SelectFormControlComponent } from '@helix/platform/shared/components';
import { BaseRecordEditorFieldDesign } from '../../base-record-editor-field/design/base-record-editor-field-design.class';
import { BooleanFieldEditingMode } from '../boolean-field.types';
import { findIndex, map as _map } from 'lodash';
import { Injector } from '@angular/core';
import { IBooleanFieldProperties } from './boolean-field-design.types';
import { Tooltip } from '@helix/platform/shared/api';

export class BooleanFieldDesignModel extends BaseRecordEditorFieldDesign {
  componentProperties$: Observable<IBooleanFieldProperties> = this.sandbox.componentProperties$;
  fieldResourceTypes = [RX_RECORD_DEFINITION.resourceTypes.boolean];

  static getInitialProperties(initialProperties?: IBooleanFieldProperties): IBooleanFieldProperties {
    return {
      ...BaseRecordEditorFieldDesign.getInitialProperties(),
      editingMode: BooleanFieldEditingMode.Switch,
      ...initialProperties
    };
  }

  constructor(public injector: Injector, public sandbox: IViewComponentDesignSandbox<IBooleanFieldProperties>) {
    super(injector, sandbox);
  }

  getInspectorConfig(): Observable<IViewDesignerInspectorConfig> {
    return super
      .getInspectorConfig()
      .pipe(map((inspectorConfig: IViewDesignerInspectorConfig) => this.editInspectorConfig(inspectorConfig)));
  }

  private editInspectorConfig(inspectorConfig: IViewDesignerInspectorConfig): IViewDesignerInspectorConfig {
    const valueControlIndex = findIndex(
      inspectorConfig.inspectorSectionConfigs[0].controls,
      (item) => item.name === 'value'
    );

    const editingModeControl = {
      name: 'editingMode',
      component: SelectFormControlComponent,
      options: {
        label: 'Editing mode',
        tooltip: new Tooltip('An optional Boolean field will always be displayed as a Switch.'),
        required: false,
        options: _map(BooleanFieldEditingMode, (value, name) => ({
          id: value,
          name
        }))
      } as ISelectFormControlOptions
    };

    if (valueControlIndex !== -1) {
      inspectorConfig.inspectorSectionConfigs[0].controls.splice(valueControlIndex, 0, editingModeControl);
    }

    return inspectorConfig;
  }
}
