  // Check if user clicked on a cell, filter popover,
  // filter by selection dialog, or filter by selection dropdown.
  // If not, then clear the selected cell.
  @HostListener('document:click', ['$event'])
  private onClickOutside(event: MouseEvent): void {
    const isClickOutside = !(
      (event.target as HTMLElement).closest('.ui-table-tbody') ||
      (event.target as HTMLElement).closest('.popover.table-filtering') ||
      (event.target as HTMLElement).closest('.rx-filter-by-card-selection-dialog') ||
      (event.target as HTMLElement).closest('.rx-filter-by-card-selection-field-dropdown')
    );

    if (isClickOutside) {
      this.clearSelectedCells();
    }
  }

