import { Component, Injector, OnInit } from '@angular/core';
import { cloneDeep, isObject, noop, pick, sortBy } from 'lodash';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { RxModalClass, RxModalService } from '@helix/platform/ui-kit';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { RecordGridViewPresetColumnsModalComponent } from '../record-grid-view-preset-columns-modal/record-grid-view-preset-columns-modal.component';
import {
  IRecordGridEditViewPresetsModalData,
  IRecordGridEditViewPresetsModalOptions,
  IRecordGridEditViewPresetsModalResult,
  IRecordGridEditViewPresetsModalTags
} from './record-grid-edit-view-presets-modal.types';
import { IRecordGridViewPresetColumnData } from '../record-grid-view-presets-widget.types';
import {
  IRecordGridViewPresetColumnsModalOptions,
  IRecordGridViewPresetColumnsModalResult
} from '../record-grid-view-preset-columns-modal/record-grid-view-preset-columns-modal.types';
import { IRecordGridSortEditorModel } from '../../record-grid-sort-editor-control';
import { IRecordGridDesignInspectorColumnConfig } from '../../record-grid-column-editor-control/record-grid-column-editor.types';
import { RX_RECORD_GRID } from '../../../../record-grid.constant';
import { RxRecordGridDesignUtilsService } from '../../../record-grid-design-utils.service';
import {
  IRecordGridNamedFilterOptionsMap,
  IRecordGridSortableObject
} from '../../../../common/types/record-grid.types';
import { RxRecordGridFilterSelectHelperService } from '../../record-grid-filter-select-control/record-grid-filter-select-helper.service';
import { forkJoin, of } from 'rxjs';
import { defaultIfEmpty, map, take } from 'rxjs/operators';
import { RxRecordGridUtilsService } from '../../../../common/services/record-grid-utils.service';
import { RxStringService } from '@helix/platform/utils';

@Component({
  selector: 'rx-record-grid-edit-view-presets-modal',
  templateUrl: './record-grid-edit-view-presets-modal.component.html',
  styleUrls: ['./record-grid-edit-view-presets-modal.component.scss']
})
export class RecordGridEditViewPresetsModalComponent extends RxModalClass implements OnInit {
  params: IRecordGridEditViewPresetsModalOptions = this.activeModalRef.getData();
  presets: IRecordGridEditViewPresetsModalData[] = [];
  isReadOnly = this.params.isReadOnly;
  private namedFilterOptions: IRecordGridNamedFilterOptionsMap;

  private gridColumns$ =
    this.params.gridColumns && this.params.recordDefinition
      ? forkJoin(
          this.params.gridColumns.map((gridColumn) => {
            if (this.rxStringService.isEmptySafe(gridColumn.title)) {
              return this.rxRecordGridUtilsService
                .getFieldDefinition(gridColumn.fieldId, this.params.recordDefinition)
                .pipe(map((fieldDefinition) => ({ ...gridColumn, fallbackTitle: `[${fieldDefinition.name}]` })));
            }

            return of({ ...gridColumn, fallbackTitle: `[${gridColumn.title}]` });
          })
        ).pipe(defaultIfEmpty([]))
      : of([]);

  constructor(
    private activeModalRef: ActiveModalRef,
    protected injector: Injector,
    private rxModalService: RxModalService,
    private rxRecordGridDesignUtilsService: RxRecordGridDesignUtilsService,
    private rxRecordGridFilterSelectHelperService: RxRecordGridFilterSelectHelperService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxStringService: RxStringService
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.gridColumns$.pipe(take(1)).subscribe((gridColumns) => {
      this.namedFilterOptions =
        this.rxRecordGridFilterSelectHelperService.getNamedFilterOptionsFromColumns(gridColumns);

      this.presets = this.params.presetsList.map(({ guid, name }, index) => {
        const presetComponent = this.params.viewPresets.find(({ data }) => data.viewPresetGuid === guid);
        const gridColumnsIds = gridColumns.filter((col) => col.filterable).map((col) => col.fieldId);
        const viewPresetGuid = presetComponent.data.viewPresetGuid;

        const presetColumns: IRecordGridViewPresetColumnData[] = cloneDeep(
          presetComponent.children.filter((col) => col.type === RX_RECORD_GRID.components.columnViewPreset)
        );

        const sortedColumn = presetColumns.find((col) => isObject(col.data.sortable));

        const sortModel: IRecordGridSortEditorModel = sortedColumn
          ? {
              fieldId: sortedColumn.data.fieldId,
              direction: (sortedColumn.data.sortable as IRecordGridSortableObject).direction
            }
          : null;

        return {
          presetName: name,
          gridColumns,
          presetColumns,
          guid: viewPresetGuid,
          tags: this.getTags(presetColumns, gridColumns),
          isOpen: this.params.activePreset ? this.params.activePreset === viewPresetGuid : index === 0,
          sortModel,
          filters: {
            basicFilters: this.rxRecordGridDesignUtilsService.getBasicRecordGridFiltersFromChildData(
              presetComponent.children
            ),
            filtersJson: presetComponent.data.filters
          },
          filterSelectControlOptions: {
            primaryRecordDefinition: {
              ...this.params.recordDefinition,
              fieldDefinitions: this.params.recordDefinition.fieldDefinitions.filter((definition) =>
                gridColumnsIds.includes(String(definition.id))
              )
            },
            selectedFieldIds: gridColumns.map((column) => column.fieldId),
            namedFilterOptions: this.namedFilterOptions
          },
          sortEditorControlOptions: {
            label: 'Initial column sort',
            gridColumns: sortBy(gridColumns, 'title'),
            recordDefinition: this.params.recordDefinition
          }
        };
      });
    });
  }

  saveChanges(): void {
    this.activeModalRef.close({
      presets: this.presets
    } as IRecordGridEditViewPresetsModalResult);
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  expandAll(event: MouseEvent): void {
    event.stopPropagation();

    this.presets.forEach((preset) => (preset.isOpen = true));
  }

  collapseAll(event: MouseEvent): void {
    event.stopPropagation();

    this.presets.forEach((preset) => (preset.isOpen = false));
  }

  openPresetColumnsModal(preset: IRecordGridEditViewPresetsModalData): void {
    this.rxModalService
      .openModal({
        title: `Edit grid columns for ${preset.presetName}`,
        content: RecordGridViewPresetColumnsModalComponent,
        size: OpenViewActionModalSize.Large as any,
        data: {
          gridColumns: preset.gridColumns,
          presetColumns: preset.presetColumns,
          isReadOnly: this.isReadOnly
        } as IRecordGridViewPresetColumnsModalOptions,
        testID: 'edit-grid-columns-for-view-preset'
      })
      .then((result: IRecordGridViewPresetColumnsModalResult) => {
        if (result?.presetColumns) {
          preset.presetColumns = result.presetColumns;
          preset.tags = this.getTags(preset.presetColumns, preset.gridColumns);
          this.markAsDirty();
        }
      })
      .catch(noop);
  }

  trackByPreset(index: number, item: IRecordGridEditViewPresetsModalData): string {
    return item.guid;
  }

  private getTags(
    presetColumns: IRecordGridViewPresetColumnData[],
    gridColumns: IRecordGridDesignInspectorColumnConfig[]
  ): IRecordGridEditViewPresetsModalTags {
    const allColumnTitles = presetColumns
      .filter((column) => column.data.visible)
      .map((column) => {
        const gridColumn = gridColumns.find((col) => col.fieldId === column.data.fieldId);
        return gridColumn.title || gridColumn.fallbackTitle;
      });

    const displayedColumnTitles = allColumnTitles.slice(0, 5);

    return {
      items: displayedColumnTitles,
      restCount: allColumnTitles.length - displayedColumnTitles.length
    };
  }

  onSortChange(sortModel: IRecordGridSortEditorModel, preset: IRecordGridEditViewPresetsModalData): void {
    preset.presetColumns.forEach((column) => {
      column.data.sortable = column.data.fieldId === sortModel?.fieldId ? pick(sortModel, 'direction') : null;
    });

    this.markAsDirty();
  }
}
