import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '@helix/platform/shared/components';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { cloneDeep, noop, without } from 'lodash';
import { IColumnEditorColumnData } from '../../record-grid-column-editor.types';
import { RxModalService } from '@helix/platform/ui-kit';
import { RecordGridNamedFilterOptionsEditorModalComponent } from './named-filter-options-editor/named-filter-options-editor-modal.component';
import { INamedFilterOptionsEditorConfig, INamedFilterOptionDesignData } from './named-filter-options.types';

@Component({
  selector: 'rx-named-filter-options',
  templateUrl: './named-filter-options.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridNamedFilterOptionsComponent,
      multi: true
    }
  ]
})
export class RecordGridNamedFilterOptionsComponent extends ValueAccessor<INamedFilterOptionDesignData[]> {
  @Input()
  column: IColumnEditorColumnData;

  @Input()
  tooltip: string;

  constructor(private rxModalService: RxModalService) {
    super();
  }

  openNamedFilterOptions(activeIndex?: number): void {
    this.rxModalService
      .openModal({
        title: `Edit named filter options for ${this.column.title}`,
        data: {
          namedFilterOptions: this.value ? cloneDeep(this.value) : [],
          isReadOnly: this.isDisabled,
          activeIndex,
          column: this.column
        } as INamedFilterOptionsEditorConfig,
        content: RecordGridNamedFilterOptionsEditorModalComponent,
        size: OpenViewActionModalSize.Large as any,
        testID: 'edit-named-filter-options'
      })
      .then((namedFilterOptions: INamedFilterOptionDesignData[]) => {
        this.value = namedFilterOptions;
      })
      .catch(noop);
  }

  editNamedFilterOption(activeIndex: number): void {
    this.openNamedFilterOptions(activeIndex);
  }

  removeFilterOption(namedFilterOption: INamedFilterOptionDesignData): void {
    this.value = without(this.value, namedFilterOption);
  }
}
