import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { cloneDeep, noop } from 'lodash';
import { ValueAccessor } from '@helix/platform/shared/components';
import { IFormControlComponent } from '@helix/platform/shared/api';
import {
  IRecordGridFilterPresetEditorControlOptions,
  IRecordGridFilterPresetEditorModalParams,
  IRecordGridFilterPresetEditorModalResult
} from './record-grid-filter-preset-editor.types';
import { IRecordGridPredefinedFilterPreset } from '../../../runtime/types/record-grid-predefined-filter-preset.interface';
import { RecordGridFilterPresetEditorModalComponent } from './record-grid-filter-preset-editor-modal/record-grid-filter-preset-editor-modal.component';
import { RxModalService } from '@helix/platform/ui-kit';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { IRecordGridNamedFilterOptionsMap } from '../../../common/types/record-grid.types';
import { RxRecordGridFilterSelectHelperService } from '../record-grid-filter-select-control/record-grid-filter-select-helper.service';

@Component({
  selector: 'rx-record-grid-filter-preset-editor',
  templateUrl: './record-grid-filter-preset-editor-control.component.html',
  styleUrls: ['./record-grid-filter-preset-editor-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridFilterPresetEditorControlComponent,
      multi: true
    }
  ]
})
export class RecordGridFilterPresetEditorControlComponent
  extends ValueAccessor<IRecordGridPredefinedFilterPreset[]>
  implements IFormControlComponent
{
  @Input()
  options: IRecordGridFilterPresetEditorControlOptions;

  filterPresets: IRecordGridPredefinedFilterPreset[] = [];

  constructor(
    private rxModalService: RxModalService,
    private rxRecordGridFilterSelectHelperService: RxRecordGridFilterSelectHelperService
  ) {
    super();
  }

  onWriteValue(filterPresets: IRecordGridPredefinedFilterPreset[]): void {
    this.filterPresets = filterPresets.slice();

    this.filterPresets.sort((filterPreset1, filterPreset2) => filterPreset1.title.localeCompare(filterPreset2.title));
  }

  removeFilterPreset(filterPresetToRemove: IRecordGridPredefinedFilterPreset): void {
    this.updateValue(this.filterPresets.filter((filterPreset) => filterPreset.guid !== filterPresetToRemove.guid));
  }

  updateValue(filterPresets: IRecordGridPredefinedFilterPreset[]): void {
    this.value = filterPresets;
    this.onWriteValue(filterPresets);
  }

  openEditor(filterPreset?: IRecordGridPredefinedFilterPreset): void {
    const selectedFieldIds = this.options.columns.map((column) => column.fieldId);

    const selectedFieldDefinitions = this.options.primaryRecordDefinition.fieldDefinitions.filter((fieldDefinition) =>
      selectedFieldIds.includes(fieldDefinition.id.toString())
    );

    const namedFilterOptions = this.rxRecordGridFilterSelectHelperService.getNamedFilterOptionsFromColumns(
      this.options.columns
    );

    const data: IRecordGridFilterPresetEditorModalParams = {
      filterPresets: this.filterPresets,
      activeFilterPreset: filterPreset,
      isReadOnly: this.isDisabled,
      namedFilterOptions,
      primaryRecordDefinition: cloneDeep({
        ...this.options.primaryRecordDefinition,
        fieldDefinitions: selectedFieldDefinitions
      }),
      selectedFieldIds
    };

    this.rxModalService
      .openModal({
        title: 'Edit filter presets',
        content: RecordGridFilterPresetEditorModalComponent,
        size: OpenViewActionModalSize.Large as any,
        testID: 'edit-filter-presets',
        data
      })
      .then((result: IRecordGridFilterPresetEditorModalResult) => {
        if (result && result.filterPresets) {
          this.updateValue(result.filterPresets);
        }
      })
      .catch(noop);
  }

  trackByForFilterPresets(index: number, filterPreset: IRecordGridPredefinedFilterPreset): string {
    return filterPreset.guid;
  }
}
