import { Component, Input } from '@angular/core';
import { Observable } from 'rxjs';
import { BaseViewComponent } from '@helix/platform/view/runtime';
import { SelectGroupFieldComponent } from './select-group-field.component';
import { ISelectGroupConfig } from './select-group.types';

@Component({
  selector: 'rx-select-group',
  templateUrl: './select-group.component.html'
})
export class SelectGroupComponent extends BaseViewComponent {
  @Input()
  config: Observable<ISelectGroupConfig>;

  private fieldComponents: Map<number, SelectGroupFieldComponent> = new Map();

  registerFieldComponent(fieldIndex: number, component: SelectGroupFieldComponent): void {
    this.fieldComponents.set(fieldIndex, component);
  }

  getValueForPreviousField(currentFieldIndex: number): any {
    const component = this.fieldComponents.get(currentFieldIndex - 1);

    return component ? component.getFieldValue() : null;
  }

  resetValueForNextFields(currentFieldIndex: number): void {
    const nextFieldIndex = currentFieldIndex + 1;
    const component = this.fieldComponents.get(nextFieldIndex);

    if (component) {
      component.resetFieldValue();

      this.resetValueForNextFields(nextFieldIndex);
    }
  }
}
