import { Component, ElementRef, Input, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '@helix/platform/shared/components';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import {
  IAssociatedRecordField,
  IAssociationRecordFieldSelectorFormControlOptions
} from './association-record-field-selector-form-control.interface';
import { noop, sortBy } from 'lodash';
import { AssociationRecordFieldSelectorEditorDialogComponent } from './association-record-field-selector-editor-dialog/association-record-field-selector-editor-dialog.component';
import { IAssociationRecordFieldSelectorEditorDialogConfig } from './association-record-field-selector-editor-dialog/association-record-field-selector-editor-dialog.interface';
import { RxModalService } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-association-record-field-selector-form-control',
  templateUrl: './association-record-field-selector-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: AssociationRecordFieldSelectorFormControlComponent,
      multi: true
    }
  ]
})
export class AssociationRecordFieldSelectorFormControlComponent
  extends ValueAccessor<IAssociatedRecordField[]>
  implements IFormControlComponent, IFormFocusable
{
  @Input()
  options: IAssociationRecordFieldSelectorFormControlOptions;

  @ViewChild('openModalButton', { read: ElementRef, static: true })
  openModalButton: ElementRef;

  selectedFields: Array<IAssociatedRecordField & { label: string }> = [];

  constructor(private rxModalService: RxModalService, private renderer: Renderer2) {
    super();
  }

  focus(): void {
    this.renderer.selectRootElement(this.openModalButton.nativeElement, true).click();
  }

  openFieldSelector(fieldToEdit?: IAssociatedRecordField): void {
    this.rxModalService
      .openModal({
        title: this.options.label,
        data: {
          fields: this.options.fields,
          selectedFields: this.value,
          fieldToEdit: fieldToEdit,
          isReadOnly: this.isDisabled
        } as IAssociationRecordFieldSelectorEditorDialogConfig,
        content: AssociationRecordFieldSelectorEditorDialogComponent
      })
      .then((fields) => {
        this.value = fields;
      })
      .catch(noop);
  }

  onSetValue(): void {
    this.updateSortedFieldList();
  }

  onWriteValue(value: IAssociatedRecordField[]): void {
    this.updateSortedFieldList();
  }

  removeField(guid: string): void {
    this.value = this.value.filter((field) => field.guid !== guid);
  }

  editField(field: IAssociatedRecordField): void {
    this.openFieldSelector(field);
  }

  private updateSortedFieldList(): void {
    this.selectedFields = sortBy(this.value, (field) => Number(field.data.index)).map((field) => ({
      ...field,
      label: this.options.fields.find((fieldDefinition) => fieldDefinition.id === Number(field.data.fieldId)).name
    }));
  }
}
