import { IPlainObject } from '@helix/platform/shared/api';
import { TextFormControlComponent } from '@helix/platform/shared/components';
import { IViewDesignerComponentModel } from '@helix/platform/view/api';
import { IViewDesignerInspectorConfig, ViewDesignerComponentModel } from '@helix/platform/view/designer';
import { keys } from 'lodash';
import { take } from 'rxjs/operators';

export class UnknownDesignModel extends ViewDesignerComponentModel implements IViewDesignerComponentModel {
  label = `Unknown component: ${this.sandbox.componentType}`;

  rxInit(): void {
    this.sandbox.componentProperties$.pipe(take(1)).subscribe((componentProperties) => {
      this.sandbox.updateInspectorConfig(this.getInspector(componentProperties));
      this.sandbox.setValidationIssues([this.sandbox.createError(this.label)]);
    });
  }

  private getInspector(componentProperties: IPlainObject): IViewDesignerInspectorConfig {
    const controls = keys(componentProperties).map((propertyName) => ({
      name: propertyName,
      component: TextFormControlComponent,
      options: {
        label: propertyName
      },
      isDisabled: true
    }));

    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls
        }
      ]
    };
  }
}
