import { AfterViewInit, Component, Injector, ViewChild } from '@angular/core';
import { isNil, isNull } from 'lodash';
import { IViewComponent } from '@helix/platform/view/runtime';
import { RxBooleanPipe } from '@helix/platform/shared/api';
import { BaseRecordEditorFieldComponent } from '../../base-record-editor-field/runtime/base-record-editor-field-component.class';
import { BooleanFieldEditingMode, BooleanFieldModelValue, IBooleanFieldConfig } from '../boolean-field.types';
import { NgModel } from '@angular/forms';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'rx-boolean-field',
  templateUrl: './boolean-field.component.html'
})
export class BooleanFieldComponent extends BaseRecordEditorFieldComponent implements IViewComponent, AfterViewInit {
  editingMode: BooleanFieldEditingMode;

  get booleanValue(): boolean | null {
    const value = this.getFieldValue();

    return isNull(value) ? null : Boolean(value);
  }

  set booleanValue(value: boolean | null) {
    if (isNull(value)) {
      this.setFieldValue(null);
    } else {
      this.setFieldValue(Number(value));
    }
  }

  @ViewChild('booleanComponent', { read: NgModel })
  ngModel: NgModel;

  constructor(injector: Injector, private rxBooleanPipe: RxBooleanPipe) {
    super(injector);
  }

  getDisplayValue(): string {
    const displayValue = super.getDisplayValue();

    if (!isNil(displayValue)) {
      return this.rxBooleanPipe.transform(displayValue);
    } else {
      return '-';
    }
  }

  onConfigInitialized(config: IBooleanFieldConfig): void {
    super.onConfigInitialized(config);

    this.editingMode = config.editingMode;
  }

  onConfigUpdated(config: IBooleanFieldConfig): void {
    super.onConfigUpdated(config);

    // Set 0 as a default value for checkbox, e.g. scenario in the create record editor mode
    if (this.shouldDisplayAsCheckbox && this.getFieldValue() === null) {
      this.setFieldValue(BooleanFieldModelValue.False, { markAsDirty: false, markAsTouched: false });
    }
  }

  ngAfterViewInit(): void {
    this.formControl.touched$
      .pipe(takeUntil(this.destroyed$))
      .subscribe((touched) =>
        touched ? this.ngModel?.control.markAsTouched() : this.ngModel?.control.markAsUntouched()
      );
  }

  get shouldDisplayAsCheckbox(): boolean {
    return this.editingMode === BooleanFieldEditingMode.Checkbox && this.isRequired;
  }

  onBlur(): void {
    this.formControl.markAsTouched();
  }
}
