import { Component, ElementRef, OnInit, Renderer2 } from '@angular/core';
import { BaseViewComponent, IViewComponentSetProperty } from '@helix/platform/view/runtime';
import { takeUntil } from 'rxjs/operators';
import { IContainerApi, IContainerConfig } from './container.types';
import { Observable, throwError } from 'rxjs';

@Component({
  selector: 'rx-container',
  templateUrl: './container.component.html',
  styleUrls: ['./container.component.scss']
})
export class ContainerComponent extends BaseViewComponent implements OnInit, IViewComponentSetProperty {
  state: IContainerConfig = {};

  api: IContainerApi = {
    setProperty: this.setProperty.bind(this)
  };

  constructor(private elementRef: ElementRef, private renderer: Renderer2) {
    super();
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.notifyPropertyChanged('api', this.api);

    this.config.pipe(takeUntil(this.destroyed$)).subscribe((config: IContainerConfig) => {
      this.isHidden = Boolean(config.hidden);
      this.state = { ...config };
    });
  }

  setProperty(propertyPath: string, propertyValue: any): void | Observable<never> {
    if (propertyPath === 'hidden') {
      this.state.hidden = propertyValue;
      this.notifyPropertyChanged(propertyPath, propertyValue);
    } else {
      const componentName = this.renderer.selectRootElement(this.elementRef.nativeElement, true).tagName.toLowerCase();

      return throwError(`${componentName}: property ${propertyPath} is not settable.`);
    }
  }
}
