import { Component, Injector, Input, OnChanges, OnDestroy, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '@helix/platform/shared/components';
import { IFormControlComponent, RxDefinitionNameService, RxGlobalCacheService } from '@helix/platform/shared/api';
import {
  IRecordGridFilterModel,
  IRecordGridFilterSelectAdvancedFiltering,
  IRecordGridFilterSelectControlOptions
} from './record-grid-filter-select-control.types';
import { RxModalService } from '@helix/platform/ui-kit';
import { cloneDeep, every, find, findIndex, isEmpty, isEqual, isFunction, reduce, uniq, values } from 'lodash';
import { AdvancedFilterOption, AdvancedFilterTag, SelectedAdvancedFilter } from '@bmc-ux/adapt-angular';
import { combineLatest, forkJoin, Observable, of, Subject, Subscription } from 'rxjs';
import {
  IRecordDefinition,
  RxRecordDefinitionCacheService,
  RxRecordDefinitionService
} from '@helix/platform/record/api';
import { RxRecordGridUtilsService } from '../../../common/services/record-grid-utils.service';
import { RxRecordGridFilterConfigService } from '../../../runtime/services/record-grid-filter-config.service';
import { IAssociationDescriptor } from '@helix/platform/association/api';
import { map, switchMap, take, tap } from 'rxjs/operators';
import { RxRecordGridFilterService } from '../../../runtime/services/record-grid-filter.service';
import { IRxRecordGridFilter } from '../../../common/types/record-grid-filter.types';
import { IRecordGridFilterOperator } from '@helix/platform/view/api';
import { RxGuidService } from '@helix/platform/utils';
import { RxRecordGridAdvancedFilteringService } from '../../../common/services/record-grid-advanced-filtering.service';
import { IRemoveFilterTagEvent } from '../../../common/components/filter-tags/filter-tags.types';
import { RxAdvancedFilteringFieldsProviderComponent } from '../../../common/components/advanced-filtering-fields-provider/advanced-filtering-fields-provider.component';
import { IRecordGridColumnNamedFilterOption } from '../../../common/types/record-grid.types';
import { RxRecordGridFilterHelperService } from '../../../common/services/record-grid-filter-helper.service';
import { ITagInfo } from '../../../common/types/record-grid-filter-config.interfaces';

@Component({
  selector: 'rx-record-grid-filter-select-control',
  templateUrl: './record-grid-filter-select-control.component.html',
  styleUrls: ['./record-grid-filter-select-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridFilterSelectControlComponent,
      multi: true
    }
  ]
})
export class RecordGridFilterSelectControlComponent
  extends ValueAccessor<IRecordGridFilterModel>
  implements IFormControlComponent, OnInit, OnChanges, OnDestroy
{
  @Input()
  options: IRecordGridFilterSelectControlOptions;
  groupFilterValue: string;
  groupFilterItems: { name: string; value: string }[];

  @ViewChild(RxAdvancedFilteringFieldsProviderComponent, { static: true })
  fieldsProvider: RxAdvancedFilteringFieldsProviderComponent;

  advancedFilterData: IRecordGridFilterSelectAdvancedFiltering = {
    filterOptions: [],
    selectedFilters: [],
    filterTexts: {
      initialDropdownAnchorLabel: 'Filter'
    },
    recordGridFilterConfigs: null,
    fieldDefinitionsById: {},
    fieldDefinitionsInitialized$: new Subject(),
    filterValueInitialized$: new Subject(),
    associationDescriptors: []
  };

  private subscription = new Subscription();
  private filtersModel: IRecordGridFilterModel;

  constructor(
    protected injector: Injector,
    private rxModalService: RxModalService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService,
    private rxRecordGridFilterConfigService: RxRecordGridFilterConfigService,
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private rxRecordGridFilterService: RxRecordGridFilterService,
    private rxRecordGridFilterHelperService: RxRecordGridFilterHelperService,
    private rxRecordDefinitionService: RxRecordDefinitionService,
    private rxGuidService: RxGuidService,
    private rxRecordGridAdvancedFilteringService: RxRecordGridAdvancedFilteringService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxDefinitionNameService: RxDefinitionNameService
  ) {
    super();

    this.getNamedFilterOptions = this.getNamedFilterOptions.bind(this);
    this.initEmptyValue();

    this.advancedFilterData.recordGridFilterConfigs = this.rxRecordGridFilterConfigService.getConfigs();

    this.subscription.add(
      combineLatest([
        this.advancedFilterData.filterValueInitialized$,
        this.advancedFilterData.fieldDefinitionsInitialized$
      ]).subscribe(() => {
        this.initSelectedAdvancedFilters(this.value);
      })
    );
  }

  getGroupFilterName(model: string): string {
    return this.groupFilterItems.find((item) => item.value === model)?.name;
  }

  onGroupFilterValueModelChange(model: string): void {
    this.groupFilterValue = model;
  }

  ngOnInit(): void {
    this.initFilterOptions();
  }

  initEmptyValue(): void {
    this.innerValue = {
      basicFilters: null,
      filtersJson: null
    };
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.options &&
      changes.options.previousValue &&
      (changes.options.currentValue as IRecordGridFilterSelectControlOptions).primaryRecordDefinition?.name !==
        (changes.options.previousValue as IRecordGridFilterSelectControlOptions).primaryRecordDefinition?.name
    ) {
      this.initEmptyValue();
      this.advancedFilterData.selectedFilters = [];
      this.advancedFilterData.filterOptions = [];
      this.initFilterOptions();
    }
  }

  initFilterOptions(): void {
    this.groupFilterItems = [];

    if (this.options.primaryRecordDefinition) {
      this.rxRecordGridUtilsService
        .getAssociationDescriptors(this.options.primaryRecordDefinition.name)
        .pipe(
          switchMap((associationDescriptors) => {
            this.advancedFilterData.associationDescriptors = associationDescriptors;

            const recordDefinitionNames = uniq(
              associationDescriptors.map((associationDescriptor) => associationDescriptor.recordDefinitionName)
            );

            const recordDefinitions$ = recordDefinitionNames.length
              ? forkJoin(
                  recordDefinitionNames.map((name) => this.rxRecordDefinitionCacheService.getRecordDefinition(name))
                )
              : of([] as IRecordDefinition[]);

            return recordDefinitions$.pipe(map((recordDefinitions) => ({ recordDefinitions, associationDescriptors })));
          }),
          take(1)
        )
        .subscribe(({ recordDefinitions, associationDescriptors }) => {
          const primaryRecordDefinition = cloneDeep(this.options.primaryRecordDefinition);

          [primaryRecordDefinition, ...recordDefinitions].forEach((recordDefinition) => {
            if (!recordDefinition.fieldDefinitionsById) {
              recordDefinition.fieldDefinitionsById =
                this.rxRecordDefinitionService.buildFieldDefinitionsByIdMap(recordDefinition);
            }
          });

          this.groupFilterValue = primaryRecordDefinition.name;

          this.advancedFilterData.fieldDefinitionsById =
            this.rxRecordGridAdvancedFilteringService.addAssociationFieldsToFieldDefinitionsById(
              primaryRecordDefinition.fieldDefinitionsById,
              associationDescriptors,
              recordDefinitions
            );

          associationDescriptors.forEach((associationDescriptor) => {
            const currentRecordDefinition = find(
              recordDefinitions,
              (recordDefinition) => recordDefinition.name === associationDescriptor.recordDefinitionName
            );

            this.groupFilterItems.push({
              name: associationDescriptor.label,
              value: associationDescriptor.label
            });

            this.advancedFilterData.filterOptions.push(
              ...this.getFilterOptions(currentRecordDefinition, associationDescriptor, associationDescriptor.label)
            );
          });

          this.groupFilterItems = this.groupFilterItems.sort((gropFilter, gropFilterToCompare) =>
            gropFilter.name.localeCompare(gropFilterToCompare.name)
          );

          const mainRecordDefinitionName = this.rxDefinitionNameService.getDisplayName(primaryRecordDefinition.name);

          this.advancedFilterData.filterOptions.push(...this.getFilterOptions(primaryRecordDefinition, null, null));

          this.groupFilterItems.unshift({
            name: mainRecordDefinitionName,
            value: primaryRecordDefinition.name
          });

          this.advancedFilterData.fieldDefinitionsInitialized$.next();
        });
    }
  }

  onRemoveFilterTag(event: IRemoveFilterTagEvent): void {
    const selectedFilters = this.advancedFilterData.selectedFilters.slice();
    const filterOptionId = event.removedTag.data.filterOption.id;
    const index = findIndex(selectedFilters, (selectedFilter) => selectedFilter.filterOptionId === filterOptionId);

    selectedFilters.splice(index, 1);
    this.onAdvancedFiltersChange(selectedFilters);
  }

  getFilterOptions(
    recordDefinition: IRecordDefinition,
    associationDescriptor: IAssociationDescriptor,
    prefix?: string
  ): AdvancedFilterOption[] {
    const fieldDefinitions = recordDefinition.fieldDefinitions;

    return fieldDefinitions
      .map((fieldDefinition): AdvancedFilterOption => {
        const filterConfig = this.advancedFilterData.recordGridFilterConfigs[fieldDefinition.resourceType];
        const label = this.rxRecordGridUtilsService.getColumnLabel(fieldDefinition, null);

        const fieldId = this.rxRecordGridUtilsService.getFieldIdForGridColumn(
          fieldDefinition.id,
          associationDescriptor
        );

        const filterOption = this.rxRecordGridAdvancedFilteringService.getAdvancedFilterOption(
          fieldId,
          label,
          fieldDefinition,
          filterConfig,
          false,
          {
            optionalBooleanFilter: this.fieldsProvider.optionalBooleanFilter,
            requiredBooleanFilter: this.fieldsProvider.requiredBooleanFilter,
            inputsForStringWithTypeAheadTemplate: this.fieldsProvider.inputsForStringWithTypeAheadTemplate,
            inputsForStringTypeTemplate: this.fieldsProvider.inputsForStringTypeTemplate,
            inputsForNumberTypeTemplate: this.fieldsProvider.inputsForNumberTypeTemplate,
            inputsForTimeTypeTemplate: this.fieldsProvider.inputsForTimeTypeTemplate,
            inputsForDateTypeTemplate: this.fieldsProvider.inputsForDateTypeTemplate,
            inputsForDatetimeTypeTemplate: this.fieldsProvider.inputsForDatetimeTypeTemplate,
            inputsForSelectionTypeTemplate: this.fieldsProvider.inputsForSelectionTypeTemplate
          },
          prefix ? `${prefix} > ${label}` : null,
          this.options.namedFilterOptions ? this.options.namedFilterOptions[fieldId] : []
        );

        const recordDefinitionName = associationDescriptor
          ? associationDescriptor.label
          : this.options.primaryRecordDefinition.name;

        filterOption.isHidden = () =>
          this.groupFilterValue !== recordDefinitionName || this.options.selectedFieldIds?.includes(fieldId) === false;

        return filterOption;
      })
      .sort((filterOption1, filterOption2) => filterOption1.label.localeCompare(filterOption2.label));
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onAdvancedFiltersChange(selectedFilters: SelectedAdvancedFilter[]): void {
    this.advancedFilterData.selectedFilters = selectedFilters;

    const oldBasicFilters = this.value.basicFilters || [];

    const basicFilters = reduce(
      selectedFilters,
      (recordGridFilters, selectedFilter) => {
        const fieldId = selectedFilter.filterOptionId;
        const fieldDefinition = this.advancedFilterData.fieldDefinitionsById[fieldId];
        const filterConfig = this.advancedFilterData.recordGridFilterConfigs[fieldDefinition.resourceType];

        const columnRecordGridFilterData = filterConfig.getRecordGridFilterData(
          selectedFilter.value,
          fieldDefinition,
          fieldId
        );

        (columnRecordGridFilterData.or || columnRecordGridFilterData.and).forEach(
          (recordGridFilterOperator: IRecordGridFilterOperator) => {
            const gridFilterValue = values(recordGridFilterOperator)[0][fieldId];
            const gridFilterDisplayValue = values(recordGridFilterOperator)[0]['$DISPLAYVALUE$'];

            const columnOldBasicFilter = find(
              oldBasicFilters,
              (oldBasicFilter) =>
                oldBasicFilter.fieldId === fieldId &&
                oldBasicFilter.value === gridFilterValue &&
                every(recordGridFilters, (gridFilter) => gridFilter.guid !== oldBasicFilter.guid)
            );

            const recordGridFilter: IRxRecordGridFilter = {
              fieldId,
              value: gridFilterValue,
              $DISPLAYVALUE$: gridFilterDisplayValue,
              guid: (columnOldBasicFilter && columnOldBasicFilter.guid) || this.rxGuidService.generate()
            };

            recordGridFilters.push(recordGridFilter);
          }
        );

        return recordGridFilters;
      },
      [] as IRxRecordGridFilter[]
    );

    const recordGridFilterData = this.rxRecordGridFilterService.getRecordGridFilterDataFromAdvancedFilter(
      selectedFilters,
      this.advancedFilterData.fieldDefinitionsById,
      this.advancedFilterData.recordGridFilterConfigs
    );

    let filtersJson = isEmpty(recordGridFilterData) ? null : JSON.stringify(recordGridFilterData);

    if (filtersJson) {
      filtersJson = this.rxRecordGridFilterHelperService.denormalizeFilterString(filtersJson, basicFilters);
    }

    this.filtersModel = {
      basicFilters,
      filtersJson
    };

    this.value = {
      basicFilters,
      filtersJson
    };
  }

  onWriteValue(value: IRecordGridFilterModel): void {
    if (!isEqual(this.filtersModel, value)) {
      this.advancedFilterData.filterValueInitialized$.next();
    }
  }

  initSelectedAdvancedFilters(value: IRecordGridFilterModel) {
    if (value?.filtersJson && !isEmpty(value.basicFilters)) {
      this.rxRecordGridFilterService
        .getSelectedFiltersFromPredefinedFilter(
          value.filtersJson,
          value.basicFilters,
          this.advancedFilterData.fieldDefinitionsById,
          this.advancedFilterData.recordGridFilterConfigs,
          this.options.primaryRecordDefinition,
          this.advancedFilterData.filterOptions,
          this.advancedFilterData.associationDescriptors
        )
        .pipe(
          map((selectedFilters) =>
            this.options.namedFilterOptions
              ? this.rxRecordGridFilterService.clearRemovedNamedFilterOptions(
                  selectedFilters,
                  this.options.namedFilterOptions
                )
              : selectedFilters
          )
        )
        .subscribe((selectedFilters) => {
          this.advancedFilterData.selectedFilters = selectedFilters;
        });
    }
  }

  getNamedFilterOptions(filterOptionConfig: AdvancedFilterOption): IRecordGridColumnNamedFilterOption[] {
    return this.options.namedFilterOptions ? this.options.namedFilterOptions[filterOptionConfig.id] : [];
  }

  getTags(filtersExpressionsTags: AdvancedFilterTag[]): Observable<AdvancedFilterTag[]> {
    const tags =
      filtersExpressionsTags?.filter((filterTag: AdvancedFilterTag) => isEmpty(filterTag.data.validationErrors)) ?? [];

    const tags$ = tags.map((tag: AdvancedFilterTag): Observable<AdvancedFilterTag> => {
      if (tag.data.filterOption) {
        const fieldId = tag.data.filterOption.id;
        const fieldDefinition = tag.data.filterOption.data;
        const filterConfig = this.advancedFilterData.recordGridFilterConfigs[fieldDefinition?.resourceType];

        if (isFunction(filterConfig?.getToolbarTagInfo)) {
          return filterConfig
            .getToolbarTagInfo({
              primaryRecordDefinition: this.options.primaryRecordDefinition,
              fieldDefinition,
              value: tag.data.value,
              tag,
              fieldId,
              namedFilterOptions: this.options.namedFilterOptions?.[fieldId] ?? []
            })
            .pipe(
              tap((tagInfo: ITagInfo) => (tag.data.value.title = tagInfo.tooltip)),
              map((tagInfo: ITagInfo): AdvancedFilterTag => ({ ...tag, text: tagInfo.text }))
            );
        }
      }

      return of({ ...tag });
    });

    return tags$.length ? forkJoin(tags$) : of([]);
  }
}
