import { Component, Injector, OnInit } from '@angular/core';
import { ActiveModalRef, DismissReasons, TreeWrap } from '@bmc-ux/adapt-angular';
import { IFieldDefinition } from '@helix/platform/record/api';
import { RxGuidService, RxStringService } from '@helix/platform/utils';
import { includes, map, pull, sortBy, transform } from 'lodash';
import {
  IAssociatedRecordFieldDialog,
  IAssociationRecordFieldSelectorEditorDialogConfig,
  IFieldLabelsMap,
  IViewAssociationRecordFieldSelectorEditorDialogItem
} from './association-record-field-selector-editor-dialog.interface';
import { RxModalClass } from '@helix/platform/ui-kit';
import { TreeNode } from 'primeng/api/treenode';
import { CdkDragDrop } from '@angular/cdk/drag-drop/drag-events';

@Component({
  selector: 'rx-association-record-field-selector-editor-dialog',
  templateUrl: './association-record-field-selector-editor-dialog.component.html',
  styleUrls: ['./association-record-field-selector-editor-dialog.component.scss']
})
export class AssociationRecordFieldSelectorEditorDialogComponent extends RxModalClass implements OnInit {
  config: IAssociationRecordFieldSelectorEditorDialogConfig;
  fieldLabelsMap: IFieldLabelsMap;
  filteredFieldDescriptors: IFieldDefinition[];
  searchValue: string;

  availableFieldTreeNodes: TreeNode<IFieldDefinition>[] = [];
  canSave = false;
  selectedFields: IViewAssociationRecordFieldSelectorEditorDialogItem[] = [];
  treeWrap: TreeWrap = TreeWrap.WrapAll;

  constructor(
    private rxStringService: RxStringService,
    private rxGuidService: RxGuidService,
    public activeModalRef: ActiveModalRef,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);

    this.config = this.activeModalRef.getData();
  }

  isDirty(): boolean {
    return this.canSave;
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.fieldLabelsMap = transform(
      this.config.fields,
      (result, field) => {
        result[field.id] = field.name;
      },
      {}
    );

    this.availableFieldTreeNodes = this.config.fields
      .map((field) => ({
        data: field,
        label: field.name
      }))
      .sort((field1, field2) => field1.label.localeCompare(field2.label));

    this.selectedFields = map(this.config.selectedFields, (field: IAssociatedRecordFieldDialog) => {
      const descriptor = this.config.fields.find((fieldItem) => fieldItem.id === Number(field.data.fieldId));

      return {
        ...field,
        labelText: field.data.label,
        indexOrder: Number(field.data.index),
        descriptor,
        isOpen: field === this.config.fieldToEdit
      };
    });

    this.refreshFieldDescriptorsList();
  }

  addField(field: IFieldDefinition): void {
    this.canSave = true;

    this.selectedFields.push({
      guid: this.rxGuidService.generate(),
      data: {
        fieldId: String(field.id),
        label: field.name,
        index: String(this.selectedFields.length)
      },
      descriptor: field,
      labelText: field.name,
      indexOrder: this.selectedFields.length,
      isOpen: true
    });

    this.refreshFieldDescriptorsList();
  }

  onSelectedFieldsListDrop(event: CdkDragDrop<any>): void {
    this.addField(event.item.data);
  }

  onSave(): void {
    const result: IAssociatedRecordFieldDialog[] = this.selectedFields.map((field) => {
      return {
        guid: field.guid,
        type: 'rx-associated-record-field',
        data: {
          fieldId: field.data.fieldId,
          index: String(field.indexOrder),
          label: field.labelText
        }
      };
    });

    this.activeModalRef.close(result);
  }

  onRemove(field: IViewAssociationRecordFieldSelectorEditorDialogItem): void {
    this.canSave = true;
    pull(this.selectedFields, field);
    this.refreshFieldDescriptorsList();
  }

  onMoveUp(field: IViewAssociationRecordFieldSelectorEditorDialogItem, index: number): void {
    this.canSave = true;
    const prevField = this.selectedFields[index - 1];

    if (prevField) {
      prevField.indexOrder++;
      field.indexOrder--;
      this.sortFields();
    }
  }

  onMoveDown(field: IViewAssociationRecordFieldSelectorEditorDialogItem, index: number): void {
    this.canSave = true;
    const nextAction = this.selectedFields[index + 1];

    if (nextAction) {
      nextAction.indexOrder--;
      field.indexOrder++;
      this.sortFields();
    }
  }

  toggleOpen(expandAll: boolean): void {
    this.selectedFields.forEach((field) => (field.isOpen = expandAll));
  }

  private sortFields(): void {
    this.selectedFields = sortBy(
      this.selectedFields,
      (field: IViewAssociationRecordFieldSelectorEditorDialogItem) => field.indexOrder
    );
  }

  refreshFieldDescriptorsList(): void {
    const selectedFieldIds = this.selectedFields.map((field) => String(field.data.fieldId));

    this.filteredFieldDescriptors = this.config.fields
      .filter((fieldDescriptor) => !includes(selectedFieldIds, String(fieldDescriptor.id)))
      .sort((field1, field2) => field1.name.localeCompare(field2.name));
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
