import {
  getStandardPropsInspectorConfigs,
  IViewComponentDesignSandbox,
  IViewDesignerInspectorConfig,
  validateAvailableOnDevicesProp,
  validateCssClassNames,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { ITextFormControlOptions, TextFormControlComponent } from '@helix/platform/shared/components';
import { Injector } from '@angular/core';
import { first, map, take, takeUntil } from 'rxjs/operators';
import { IExtensionContainerProperties } from './extension-container-design.types';
import { combineLatest } from 'rxjs';
import { Tooltip } from '@helix/platform/shared/api';
import {
  RX_AVAILABLE_ON_DEVICES_PROP_NAME,
  RX_STANDARD_PROPS_DEFAULT_VALUES,
  RxViewComponentType
} from '@helix/platform/view/api';
import { compact, flatten, isEmpty } from 'lodash';

export class ExtensionContainerDesignModel extends ViewDesignerComponentModel {
  componentProperties$ = this.sandbox.componentProperties$;
  parentGuid$ = this.sandbox.getParentComponentGuid(RxViewComponentType.RecordEditor).pipe(take(1));

  initialProperties: IExtensionContainerProperties = {
    name: '',
    recordDefinition: '',
    recordInstance: '',
    ...RX_STANDARD_PROPS_DEFAULT_VALUES
  };

  constructor(
    protected injector: Injector,
    protected sandbox: IViewComponentDesignSandbox<IExtensionContainerProperties>
  ) {
    super(injector, sandbox);

    combineLatest([this.parentGuid$, this.componentProperties$])
      .pipe(first())
      .subscribe(([parentGuid, componentProperties]) => {
        const properties = {
          ...this.initialProperties,
          ...componentProperties,
          recordInstance: `\${view.components.${parentGuid}.recordInstance}`,
          recordDefinition: `\${view.components.${parentGuid}.recordDefinition}`
        };

        this.sandbox.updateComponentProperties(properties);
        this.sandbox.updateInspectorConfig(this.getInspectorConfig());
      });

    combineLatest([
      this.sandbox.getComponentPropertyValue('name').pipe(
        map((value) => {
          if (isEmpty(value)) {
            return this.sandbox.createError('Name cannot be blank.', 'name');
          }
        })
      ),
      this.sandbox.getComponentPropertyValue('styles').pipe(map(validateCssClassNames)),
      this.sandbox
        .getComponentPropertyValue(RX_AVAILABLE_ON_DEVICES_PROP_NAME)
        .pipe(map(validateAvailableOnDevicesProp))
    ])
      .pipe(map(flatten), map(compact), takeUntil(this.sandbox.destroyed$))
      .subscribe((validationIssues) => this.sandbox.setValidationIssues(validationIssues));
  }

  private getInspectorConfig(): IViewDesignerInspectorConfig {
    return {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls: [
            {
              name: 'name',
              component: TextFormControlComponent,
              options: {
                label: 'Name',
                required: true,
                tooltip: new Tooltip('Enter a name to uniquely identify the Extension container')
              } as ITextFormControlOptions
            },
            ...getStandardPropsInspectorConfigs()
          ]
        }
      ]
    };
  }
}
