import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { RxJsonParserService } from '@helix/platform/utils';
import {
  IContainerViewComponentDefinition,
  IViewDefinition,
  IViewLayout,
  RxViewDefinitionParserService,
  RxViewLayout
} from '@helix/platform/view/api';
import { find, forEach, pull } from 'lodash';
import { RX_RECORD_GRID } from '../record-grid.constant';

@Injectable({
  providedIn: 'root'
})
export class RxRecordGridDesignAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition>
{
  constructor(
    private rxJsonParserService: RxJsonParserService,
    private rxViewDefinitionParserService: RxViewDefinitionParserService
  ) {}

  adaptDefinition(elementDefinition: IContainerViewComponentDefinition): void {
    const layout: IViewLayout = this.rxJsonParserService.tryParseJson(elementDefinition.layout);
    const rowActionsOutlet = find(layout.outlets, { name: RX_RECORD_GRID.rowActionsOutletName });

    elementDefinition.propertiesByName.enableFilterPresets =
      elementDefinition.propertiesByName.enableFilterPresets ?? 'true';

    if (!rowActionsOutlet) {
      layout.outlets.push(RxViewLayout.getOutlet(RX_RECORD_GRID.rowActionsOutletName));
    } else {
      // move row actions to the end to make sure they are in the same order as in layout outlet
      forEach(rowActionsOutlet.columns[0]?.children, (rowActionGuid) => {
        const rowAction = find(elementDefinition.componentDefinitions, { guid: rowActionGuid });

        pull(elementDefinition.componentDefinitions, rowAction).push(rowAction);
      });
    }

    elementDefinition.layout = JSON.stringify(layout);

    Object.assign(elementDefinition.propertiesByName, {
      expandable: elementDefinition.propertiesByName.expandable ?? false,
      showDataForAllLocales: elementDefinition.propertiesByName.showDataForAllLocales ?? false,
      requiredFilters:
        elementDefinition.propertiesByName.requiredFilters ??
        (elementDefinition.propertiesByName.requireFiltering === 'true' ? 1 : 0)
    });

    delete elementDefinition.propertiesByName.requireFiltering;

    this.rxViewDefinitionParserService
      .getComponents(elementDefinition)
      .map((componentDefinition) => componentDefinition.componentDefinition as IContainerViewComponentDefinition)
      .forEach((componentDefinition) => {
        if (componentDefinition.type === RX_RECORD_GRID.components.column) {
          Object.assign(componentDefinition.propertiesByName, {
            wrapText: componentDefinition.propertiesByName.wrapText ?? false,
            typeaheadKeystrokeCount: Number(
              componentDefinition.propertiesByName.typeaheadKeystrokeCount ??
                RX_RECORD_GRID.defaultTypeaheadKeystrokeCount
            )
          });
        }
      });
  }
}
