import { IDataDictionary, IExpressionConfigurator, RxExpressionConfigurator } from '@helix/platform/shared/api';
import { Observable, of } from 'rxjs';
import { IColumnEditorColumnData } from '../../record-grid-column-editor.types';

export class RecordGridCellDisplayPropertiesExpressionConfigurator
  extends RxExpressionConfigurator
  implements IExpressionConfigurator {
  commonDataDictionary$: Observable<IDataDictionary>;

  cellDisplayExpressionDataDictionary(columns: IColumnEditorColumnData[]): Observable<IDataDictionary> {
    if (columns.length) {
      return of(
        columns.map((column: IColumnEditorColumnData) => ({
          label: column.label,
          icon: 'd-icon-field_text',
          expression: `\${view.components.grid.clickableRow.${column.fieldId}}`,
          resourceType: column.fieldDefinition.resourceType
        }))
      );
    } else {
      return of([]);
    }
  }
}
