import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AdaptFilterControlsComponent, AdvancedFilterOption } from '@bmc-ux/adapt-angular';
import { reduce } from 'lodash';
import { IRecordGridColumnNamedFilterOption } from '../../types/record-grid.types';
import { RxRecordGridAdvancedFilterValue } from '../../types/record-grid-advanced-filter-value.class';

@Component({
  selector: 'rx-named-filter-options-list',
  templateUrl: './named-filter-options-list.component.html',
  styleUrls: ['./named-filter-options-list.component.scss']
})
export class RxNamedFilterOptionsListComponent {
  @Input()
  namedFilterOptions: IRecordGridColumnNamedFilterOption[];

  @Input()
  filterOption: AdvancedFilterOption;

  @Input()
  set filterValue(values: RxRecordGridAdvancedFilterValue) {
    const ids = values?.namedOptions;

    this._filterValue = values;
    Object.keys(this.namedFilterOptionsModel).forEach((key) => (this.namedFilterOptionsModel[key] = false));
    ids?.forEach((id) => (this.namedFilterOptionsModel[id] = true));
  }

  get filterValue(): RxRecordGridAdvancedFilterValue {
    return this._filterValue;
  }

  @Input()
  filterControlsComponent: AdaptFilterControlsComponent;

  @Input()
  defaultFilterValue: any = [];

  @Output()
  readonly namedFilterOptionsChange: EventEmitter<string[]> = new EventEmitter<string[]>();

  private _filterValue: RxRecordGridAdvancedFilterValue;
  namedFilterOptionsModel: { [id: string]: boolean } = {};

  trackByOptionGuid(index: number, option: IRecordGridColumnNamedFilterOption): string {
    return option['guid'];
  }

  onNamedFilterOptionChange(): void {
    const selectedGuids = this.getSelectedNamedOptionsIds();
    this.namedFilterOptionsChange.emit(selectedGuids);

    const value = new RxRecordGridAdvancedFilterValue(
      this.filterValue?.filterValue ?? this.defaultFilterValue,
      selectedGuids
    );

    this.filterControlsComponent.onFilterOptionModelChange(value);
  }

  private getSelectedNamedOptionsIds(): string[] {
    return reduce(
      this.namedFilterOptionsModel,
      (result, value, id) => {
        if (value) {
          result.push(id);
        }

        return result;
      },
      []
    );
  }
}
