import { Injector } from '@angular/core';
import {
  IAssociationDefinition,
  RX_ASSOCIATED_RECORD_NODE_SIDES,
  RX_ASSOCIATION_DEFINITION,
  RxAssociationDefinitionCacheService
} from '@helix/platform/association/api';
import { IFieldDefinition } from '@helix/platform/record/api';
import {
  ISelectFormControlOptions,
  IValidationFormControlOptions,
  RxDefinitionPickerComponent,
  RxDefinitionPickerType,
  SelectFormControlComponent,
  ValidationFormControlComponent
} from '@helix/platform/shared/components';
import { RxGuidService } from '@helix/platform/utils';
import { RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE, RxViewComponentType } from '@helix/platform/view/api';
import {
  IViewComponentDesignData,
  IViewComponentDesignSandbox,
  IViewDesignerInspectorConfig,
  ViewComponentAddComponentPayload,
  ViewDesignerComponentModel
} from '@helix/platform/view/designer';
import { defaults, get, isEqual, isNil, isUndefined, map as _map, omit } from 'lodash';
import { combineLatest, merge, Observable, of, Subject } from 'rxjs';
import {
  concatMap,
  concatMapTo,
  distinctUntilChanged,
  filter,
  map,
  mapTo,
  mergeMap,
  pairwise,
  shareReplay,
  skip,
  switchMap,
  take,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { IRecordEditorProperties } from '../../../record-editor/design/record-editor-design.types';
import { RX_ASSOCIATION, RxAssociationEditingMode } from '../association.types';
import { AssociationDesignHelperService } from './association-design-helper.service';
import {
  IAssociationChildComponents,
  IAssociationDesignProperties,
  IAssociationProperties
} from './association-design.types';
import {
  IAssociatedRecordField,
  IAssociationChild,
  IAssociationFilter
} from './association-record-field-selector-field';
import { RxDefinitionNameService } from '@helix/platform/shared/api';
import { RecordEditorDesignModel } from '../../../record-editor/design/record-editor-design.model';

export class AssociationDesignModel extends ViewDesignerComponentModel {
  private rxAssociationDefinitionCacheService = this.injector.get<RxAssociationDefinitionCacheService>(
    RxAssociationDefinitionCacheService
  );

  private rxGuidService = this.injector.get<RxGuidService>(RxGuidService);
  private rxDefinitionNameService = this.injector.get<RxDefinitionNameService>(RxDefinitionNameService);

  private associationDesignHelperService =
    this.injector.get<AssociationDesignHelperService>(AssociationDesignHelperService);

  private updateInspector$: Subject<IViewDesignerInspectorConfig> = new Subject();
  private recordEditorGuid: string;
  private recordDefinitionNameFromHostRecordEditor$: Observable<string>;
  private childComponents$ = this.sandbox.getChildComponentsTree();
  private associationChildComponentsData$ = this.getChildComponentsData();

  private associationDefinitions: IAssociationDefinition[] = [];
  private associationDefinitionFilters: IAssociationDefinition[] = [];
  private recordDefinitionFields: IFieldDefinition[] = [];
  private associatedRecordNodeSide: IAssociationDefinition[] = [];
  private previousDropdownFieldDefinition: IFieldDefinition;
  private isManyToManyAssociation = false;

  readonly otherAssociationRecordEditorComponentGuids$: Observable<string[]>;
  readonly recordEditorGuid$: Observable<string>;
  readonly editingMode$ = this.sandbox.getComponentPropertyValue('editingMode');
  readonly label$ = this.sandbox.getComponentPropertyValue('label');

  readonly displayedFields$: Observable<IAssociatedRecordField[]>;

  static getInitialProperties(initialProperties: IAssociationDesignProperties): IAssociationDesignProperties {
    return {
      ...RX_AVAILABLE_ON_DEVICES_DEFAULT_VALUE,
      ...initialProperties
    };
  }

  constructor(protected injector: Injector, public sandbox: IViewComponentDesignSandbox<IAssociationDesignProperties>) {
    super(injector, sandbox);

    // TODO: performance
    this.updateInspector$
      .pipe(distinctUntilChanged(isEqual), takeUntil(this.sandbox.destroyed$))
      .subscribe((inspector) => {
        this.sandbox.updateInspectorConfig(inspector);
      });

    const recordDefinitionName$ = sandbox.getComponentPropertyValue('recordDefinitionName');

    const recordDefinitionFields$ = recordDefinitionName$.pipe(
      switchMap((recordDefinitionName) =>
        recordDefinitionName ? this.updateRecordDefinitionFields(recordDefinitionName) : of([])
      )
    );

    this.displayedFields$ = combineLatest([
      this.sandbox.getChildComponents(
        (component) => component.type === RX_ASSOCIATION.componentTypes.associatedRecordField
      ),
      recordDefinitionFields$
    ]).pipe(
      map(([components, availableFields]: [IAssociatedRecordField[], IFieldDefinition[]]) =>
        this.associationDesignHelperService.getFieldsInChildComponentsWithNames(components, availableFields)
      )
    );

    this.recordEditorGuid$ = this.sandbox.getParentComponentGuid(RxViewComponentType.RecordEditor).pipe(take(1));

    this.otherAssociationRecordEditorComponentGuids$ = this.recordEditorGuid$.pipe(
      map((guid) => this.sandbox.getComponentModel<RecordEditorDesignModel>(guid)),
      switchMap((model) =>
        model.associationComponentGuids$.pipe(map((guids) => guids.filter((guid) => guid !== this.sandbox.guid)))
      ),
      shareReplay(1),
      takeUntil(this.sandbox.destroyed$)
    );

    this.recordDefinitionNameFromHostRecordEditor$ = this.recordEditorGuid$.pipe(
      switchMap((recordEditorGuid) => {
        return this.sandbox.getComponentPropertyValue<'recordDefinitionName', IRecordEditorProperties>(
          'recordDefinitionName',
          recordEditorGuid
        );
      }),
      shareReplay(1)
    );

    this.recordDefinitionNameFromHostRecordEditor$
      .pipe(
        switchMap((recordDefinitionRecordEditorName) =>
          recordDefinitionRecordEditorName
            ? combineLatest([this.sandbox.componentProperties$, this.associationChildComponentsData$]).pipe(
                switchMap(([componentProperties, associationChildComponentsData]) => {
                  const updateParameters = recordDefinitionRecordEditorName
                    ? this.generateInitialUpdateParameters(componentProperties)
                    : [];

                  const updateParameters$ = updateParameters.length > 0 ? combineLatest(updateParameters) : of(null);

                  return updateParameters$.pipe(
                    map(() => ({
                      componentProperties: { ...associationChildComponentsData, ...componentProperties },
                      recordDefinitionRecordEditorName
                    }))
                  );
                }),
                take(1)
              )
            : of({
                componentProperties: null,
                recordDefinitionRecordEditorName: null
              })
        ),
        withLatestFrom(this.recordEditorGuid$),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(([{ componentProperties, recordDefinitionRecordEditorName }, recordEditorGuid]) => {
        const initialProperties = this.associationDesignHelperService.getInitialProperties();

        const properties: IAssociationDesignProperties = {
          ...initialProperties,
          ...componentProperties,
          useDefaultRoles: Boolean(componentProperties?.useDefaultRoles === 'true')
        };

        this.recordEditorGuid = recordEditorGuid;
        this.sandbox.updateComponentProperties(properties);

        this.updateInspector$.next(
          this.getInspectorConfig(properties, recordDefinitionRecordEditorName, recordEditorGuid)
        );
      });

    const childComponentGuids$ = this.childComponents$.pipe(
      map((components) => components.map((component) => component.guid))
    );

    this.sandbox
      .getComponentPropertyValue('recordDefinitionName')
      .pipe(skip(1), withLatestFrom(childComponentGuids$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([recordDefinitionName, childComponentGuids]) => {
        this.sandbox.updateComponentProperties({
          recordDefinitionName,
          label: null,
          associationDefinitionName: null,
          associatedRecordNodeSide: null,
          editingMode: RxAssociationEditingMode.Views,
          viewDefinitionNameForSelect: null,
          viewDefinitionNameForCreate: null,
          fieldId: null,
          useDefaultRoles: false,
          nodeARole: null,
          nodeBRole: null,
          displayedFields: [],
          filterByAssociation: null,
          recordId: null
        });

        this.sandbox.removeComponents(childComponentGuids);
      });

    this.sandbox
      .getComponentPropertyValue('recordDefinitionName')
      .pipe(
        skip(1),
        concatMapTo(this.sandbox.componentProperties$),
        concatMap((props) =>
          combineLatest([
            this.updateAssociationDefinitionsOptions(props.recordDefinitionName),
            this.updateRecordDefinitionFields(props.recordDefinitionName),
            this.updateAssociationDefinitionsFilterOptions(props.recordDefinitionName),
            this.updateAssociationNodeSideOptions(props.associationDefinitionName),
            this.updatePreviousDropdownFieldDefinition(props.recordDefinitionName, props.fieldId)
          ]).pipe(take(1), mapTo(props))
        ),
        withLatestFrom(this.recordDefinitionNameFromHostRecordEditor$, this.recordEditorGuid$),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(([componentProps, recordDefinitionNameFromHostRecordEditor, recordEditorGuid]) => {
        this.updateInspector$.next(
          this.getInspectorConfig(componentProps, recordDefinitionNameFromHostRecordEditor, recordEditorGuid)
        );
      });

    this.sandbox
      .getComponentPropertyValue('editingMode')
      .pipe(skip(1), withLatestFrom(childComponentGuids$), takeUntil(this.sandbox.destroyed$))
      .subscribe(([editingMode, childComponentGuids]) => {
        this.sandbox.updateComponentProperties({
          editingMode,
          viewDefinitionNameForSelect: null,
          viewDefinitionNameForCreate: null,
          fieldId: null,
          useDefaultRoles: false,
          nodeARole: null,
          nodeBRole: null,
          displayedFields: [],
          filterByAssociation: null,
          recordId: null
        });

        this.sandbox.removeComponents(childComponentGuids);
      });

    this.sandbox.componentProperties$
      .pipe(
        skip(1),
        pairwise(),
        withLatestFrom(this.childComponents$),
        switchMap(
          ([[previousProperties, properties], children]: [
            [IAssociationDesignProperties, IAssociationDesignProperties],
            IAssociationChild[]
          ]) => {
            const updatedProperties = { ...properties };
            const updatedParametersStreams = [];

            switch (true) {
              case previousProperties.viewDefinitionNameForSelect !== properties.viewDefinitionNameForSelect:
                this.updateChildActionButtonDefinition(
                  properties,
                  children as IAssociatedRecordField[],
                  RX_ASSOCIATION.actions.associate
                );

                break;

              case previousProperties.viewDefinitionNameForCreate !== properties.viewDefinitionNameForCreate:
                this.updateChildActionButtonDefinition(
                  properties,
                  children as IAssociatedRecordField[],
                  RX_ASSOCIATION.actions.createAndAssociate
                );

                break;

              case previousProperties.filterByAssociation !== properties.filterByAssociation:
              case previousProperties.recordId !== properties.recordId:
                const filterComponent = children.find(
                  (component) => component.type === RX_ASSOCIATION.componentTypes.associationFilter
                ) as IAssociationFilter;

                this.updateAssociationFilterComponent(properties, filterComponent);

                break;

              case !isEqual(previousProperties.displayedFields, properties.displayedFields):
                this.updateComponentRecordFields(properties.displayedFields, children as IAssociatedRecordField[]);

                break;

              case previousProperties.associationDefinitionName !== properties.associationDefinitionName:
                updatedProperties.editingMode = RxAssociationEditingMode.Views;
                this.associationDesignHelperService.cleanEditingModeDependentData(updatedProperties);

                if (properties.associationDefinitionName) {
                  updatedParametersStreams.push(
                    this.rxAssociationDefinitionCacheService
                      .getAssociationDefinition(properties.associationDefinitionName)
                      .pipe(
                        map((associationDefinition) => {
                          updatedProperties.associatedRecordNodeSide =
                            associationDefinition.nodeAId === properties.recordDefinitionName
                              ? RX_ASSOCIATED_RECORD_NODE_SIDES.nodeA.value
                              : RX_ASSOCIATED_RECORD_NODE_SIDES.nodeB.value;

                          this.setIsManyToManyAssociation(associationDefinition);
                          this.sandbox.updateComponentProperties(updatedProperties);

                          if (this.isManyToManyAssociation) {
                            this.associationDesignHelperService.resetRolesData(updatedProperties);
                          }
                        })
                      )
                  );
                } else {
                  this.isManyToManyAssociation = false;
                }

                updatedParametersStreams.push(
                  this.updateAssociationNodeSideOptions(properties.associationDefinitionName)
                );

                break;

              case previousProperties.associatedRecordNodeSide !== properties.associatedRecordNodeSide:
                updatedProperties.editingMode = RxAssociationEditingMode.Views;
                this.associationDesignHelperService.cleanEditingModeDependentData(updatedProperties);

                break;

              case previousProperties.useDefaultRoles !== properties.useDefaultRoles:
                if (properties.useDefaultRoles === true) {
                  updatedProperties.nodeARole = null;
                  updatedProperties.nodeBRole = null;
                }

                break;

              case previousProperties.nodeARole !== properties.nodeARole ||
                previousProperties.nodeBRole !== properties.nodeBRole:
                if (isUndefined(properties.useDefaultRoles)) {
                  defaults(updatedProperties, {
                    nodeARole: null,
                    nodeBRole: null,
                    useDefaultRoles: false
                  });
                } else {
                  updatedProperties.useDefaultRoles = false;
                }

                break;
            }

            const updateParameters$ =
              updatedParametersStreams.length > 0 ? combineLatest(updatedParametersStreams) : of(null);

            return updateParameters$.pipe(
              map((parameters) => ({ parameters, componentProperties: updatedProperties })),
              withLatestFrom(this.recordDefinitionNameFromHostRecordEditor$, this.recordEditorGuid$)
            );
          }
        ),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe(
        ([{ parameters, componentProperties }, recordDefinitionNameFromHostRecordEditor, recordEditorGuid]) => {
          const initialProperties = this.associationDesignHelperService.getInitialProperties();

          this.recordEditorGuid = recordEditorGuid;

          this.updateInspector$.next(
            this.getInspectorConfig(
              { ...initialProperties, ...componentProperties },
              recordDefinitionNameFromHostRecordEditor,
              recordEditorGuid
            )
          );
        }
      );

    this.sandbox
      .getComponentPropertyValue('fieldId')
      .pipe(
        // transform initial value to null if it is undefined
        map((value) => (isNil(value) ? null : value)),
        distinctUntilChanged(),
        pairwise()
      )
      .pipe(
        withLatestFrom(this.sandbox.componentProperties$),
        // Check if editing mode is dropdown to prevent label reset after change fieldId to null.
        filter(([fieldIds, props]) => Boolean(props.editingMode === RxAssociationEditingMode.Dropdown)),
        switchMap(([[prevFieldId, fieldId], props]) =>
          this.associationDesignHelperService.getRecordDefinition(props.recordDefinitionName).pipe(
            map((recordDefinition) => {
              const prevFieldDefinitionName = recordDefinition.fieldDefinitions.find(
                (field) => field.id === Number(prevFieldId)
              )?.name;

              const currentFieldDefinitionName = recordDefinition.fieldDefinitions.find(
                (field) => field.id === Number(fieldId)
              )?.name;

              // If the label is changed by user it must not be updated after fieldId change.
              return !props.label || prevFieldDefinitionName === props.label ? currentFieldDefinitionName : props.label;
            })
          )
        ),
        distinctUntilChanged(),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe((label) => {
        this.sandbox.updateComponentProperties({ label });
      });

    combineLatest([
      this.sandbox.componentProperties$,
      this.associationChildComponentsData$.pipe(distinctUntilChanged(isEqual))
    ])
      .pipe(
        switchMap(([componentProperties, childComponents]) => {
          const association$: Observable<IAssociationDefinition> = componentProperties.associationDefinitionName
            ? this.rxAssociationDefinitionCacheService.getAssociationDefinition(
                componentProperties.associationDefinitionName
              )
            : of({});

          return association$.pipe(
            map((associationDefinition) => {
              return this.associationDesignHelperService.validate(
                this.sandbox,
                componentProperties,
                associationDefinition,
                childComponents
              );
            })
          );
        }),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe((validationIssues) => {
        this.sandbox.setValidationIssues(validationIssues);
      });

    combineLatest([
      this.label$,
      this.sandbox
        .getComponentPropertyValue('associationDefinitionName')
        .pipe(
          map((associationDefinitionName) =>
            associationDefinitionName ? this.rxDefinitionNameService.getDisplayName(associationDefinitionName) : null
          )
        )
    ])
      .pipe(
        map(([label, definitionDisplayName]) => {
          let componentName = this.sandbox.descriptor.name;

          if (definitionDisplayName && label) {
            componentName += ` (${definitionDisplayName}: ${label})`;
          } else if (definitionDisplayName || label) {
            componentName += ` (${definitionDisplayName || label})`;
          }

          return componentName;
        }),
        distinctUntilChanged(),
        takeUntil(this.sandbox.destroyed$)
      )
      .subscribe((componentName) => {
        this.sandbox.setSettablePropertiesDataDictionary(componentName, [
          {
            label: 'Disabled',
            expression: this.getExpressionForProperty('disabled')
          },
          {
            label: 'Hidden',
            expression: this.getExpressionForProperty('hidden')
          }
        ]);
      });
  }

  getPropertiesByName(properties: IAssociationDesignProperties): IAssociationProperties {
    return omit(
      {
        ...properties,
        recordInstance: this.recordEditorGuid
          ? `\${view.components.${this.recordEditorGuid}.recordInstance}`
          : properties.recordInstance,
        recordDefinition: this.recordEditorGuid
          ? `\${view.components.${this.recordEditorGuid}.recordDefinition}`
          : properties.recordDefinition
      },
      [
        'displayedFields',
        'filterByAssociation',
        'recordId',
        'viewDefinitionNameForSelect',
        'viewDefinitionNameForCreate'
      ]
    ) as IAssociationProperties;
  }

  private updateAssociationFilterComponent(
    componentProperties: IAssociationDesignProperties,
    filterComponent: IAssociationFilter
  ): void {
    if (filterComponent) {
      this.sandbox.removeComponents([filterComponent.guid]);
    }

    if (componentProperties.filterByAssociation) {
      const filterNew = {
        guid: this.rxGuidService.generate(),
        type: RX_ASSOCIATION.componentTypes.associationFilter,
        propertiesByName: {
          associationDefinitionName: componentProperties.filterByAssociation,
          recordInstanceId: componentProperties.recordId
        }
      };

      this.sandbox.addComponent([filterNew]);
    }
  }

  private updateChildActionButtonDefinition(
    properties: IAssociationDesignProperties,
    children: IAssociatedRecordField[],
    action: string
  ): void {
    const actionButtonComponent = children.find(
      (component) => component.type === RxViewComponentType.ActionButton && component.data['action'] === action
    );

    const viewDefinitionNameToUpdate =
      action === RX_ASSOCIATION.actions.createAndAssociate
        ? properties.viewDefinitionNameForCreate
        : properties.viewDefinitionNameForSelect;

    if (actionButtonComponent) {
      this.sandbox.removeComponents([actionButtonComponent.guid]);
    }

    if (viewDefinitionNameToUpdate) {
      const button = this.associationDesignHelperService.getActionButtonDefinition(
        properties,
        action,
        this.sandbox['guid']
      );

      this.sandbox.addComponent([button]);
    }
  }

  private updateComponentRecordFields(
    displayedFields: IAssociatedRecordField[],
    children: IAssociatedRecordField[]
  ): void {
    const guids = children
      .filter((component) => component.type === RX_ASSOCIATION.componentTypes.associatedRecordField)
      .map((component) => component.guid);

    const recordFields: ViewComponentAddComponentPayload[] = displayedFields.map((field) => ({
      type: RX_ASSOCIATION.componentTypes.associatedRecordField,
      guid: this.rxGuidService.generate(),
      propertiesByName: {
        fieldId: field.data.fieldId,
        label: field.data.label || '',
        index: field.data.index
      }
    }));

    if (guids.length > 0) {
      this.sandbox.removeComponents(guids);
    }

    this.sandbox.addComponent(recordFields);
  }

  private getChildComponentsData(): Observable<IAssociationChildComponents> {
    return this.childComponents$.pipe(
      map((components: IViewComponentDesignData[]) => {
        const associationChildComponentsData: IAssociationChildComponents = {};

        const associationFilters = components.filter(
          (component) => component.type === RX_ASSOCIATION.componentTypes.associationFilter
        );

        associationChildComponentsData.displayedFields = components.filter(
          (definition) => definition.type === RX_ASSOCIATION.componentTypes.associatedRecordField
        ) as IAssociatedRecordField[];

        if (associationFilters.length > 0) {
          associationChildComponentsData.recordId = associationFilters[0].data?.recordInstanceId;
          associationChildComponentsData.filterByAssociation = associationFilters[0].data?.associationDefinitionName;
        }

        const createAndAssociateView = this.getViewDefinitionNameForAssociation(
          components,
          RX_ASSOCIATION.actions.createAndAssociate
        );

        if (createAndAssociateView) {
          associationChildComponentsData.viewDefinitionNameForCreate = createAndAssociateView;
        }

        const associateView = this.getViewDefinitionNameForAssociation(components, RX_ASSOCIATION.actions.associate);

        if (associateView) {
          associationChildComponentsData.viewDefinitionNameForSelect = associateView;
        }

        return associationChildComponentsData;
      })
    );
  }

  private generateInitialUpdateParameters(
    componentProperties: IAssociationProperties
  ): Observable<IFieldDefinition[] | IFieldDefinition | IAssociationDefinition[]>[] {
    const updateParameters = [];

    if (componentProperties.recordDefinitionName) {
      updateParameters.push(this.updateRecordDefinitionFields(componentProperties.recordDefinitionName));

      if (componentProperties.fieldId) {
        updateParameters.push(
          this.updatePreviousDropdownFieldDefinition(
            componentProperties.recordDefinitionName,
            componentProperties.fieldId
          )
        );
      }

      updateParameters.push(this.updateAssociationDefinitionsOptions(componentProperties.recordDefinitionName));
    }

    if (componentProperties.associationDefinitionName) {
      updateParameters.push(this.updateAssociationDefinitionsFilterOptions(componentProperties.recordDefinitionName));

      updateParameters.push(
        this.rxAssociationDefinitionCacheService
          .getAssociationDefinition(componentProperties.associationDefinitionName)
          .pipe(map((associationDefinition) => this.setIsManyToManyAssociation(associationDefinition)))
      );
    }

    if (componentProperties.associatedRecordNodeSide) {
      updateParameters.push(this.updateAssociationNodeSideOptions(componentProperties.associationDefinitionName));
    }

    return updateParameters;
  }

  private updatePreviousDropdownFieldDefinition(
    recordDefinitionToAssociateName: string,
    fieldId: string
  ): Observable<IFieldDefinition> {
    return this.updateRecordDefinitionFields(recordDefinitionToAssociateName).pipe(
      map((fields) => {
        return (this.previousDropdownFieldDefinition = fields.find((field) => field.id === Number(fieldId)));
      })
    );
  }

  private setIsManyToManyAssociation(associationDefinition: IAssociationDefinition): void {
    if (associationDefinition) {
      this.isManyToManyAssociation =
        associationDefinition.cardinality === RX_ASSOCIATION_DEFINITION.cardinality.manyToMany.value;
    }
  }

  private getViewDefinitionNameForAssociation(components: IViewComponentDesignData[], action: string): string | null {
    const actionButton = components.find((definition) => definition.data?.action === action);

    return actionButton && actionButton.children ? this.getViewDefinitionNameFromActionButton(actionButton) : null;
  }

  private getViewDefinitionNameFromActionButton(actionButton: IViewComponentDesignData): string | null {
    const actionComponent = actionButton.children.find((children) => children.type === RxViewComponentType.Action);

    return get(actionComponent, 'data.viewDefinitionName', null);
  }

  private updateRecordDefinitionFields(recordDefinitionToAssociateName: string): Observable<IFieldDefinition[]> {
    return (
      recordDefinitionToAssociateName
        ? this.associationDesignHelperService
            .getRecordDefinition(recordDefinitionToAssociateName)
            .pipe(map((recordDefinition) => recordDefinition.fieldDefinitions))
        : of([])
    ).pipe(tap((fieldDefinitions) => (this.recordDefinitionFields = fieldDefinitions)));
  }

  private updateAssociationNodeSideOptions(associationDefinitionName: string): Observable<IAssociationDefinition[]> {
    const associatedRecordNodeSideOptions$ = associationDefinitionName
      ? this.rxAssociationDefinitionCacheService
          .getAssociationDefinition(associationDefinitionName)
          .pipe(
            map((associationDefinition) =>
              this.associationDesignHelperService.getNodeSideOptions(associationDefinition)
            )
          )
      : of([]);

    return associatedRecordNodeSideOptions$.pipe(
      tap((associatedRecordNodeSideOptions) => (this.associatedRecordNodeSide = associatedRecordNodeSideOptions))
    );
  }

  private updateAssociationDefinitionsOptions(
    recordDefinitionToAssociateName: string
  ): Observable<IAssociationDefinition[]> {
    return this.recordDefinitionNameFromHostRecordEditor$.pipe(
      switchMap((associatedRecordName) =>
        recordDefinitionToAssociateName && associatedRecordName
          ? this.associationDesignHelperService.getAvailableAssociations(
              recordDefinitionToAssociateName,
              associatedRecordName
            )
          : of([])
      ),
      tap((associationDefinitionOptions) => (this.associationDefinitions = associationDefinitionOptions))
    );
  }

  private updateAssociationDefinitionsFilterOptions(
    recordDefinitionToAssociateName: string
  ): Observable<IAssociationDefinition[]> {
    return this.otherAssociationRecordEditorComponentGuids$.pipe(
      mergeMap((guids) =>
        guids.length
          ? combineLatest(
              guids.map((guid) =>
                this.sandbox.getComponentPropertyValue<'recordDefinitionName', IAssociationDesignProperties>(
                  'recordDefinitionName',
                  guid
                )
              )
            ).pipe(take(1))
          : of([])
      ),
      switchMap((associatedRecordNames) =>
        associatedRecordNames.length && recordDefinitionToAssociateName
          ? merge(
              ...associatedRecordNames.map((associatedRecordName) =>
                this.associationDesignHelperService.getAvailableAssociations(
                  recordDefinitionToAssociateName,
                  associatedRecordName
                )
              )
            )
          : of([])
      ),
      map((associationDefinitionFilters) => (this.associationDefinitionFilters = associationDefinitionFilters))
    );
  }

  private getInspectorConfig(
    properties: IAssociationDesignProperties,
    recordDefinitionNameFromHostRecordEditor: string,
    recordEditorGuid: string
  ): IViewDesignerInspectorConfig {
    const inspectorConfig: IViewDesignerInspectorConfig = {
      inspectorSectionConfigs: [
        {
          label: 'General',
          controls: []
        }
      ]
    };

    if (recordDefinitionNameFromHostRecordEditor) {
      const generalSectionControls = inspectorConfig.inspectorSectionConfigs[0].controls;

      generalSectionControls.push({
        name: 'recordDefinitionName',
        component: RxDefinitionPickerComponent,
        options: {
          label: 'Record definition to associate',
          definitionType: RxDefinitionPickerType.StandardDataRecord,
          required: true
        }
      });

      if (properties.recordDefinitionName) {
        generalSectionControls.push({
          name: 'associationDefinitionName',
          component: SelectFormControlComponent,
          options: {
            label: 'Association to use',
            options: this.associationDefinitions.map((associationDefinition) => ({
              id: associationDefinition.name,
              name: this.rxDefinitionNameService.getDisplayName(associationDefinition.name)
            })),
            emptyOption: true,
            required: true
          } as ISelectFormControlOptions
        });
      }

      if (properties.recordDefinitionName && this.associatedRecordNodeSide.length > 0) {
        generalSectionControls.push({
          name: 'associatedRecordNodeSide',
          component: SelectFormControlComponent,
          options: {
            label: 'Associated record node Side',
            options: this.associatedRecordNodeSide,
            required: true
          } as ISelectFormControlOptions
        });
      }

      if (
        properties.recordDefinitionName &&
        this.associationDesignHelperService.isCardinalityOne(properties, this.associationDefinitions)
      ) {
        generalSectionControls.push({
          name: 'editingMode',
          component: SelectFormControlComponent,
          options: {
            label: 'Association editing mode',
            options: _map(RxAssociationEditingMode, (id, name) => ({ id, name })),
            required: true
          } as ISelectFormControlOptions
        });
      }

      // View Mode
      if (!properties.editingMode || properties.editingMode === RxAssociationEditingMode.Views) {
        generalSectionControls.push(...this.associationDesignHelperService.getViewModeInspectorControls());

        if (this.isManyToManyAssociation && properties.associationDefinitionName) {
          generalSectionControls.push(this.associationDesignHelperService.getUseDefaultRolesControl());

          if (!properties.useDefaultRoles) {
            generalSectionControls.push(...this.associationDesignHelperService.getNodeABRoleControls());
          }
        }

        if (properties.recordDefinitionName) {
          generalSectionControls.push(
            this.associationDesignHelperService.getDisplayedFieldsControl(this.recordDefinitionFields)
          );
        }
      }

      // Dropdown Mode
      if (properties.editingMode === RxAssociationEditingMode.Dropdown) {
        generalSectionControls.push(
          ...this.associationDesignHelperService.getDropdownModeInspectorControls(
            this.recordDefinitionFields,
            this.associationDefinitionFilters
          )
        );

        if (properties.filterByAssociation) {
          generalSectionControls.push(this.associationDesignHelperService.getRecordIdControl());
        }
      }

      generalSectionControls.push(
        ...this.associationDesignHelperService.getInitialInspectorControls(this.expressionConfigurator)
      );
    } else {
      inspectorConfig.inspectorSectionConfigs[0].controls = [
        {
          component: ValidationFormControlComponent,
          options: {
            text: 'You must select a Record definition before editing field component.',
            componentGuid: recordEditorGuid,
            propertyName: 'recordDefinitionName'
          } as IValidationFormControlOptions
        }
      ];
    }

    return inspectorConfig;
  }
}
