import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IViewDesignerInspectorConfig } from '@helix/platform/view/designer';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { BaseRecordEditorFieldDesign } from '../../base-record-editor-field/design/base-record-editor-field-design.class';
import { IBaseRecordEditorFieldProperties } from '../../base-record-editor-field/design/base-record-editor-field-design.types';

export class AttachmentFieldDesignModel extends BaseRecordEditorFieldDesign {
  fieldResourceTypes = [RX_RECORD_DEFINITION.resourceTypes.attachment];

  static getInitialProperties(initialProperties?: IBaseRecordEditorFieldProperties): IBaseRecordEditorFieldProperties {
    const result = BaseRecordEditorFieldDesign.getInitialProperties(initialProperties);

    delete result.value;

    return result;
  }

  getInspectorConfig(): Observable<IViewDesignerInspectorConfig> {
    return super.getInspectorConfig().pipe(
      map((inspectorConfig: IViewDesignerInspectorConfig) => {
        inspectorConfig.inspectorSectionConfigs[0].controls =
          inspectorConfig.inspectorSectionConfigs[0].controls.filter((field) => field.name !== 'value');

        return inspectorConfig;
      })
    );
  }
}
